import React, { useEffect, useState } from 'react';
import { Chart } from 'react-google-charts';
const MapChart = ({ states }) => {
  const [stateMap, setStateMap] = useState([['State', 'State FPO']]);
  const [stateMapObj, setStateMapObj] = useState([['State', 'State FPO']]);
  const [selectedState, setSelectedState] = useState(false);
  const [style, setStyle] = useState(false);
  useEffect(() => {
    if (!states) return;
    let tempMapArray = [...stateMap];
    let tempMapArrayObj = [...stateMap];
    states.map((state, index) => {
      tempMapArray.push([state.stateName, 60]);
      tempMapArrayObj.push(state);
    });
    setStateMap(tempMapArray);
    setStateMapObj(tempMapArrayObj);
    setStyle(true);
  }, [states]);

  const handleStateSelect = (state, index) => {
    let customeState = [];
    stateMap.map((state, key) => {
      let stateC = [...state];
      if (key !== 0) {
        stateC[1] = 50;
        setSelectedState(stateMapObj[index]);
      }
      if (index === key) {
        stateC[1] = 20;
      }
      customeState.push(stateC);
    });
    setStateMap(customeState);
  };

  return (
    <div className='row'>
      <div
        className='col-md-5 text-center ind-map-container'
        style={style ? { overflow: 'hidden' } : {}}
      >
        <Chart
          chartType='GeoChart'
          width='150%'
          data={stateMap}
          options={{
            region: 'IN',
            domain: 'IN',
            displayMode: 'regions',
            colorAxis: { colors: ['#28a5eb', '#c5eaff'] },
            resolution: 'provinces',
            // backgroundColor: '#81d4fa',
            datalessRegionColor: '#fff',
            // defaultColor: '#fff',
            legend: 'none',
            tooltip: {
              trigger: 'none',
            },
          }}
          chartEvents={[
            {
              eventName: 'select',
              callback: ({ chartWrapper }) => {
                const chart = chartWrapper.getChart();
                const selection = chart.getSelection();
                if (selection.length === 0) return;
                handleStateSelect(
                  stateMap[selection[0].row + 1],
                  selection[0].row + 1
                );
              },
            },
          ]}
        />
      </div>
      <div className='col-md-7'>
        <h5 className='f20 text-bold mb-1'>States FPOs</h5>
        {selectedState ? (
          <>
            <h6 className='text-primary text-bold'>
              {selectedState.stateName}
            </h6>
            <table className='table no-strip map-chart-table'>
              <tbody>
                <tr>
                  <td className='text-right'>Total FPOs</td>
                  <td className='text-primary text-bold'>
                    <span className='pl-1'>{selectedState['Total FPOs']}</span>
                  </td>
                </tr>
                <tr>
                  <td className='text-right'>Avg. Vintage</td>
                  <td className='text-primary text-bold'>
                    <span className='pl-1'>
                      {selectedState['Average_Vintage']}
                    </span>
                  </td>
                </tr>
                <tr>
                  <td className='text-right'>Avg. Membership</td>
                  <td className='text-primary text-bold'>
                    <span className='pl-1'>
                      {selectedState['Average_Membership']}
                    </span>
                  </td>
                </tr>
                <tr>
                  <td className='text-right'>Grades</td>
                  <td
                    className='text-primary text-bold'
                    style={{ width: '48%' }}
                  >
                    <div className='pl-1 d-flex justify-content-between'>
                      <div className='d-flex flex-column text-center mr-2'>
                        <p>A</p>
                        <p>{selectedState['Grading A Count']}</p>
                      </div>
                      <div className='d-flex flex-column text-center mr-2'>
                        <p>B</p>
                        <p>{selectedState['Grading B Count']}</p>
                      </div>
                      <div className='d-flex flex-column text-center mr-2'>
                        <p>C</p>
                        <p>{selectedState['Grading C Count']}</p>
                      </div>
                      <div className='d-flex flex-column text-center '>
                        <p>D</p>
                        <p>{selectedState['Grading D Count']}</p>
                      </div>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </>
        ) : (
          <h6 className='text-warning text-bold'>Please select a state.</h6>
        )}
      </div>
    </div>
  );
};

export default MapChart;
