import React, { useEffect, useState } from 'react';
import './charts.scss';
import {
  Chart,
  Label,
  Legend,
  Series,
  Tooltip,
  CommonSeriesSettings,
  ValueAxis,
} from 'devextreme-react/chart';
import formatCurrency from '../../utils/currency';

const SamVintageChart = ({ documents, title, legendName, keyName }) => {
  const [chartData, setchartData] = useState([]);
  useEffect(() => {
    if (!documents || !keyName || keyName.length < 0) return;

    let tempoArray = [];
    documents[keyName[0]].map((val, index) => {
      tempoArray.push({
        arg1: val.year ? val.year + '' : null,
        val1: val.value === '0' ? null : parseFloat(val.value),
      });

      if (keyName.length > 1) {
        tempoArray[index] = {
          ...tempoArray[index],
          arg2: documents[keyName[1]][index].year
            ? documents[keyName[1]][index].year + ''
            : null,
          val2:
            documents[keyName[1]][index].value === '0'
              ? null
              : documents[keyName[1]][index].value,
        };
      }
    });
    setchartData(tempoArray);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [documents]);

  const toLaksh = (num) => {
    return (
      formatCurrency(parseFloat(parseFloat(num / 100000)).toFixed(2))[
        'maskedValue'
      ] + ' L'
    );
  };

  const customizeText = (arg) => {
    return toLaksh(arg.value);
  };

  const customizeTooltip = (pointInfo) => {
    let p1, p2;
    if (pointInfo.points.length > 1) {
      p1 = 1;
      p2 = 0;
    } else {
      p1 = 0;
    }
    return {
      html: `
    <table className='table chart-tooltip-table table-borderless'>
    <thead>
    <tr>
    <th colspan='2' >${pointInfo.points[0].argumentText}</th></tr></thead>
    <tbody>
    <tr>
    <th>${pointInfo.points[p1].seriesName}:</th>
    <td className='text-right' >${toLaksh(
      pointInfo.points[p1].originalValue
    )}</td>
    </tr>
    ${
      p2 !== undefined
        ? `<tr>
    <th>${pointInfo.points[p2].seriesName}:</th>
    <td className='text-right' >${toLaksh(
      pointInfo.points[p2].originalValue
    )}</td>
    </tr>`
        : ''
    }
    </tbody>
    </table>
   `,
    };
  };

  return (
    <div className='sam-vintage-chart-container'>
      {chartData.length > 3 && (
        <>
          <h5 className={`f16 text-bold mb-2 `}>{title}</h5>
          <Chart dataSource={chartData} className='chart'>
            <CommonSeriesSettings
              argumentField='state'
              type='stackedBar'
              barPadding={0.5}
            />
            {legendName && legendName.length > 1 && (
              <Series
                valueField='val2'
                argumentField='arg2'
                color='#F58D45'
                name={legendName && legendName.length > 1 ? legendName[1] : ''}
              />
            )}

            <Series
              valueField='val1'
              argumentField='arg1'
              color='#00A3FF'
              name={legendName && legendName.length > 0 ? legendName[0] : ''}
            />

            <Tooltip
              enabled={true}
              shared={true}
              customizeTooltip={customizeTooltip}
            />
            <Legend verticalAlignment='bottom' horizontalAlignment='center' />
            <ValueAxis maxValueMargin={0.01}>
              <Label customizeText={customizeText} />
            </ValueAxis>
          </Chart>
        </>
      )}
    </div>
  );
};

export default SamVintageChart;
