import React from 'react';
import SwiperCore, { Navigation } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import left_arrow from './../../assets/Images/Icons/swipe_btn_left.svg';
import right_arrow from './../../assets/Images/Icons/swipe_btn_right.svg';

SwiperCore.use([Navigation]);

const { innerWidth: screenWidth, innerHeight: screenHeight } = window;

const HorizontalSwiper = ({ childs, spaceBetween }) => {
  const [slidesPerView, setslidesPerview] = React.useState(1);
  const [sliderIndex, setSliderIndex] = React.useState('start');
  React.useEffect(() => {
    if (screenWidth >= 280 && screenWidth <= 500) {
      setslidesPerview(1);
    } else if (screenWidth >= 500 && screenWidth <= 900) {
      setslidesPerview(2);
    } else if (screenWidth >= 900 && screenWidth <= 1300) {
      setslidesPerview(3);
    } else {
      setslidesPerview(4);
    }
  }, []);

  const navigationPrevRef = React.useRef(null);
  const navigationNextRef = React.useRef(null);
  return (
    <div style={{ position: 'relative' }}>
      <Swiper
        spaceBetween={spaceBetween}
        slidesPerView={slidesPerView}
        onSlideChange={(change) => {
          if (change.isBeginning) {
            setSliderIndex('start');
          }
          if (change.isEnd) {
            setSliderIndex('end');
          }
        }}
        onSwiper={(swiper) => console.log(swiper)}
        navigation={{
          prevEl: navigationPrevRef.current,
          nextEl: navigationNextRef.current,
        }}
        onBeforeInit={(swiper) => {
          swiper.params.navigation.prevEl = navigationPrevRef.current;
          swiper.params.navigation.nextEl = navigationNextRef.current;
        }}
      >
        {childs.map((child, index) => {
          return <SwiperSlide key={index}>{child}</SwiperSlide>;
        })}
      </Swiper>

      {childs.length > slidesPerView ? (
        <>
          <img
            ref={navigationPrevRef}
            src={left_arrow}
            alt='prev'
            className={`swiper_prev_button ${
              sliderIndex === 'start' && 'd-none'
            }`}
          />
          <img
            ref={navigationNextRef}
            src={right_arrow}
            alt='next'
            className={`swiper_next_button ${
              sliderIndex === 'end' && 'd-none'
            }`}
          />
        </>
      ) : null}
    </div>
  );
};
export default HorizontalSwiper;
