import { Form } from 'react-bootstrap';
const InputBox = ({
  readOnly,
  label,
  type,
  className,
  placeHolder,
  name,
  register,
  required,
  error,
  handleChange,
  min,
  max,
  minLength,
  maxLength,
  value,
  labelOnly,
}) => {
  if (typeof handleChange !== 'function') handleChange = function () {};
  type = type ? type : 'text';
  return (
    <Form.Group
      className={`mb-3 col-md-6 col-lg-4 ${className && className}`}
      style={{ padding: '0% 3%' }}
    >
      <Form.Label htmlFor={name} className='f14 form-label'>
        {label}
        {required && <small className='text-danger f20'>*</small>}
      </Form.Label>
      {!labelOnly ? (
        <Form.Control
          id={name}
          className={``}
          readOnly={readOnly ? true : false}
          type={type}
          placeholder={placeHolder}
          min={min}
          {...(register
            ? register(name, {
                required,
                type,
                min,
                max,
                minLength,
                maxLength,
              })
            : {})}
          // onChange={(e) => handleChange(e.target.value)}
        />
      ) : (
        <Form.Control
          id={name}
          className={``}
          readOnly={true}
          type={'text'}
          value={value ? value : ''}
        />
      )}

      {error && (error.message ? 
        <small className='text-danger'>{error.message}</small>
       : 
        <small className='text-danger'>Enter valid {label}</small>)
      }
    </Form.Group>
  );
};

export default InputBox;
