import Modal from 'react-bootstrap/Modal';
import React, { useEffect } from 'react';
import './adminStyle.css';
import { Form } from 'react-bootstrap';
import { ADMIN_APPLICATION_STATUS, LOAN_FIELDS } from '../../constants';
import { useForm } from 'react-hook-form';
import { CloseOutline } from 'react-ionicons';

function ApplicationSendbackModal({ show, onHide, handleSendBackApplication }) {
  const handleClose = () => onHide(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    defaultValues: {
      [ADMIN_APPLICATION_STATUS[LOAN_FIELDS.sendBack]]: '',
    },
  });

  useEffect(() => {
    if (show) reset();
  }, [show]);

  return (
    <Modal
      show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      dialogClassName='modal-40w senback-modal'
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 ml-3'
        >
          Please provide reasons for sending back <br /> application to FPO
        </Modal.Title>
        <button
          className='h5 text-bold f24  btn p-0 mt-1 mr-1 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body className='m-3'>
        <Form
          onSubmit={handleSubmit((data) => {
            handleSendBackApplication(data);
            onHide(false);
          })}
        >
          <Form.Control
            as='textarea'
            className=' p-2 pl-3 w-579 text-area'
            {...register(ADMIN_APPLICATION_STATUS[LOAN_FIELDS.sendBack], {
              required: true,
              minLength: 5,
            })}
          />
          {errors[ADMIN_APPLICATION_STATUS[LOAN_FIELDS.sendBack]] && (
            <span className='text-danger'>
              Please enter reason for send back above 4 characters
            </span>
          )}
          <br />
          <button className='btn btn-primary br-12 mt-2 share-btn my-4'>
            SHARE REASON
          </button>
        </Form>
      </Modal.Body>
    </Modal>
  );
}
export default ApplicationSendbackModal;
