/* eslint-disable jsx-a11y/role-supports-aria-props */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import './Navbar.css';
import './Navbar.scss';
import Logo from '../../../assets/Images/Logo/logo_1x.jpg';
import { authActions } from '../../../redux/actions';
import { NavLink, useNavigate, Link } from 'react-router-dom';
import { BANK_MENU, MENUS, ROLES } from '../../../constants';
import AllNotifications from '../../../components/Modals/AllNotifications';
import { notification_lists } from '../../../data';
import Image from 'react-bootstrap/Image';
import { Col, Row, Table } from 'react-bootstrap';

const Navbar = () => {
  const auth = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [reviewDoc, setReviewDoc] = useState(false);
  const logOut = () => {
    if (auth.userInfo.usertype === ROLES.FPO) {
      localStorage.removeItem('login');
      window.location = 'https://fpogateway.com/login';
    } else {
      navigate('/');
      dispatch(authActions.logOut());
    }
  };

  const UserDropDown = ({ userInfo }) => {
    return (
      <div
        className='dropdown-menu shadow-sm sm-menu user-menu'
        aria-labelledby='dropdown02'
      >
        <a className='dropdown-item' href='#'>
          {userInfo.username ? (
            userInfo.username
          ) : (
            <span className='text-uppercase'>{userInfo.usertype}</span>
          )}
        </a>
        {auth.userInfo.usertype === ROLES.ADMIN && (
          <Link className='dropdown-item' to='/admin/user-management'>
            User Management
          </Link>
        )}
        <button className='dropdown-item' onClick={logOut}>
          LogOut
        </button>
      </div>
    );
  };

  const NotificationDropDown = () => {
    const [allNotification, setAllNotification] = useState(false);
    const colors = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];

    return (
      <div
        className='dropdown-menu shadow-sm sm-menu user-menu notification-menu '
        aria-labelledby='dropdown01'
      >
        <Row>
          <Col md={7}>
            <p className='f24 ml-3 mt-3 mb-3 text-bold'>Notifications 3</p>
          </Col>
          <Col md={5} className='text-right'>
            <button className='h5 text-muted text-bold f24 btn p-0 shadow-none'>
              <p className='mark-all f12 mr-3 mt-4 mb-3'>Mark All As Read</p>
            </button>
          </Col>
        </Row>
        <Table className='upcoming custom-table-class'>
          <tbody className='f14 medium-font'>
            {notification_lists.map((notification_list, key) => (
              <tr key={key}>
                <td>
                  <div className='notification-dot-icon'>
                    <Image
                      className='img-icon'
                      src='data:image/jpeg;base64,'
                    />

                    <span
                      style={{ color: colors[key] }}
                      key={key}
                      className='notification-icon-button'
                    ></span>
                  </div>
                </td>
                <td className='pl-1'>{notification_list.notificationName}</td>
              </tr>
            ))}
          </tbody>
        </Table>
        <div className='text-center notification-view-btn'>
          <button
            className='btn br-12 notification-view-btn text-bold m-0'
            variant='primary'
            onClick={() => setAllNotification(true)}
          >
            View All
          </button>
        </div>
        <AllNotifications
          show={allNotification}
          onHide={() => setAllNotification(false)}
        />
      </div>
    );
  };

  const RenderMenu = ({ menu }) => (
    <ul className='navbar-nav mr-auto pl-lg-4 main-navbar'>
      <>
        {menu.map((prop, key) => {
          return (
            <li className='nav-item px-lg-2' key={key}>
              {prop.target && prop.target === '_blank' ? (
                <a
                  className='nav-link'
                  href={prop.route}
                  activeclassname='active'
                  target={'_blank'}
                  rel='noreferrer'
                >
                  <span className='d-inline-block d-lg-none icon-width'>
                    <i className={prop.icon}></i>
                  </span>
                  {prop.text}
                </a>
              ) : (
                <NavLink
                  className='nav-link'
                  to={prop.route}
                  activeclassname='active'
                >
                  <span className='d-inline-block d-lg-none icon-width'>
                    <i className={prop.icon}></i>
                  </span>
                  {prop.text}
                </NavLink>
              )}
            </li>
          );
        })}
      </>
    </ul>
  );

  return (
    <div className='nav text-left primary-bg'>
      <nav className='navbar navbar-expand-lg navbar-light bg-transparent col-12 '>
        <div className='container col-10 p-4'>
          <a className='navbar-brand d-flex align-items-center' href='#'>
            <img src={Logo} alt='' />{' '}
            <span className='ml-3 font-weight-bold'>AgFinMart</span>
          </a>
          <button
            className='navbar-toggler navbar-toggler-right border-0'
            type='button'
            data-toggle='collapse'
            data-target='#navbar4'
          >
            <span className='navbar-toggler-icon'></span>
          </button>
          <div className='collapse navbar-collapse' id='navbar4'>
            {auth && auth.userInfo && auth.userInfo.usertype && (
              <RenderMenu menu={MENUS[auth.userInfo.usertype]} />
            )}

            <ul className='navbar-nav ml-auto mt-3 mt-lg-0'>
              <li className='nav-item px-lg-2 dropdown d-menu'>
                <a
                  className='nav-link'
                  href='#'
                  id='dropdown01'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <span className='navbar-notification'>
                    <span className='d-inline-block d-lg-none icon-width'>
                      <i className='fa fa-caret-square-o-down'></i>
                    </span>
                    3
                  </span>
                </a>
                {auth && auth.isLoggedIn && auth.userInfo && (
                  <NotificationDropDown />
                )}
              </li>
              <li className='nav-item px-lg-2 dropdown d-menu'>
                <a
                  className='nav-link px-0'
                  href='#'
                  id='dropdown02'
                  data-toggle='dropdown'
                  aria-haspopup='true'
                  aria-expanded='false'
                >
                  <span className='d-inline-block d-lg-none icon-width'>
                    <i className='fa fa-caret-square-o-down '></i>
                  </span>
                  <i className='fa fa-user f20important'></i>
                  {` `}
                  {/* <i className='fa fa-chevron-down f14 d-md-none'></i> */}
                </a>
                {auth && auth.isLoggedIn && auth.userInfo && (
                  <UserDropDown userInfo={auth.userInfo} />
                )}
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
