import React from 'react';
import { NavDropdown } from 'react-bootstrap';
import arrowRight from '../../assets/Images/Icons/arrow-right.svg';
import dotsVertical from '../../assets/Images/Icons/dots-v.svg';
const CardTable = ({
  titles,
  details,
  type,
  handleDraftClickView,
  handleDraftClickDelete,
}) => {
  return (
    <table className='table w-100'>
      <thead className=''>
        <tr className='muted f12 text-align-center' style={{ position: 'relative' }}>
          {titles.map((title, index) => {
            return <th key={index}>{title}</th>;
          })}
          {type === 'draftApplication' && (
            <>
              <th colSpan={2} className='text-center'>
                <div className='d-flex justify-content-around draft-info-legends'>
                  <p>
                    <span className='draf'></span>Draft
                  </p>
                  <p>
                    <span className='re-apply'></span>Re-Apply
                  </p>
                </div>
              </th>
            </>
          )}
        </tr>
      </thead>
      <tbody className='f12 medium-font'>
        {details.map((detail, index) => {
          return (
            <tr key={index} className='text-align-center'>
              <td
                className={`${
                  detail.status_remarks === 'sent_back_application_admin'
                    ? 're-apply'
                    : 'draft'
                }`}
              >
                {detail.applicationuid ? detail.applicationuid : detail.id}
              </td>
              {type === 'repayments' && (
                <>
                  <td className='text-center'>{detail.dueBy}</td>
                  <td className='text-center'>{detail.amount}</td>
                </>
              )}

              {type === 'management' && (
                <>
                  {' '}
                  <td>{detail.bank}</td>
                  <td>{detail.date}</td>
                  <td className='text-center'>
                    <span className='badge badge-warning text-white py-1 br-12 d-flex justify-content-center'>
                      {detail.status}
                    </span>
                  </td>
                  <td className='text-center d-flex  justify-content-center'>
                    <i className='muted fa fa-pencil'> </i>
                  </td>
                </>
              )}
              {type === 'draftApplication' && (
                <>
                  <td className='text-center'>
                    <button
                      className='btn px-2 py-0 shadow-none f12'
                      onClick={() =>
                        handleDraftClickView(
                          detail.applicationuid,
                          detail.loanproductname
                        )
                      }
                    >
                      <img src={arrowRight} alt='' />
                    </button>
                  </td>
                  <td className='text-center'>
                    <NavDropdown
                      title={
                        <img src={dotsVertical} alt='' className='mt-n1' />
                      }
                      id='nav-dropdown'
                      className='dots-container requested-document-upload'
                    >
                      <NavDropdown.Item
                        className='text-dark dots-link f14'
                        eventKey='delete'
                        onClick={() => {
                          handleDraftClickDelete(detail.applicationuid);
                        }}
                      >
                        Delete
                      </NavDropdown.Item>
                    </NavDropdown>
                  </td>
                </>
              )}
            </tr>
          );
        })}
      </tbody>
    </table>
  );
};

export default CardTable;
