import React from 'react';
import { Routes, Route, useLocation, Navigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import Login from '../pages/login/BankLogin';
import Layout from '../pages/Layout/Layout';
import BankDashboard from '../pages/bank/dashboard';
import BankLayout from '../layout/BankLayout';
import FpoLayout from '../layout/FpoLayout';
import AdminLayout from '../layout/AdminLayout';
import { switchRoutes } from '../utils/utils';
import { authActions } from '../redux/actions';
import { SIGNIN_FPO } from '../redux/types';

const NoMatchRoute = () => <h1>Invalid route</h1>;

const AuthLayout = ({ children }) => {
  let location = useLocation();
  const auth = useSelector((state) => state.auth);
  if (!auth.isLoggedIn) {
    return <Navigate to='/' state={{ from: location }} />;
  } else {
    return children;
  }
};

const WithOutAuth = ({ children }) => {
  let location = useLocation();
  const dispatch = useDispatch();

  const auth = useSelector((state) => state.auth);

  const params = new URLSearchParams(location.search);

  let token = params.get('token');

  if (token) dispatch({ type: SIGNIN_FPO, payload: token });
  if (auth.isLoggedIn && location.pathname === '/') {
    var to = '/';
    if (location?.state?.from && auth && auth.localTokenLogin === true) {
      to = location?.state?.from?.pathname + location?.state?.from?.search;
    } else {
      to = switchRoutes(auth.userInfo?.usertype);
    }

    return <Navigate to={to} />;
  } else {
    let token = localStorage.getItem('login');
    if (token) {
      dispatch(authActions.signToken(token));
      return null;
    } else return children;
  }
};

const AppRoutes = () => {
  return (
    <>
      <Routes>
        <Route
          path='/'
          exact
          element={
            <WithOutAuth>
              <Login />
            </WithOutAuth>
          }
        />
        <Route
          path='/'
          exact
          element={
            <AuthLayout>
              <Layout></Layout>
            </AuthLayout>
          }
        >
          <Route exact path='bank/*' element={<BankLayout />} />
          <Route exact path='fpo/*' element={<FpoLayout />} />
          <Route exact path='admin/*' element={<AdminLayout />} />

          <Route exact path='test' element={<BankDashboard />} />
        </Route>
        <Route exact path='*' element={<NoMatchRoute />} />
      </Routes>
    </>
  );
};

export default AppRoutes;
