import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Form, Spinner } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { adminActions } from '../../../redux/actions';
import 'react-perfect-scrollbar/dist/css/styles.css';
import { useLocation } from 'react-router';
import ApplcationFilter from '../../../components/Forms/Common/ApplcationFilter';
import ApplicationCard from '../../../components/Cards/ApplicationCard';
import ShadowBox from '../../../components/ShadowBox';
import AdminApplicationForm from '../../../components/Forms/Admin/Application/AdminApplicationForm';
import LoanContainer from '../../../components/Forms/Admin/Loan/LoanContainer';
import Toast from '../../../utils/toast';

export default function FpoApplication() {
  const auth = useSelector((state) => state.auth);
  const admin = useSelector((state) => state.admin);
  const dispatch = useDispatch();

  const [selectedApplication, setselectedApplication] = useState(null);
  const [selectedLoan, setselectedLoan] = useState(null);
  const [activeTabKey, setActiveTabKey] = useState('applications');
  const [category, setCategory] = useState('');
  const [loanCategory, setLoanCategory] = useState('');
  const [applicationList, setapplicationList] = useState([]);
  const [loansList, setLoansList] = useState([]);
  const location = useLocation();
  const params = new URLSearchParams(location.search);

  let qry_application_id = params.get('application_id');
  let qry_type = params.get('type');
  let qry_category = params.get('category');

  useEffect(() => {
    if (!auth.isLoggedIn) return;
    const organizationid = auth.userInfo.organizationid;
    dispatch(adminActions.getAllApplicationsInProgress({ organizationid }));
    dispatch(adminActions.getAllLoansInProgress({ organizationid }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [auth]);

  useEffect(() => {
    if (
      qry_type &&
      !qry_application_id &&
      admin &&
      admin.applications &&
      admin.applications.success &&
      admin.loans &&
      admin.loans.success
    ) {
      setActiveTabKey(qry_type);
      if (qry_type === 'applications' && qry_category) {
        setCategory(qry_category + '_application');
        handleCategory(qry_category + '_application');
      } else if (qry_type === 'loans' && qry_category) {
        setLoanCategory(qry_category + '_loans');
        handleLoanCategory(qry_category + '_loans');
      }
    }
  }, [qry_type, admin?.applications, admin?.loans]);

  // applications filter
  useEffect(() => {
    if (
      !category &&
      admin &&
      admin.applications &&
      admin.applications.success &&
      qry_type &&
      qry_application_id &&
      qry_category
    ) {
      setActiveTabKey(qry_type);
      if (qry_type === 'applications') {
        let applications = {
          new_application: admin.applications.success.new_application,
          active_application: admin.applications.success.active_application,
          closed_application: admin.applications.success.closed_application,
        };
        if (applications[`${qry_category}_application`]) {
          setCategory(`${qry_category}_application`);
          setapplicationList(applications[`${qry_category}_application`]);
          let find_application = applications[
            `${qry_category}_application`
          ].find((o) => o.applicationuid === qry_application_id);
          if (find_application) {
            setApplication(find_application);
          } else {
            invalidApplicationBucket(
              qry_application_id,
              qry_category,
              qry_type
            );
          }
        }
      }
    }
  }, [admin.applications.success]);

  // loan filter
  useEffect(() => {
    if (
      !loanCategory &&
      admin &&
      admin.loans &&
      admin.loans.success &&
      qry_type &&
      qry_application_id &&
      qry_category
    ) {
      if (qry_type === 'loans') {
        let loans = {
          new_loans: admin.loans.success.new_loans,
          active_loans: admin.loans.success.active_loans,
          closed_loans: admin.loans.success.closed_loans,
        };
        if (loans[`${qry_category}_loans`]) {
          setLoanCategory(`${qry_category}_loans`);
          setLoansList(loans[`${qry_category}_loans`]);
          let find_application = loans[`${qry_category}_loans`].find(
            (o) => o.applicationuid === qry_application_id
          );
          if (find_application) {
            setLoan(find_application);
          } else {
            invalidApplicationBucket(
              qry_application_id,
              qry_category,
              qry_type
            );
          }
        }
      }
    }
  }, [admin.loans.success]);

  const invalidApplicationBucket = (qpplication_id, category, type) => {
    Toast({
      type: 'error',
      title: `The application ID "${qpplication_id}" is not in the ${category} ${type} bucket.`,
    });
  };

  // default application selection
  useEffect(() => {
    if (
      !category &&
      admin.applications.success &&
      ((!qry_type && !qry_application_id && !qry_category) ||
        qry_type === 'loans')
    ) {
      for (let key in admin.applications.success) {
        if (admin.applications.success.hasOwnProperty(key)) {
          if (admin.applications.success[key].length > 0 && !category) {
            setCategory(key);
            setapplicationList(admin.applications.success[key]);
            setApplication(admin.applications.success[key][0]);
            break;
          }
        }
      }
    }
  }, [category, admin?.applications]);

  // default loan selection
  useEffect(() => {
    if (
      !loanCategory &&
      admin.loans.success &&
      ((!qry_type && !qry_application_id && !qry_category) ||
        qry_type === 'applications')
    ) {
      for (let key in admin.loans.success) {
        if (admin.loans.success.hasOwnProperty(key)) {
          if (admin.loans.success[key].length > 0 && !loanCategory) {
            setLoanCategory(key);
            setLoansList(admin.loans.success[key]);
            setLoan(admin.loans.success[key][0]);
            break;
          }
        }
      }
    }
  }, [loanCategory, admin?.loans]);

  useEffect(() => {
    if (admin.applicationFilter.success) {
      if (admin.applicationFilter.data.length > 0) {
        setapplicationList(admin.applicationFilter.data);
        setselectedApplication(admin.applicationFilter.data[0]);
        fetchApplicationApi(admin.applicationFilter.data[0]);
      } else {
        setselectedApplication('');
        setapplicationList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.applicationFilter?.success]);

  useEffect(() => {
    if (admin.loanFilter.success) {
      if (admin.loanFilter.data.length > 0) {
        setLoansList(admin.loanFilter.data);
        setselectedLoan(admin.loanFilter.data[0]);
        fetchApplicationApi(admin.applicationFilter.data[0]);
      } else {
        setselectedLoan('');
        setLoansList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.loanFilter?.success]);

  const fetchApplicationApi = (application, useDirect) => {
    dispatch(
      adminActions.getApplicationDetailsInProgress(
        useDirect ? application : selectedApplication
      )
    );
  };

  const setApplication = (application) => {
    fetchApplicationApi(application, true);
    setselectedApplication(application);
  };

  const fetchLoanApi = (loan, useDirect) => {
    loan = useDirect ? loan : selectedLoan;
    dispatch(adminActions.getLoanSummaryInProgress(loan));
    dispatch(adminActions.getLoanDocumentsInProgress(loan));
    dispatch(adminActions.getLoanRepaymentStatusInProgress(loan));
  };

  const setLoan = (loan) => {
    fetchLoanApi(loan, true);
    setselectedLoan(loan);
  };

  const getDocsCounts = (type, keys) => {
    let count = 0;
    keys.forEach((key) => {
      if (admin[type].success[key]) {
        count += admin[type].success[key].length;
      }
    });
    return count;
  };

  const handleCategory = (key) => {
    setCategory(key);
    let applications = admin.applications.success[key];
    setapplicationList(applications);
    setApplication(applications?.[0]);
  };

  const onApplicationClick = (application) => {
    setApplication(application);
  };

  const onLoanClick = (loan) => {
    setLoan(loan);
  };

  const handleLoanCategory = (key) => {
    setLoanCategory(key);
    let loans = admin.loans.success[key];
    setLoansList(loans);
    setLoan(loans?.[0]);
  };

  const handleFormData = (formData, type) => {
    let payload = { formData, category };
    if (type === 'application') {
      dispatch(adminActions.applicationFilterInProgress(payload));
    } else if (type === 'loan') {
      dispatch(adminActions.loanFilterInProgress(payload));
    }
  };

  return (
    <div className='container-fluid pb-5'>
      <div className='row'>
        <div className='col-md-3'>
          <h2 className='medium-font f24 mb-4'>Loan Applications</h2>
          <div>
            <Tabs
              defaultActiveKey={activeTabKey}
              activeKey={activeTabKey}
              onSelect={(k) => setActiveTabKey(k)}
              id='uncontrolled-tab-example'
              className='mb-3 bank-loan-tabs'
            >
              <Tab
                eventKey='applications'
                title={
                  <>
                    APPLICATIONS{' '}
                    <span className='default-badge'>
                      {`${
                        admin.applications.success
                          ? getDocsCounts('applications', [
                              'new_application',
                              'active_application',
                            ])
                          : 0
                      }`}
                    </span>
                  </>
                }
              >
                <>
                  {admin &&
                  admin.applications &&
                  admin.applications.inProgress ? (
                    <div className='d-flex justify-content-center'>
                      <Spinner
                        animation='border'
                        role='status'
                        className='TEST'
                      >
                        <span className='visually-hidden'></span>
                      </Spinner>
                    </div>
                  ) : (
                    <>
                      <ApplcationFilter
                        category={category}
                        type={'application'}
                        handleFormData={handleFormData}
                        spinner={
                          admin &&
                          admin.applicationFilter &&
                          admin.applicationFilter.inProgress
                        }
                      />
                      <Form.Select
                        className='form-control br-12 mb-4'
                        onChange={(e) => handleCategory(e.target.value)}
                        value={category}
                      >
                        <option value='new_application'>
                          New Applications{' '}
                          {(admin.applications.success.new_application &&
                            admin.applications.success.new_application
                              .length) ||
                            '0'}
                        </option>
                        <option value='active_application'>
                          Active Applications{' '}
                          {(admin.applications.success.active_application &&
                            admin.applications.success.active_application
                              .length) ||
                            '0'}
                        </option>
                        <option value='closed_application'>
                          Closed Applications{' '}
                          {(admin.applications.success.closed_application &&
                            admin.applications.success.closed_application
                              .length) ||
                            '0'}
                        </option>
                      </Form.Select>
                      <div
                        className='custom-scrollbar'
                        style={{ height: '600px' }}
                      >
                        {applicationList &&
                          applicationList.map((value, key) => {
                            return (
                              <div className='mb-2' key={key}>
                                <ApplicationCard
                                  isActive={
                                    selectedApplication &&
                                    selectedApplication.applicationuid ===
                                      value.applicationuid
                                      ? true
                                      : false
                                  }
                                  applicationId={value.applicationuid}
                                  fpoName={value.fponame}
                                  fpoState={value.fpostate}
                                  applicationDate={value.applicationdate}
                                  loanName={value.loanproductname}
                                  loanAmount={value.loanamount}
                                  applicationStatus={value.applicationstatus}
                                  participatingLendersCount={
                                    value.participating_lenders_count
                                  }
                                  onClick={() => onApplicationClick(value)}
                                />
                              </div>
                            );
                          })}
                      </div>
                    </>
                  )}
                </>
              </Tab>
              <Tab
                eventKey='loans'
                title={
                  <>
                    Loans{' '}
                    <span className='default-badge'>
                      {`${
                        admin.loans.success
                          ? getDocsCounts('loans', [
                              'new_loans',
                              'active_loans',
                            ])
                          : 0
                      }`}
                    </span>
                  </>
                }
              >
                <>
                  {admin && admin.loans && admin.loans.inProgress ? (
                    <div className='d-flex justify-content-center'>
                      <Spinner animation='border' role='status'>
                        <span className='visually-hidden'></span>
                      </Spinner>
                    </div>
                  ) : (
                    <>
                      <ApplcationFilter
                        category={loanCategory}
                        type={'loan'}
                        handleFormData={handleFormData}
                        spinner={
                          admin &&
                          admin.loanFilter &&
                          admin.loanFilter.inProgress
                        }
                      />
                      <Form.Select
                        className='form-control br-12 mb-4'
                        onChange={(e) => handleLoanCategory(e.target.value)}
                        value={loanCategory}
                      >
                        <option value='new_loans'>
                          Sanctioned Loans{' '}
                          {(admin.loans.success.new_loans &&
                            admin.loans.success.new_loans.length) ||
                            '0'}
                        </option>
                        <option value='active_loans'>
                          Live Loans{' '}
                          {(admin.loans.success.active_loans &&
                            admin.loans.success.active_loans.length) ||
                            '0'}
                        </option>
                        <option value='closed_loans'>
                          Matured Loans{' '}
                          {(admin.loans.success.closed_loans &&
                            admin.loans.success.closed_loans.length) ||
                            '0'}
                        </option>
                      </Form.Select>
                      <div
                        className='custom-scrollbar'
                        style={{ height: '600px' }}
                      >
                        {loansList.map((value, key) => {
                          return (
                            <div className='mb-2' key={key}>
                              <ApplicationCard
                                isActive={
                                  selectedLoan &&
                                  selectedLoan.applicationuid ===
                                    value.applicationuid
                                    ? true
                                    : false
                                }
                                applicationId={value.applicationuid}
                                fpoName={value.fponame}
                                fpoState={value.fpostate}
                                applicationDate={value.applicationdate}
                                loanName={value.loanproductname}
                                loanAmount={value.loanamount}
                                participatingLendersCount={
                                  value.participating_lenders_count
                                }
                                onClick={() => onLoanClick(value)}
                              />
                            </div>
                          );
                        })}{' '}
                      </div>
                    </>
                  )}
                </>
              </Tab>
            </Tabs>
          </div>
        </div>
        <ShadowBox className='col-md-9  br-3 '>
          {activeTabKey === 'applications' &&
            (admin &&
            admin.selectedApplication &&
            admin.selectedApplication.details &&
            admin.selectedApplication.details.inProgress ? (
              <div className='d-flex justify-content-center pt-5'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            ) : (
              <>
                <AdminApplicationForm
                  publishStatusFlag={
                    admin &&
                    admin.selectedApplication &&
                    admin.selectedApplication.details &&
                    admin.selectedApplication.details.applicationstatus ===
                      'Applied'
                      ? false
                      : true
                  }
                  closedStatusFlag={
                    admin &&
                    admin.selectedApplication &&
                    admin.selectedApplication.details &&
                    (admin.selectedApplication.details.applicationstatus ===
                      'Closed' ||
                      admin.selectedApplication.details.applicationstatus ===
                        'Cancelled')
                      ? true
                      : false
                  }
                />
              </>
            ))}

          {activeTabKey === 'loans' &&
            (admin &&
            admin.selectedLoan &&
            admin.selectedLoan.loanSummary &&
            admin.selectedLoan.loanSummary.inProgress ? (
              <div className='d-flex justify-content-center pt-5'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            ) : (
              <>
                <LoanContainer />
              </>
            ))}
        </ShadowBox>
      </div>
    </div>
  );
}
