import axios from 'axios';
import { tokenValidation } from '../interceptors';

const AdminHttpInstance = axios.create({
  baseURL: `${process.env.REACT_APP_API_URL}/admin/`,
  withCredentials: false,
  headers: {
    'Access-Control-Allow-Origin': '*',
  },
});

AdminHttpInstance.interceptors.response.use(
  (response) => Promise.resolve(response),
  tokenValidation
);

export { AdminHttpInstance };
