import React, { useEffect, useState } from 'react';
import { Accordion, Spinner, Alert } from 'react-bootstrap';
import MakeAnOffer from '../../../Modals/LoanApplicationModal/MakeAnOffer';
import './LendersOffer.scss';
import * as dayjs from 'dayjs';
import { useDispatch, useSelector } from 'react-redux';
import {
  FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS,
  FPO_PUT_ACCEPT_OFFER_IN_PROGRESS,
  FPO_PUT_REJECT_OFFER_IN_PROGRESS,
} from '../../../../redux/types/fpotypes';
import { DEAL_STATUS } from '../../../../constants';

const LendersOffers = ({ application_id, offers }) => {
  const [activeAccordion, setActiveAccordion] = useState(false);
  const [offerStatusBtn, setOfferStatusBtn] = useState(true);
  const fpo = useSelector((state) => state.fpo);
  return (
    <>
      <div className='col-12 my-3 '>
        <div className='row'>
          <div className='col-md-12 offset-lg-7 col-lg-5'>
            <div className='dflex-offer row'>
              <div className='dflex  col-md-4 text-right'>
                <div className='circle bg-accepted '></div>
                <span className='ml-2'>Accepted</span>
              </div>
              <div className='dflex  col-md-4 text-right'>
                <div className='circle bg-rejected '></div>
                <span className='ml-2'>Rejected</span>
              </div>
              <div className='dflex  col-md-4 text-right'>
                <div className='circle bg-updated '></div>
                <span className='ml-2'>Updated</span>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Accordion
        flush
        className='fpo-offers-accordion'
        activeKey={activeAccordion}
      >
        {offers.map((offer, key) => {
          return (
            <>
              <Accordion.Item
                key={key}
                eventKey={offer.proposal_id}
                className={`mb-3 offers-list ${
                  fpo &&
                  fpo.selectedOffer &&
                  fpo.selectedOffer[offer.proposal_id] &&
                  fpo.selectedOffer[offer.proposal_id].details &&
                  fpo.selectedOffer[offer.proposal_id].details.deal_status
                }`}
              >
                <Accordion.Header
                  as='div'
                  onClick={() =>
                    setActiveAccordion(
                      offer.proposal_id === activeAccordion
                        ? false
                        : offer.proposal_id
                    )
                  }
                >
                  <div className='d-flex  justify-content-between  text-left p-16'>
                    <div className='col-md-12'>
                      <div className='row'>
                        <div className='col-md-4'>
                          <h4 className='text-dark medium-font f18 m-0 mb-1'>
                            {offer.Lender_Name}
                          </h4>
                          <p className='f16 text-dark fw400 mb-0'>
                            Lender Name
                          </p>
                        </div>
                        <div className='col-md-3'>
                          {offer.deal_status === DEAL_STATUS['participate'] && (
                            <>
                              <h4 className='text-dark medium-font f18 m-0 mb-1'>
                                {offer.show_interest_date
                                  ? dayjs(offer.show_interest_date).format(
                                      'D MMMM YYYY'
                                    )
                                  : 'NIL'}
                              </h4>
                              <p className='f16 text-dark fw400 mb-0'>
                                Interest Date
                              </p>
                            </>
                          )}
                          {offer.deal_status === DEAL_STATUS['offer'] && (
                            <>
                              <h4 className='text-dark medium-font f18 m-0 mb-1'>
                                {offer.offer_date
                                  ? dayjs(offer.offer_date).format(
                                      'D MMMM YYYY'
                                    )
                                  : 'NIL'}
                              </h4>
                              <p className='f16 text-dark fw400 mb-0'>
                                Offer Date
                              </p>
                            </>
                          )}
                          {offer.deal_status === DEAL_STATUS['won'] && (
                            <>
                              <h4 className='text-dark medium-font f18 m-0 mb-1'>
                                {offer.fpo_accepted_date
                                  ? dayjs(offer.fpo_accepted_date).format(
                                      'D MMMM YYYY'
                                    )
                                  : 'NIL'}
                              </h4>
                              <p className='f16 text-dark fw400 mb-0'>
                                Accepted Date
                              </p>
                            </>
                          )}
                          {offer.deal_status === DEAL_STATUS['reject'] && (
                            <>
                              <h4 className='text-dark medium-font f18 m-0 mb-1'>
                                {offer.fpo_rejected_date
                                  ? dayjs(offer.fpo_rejected_date).format(
                                      'D MMMM YYYY'
                                    )
                                  : 'NIL'}
                              </h4>
                              <p className='f16 text-dark fw400 mb-0'>
                                Rejected Date
                              </p>
                            </>
                          )}
                        </div>
                        <div className='col-md-4'>
                          <h4 className='text-dark text-capitalize medium-font f18 m-0 mb-1'>
                            {offer.deal_status}
                          </h4>
                          <p className='f16 text-dark fw400 mb-0'>Status</p>
                        </div>
                      </div>
                    </div>
                    <div className=' d-flex justify-content-center align-items-center '>
                      <span className='toggle-on float-right'>
                        <i
                          className='
                                fa
                                f18
                                text-muted
                                fw-400
                                accordion-tick
                                fa-chevron-down
                              '
                        ></i>
                      </span>
                    </div>
                  </div>
                </Accordion.Header>
                <Accordion.Body>
                  <FpoOfferDetails
                    application_id={offer.applicationuid}
                    proposal_id={offer.proposal_id}
                    offerStatusBtn={offerStatusBtn}
                    setOfferStatusBtn={setOfferStatusBtn}
                  />
                </Accordion.Body>
              </Accordion.Item>
            </>
          );
        })}
      </Accordion>
    </>
  );
};

export default LendersOffers;

const FpoOfferDetails = ({ application_id, proposal_id, offerStatusBtn }) => {
  const [defaultValues, setDefaultValues] = useState(false);
  const fpo = useSelector((state) => state.fpo);
  const dispatch = useDispatch();

  useEffect(() => {
    if (
      !proposal_id ||
      (!fpo.selectedOffer &&
        !fpo.selectedOffer[proposal_id] &&
        fpo.selectedOffer[proposal_id].details)
    )
      return;
    let payload = { proposal_id: proposal_id };
    dispatch({
      type: FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS,
      payload: payload,
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (!fpo.selectedOffer[proposal_id]) return;
    let defaults = {};
    for (const key in fpo.selectedOffer[proposal_id].details) {
      if (fpo.selectedOffer[proposal_id].details.hasOwnProperty(key)) {
        // if (
        //   key !== MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter] &&
        //   key !== MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments]
        // ) {
        defaults[key] = fpo.selectedOffer[proposal_id].details[key];
        // }
      }
    }
    setDefaultValues(defaults);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpo.selectedOffer]);

  const handleOfferButtons = (proposal_id, application_id, type) => {
    let payload = {
      applicationuid: application_id,
      proposal_id: proposal_id,
    };
    if (type === 'accept') {
      dispatch({
        type: FPO_PUT_ACCEPT_OFFER_IN_PROGRESS,
        payload: payload,
      });
    } else if (type === 'reject') {
      dispatch({
        type: FPO_PUT_REJECT_OFFER_IN_PROGRESS,
        payload: payload,
      });
    }
  };
  return (
    <>
      {fpo &&
        fpo.selectedOffer[proposal_id] &&
        fpo.selectedOffer[proposal_id].details && (
          <div>
            <MakeAnOffer
              isFPO={true}
              defaultValues={defaultValues}
              readOnly={true}
            />
            <div className='row'>
              <div className='col-md-12 offset-lg-6 col-lg-6 col-sm-12'>
                <div className=' d-flex justify-content-around m-3 align-items-center'>
                  {fpo &&
                  fpo.selectedLoan &&
                  fpo.selectedLoan.loanInfo &&
                  fpo.selectedLoan.loanInfo.applicationstatus !== 'Cancelled' &&
                  fpo.selectedOffer[proposal_id].details.deal_status ===
                    'offer' ? (
                    <>
                      {fpo &&
                      fpo.selectedOffer &&
                      fpo.selectedOffer[proposal_id] &&
                      fpo.selectedOffer[proposal_id].actions &&
                      fpo.selectedOffer[proposal_id].actions.inProgress
                        ? null
                        : fpo &&
                          fpo.selectedOffer &&
                          fpo.selectedOffer[proposal_id] &&
                          fpo.selectedOffer[proposal_id].actions &&
                          !fpo.selectedOffer[proposal_id].actions.success && (
                            <div className=''>
                              <button
                                className='btn btn-danger br-12 fpo-offers-action-btns f16'
                                onClick={() =>
                                  handleOfferButtons(
                                    proposal_id,
                                    application_id,
                                    'reject'
                                  )
                                }
                              >
                                REJECT
                              </button>
                            </div>
                          )}
                      {fpo &&
                      fpo.selectedOffer &&
                      fpo.selectedOffer[proposal_id] &&
                      fpo.selectedOffer[proposal_id].actions &&
                      fpo.selectedOffer[proposal_id].actions.inProgress ? (
                        <div className='' style={{ width: '100%' }}>
                          <Spinner animation='border' role='status'>
                            <span className='visually-hidden'></span>
                          </Spinner>
                        </div>
                      ) : (
                        fpo &&
                        fpo.selectedOffer &&
                        fpo.selectedOffer[proposal_id] &&
                        fpo.selectedOffer[proposal_id].actions &&
                        !fpo.selectedOffer[proposal_id].actions.success && (
                          <div className=''>
                            <button
                              className='btn btn-primary br-12 fpo-offers-action-btns f16'
                              onClick={() =>
                                handleOfferButtons(
                                  proposal_id,
                                  application_id,
                                  'accept'
                                )
                              }
                            >
                              ACCEPT
                            </button>
                          </div>
                        )
                      )}
                    </>
                  ) : null}
                </div>
                <div className='d-flex justify-content-center align-items-center'>
                  {fpo &&
                    fpo.selectedOffer[proposal_id] &&
                    fpo.selectedOffer[proposal_id].actions &&
                    fpo.selectedOffer[proposal_id].actions.error &&
                    !fpo.selectedOffer[proposal_id].actions.success &&
                    !fpo.selectedOffer[proposal_id].actions.inProgress && (
                      <div
                        className='d-flex justify-content-center align-items-center'
                        style={{ width: '100%' }}
                      >
                        <Alert
                          variant={'danger'}
                          style={{ width: '30vw' }}
                          className='text-center'
                        >
                          {fpo.selectedOffer[proposal_id].actions.message}
                        </Alert>
                      </div>
                    )}
                  {fpo &&
                    fpo.selectedOffer[proposal_id] &&
                    fpo.selectedOffer[proposal_id].actions &&
                    fpo.selectedOffer[proposal_id].actions.success &&
                    !fpo.selectedOffer[proposal_id].actions.error &&
                    !fpo.selectedOffer[proposal_id].actions.inProgress && (
                      <Alert
                        variant={'success'}
                        style={{ width: '30vw' }}
                        className='text-center'
                      >
                        {fpo.selectedOffer[proposal_id].actions.message}
                      </Alert>
                    )}
                </div>
              </div>
            </div>
          </div>
        )}
    </>
  );
};
