

import Modal from 'react-bootstrap/Modal';
import { CloseOutline } from 'react-ionicons';

function ProgramsList({ show, onHide, props }) {
  const handleClose = () => onHide(false);
  return (
    <Modal
      
      size='xl'
      show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      className='modal_width'
      
      dialogClassName='modal_width'
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 ml-3'
        >
          Programs List
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body>
        <table className='request text-left'>
          <thead className='tbl-head'>
            <tr className='muteds f12'>
              <th>S.NO</th>
              <th>PARTNER NAME</th>
              <th>CATAGORY</th>
              <th>DESCRIPTION</th>
              <th>DETAILS</th>
              <th>ACTION</th>
            </tr>
          </thead>
          <tbody className='f12 medium-font'>
            <tr>
              <td>1</td>
              <td>Partner</td>
              <td>Co Terminus</td>
              <td>ABC Financials State Bank of India</td>
              <td>qdqdqknskjllajlklajflkasjkldaklfjlajdlsajjskg</td>
              <td>
                <a href=''>EDIT</a>
              </td>
            </tr>
            <tr>
              <td>2</td>
              <td>Partner</td>
              <td>Co Terminus</td>
              <td>ABC Financials State Bank of India</td>
              <td>qdqdqknskjllajlklajflkasjkldaklfjlajdlsajjskg</td>
              <td>
                <a href=''>EDIT</a>
              </td>
            </tr>
            <tr>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </Modal.Body>
    </Modal>
  );
}
export default ProgramsList;
