import React, { useEffect, useState } from 'react';
import {
  FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  CLEAN_SANSACTION_DOCUMETN_UPLOADS,
  FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
} from '../../redux/types/fpotypes';
import { useDispatch, useSelector } from 'react-redux';

import { Spinner, Alert } from 'react-bootstrap';
import ShadowBox from '../ShadowBox';
import './ActionsTab.scss';
import RequestedDocuments from './RequestedDocuments';

const FpoActionsTab = ({ application_id }) => {
  const dispatch = useDispatch();
  const fpo = useSelector((state) => state.fpo);
  const [sansactionDocuments, setSansactionDocuments] = useState({});
  useEffect(() => {
    if (application_id) {
      fetchApis();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [application_id]);

  useEffect(() => {
    if (
      (fpo && fpo.postSansactionDocumentUpload.success) ||
      (fpo && fpo.preSansactionDocumentUpload.success)
    ) {
      fetchApis();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpo]);

  useEffect(() => {
    let documents = {};
    if (
      fpo &&
      fpo.postSansactionRequestDocuments &&
      fpo.postSansactionRequestDocuments &&
      fpo.postSansactionRequestDocuments.documents &&
      fpo.preSansactionRequestDocuments &&
      fpo.preSansactionRequestDocuments &&
      fpo.preSansactionRequestDocuments.documents
    ) {
      for (const key in fpo.postSansactionRequestDocuments.documents) {
        documents = {
          ...documents,
          [key]: [
            ...(documents[key] ? documents[key] : []),
            ...fpo.postSansactionRequestDocuments.documents[key],
          ],
        };
      }

      for (const key in fpo.preSansactionRequestDocuments.documents) {
        documents = {
          ...documents,
          [key]: [
            ...(documents[key] ? documents[key] : []),
            ...fpo.preSansactionRequestDocuments.documents[key],
          ],
        };
      }
      setSansactionDocuments(documents);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpo.postSansactionRequestDocuments, fpo.preSansactionRequestDocuments]);

  const fetchApis = () => {
    let payload = { applicationuid: application_id };
    dispatch({
      type: FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
      payload: payload,
    });
    dispatch({
      type: FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
      payload: payload,
    });
    dispatch({
      type: CLEAN_SANSACTION_DOCUMETN_UPLOADS,
      payload: {},
    });
  };

  const handleDocumentSubmit = (data, ld_recid, type, documentName) => {
    const formData = new FormData();
    formData.append(documentName, data[ld_recid][0]);
    formData.append('ld_recid', ld_recid);
    let payload = { applicationuid: application_id, formData };
    if (type === 'post') {
      dispatch({
        type: FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
        payload: payload,
      });
    } else if (type === 'pre') {
      dispatch({
        type: FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
        payload: payload,
      });
    }
  };

  return (
    <div className='p-1'>
      <div className='mt-2'>
        {/* <FpoInfo applicationStatus={true} /> */}

        <div className='col-12 mt-0 py-3 px-0'>
          <div className='row nogutters' id='added'>
            <div className='col-12 pb-2'>
              <h4 className='f20 medium-font op70'>Requested Documents</h4>
            </div>
          </div>
        </div>

        <div className='col-12 pb-3 px-0'>
          <div className='row nogutters'>
            <div className='col-md-12 p-0 mb-5'>
              <div className='row '>
                <ShadowBox className='col-12 p-3'>
                  {(fpo && fpo.postSansactionRequestDocuments.inProgress) ||
                    (fpo.preSansactionRequestDocuments.inProgress && (
                      <Spinner animation='border' role='status'>
                        <span className='visually-hidden'></span>
                      </Spinner>
                    ))}

                  {fpo && sansactionDocuments ? (
                    <>
                      <RequestedDocuments
                        sansactionDocuments={sansactionDocuments}
                        handleDocumentSubmit={handleDocumentSubmit}
                      />
                    </>
                  ) : null}
                  {fpo &&
                    fpo.sansactionDocumentUpload &&
                    fpo.sansactionDocumentUpload.inProgress && (
                      <div className='absolute-center'>
                        <Spinner animation='border' role='status'>
                          <span className='visually-hidden'></span>
                        </Spinner>
                      </div>
                    )}
                </ShadowBox>
              </div>
            </div>
          </div>
          {fpo &&
            fpo.sansactionDocumentUpload &&
            !fpo.sansactionDocumentUpload.inProgress &&
            fpo.sansactionDocumentUpload.error && (
              <div className='d-flex justify-content-center'>
                <Alert
                  variant={'danger'}
                  style={{ width: '30vw' }}
                  className='text-center'
                >
                  {fpo.sansactionDocumentUpload.message
                    ? fpo.sansactionDocumentUpload.message
                    : 'Document upload Failed.'}
                </Alert>
              </div>
            )}

          {fpo &&
            fpo.sansactionDocumentUpload &&
            !fpo.sansactionDocumentUpload.inProgress &&
            fpo.sansactionDocumentUpload.success && (
              <div className='d-flex justify-content-center'>
                <Alert
                  variant={'success'}
                  style={{ width: '30vw' }}
                  className='text-center'
                >
                  {fpo.sansactionDocumentUpload.message
                    ? fpo.sansactionDocumentUpload.message
                    : 'Document upload Success.'}
                </Alert>
              </div>
            )}
        </div>
      </div>
    </div>
  );
};

export default FpoActionsTab;
