import {
  GET_PENDING_LOAN_APP_IN_ERROR,
  GET_PENDING_LOAN_APP_IN_PROGRESS,
  GET_PENDING_LOAN_APP_IN_SUCCESS,
  GET_LOAN_APP_DETAILS_IN_PROGRESS,
  GET_LOAN_APP_DETAILS_IN_SUCCESS,
  GET_LOAN_APP_DETAILS_IN_ERROR,
  GET_LOAN_DETAILS_IN_ERROR,
  GET_LOAN_DETAILS_IN_PROGRESS,
  GET_LOAN_DETAILS_IN_SUCCESS,
  GET_PENDING_LOAN_IN_ERROR,
  GET_PENDING_LOAN_IN_PROGRESS,
  GET_PENDING_LOAN_IN_SUCESS,
  SAVE_SHOW_INTEREST_IN_PROGRESS,
  SAVE_SHOW_INTEREST_IN_SUCCESS,
  SAVE_SHOW_INTEREST_IN_ERROR,
  SAVE_REJECT_INTEREST_IN_PROGRESS,
  SAVE_REJECT_INTEREST_IN_SUCCESS,
  SAVE_REJECT_INTEREST_IN_ERROR,
  SAVE_MAKE_AN_OFFER_IN_PROGRESS,
  SAVE_MAKE_AN_OFFER_IN_SUCCESS,
  SAVE_MAKE_AN_OFFER_IN_ERROR,
  CLEAN_SHOW_INTEREST,
  GET_PROFILE_DATA_IN_PROGRESS,
  GET_PROFILE_DATA_IN_SUCCESS,
  GET_PROFILE_DATA_IN_ERROR,
  GET_DOCUMENT_DATA_IN_PROGRESS,
  GET_DOCUMENT_DATA_IN_SUCCESS,
  GET_DOCUMENT_DATA_IN_ERROR,
  GET_SAM_ANALYSIS_IN_PROGRESS,
  GET_SAM_ANALYSIS_IN_SUCCESS,
  GET_SAM_ANALYSIS_IN_ERROR,
  PUT_SANSACTION_DOCUMENT_REQUEST_IN_PROGRESS,
  PUT_SANSACTION_DOCUMENT_REQUEST_IN_SUCCESS,
  PUT_SANSACTION_DOCUMENT_REQUEST_IN_ERROR,
  CLEAR_PUT_SANSACTION_DOCUMENT_REQUEST,
  GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS,
  GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR,
  GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS,
  GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR,
  GET_SAVED_OFFER_IN_PROGRESS,
  GET_SAVED_OFFER_IN_SUCCESS,
  GET_SAVED_OFFER_IN_ERROR,
  CLEAR_POST_MAKE_OFFER,
  PUT_MOVE_TO_LIVE_LOAN_IN_PROGRESS,
  PUT_MOVE_TO_LIVE_LOAN_IN_SUCCESS,
  PUT_MOVE_TO_LIVE_LOAN_IN_ERROR,
  LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_PROGRESS,
  LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_SUCCESS,
  LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_ERROR,
  GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS,
  GET_LOAN_REPAYMENT_SCHEDULES_IN_SUCCESS,
  GET_LOAN_REPAYMENT_SCHEDULES_IN_ERROR,
  FPO_GET_SAM_KEY_FINANCIALS_IN_PROGRESS,
  FPO_GET_SAM_KEY_FINANCIALS_IN_SUCCESS,
  FPO_GET_SAM_KEY_FINANCIALS_IN_ERROR,
  FPO_GET_SAM_RATIO_ANALYSIS_IN_PROGRESS,
  FPO_GET_SAM_RATIO_ANALYSIS_IN_SUCCESS,
  FPO_GET_SAM_RATIO_ANALYSIS_IN_ERROR,
  FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_PROGRESS,
  FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_SUCCESS,
  FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_ERROR,
  BANK_APPLICATION_FILTER_IN_PROGRESS,
  BANK_APPLICATION_FILTER_IN_SUCCESS,
  BANK_APPLICATION_FILTER_IN_ERROR,
  BANK_LOAN_FILTER_IN_PROGRESS,
  BANK_LOAN_FILTER_IN_SUCCESS,
  BANK_LOAN_FILTER_IN_ERROR,
  BANK_GET_EXPLORE_FPO_IN_PROGRESS,
  BANK_GET_EXPLORE_FPO_IN_SUCCESS,
  BANK_GET_EXPLORE_FPO_IN_ERROR,
  BANK_POST_EXPLORE_FPO_IN_PROGRESS,
  BANK_POST_EXPLORE_FPO_IN_SUCCESS,
  BANK_POST_EXPLORE_FPO_IN_ERROR,
  BANK_POST_COMPARE_FPO_IN_PROGRESS,
  BANK_POST_COMPARE_FPO_IN_SUCCESS,
  BANK_POST_COMPARE_FPO_IN_ERROR,
  BANK_GET_FPO_STATUS_COUNTS_IN_PROGRESS,
  BANK_GET_FPO_STATUS_COUNTS_IN_SUCCESS,
  BANK_GET_FPO_STATUS_COUNTS_IN_ERROR,
  BANK_GET_DASHBOARD_METRICS_IN_PROGRESS,
  BANK_GET_DASHBOARD_METRICS_IN_SUCCESS,
  BANK_GET_DASHBOARD_METRICS_IN_ERROR,
} from '../../types/banktypes';

const apiStatus = { inProgress: false, success: false, error: false };

const initialState = {
  loans: {
    documents: {},
    ...apiStatus,
  },
  selectedAppLoan: {
    ...apiStatus,
  },
  applications: {
    documents: {},
    ...apiStatus,
  },
  selectedLoan: {
    loanInfo: {},
    documents: {},
    ...apiStatus,
  },
  showInterest: {
    message: undefined,
    proposal_id: undefined,
    ...apiStatus,
  },
  rejectShowInterest: {
    message: undefined,
    ...apiStatus,
  },
  makeAnOffer: {
    message: undefined,
    ...apiStatus,
  },
  profileData: {
    message: undefined,
    ...apiStatus,
  },
  documentData: {
    message: undefined,
    documents: [],
    ...apiStatus,
  },
  samAnalysis: {
    documents: [],
    ...apiStatus,
  },
  sansactionDocumentRequest: {
    ...apiStatus,
  },
  preSansactionRequestDocuments: {
    ...apiStatus,
    documents: [],
  },
  postSansactionRequestDocuments: {
    ...apiStatus,
    documents: [],
  },
  savedOffer: {
    ...apiStatus,
    data: {},
  },
  samKeyFinanials: {
    ...apiStatus,
  },
  samRatioAnalysis: {
    ...apiStatus,
  },
  applicationFilter: {
    ...apiStatus,
  },
  loanFilter: {
    ...apiStatus,
  },
  exploreFpo: {
    ...apiStatus,
  },
  fpoStatusCounts: {
    ...apiStatus,
  },
  dashboardMetrics: {
    ...apiStatus,
  },
};
export const bankReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case GET_PENDING_LOAN_APP_IN_PROGRESS:
      return {
        ...state,
        ...(state.loans.inProgress = true),
        ...(state.loans.error = false),
        ...(state.loans.documents = []),
      };

    case GET_PENDING_LOAN_APP_IN_SUCCESS:
      return {
        ...state,
        loans: { ...state.loans, documents: payload.documents },
      };

    case GET_PENDING_LOAN_APP_IN_ERROR:
      return { ...state, inProgress: false, error: payload, documents: [] };

    case GET_LOAN_APP_DETAILS_IN_PROGRESS:
      return {
        ...state,
        selectedAppLoan: {
          loanInfo: payload.loanInfo,
          ...apiStatus,
          inProgress: true,
        },
      };

    case GET_LOAN_APP_DETAILS_IN_SUCCESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          loanSummary: payload.documents,
          basicDetails: payload.basicDetails,
          ...apiStatus,
          success: true,
        },
      };

    case GET_LOAN_APP_DETAILS_IN_ERROR:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          error: true,
          message: payload,
          documents: {},
        },
      };

    case PUT_MOVE_TO_LIVE_LOAN_IN_PROGRESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          moveToLive: { ...apiStatus, inProgress: true },
        },
      };

    case PUT_MOVE_TO_LIVE_LOAN_IN_SUCCESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          moveToLive: { ...apiStatus, success: true, message: payload.message },
        },
      };

    case PUT_MOVE_TO_LIVE_LOAN_IN_ERROR:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          moveToLive: { ...apiStatus, error: true, message: payload.message },
        },
      };

    case LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_PROGRESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentScheduleUpload: { ...apiStatus, inProgress: true },
        },
      };

    case LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_SUCCESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentScheduleUpload: {
            ...apiStatus,
            success: true,
            message: payload.message,
          },
        },
      };

    case LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_ERROR:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentScheduleUpload: {
            ...apiStatus,
            error: true,
            message: payload.message,
          },
        },
      };

    case GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentSchedules: { ...apiStatus, inProgress: true },
        },
      };

    case GET_LOAN_REPAYMENT_SCHEDULES_IN_SUCCESS:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentSchedules: {
            ...apiStatus,
            success: true,
            ...action.payload,
          },
        },
      };

    case GET_LOAN_REPAYMENT_SCHEDULES_IN_ERROR:
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          repaymentSchedules: {
            ...apiStatus,
            error: true,
            message: payload.message,
          },
        },
      };

    case GET_PENDING_LOAN_IN_PROGRESS:
      return {
        ...state,
        ...(state.applications.inProgress = true),
        ...(state.applications.error = false),
        ...(state.applications.documents = []),
      };

    case GET_PENDING_LOAN_IN_SUCESS:
      return { ...state, ...payload };

    case GET_PENDING_LOAN_IN_ERROR:
      return { ...state, inProgress: false, error: payload, documents: [] };

    case GET_LOAN_DETAILS_IN_PROGRESS:
      return {
        ...state,
        ...(state.selectedLoan.inProgress = true),
        ...(state.selectedLoan.error = false),
        ...(state.selectedLoan.documents = []),
        ...(state.selectedLoan.loanInfo = payload.application),
      };

    case GET_LOAN_DETAILS_IN_SUCCESS:
      return {
        ...state,
        ...(state.selectedLoan.documents = payload),
        ...(state.selectedLoan.inProgress = false),
        ...(state.selectedLoan.success = true),
        ...(state.selectedLoan.error = undefined),
      };

    case GET_LOAN_DETAILS_IN_ERROR:
      return { ...state, selectedLoan: { ...apiStatus, ...payload } };

    case SAVE_SHOW_INTEREST_IN_PROGRESS: {
      return {
        ...state,
        ...(state.showInterest.inProgress = true),
        ...(state.showInterest.error = false),
        ...(state.showInterest.success = false),
      };
    }

    case SAVE_SHOW_INTEREST_IN_SUCCESS: {
      return { ...state, ...payload };
    }

    case SAVE_SHOW_INTEREST_IN_ERROR: {
      return { ...state, ...payload };
    }

    case SAVE_REJECT_INTEREST_IN_PROGRESS: {
      return {
        ...state,
        ...(state.rejectShowInterest.inProgress = true),
        ...(state.rejectShowInterest.error = false),
        ...(state.rejectShowInterest.success = false),
      };
    }

    case SAVE_REJECT_INTEREST_IN_SUCCESS: {
      return { ...state, ...payload };
    }

    case SAVE_REJECT_INTEREST_IN_ERROR: {
      return { ...state, ...payload };
    }

    case SAVE_MAKE_AN_OFFER_IN_PROGRESS: {
      return {
        ...state,
        ...(state.makeAnOffer.inProgress = true),
        ...(state.makeAnOffer.error = false),
        ...(state.makeAnOffer.success = false),
      };
    }

    case SAVE_MAKE_AN_OFFER_IN_SUCCESS: {
      return { ...state, ...payload };
    }

    case SAVE_MAKE_AN_OFFER_IN_ERROR: {
      return { ...state, ...payload };
    }

    case CLEAN_SHOW_INTEREST: {
      return {
        ...state,
        showInterest: { ...apiStatus },
        rejectShowInterest: { ...apiStatus },
      };
    }

    case GET_PROFILE_DATA_IN_PROGRESS: {
      return {
        ...state,
        profileData: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_PROFILE_DATA_IN_SUCCESS: {
      return {
        ...state,
        profileData: {
          ...payload,
          ...apiStatus,
          success: true,
        },
      };
    }

    case GET_PROFILE_DATA_IN_ERROR: {
      return {
        ...state,
        profileData: {
          ...payload,
          ...apiStatus,
          error: true,
          documents: [],
        },
      };
    }

    case GET_DOCUMENT_DATA_IN_PROGRESS: {
      return {
        ...state,
        documentData: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_DOCUMENT_DATA_IN_SUCCESS: {
      return {
        ...state,
        documentData: {
          ...payload,
          ...apiStatus,
          success: true,
        },
      };
    }

    case GET_DOCUMENT_DATA_IN_ERROR: {
      return {
        ...state,
        documentData: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case GET_SAM_ANALYSIS_IN_PROGRESS: {
      return {
        ...state,
        samAnalysis: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_SAM_ANALYSIS_IN_SUCCESS: {
      return {
        ...state,
        samAnalysis: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case GET_SAM_ANALYSIS_IN_ERROR: {
      return {
        ...state,
        samAnalysis: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case PUT_SANSACTION_DOCUMENT_REQUEST_IN_PROGRESS: {
      return {
        ...state,
        sansactionDocumentRequest: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case PUT_SANSACTION_DOCUMENT_REQUEST_IN_SUCCESS: {
      return {
        ...state,
        sansactionDocumentRequest: {
          ...payload,
          ...apiStatus,
          success: true,
        },
      };
    }

    case PUT_SANSACTION_DOCUMENT_REQUEST_IN_ERROR: {
      return {
        ...state,
        sansactionDocumentRequest: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case CLEAR_PUT_SANSACTION_DOCUMENT_REQUEST: {
      return {
        ...state,
        sansactionDocumentRequest: {
          ...apiStatus,
        },
      };
    }

    case GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case GET_SAVED_OFFER_IN_PROGRESS: {
      return {
        ...state,
        savedOffer: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case GET_SAVED_OFFER_IN_SUCCESS: {
      return {
        ...state,
        savedOffer: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case GET_SAVED_OFFER_IN_ERROR: {
      return {
        ...state,
        savedOffer: {
          ...apiStatus,
          ...payload,
          data: {},
          error: true,
        },
      };
    }

    case CLEAR_POST_MAKE_OFFER: {
      return {
        ...state,
        makeAnOffer: {
          ...apiStatus,
        },
      };
    }

    case FPO_GET_SAM_KEY_FINANCIALS_IN_PROGRESS: {
      return {
        ...state,
        samKeyFinanials: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_SAM_KEY_FINANCIALS_IN_SUCCESS: {
      return {
        ...state,
        samKeyFinanials: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_SAM_KEY_FINANCIALS_IN_ERROR: {
      return {
        ...state,
        samKeyFinanials: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case FPO_GET_SAM_RATIO_ANALYSIS_IN_PROGRESS: {
      return {
        ...state,
        samRatioAnalysis: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_SAM_RATIO_ANALYSIS_IN_SUCCESS: {
      return {
        ...state,
        samRatioAnalysis: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_SAM_RATIO_ANALYSIS_IN_ERROR: {
      return {
        ...state,
        samRatioAnalysis: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_PROGRESS: {
      return {
        ...state,
        samFinancialPerformance: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_SUCCESS: {
      return {
        ...state,
        samFinancialPerformance: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_ERROR: {
      return {
        ...state,
        samFinancialPerformance: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case BANK_APPLICATION_FILTER_IN_PROGRESS: {
      return {
        ...state,
        applicationFilter: {
          ...apiStatus,
          category: payload.category,
          inProgress: true,
        },
      };
    }

    case BANK_APPLICATION_FILTER_IN_SUCCESS: {
      return {
        ...state,
        applicationFilter: {
          ...state.applicationFilter,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_APPLICATION_FILTER_IN_ERROR: {
      return {
        ...state,
        applicationFilter: {
          ...state.applicationFilter,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_LOAN_FILTER_IN_PROGRESS: {
      return {
        ...state,
        loanFilter: {
          ...apiStatus,
          category: payload.category,
          inProgress: true,
        },
      };
    }

    case BANK_LOAN_FILTER_IN_SUCCESS: {
      return {
        ...state,
        loanFilter: {
          ...state.loanFilter,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_LOAN_FILTER_IN_ERROR: {
      return {
        ...state,
        loanFilter: {
          ...state.loanFilter,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_GET_EXPLORE_FPO_IN_PROGRESS: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case BANK_GET_EXPLORE_FPO_IN_SUCCESS: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_GET_EXPLORE_FPO_IN_ERROR: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_POST_EXPLORE_FPO_IN_PROGRESS: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case BANK_POST_EXPLORE_FPO_IN_SUCCESS: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_POST_EXPLORE_FPO_IN_ERROR: {
      return {
        ...state,
        exploreFpo: {
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_POST_COMPARE_FPO_IN_PROGRESS: {
      return {
        ...state,
        compareFpo: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case BANK_POST_COMPARE_FPO_IN_SUCCESS: {
      return {
        ...state,
        compareFpo: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_POST_COMPARE_FPO_IN_ERROR: {
      return {
        ...state,
        compareFpo: {
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_GET_FPO_STATUS_COUNTS_IN_PROGRESS: {
      return {
        ...state,
        fpoStatusCounts: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case BANK_GET_FPO_STATUS_COUNTS_IN_SUCCESS: {
      return {
        ...state,
        fpoStatusCounts: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_GET_FPO_STATUS_COUNTS_IN_ERROR: {
      return {
        ...state,
        fpoStatusCounts: {
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case BANK_GET_DASHBOARD_METRICS_IN_PROGRESS: {
      return {
        ...state,
        dashboardMetrics: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case BANK_GET_DASHBOARD_METRICS_IN_SUCCESS: {
      return {
        ...state,
        dashboardMetrics: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case BANK_GET_DASHBOARD_METRICS_IN_ERROR: {
      return {
        ...state,
        dashboardMetrics: {
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    default:
      return state;
  }
};
