import React, { useEffect, useState } from 'react';
import { Form, Spinner } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import {
  APPLICATION_ACTION_FILE_UPLOAD,
  LOAN_FIELDS,
} from '../../../constants';
import { application_action_docs_types } from '../../../data';
import { CLEAR_PUT_SANSACTION_DOCUMENT_REQUEST } from '../../../redux/types/banktypes';
import { useSelector } from 'react-redux';
let fields_count = 1;

const SansactionDocumentRequest = ({ handleDocumentSubmit, resetRequest }) => {
  const {
    register,
    handleSubmit,
    reset,
    unregister,
    formState: { errors },
  } = useForm();
  const [reqDocuments, setReqDocuments] = useState([1]);
  useEffect(() => {
    reset();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [resetRequest]);
  const bank = useSelector((state) => state.bank);

  useEffect(() => {
    if (bank.sansactionDocumentRequest.success) {
      reset();
      // eslint-disable-next-line array-callback-return
      reqDocuments.map((val) => {
        unregister(`documentdescription_${val}`);
        unregister(`documentname_${val}`);
        unregister(`documenttype_${val}`);
      });
      fields_count = 1;
      setReqDocuments([1]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.sansactionDocumentRequest?.success]);

  const handleNewRequest = () => {
    fields_count++;
    setReqDocuments([...reqDocuments, fields_count]);
    // dispatch({
    //   type: CLEAR_PUT_SANSACTION_DOCUMENT_REQUEST,
    //   payload: {},
    // });
  };
  const handleDocsSubmit = (data) => {
    let forms = { pre: [], post: [] };
    // eslint-disable-next-line array-callback-return
    reqDocuments.map((val, key) => {
      let form = {
        [APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentName]]:
          data[
            `${APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentName]}_${val}`
          ],
        [APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentNameDescription]]:
          data[
            `${
              APPLICATION_ACTION_FILE_UPLOAD[
                LOAN_FIELDS.documentNameDescription
              ]
            }_${val}`
          ],
      };
      if (data[`documenttype_${val}`] === 'pre_sanction') {
        forms.pre.push(form);
      }

      if (data[`documenttype_${val}`] === 'post_sanction') {
        forms.post.push(form);
      }
    });
    handleDocumentSubmit(forms);
  };

  const removeItem = (index, val) => {
    let array = reqDocuments;
    array.splice(index, 1);
    setReqDocuments([...array]);
    unregister(`documentdescription_${val}`);
    unregister(`documentname_${val}`);
    unregister(`documenttype_${val}`);
  };

  return (
    <div className='py-2'>
      <Form onSubmit={handleSubmit(handleDocsSubmit)}>
        {reqDocuments.map((val, key) => (
          <div
            className='row nogutters d-flex justify-content-between mb-3'
            key={key}
          >
            <div className='col-md-3'>
              <label className='f14 medium-font' htmlFor=''>
                Document Name <span className='text-danger h5'>*</span>
              </label>
              <input
                type='text'
                className='form-control '
                name={
                  APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentName] +
                  `_${val}`
                }
                {...register(
                  APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentName] +
                    `_${val}`,
                  {
                    required: true,
                  }
                )}
              />
              {errors[
                APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentName] +
                  `_${val}`
              ] && (
                <span className='text-danger'>Enter valid Document Name.</span>
              )}
            </div>
            <div className='col-md-3'>
              <label className='f14 medium-font' htmlFor=''>
                Details
              </label>
              <input
                type='text'
                className='form-control'
                name={
                  APPLICATION_ACTION_FILE_UPLOAD[
                    LOAN_FIELDS.documentNameDescription
                  ] + `_${val}`
                }
                {...register(
                  APPLICATION_ACTION_FILE_UPLOAD[
                    LOAN_FIELDS.documentNameDescription
                  ] + `_${val}`,
                  {}
                )}
              />
            </div>
            <div className='col-md-3'>
              <label className='f14 medium-font' htmlFor=''>
                Type <span className='text-danger h5'>*</span>
              </label>
              <select
                name={
                  APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentType] +
                  `_${val}`
                }
                className='form-control '
                {...register(
                  APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentType] +
                    `_${val}`,
                  {
                    required: true,
                  }
                )}
              >
                <option value=''>Select</option>
                {application_action_docs_types.map(({ value, text }, key) => (
                  <option key={key} value={value}>
                    {text}
                  </option>
                ))}
              </select>
              {errors[
                APPLICATION_ACTION_FILE_UPLOAD[LOAN_FIELDS.documentType] +
                  `_${val}`
              ] && <span className='text-danger'>Select document type.</span>}
            </div>
            <div className='ml-n5 mt-1 mr-4'>
              <button
                type='button'
                className='btn p-0 shadow-none mt-4 text-danger mt-1'
                onClick={() => removeItem(key, val)}
              >
                <i className='fa fa-close'></i>
              </button>
            </div>
          </div>
        ))}
        <div className='d-flex justify-content-between align-items-center mt-5 px-3'>
          <button
            type='button'
            className='btn btn-default f16 bold-font br-12'
            onClick={() => handleNewRequest()}
          >
            + Add New Request
          </button>
          {bank &&
            bank.sansactionDocumentRequest &&
            !bank.sansactionDocumentRequest.inProgress && (
              <button type='submit' className='btn  btn-primary br-12'>
                Save
              </button>
            )}
          {bank &&
            bank.sansactionDocumentRequest &&
            bank.sansactionDocumentRequest.inProgress && (
              <Spinner animation='border' role='status'>
                <span className='visually-hidden'></span>
              </Spinner>
            )}
        </div>
      </Form>
    </div>
  );
};

export default SansactionDocumentRequest;
