import Modal from 'react-bootstrap/Modal';
import { CloseOutline } from 'react-ionicons';
import SimpleApplicationListTable from '../Tables/SimpleApplicationListTable';

function MaturedLoanModal({ show, onHide, applications, detailsUrl }) {
  const handleClose = () => onHide(false);
  return (
    <Modal
      size='md'
      aria-labelledby='contained-modal-title-vcenter'
      centered
      show={show}
      onHide={handleClose}
      keyboard={true}
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 '
        >
          Matured Loan
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body className='custom-scrollbar' style={{ height: '500px' }}>
        <SimpleApplicationListTable
          applications={applications}
          detailsUrl={detailsUrl}
        />
      </Modal.Body>
    </Modal>
  );
}

export default MaturedLoanModal;
