import { Alert, Spinner } from 'react-bootstrap';
import { useSelector } from 'react-redux';
const ShowInterestForm = (props) => {
  const bankState = useSelector((state) => state.bank);
  return (
    <>
      <div className='d-flex justify-content-center mt-5'>
        <div className='col-6 mt-5'>
          <div className='d-flex align-items-center justify-content-center flex-column mt-3'>
            <div className=''>
              <i className='fa fa-thumbs-up rounded-circle show-interest-heart'></i>
            </div>
            <div className='pt-3'>
              <div className='show-interest-text mb-5 mt-4 '>
                "Show Interest" to unlock the following Sections <br />
                Detailed Profile, Documents and Samunnati Analysis
              </div>
            </div>
            <div className='pt-1'>
              <section className='d-flex justify-content-end ml-3'>
                {bankState &&
                bankState.showInterest.inProgress |
                  bankState.rejectShowInterest.inProgress ? null : (
                  <button
                    className='loan-application-btn btn btn-secondary ml-4'
                    onClick={() => props.openRejectModel(true)}
                    disabled={props.disableShowInterestBtn}
                  >
                    IGNORE
                  </button>
                )}
                {bankState &&
                bankState.showInterest.inProgress |
                  bankState.rejectShowInterest.inProgress ? (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                ) : (
                  <button
                    className='loan-application-btn btn btn-primary ml-4'
                    onClick={() =>
                      props.handleShowInterest(
                        props.activeKey,
                        'next',
                        'interest'
                      )
                    }
                    disabled={props.disableShowInterestBtn}
                  >
                    SHOW INTEREST
                  </button>
                )}
              </section>
            </div>
            <br />
            <br />
            {bankState &&
            bankState.showInterest.error &&
            !bankState.showInterest.inProgress ? (
              <Alert
                variant={'danger'}
                style={{ width: '30vw' }}
                className='text-center'
              >
                {bankState.showInterest.message
                  ? bankState.showInterest.message
                  : 'Failed to save Show Interest'}
              </Alert>
            ) : (
              <>
                {bankState && bankState.showInterest.success && (
                  <Alert
                    variant={'success'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {bankState.showInterest.message
                      ? bankState.showInterest.message
                      : 'Successfully saved save Show Interest'}
                  </Alert>
                )}
              </>
            )}

            {bankState &&
            bankState.rejectShowInterest.error &&
            !bankState.rejectShowInterest.inProgress ? (
              <Alert
                variant={'danger'}
                style={{ width: '30vw' }}
                className='text-center'
              >
                {bankState.rejectShowInterest.message
                  ? bankState.rejectShowInterest.message
                  : 'Failed to save Ignore show Interest'}
              </Alert>
            ) : (
              <>
                {bankState && bankState.rejectShowInterest.success && (
                  <Alert
                    variant={'success'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {bankState.rejectShowInterest.message
                      ? bankState.rejectShowInterest.message
                      : 'Successfully saved save Ignore show Interest'}
                  </Alert>
                )}
              </>
            )}
          </div>
        </div>
      </div>
      <button
        className='loan-application-btn btn-secondary show-interest-back-btn '
        onClick={() => props.handleTabChange(props.activeKey, 'prev')}
      >
        BACK
      </button>
    </>
  );
};

export default ShowInterestForm;
