import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Spinner } from 'react-bootstrap';
import { request_managements, upcoming_repayments } from '../../../data';
import { adminActions } from '../../../redux/actions';
import './admindashboard.css';
import { useDispatch, useSelector } from 'react-redux';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import Form from 'react-bootstrap/Form';
import MaturedLoanModal from '../../../components/Modals/MaturedLoanModal';
import UpcomingRepaymentsModal from '../../../components/Modals/UpcomingRepaymentsModal';

import RequestManagementModal from '../../../components/Modals/RequestManagementModal';
import GeneralBox from '../../../components/GeneralBox';
import ProfileStatusTable from '../../../components/Tables/profileStatusTable';
import SimpleApplicationListTable from '../../../components/Tables/SimpleApplicationListTable';

export default function AdminDashboard() {
  const adminState = useSelector((state) => state.admin);
  const [modalShow, setModalShow] = useState(false);

  const [upcomingShow, setUpcomingShow] = useState(false);
  const [maturedLoanModal, setMaturedLoanModal] = useState(false);
  const [fpoApplicationType, setFpoApplicationType] = useState('applications');
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [activeApplicationTab, setactiveApplicationTab] = useState('new');
  const [activeLoanTab, setactiveLoanTab] = useState('new');

  useEffect(() => {
    dispatch(adminActions.getAllApplicationsInProgress());
    dispatch(adminActions.getAllLoansInProgress());
  }, []);

  const handleFpoViewAll = () => {
    let path = '/admin/fpo-applications/?type=' + fpoApplicationType;
    if (fpoApplicationType === 'applications') {
      path += '&&category=' + activeApplicationTab;
    } else {
      path += '&&category=' + activeLoanTab;
    }
    navigate(path);
  };

  const handleApplicationViewButton = (application) => {
    dispatch(adminActions.getApplicationDetailsInProgress(application));
    let path = '/admin/application/status';
    navigate(path + `?application_id=${application.applicationuid}`);
  };

  const handleViewApplicationDetails = ({ applicationuid }) => {
    let path = '/admin/fpo-applications/';
    navigate(
      path +
        `?application_id=${applicationuid}&&type=${fpoApplicationType}&&category=${activeApplicationTab}`
    );
  };

  const handleViewLoanDetails = ({ applicationuid }) => {
    let path = '/admin/fpo-applications/';
    navigate(
      path +
        `?application_id=${applicationuid}&&type=${fpoApplicationType}&&category=${activeLoanTab}`
    );
  };

  return (
    <section className='primary-bg pb-5'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-6'>
            {fpoApplicationType === 'applications' && (
              <GeneralBox
                isButtonPresent
                buttonPosition='bottom'
                title='FPO Applications'
                applicationPills
                onTabChange={(tabId) => setactiveApplicationTab(tabId)}
                handleViewAll={handleFpoViewAll}
                pills={['new', 'active', 'closed']}
                selectBox={
                  <Form.Select
                    className='form-control'
                    onChange={(e) => setFpoApplicationType(e.target.value)}
                    value={fpoApplicationType}
                  >
                    <option value='applications'>Applications</option>
                    <option value='loans'>Loans</option>
                  </Form.Select>
                }
                type='applications'
                applicationsCounts={
                  adminState &&
                  !adminState.applications.inProgress &&
                  adminState.applications.success && {
                    applied:
                      adminState.applications.success['new_application'].length,
                    active:
                      adminState.applications.success['active_application']
                        .length,
                    closed:
                      adminState.applications.success['closed_application']
                        .length,
                  }
                }
              >
                {adminState &&
                  !adminState.applications.inProgress &&
                  adminState.applications.success && (
                    <div
                      className='col-12 mt-4 pl-2 pr-2 custom-scrollbar'
                      style={{ height: '300px' }}
                    >
                      <ProfileStatusTable
                        applications={adminState.applications.success[
                          activeApplicationTab + '_application'
                        ].slice(0, 4)}
                        fpoActive={
                          activeApplicationTab === 'active' ? true : false
                        }
                        fpoApplied={
                          activeApplicationTab === 'applied' ? true : false
                        }
                        fpoClosed={
                          activeApplicationTab === 'closed' ? true : false
                        }
                        handleDetailsButton={handleViewApplicationDetails}
                        handleViewButton={handleApplicationViewButton}
                      />
                    </div>
                  )}
              </GeneralBox>
            )}

            {fpoApplicationType === 'loans' && (
              <GeneralBox
                isButtonPresent
                buttonPosition='bottom'
                title='FPO Applications'
                loanTabPills
                onTabChange={(tabId) => setactiveLoanTab(tabId)}
                handleViewAll={handleFpoViewAll}
                type='loans'
                pills={['new', 'active', 'closed']}
                selectBox={
                  <Form.Select
                    className='form-control'
                    onChange={(e) => setFpoApplicationType(e.target.value)}
                    value={fpoApplicationType}
                  >
                    <option value='applications'>Applications</option>
                    <option value='loans'>Loans</option>
                  </Form.Select>
                }
                applicationsCounts={
                  adminState &&
                  !adminState.loans.inProgress &&
                  adminState.loans.success && {
                    new: adminState.loans.success['new_loans'].length,
                    active: adminState.loans.success['active_loans'].length,
                    closed: adminState.loans.success['closed_loans'].length,
                  }
                }
              >
                {adminState &&
                  !adminState.loans.inProgress &&
                  adminState.loans.success && (
                    <div
                      className='col-12 mt-4 pl-2 pr-2 custom-scrollbar'
                      style={{ height: '300px' }}
                    >
                      <ProfileStatusTable
                        applications={adminState.loans.success[
                          activeLoanTab + '_loans'
                        ].slice(0, 4)}
                        fpoActive={activeLoanTab === 'active' ? true : false}
                        fpoApplied={activeLoanTab === 'applied' ? true : false}
                        fpoClosed={activeLoanTab === 'closed' ? true : false}
                        handleDetailsButton={handleViewLoanDetails}
                        handleViewButton={handleApplicationViewButton}
                      />
                    </div>
                  )}
              </GeneralBox>
            )}
          </div>

          <div className='col-md-6'>
            <div className='general-box bg-white '>
              <div className='row '>
                <div className='col-8 p-0 pl-2 mt-2 text-left'>
                  <h4 className='f20 medium-font fw600 pl-3 ml-1 pt-3'>
                    Request Management
                  </h4>
                </div>
                <div className='text-right'>
                  <button className='btn btn-blue and btn btn-default br-12 mt-3 req-btn'>
                    Bank
                  </button>
                  {/* <AddLendarPartner
                    show={reviewDoc}
                    onHide={() => setReviewDoc(false)}
                  /> */}
                  <button className='btn btn-primary br-12 mt-3 fpo-btn'>
                    {' '}
                    FPOs
                  </button>
                </div>
                <div className='col-12'>
                  <div className='pl-3 mt-3 pr-3'>
                    <table className='table  w-100'>
                      <thead className=''>
                        <tr className='muted f12'>
                          <th>LOAN ID</th>
                          <th>BANK</th>
                          <th>DATE</th>
                          <th>STATUS</th>
                          <th>ACTION</th>
                        </tr>
                      </thead>
                      <tbody className='f12 medium-font'>
                        {request_managements.map((request_management, key) => (
                          <tr key={key}>
                            <td>{request_management.loan_id}</td>
                            <td>{request_management.bank}</td>
                            <td>{request_management.date}</td>
                            <td>
                              <span className='status-bg'>
                                {request_management.status}
                              </span>
                            </td>
                            <td>
                              <i className='muted fa fa-eye p-1' />
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                  <div className='text-center py-4'>
                    <button
                      className='btn btn-default br-12'
                      variant='primary'
                      onClick={() => setModalShow(true)}
                    >
                      View All
                    </button>
                  </div>
                  <RequestManagementModal
                    show={modalShow}
                    onHide={() => setModalShow(false)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className='row mt-3'>
          <div className='col-md-5'>
            <div className='general-box bg-white'>
              <div className='row pt-2'>
                <div className='col-8 text-left'>
                  <h4 className='f20 medium-font fw600 pl-2 ml-1 pt-3'>
                    Upcoming Repayments
                  </h4>
                </div>
                <div className='col-4'>
                  <button
                    className='btn btn-default br-12 mt-3 view-btn'
                    variant='primary'
                    onClick={() => setUpcomingShow(true)}
                  >
                    View All
                  </button>
                </div>
                <div
                  style={{
                    display: 'block',
                    width: 700,
                    padding: 30,
                    height: 310,
                  }}
                >
                  <Tabs defaultActiveKey='input' className='Custom-tabs'>
                    <Tab eventKey='input' title='Input 5'>
                      <table className='table  w-100'>
                        <thead className=''>
                          <tr className='muted f12'>
                            <th>LOAN ID</th>
                            <th>DUE BY</th>
                            <th>AMOUNT</th>
                          </tr>
                        </thead>
                        <tbody className='f12 medium-font'>
                          {upcoming_repayments.map(
                            (upcoming_repayment, key) => (
                              <tr key={key}>
                                <td>{upcoming_repayment.loan_id}</td>
                                <td>{upcoming_repayment.date}</td>
                                <td>{upcoming_repayment.amount}</td>
                              </tr>
                            )
                          )}
                        </tbody>
                      </table>
                    </Tab>
                    <Tab eventKey='output' title='Output 7'></Tab>
                    <Tab eventKey='infra' title='InfraStructure'></Tab>
                  </Tabs>
                </div>
              </div>
            </div>
          </div>

          <div className='col-md-4 p-0 '>
            <GeneralBox
              isButtonPresent
              buttonPosition='top'
              title='Matured Loan'
              handleViewAll={() => setMaturedLoanModal(true)}
            >
              {adminState && adminState.loans && adminState.loans.inProgress && (
                <div className='container d-flex justify-content-center'>
                  <Spinner animation='border' role='status' className='TEST'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                </div>
              )}
              {adminState &&
                adminState.loans &&
                adminState.loans.success &&
                !adminState.loans.inProgress && (
                  <div
                    className='container custom-scrollbar'
                    style={{ height: '280px' }}
                  >
                    <SimpleApplicationListTable
                      applications={adminState?.loans?.success?.closed_loans?.slice(
                        0,
                        5
                      )}
                      detailsUrl={
                        'admin/fpo-applications/?type=loans&&category=closed'
                      }
                    />
                  </div>
                )}
            </GeneralBox>
          </div>
        </div>
      </div>
      <MaturedLoanModal
        show={maturedLoanModal}
        onHide={() => setMaturedLoanModal(false)}
        applications={adminState?.loans?.success?.closed_loans}
        detailsUrl={'admin/fpo-applications/?type=loans&&category=closed'}
      />
      <UpcomingRepaymentsModal
        show={upcomingShow}
        onHide={() => setUpcomingShow(false)}
        title={' Upcoming Repayments'}
      />
    </section>
  );
}
