import BankDashboard from '../pages/bank/dashboard';
import ExploreFpo from '../pages/bank/fpo/ExploreFpo';
import BankLoan from '../pages/bank/loan/BankLoan';

const BankRoutes = [
  {
    name: 'Bank Dashboard',
    path: '/dashboard',
    base: '',
    element: <BankDashboard />,
  },
  {
    name: 'Bank Loan',
    path: '/loan-applications',
    base: '',
    element: <BankLoan />,
  },
  {
    name: 'Bank FPOs',
    path: '/explore-fpos',
    base: '',
    element: <ExploreFpo />,
  },
];

export default BankRoutes;
