import { useEffect } from "react"
import {
    Routes,
    Route
} from "react-router-dom";
import FpoRoutes from "../routes/fpo.routes";

export default function FpoLayout(){

    useEffect(() => {

    }, []); 

    const renderRoutes = (routes) =>  {
        return routes.map(({path, element}, index) => {
            return (
                <Route path={path} element={element} key={`route-fpo-${index}`} />
            )
        })
    }

    return (
       <Routes> 
           {renderRoutes(FpoRoutes)}
       </Routes>
    )
}