import Modal from 'react-bootstrap/Modal';
import React, { useEffect } from 'react';
import Form from 'react-bootstrap/Form';
import './adminStyle.css';
import { CloseOutline } from 'react-ionicons';
import { useForm } from 'react-hook-form';
import { ADMIN_APPLICATION_STATUS, LOAN_FIELDS } from '../../constants';

function CancelApplicationModal({ show, onHide, handleCancelApplication }) {
  const handleClose = () => onHide(false);
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({
    defaultValues: {
      [ADMIN_APPLICATION_STATUS[LOAN_FIELDS.adminCancelApplication]]: '',
    },
  });

  useEffect(() => {
    if (show) reset();
  }, [show]);

  return (
    <Modal
      show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      dialogClassName='modal-40w'
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 ml-3'
        >
          Cancel Application
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body>
        <div className='text-center cancel-app-sec'>
          <i
            className='fa fa-times-circle-o cancle-app-icon'
            aria-hidden='true'
          ></i>
          <p className='f17 ml-3 mr-3'>
            Are You Sure You Want To Withdraw This Application? <br /> This
            Action Cannot Be Reversed
          </p>
          <Form
            onSubmit={handleSubmit((data) => {
              handleCancelApplication(data);
              onHide(false);
            })}
          >
            <Form.Control
              as='textarea'
              className=' p-2 pl-3 w-579 text-area'
              {...register(
                ADMIN_APPLICATION_STATUS[LOAN_FIELDS.adminCancelApplication],
                {
                  required: true,
                  minLength: 5,
                }
              )}
            />
            {errors[
              ADMIN_APPLICATION_STATUS[LOAN_FIELDS.adminCancelApplication]
            ] && (
              <span className='text-danger'>
                Please enter reason for cancel above 4 characters
              </span>
            )}
            <br />
            <button className='btn btn-danger br-12 cancle-app-btn mb-3 mt-2'>
              CANCEL APPLICATION
            </button>
          </Form>
        </div>
      </Modal.Body>
    </Modal>
  );
}
export default CancelApplicationModal;
