import { Table, Badge } from 'react-bootstrap';
import Modal from 'react-bootstrap/Modal';
import { CloseOutline } from 'react-ionicons';

function BankRequestManagementModal({ show, onHide, applications }) {
  const handleClose = () => onHide(false);
  return (
    <Modal
      size='lg'
      aria-labelledby='contained-modal-title-vcenter'
      centered
      show={show}
      onHide={handleClose}
      keyboard={true}
    >
      <Modal.Header>
        <Modal.Title id='contained-modal-title-vcenter'>
          Request Management
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body className='custom-scrollbar' style={{ height: '500px' }}>
        <div className='container'>
          <Table className=' bank-request-table'>
            <thead className=''>
              <tr className='muteds f12'>
                <th>REQUEST ID</th>
                <th>DATE</th>
                <th className='text-center'>STATUS</th>
                <th className='text-right'>ACTION</th>
              </tr>
            </thead>
            <tbody className='f12 medium-font'>
              {applications &&
                applications.map((application, key) => (
                  <tr key={key}>
                    <td>{application.applicationuid}</td>
                    <td>{application.date}</td>
                    <td className='text-center'>
                      <Badge
                        className={`bank-request-badge ${application.status.toLowerCase()}`}
                      >
                        {application.status}
                      </Badge>
                    </td>
                    <td className='text-right'>
                      <i className='fa fa-pencil'></i>
                    </td>
                  </tr>
                ))}
            </tbody>
          </Table>
        </div>
      </Modal.Body>
    </Modal>
  );
}
export default BankRequestManagementModal;
