import './Card.scss';
import * as dayjs from 'dayjs';

const FpoCard = ({
  fpoid,
  fponame,
  foundedon,
  membershipbase,
  villagescovered,
  turnover,
  sharecapital,
  fpcgrade,
  handleFpoCompareSelect,
}) => {
  return (
    <div className='fpo-card-container'>
      <div className='box-top d-flex justify-content-between'>
        <div className='top-txt'>
          <p className='fpo-title'>{fponame}</p>
        </div>
        <div className='check-radi0-btn ml-2'>
          <input
            type='checkbox'
            className='select-input d-none'
            id={fpoid}
            onChange={() => handleFpoCompareSelect(fpoid)}
          />
          <label
            htmlFor={fpoid}
            className='f14 medium-font text-muted d-flex align-items-center mb-0'
          >
            <SVG />
          </label>
        </div>
      </div>

      <div className='fpo-info-container'>
        <div className='row'>
          <div className=' d-flex'>
            <div className=' d-flex mr-2'>
              <i className='fa fa-calendar-o mr-1'></i>
              <p>{foundedon ? dayjs(foundedon).format('YYYY-MM-DD') : 'NIL'}</p>
            </div>
            <div className=' d-flex mr-2'>
              <i className='fa fa-users mr-1'></i>
              <p>{membershipbase ? membershipbase : 0}</p>
            </div>
            <div className=' d-flex mr-2'>
              <i className='fa fa-home mr-1'></i>
              <p>{villagescovered ? villagescovered : 0}</p>
            </div>
          </div>
        </div>
        <div className='row'>
          <div className=' d-flex'>
            <div className=' d-flex mr-2'>
              <i className='fa fa-money mr-1'></i>
              <p>{turnover ? turnover : 0}</p>
            </div>
            <div className=' d-flex mr-2'>
              <i className='fa fa-handshake-o mr-1'></i>
              <p>{sharecapital ? sharecapital : 0}</p>
            </div>
            <div className=' d-flex mr-2'>
              <i className='fa fa-tachometer mr-1'></i>
              <p>{fpcgrade ? fpcgrade : '-'}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

function SVG() {
  return (
    <svg
      className='fpo-check-box'
      width='100'
      height='100'
      viewBox='0 0 100 100'
      xmlns='http://www.w3.org/2000/svg'
    >
      <path
        d='M13 50.986L37.334 75 88 25'
        strokeWidth='10'
        stroke='#fff'
        fill='none'
        fillRule='evenodd'
        strokeDasharray='150'
        strokeDashoffset='0'
        transform='translate(50,80)'
      />
    </svg>
  );
}

export default FpoCard;
