import { takeEvery, call, put, takeLatest } from 'redux-saga/effects';
import Toast from '../../../utils/toast';
import _ from 'lodash';
import {
  getFPOApplicationsAll,
  getFpoDocumentDataById,
  getFpoLoanInfoById,
  getLoanId,
  saveFpoLoanInfo,
  submitLoanInfo,
  getLendersOfferList,
  getLenderOfferDetails,
  putAcceptOffer,
  putRejectOffer,
  getDocumentData,
  getProfileData,
  getPreSansactionRequestDocuments,
  getPostSansactionRequestDocuments,
  postSansactionDocumentUpload,
  preSansactionDocumentUpload,
  deleteDraftApplication,
  getFPOLoansAll,
  getLoanSummary,
  getRepaymentStatus,
  applicationFilter,
  loanFilter,
} from '../../../services/http/fpo/fpo';
import { fpoActions } from '../../actions';
import {
  FPO_APPLICATIONS_LIST_ALL_IN_PROGRESS,
  FPO_CREATE_IN_PROCESS,
  FPO_SAVE_IN_PROCESS,
  FPO_SUBMIT_IN_PROCESS,
  GET_FPO_DOCUMENTS_IN_PROGRESS,
  SET_SELECTED_LOAN,
  FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS,
  FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS,
  FPO_PUT_ACCEPT_OFFER_IN_PROGRESS,
  FPO_PUT_REJECT_OFFER_IN_PROGRESS,
  GET_FPO_DOCUMENT_DATA_IN_PROGRESS,
  GET_FPO_PROFILE_DATA_IN_PROGRESS,
  FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
  FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS,
  FPO_LOAN_LIST_ALL_IN_PROGRESS,
  FPO_GET_LOAN_SUMMARY_IN_PROGRESS,
  FPO_GET_REPAYMENT_STATUS_IN_PROGRESS,
  FPO_GET_LOAN_DOCUMENTS_IN_PROGRESS,
  FPO_APPLICATION_FILTER_IN_PROGRESS,
  FPO_LOAN_FILTER_IN_PROGRESS,
} from '../../types/fpotypes';

function* loanInProgress(action) {
  try {
    const formData = new FormData();
    for (var key in action.payload) {
      formData.append(key.toString(), action.payload[key].toString());
    }
    const loanInfo = yield call(getLoanId, formData);
    const { data } = loanInfo;
    yield put(fpoActions.applyLoanSuccess(data));
  } catch (error) {
    yield put(
      fpoActions.applyLoanError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* saveLoanInformation(action) {
  try {
    const formData = new FormData();
    for (var key in action.payload) {
      formData.append(key.toString(), action.payload[key].toString());
    }
    const loanInfo = yield call(saveFpoLoanInfo, {
      loanInfo: formData,
      applicationId: action.payload.applicationuid,
    });
    const { data } = loanInfo;
    yield put(fpoActions.saveLoanSuccess(data));
  } catch (error) {
    yield put(
      fpoActions.saveLoanError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* submitLoanInformation(action) {
  try {
    const formData = new FormData();
    for (var key in action.payload) {
      formData.append(key.toString(), action.payload[key].toString());
    }
    const loanInfo = yield call(submitLoanInfo, {
      loanInfo: formData,
      applicationId: action.payload.applicationuid,
    });
    const { data } = loanInfo;
    yield put(fpoActions.submitLoanSuccess(data));
  } catch (error) {
    yield put(
      fpoActions.submitLoanError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* getLoanInformation(action) {
  try {
    const loanInfo = yield call(getFpoLoanInfoById, action.payload);
    const { data } = loanInfo;
    yield put(fpoActions.setSelectedLoanSuccess(data));
  } catch (error) {
    yield put(
      fpoActions.setSelectedLoanError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* getFpoDocuments(action) {
  try {
    const documentInfo = yield call(getFpoDocumentDataById, action.payload);
    const { data } = documentInfo;
    yield put(fpoActions.getFpoDocumentsSuccess(data));
  } catch (error) {
    yield put(fpoActions.getFpoDocumentsError(error));
  }
}

function* getAllApplications(action) {
  try {
    const applications = yield call(getFPOApplicationsAll, action.payload);
    const { data } = applications;
    yield put(fpoActions.getFPOAllApplicationsInSuccess(data['data']));
  } catch (error) {
    yield put(fpoActions.getFPOAllApplicationsInError(error));
  }
}

function* getAllLoans(action) {
  try {
    const { data } = yield call(getFPOLoansAll, action.payload);
    yield put(fpoActions.getFPOAllLoanInSuccess(data['data']));
  } catch (error) {
    yield put(fpoActions.getFPOAllLoansInError(error));
  }
}

function* getLendersOffer(action) {
  try {
    const lenders_offers = yield call(getLendersOfferList, action.payload);
    const { data } = lenders_offers;
    yield put(fpoActions.getLendersOfferInSuccess(data['data']));
  } catch (error) {
    yield put(fpoActions.getLendersOfferInError(error));
  }
}

function* lenderOfferDetails(action) {
  try {
    const lenders_offers = yield call(getLenderOfferDetails, action.payload);
    const { data } = lenders_offers;
    data['data'].proposal_id = action.payload.proposal_id;
    yield put(fpoActions.getSelectedOfferInSuccess(data['data']));
  } catch (error) {
    yield put(fpoActions.getSelectedOfferInError(error));
  }
}

function* putAcceptOfferInProgress(action) {
  try {
    const lenders_offers = yield call(putAcceptOffer, action.payload);
    const { data } = lenders_offers;
    let payload = {
      message: data.message,
      proposal_id: action.payload.proposal_id,
    };
    yield put(fpoActions.putAcceptOfferInSuccess(payload));
  } catch (error) {
    let payload = {
      message: error,
      proposal_id: action.payload.proposal_id,
    };
    yield put(fpoActions.putAcceptOfferInError(payload));
  }
}

function* putRejectOfferInProgress(action) {
  try {
    const lenders_offers = yield call(putRejectOffer, action.payload);
    const { data } = lenders_offers;
    let payload = {
      message: data.message,
      proposal_id: action.payload.proposal_id,
    };
    yield put(fpoActions.putRejectOfferInSuccess(payload));
  } catch (error) {
    let payload = {
      message: error,
      proposal_id: action.payload.proposal_id,
    };

    yield put(fpoActions.putRejectOfferInError(payload));
  }
}

function* getdDocumentDataInProgress(action) {
  try {
    const { data } = yield call(getDocumentData, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      documents: data,
    };
    yield put(fpoActions.getDocumentDataInSuccess(payload));
  } catch (error) {
    yield put(fpoActions.getDocumentDataInError(error));
  }
}

function* getProfileDataInProgress(action) {
  try {
    const { data } = yield call(getProfileData, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      documents: data,
    };
    yield put(fpoActions.getProfileDataInSuccess(payload));
  } catch (error) {
    error.proposal_id = action.payload.proposal_id;
    yield put(fpoActions.getProfileDataInError(error));
  }
}

function* getPreSansactionRequestDocumentsInProgress(action) {
  try {
    const { data } = yield call(
      getPreSansactionRequestDocuments,
      action.payload
    );
    let documents = data.data;
    documents.map((element) => (element.type = 'pre'));
    documents = { all: documents };
    documents = { ...documents, ..._.groupBy(data.data, 'Lender_Name') };
    let payload = {
      inProgress: false,
      error: false,
      documents,
    };
    yield put(fpoActions.getPreSansactionDocumentsInSuccess(payload));
  } catch (error) {
    yield put(fpoActions.getPreSansactionDocumentsInError(error));
  }
}

function* getPostSansactionRequestDocumentsInProgress(action) {
  try {
    const { data } = yield call(
      getPostSansactionRequestDocuments,
      action.payload
    );
    let documents = data.data;
    documents.map((element) => (element.type = 'post'));
    documents = { all: documents };
    documents = { ...documents, ..._.groupBy(data.data, 'Lender_Name') };
    let payload = {
      inProgress: false,
      error: false,
      documents,
    };
    yield put(fpoActions.getPostSansactionDocumentsInSuccess(payload));
  } catch (error) {
    yield put(fpoActions.getPostSansactionDocumentsInError(error));
  }
}

function* postSansactionDocumentUploadInProgress(action) {
  try {
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
      payload: {},
    });
    const { data } = yield call(postSansactionDocumentUpload, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      message: data.message,
    };
    yield put(fpoActions.postSansactionDocumentUploadInSuccess(payload));
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
      payload: payload,
    });
  } catch (error) {
    yield put(fpoActions.postSansactionDocumentUploadInError(error));
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
      payload: { message: error },
    });
  }
}

function* preSansactionDocumentUploadInProgress(action) {
  try {
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
      payload: {},
    });
    const { data } = yield call(preSansactionDocumentUpload, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      message: data.message,
    };
    yield put(fpoActions.preSansactionDocumentUploadInSuccess(payload));
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
      payload: payload,
    });
  } catch (error) {
    yield put(fpoActions.preSansactionDocumentUploadInError(error));
    yield put({
      type: FPO_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
      payload: { message: error },
    });
  }
}

function* deleteDraftApplicationInProgress(action) {
  try {
    const { data } = yield call(deleteDraftApplication, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      message: data.message,
    };
    yield put(fpoActions.deleteDraftApplicationInSuccess(payload));
    yield call(Toast, { title: data['message'] });
  } catch (error) {
    yield put(fpoActions.deleteDraftApplicationInError(error));
    yield call(Toast, {
      title: 'Problem while communicating server.',
      type: 'error',
    });
  }
}

function* getLoanSummaryInProgress(action) {
  try {
    const { data } = yield call(getLoanSummary, action.payload.applicationuid);
    let payload = {
      inProgress: false,
      error: false,
      loanSummary: data['data'],
    };
    yield put(fpoActions.getLoanSummaryInSuccess(payload));
  } catch (error) {
    yield put(
      fpoActions.getLoanSummaryInError('Problem while communicating server.')
    );
  }
}

function* getRepaymentStatusInProgress(action) {
  try {
    const { data } = yield call(
      getRepaymentStatus,
      action.payload.applicationuid
    );
    let payload = {
      inProgress: false,
      error: false,
      repaymentSchdules: data['repayment_schedule_list'],
    };
    yield put(fpoActions.getLoanSummaryInSuccess(payload));
  } catch (error) {
    yield put(
      fpoActions.getLoanSummaryInError('Problem while communicating server.')
    );
  }
}

function* getLoanDocumentsInProgress(action) {
  try {
    let payload;

    const docsData = yield call(
      getFpoDocumentDataById,
      action.payload.applicationuid
    );
    payload = {
      success: docsData['data']['documentdata'] ? true : false,
      ...docsData['data'],
    };
    yield put(fpoActions.getLoanDocumentsInSuccess(payload));
    // ------------------------------------------------------------------------
    const preData = yield call(
      getPreSansactionRequestDocuments,
      action.payload
    );
    payload = {
      ...preData['data'],
      success: preData['data']['data'] ? true : false,
    };
    yield put(fpoActions.loanPreDocsInSuccess(payload));
    // ----------------------------------------------------------------------
    const postData = yield call(
      getPostSansactionRequestDocuments,
      action.payload
    );
    payload = {
      ...postData['data'],
      success: postData['data']['data'] ? true : false,
    };
    yield put(fpoActions.loanPostDocsInSuccess(payload));
  } catch (error) {
    yield put(
      fpoActions.getLoanDocumentsInError('Problem while communicating server.')
    );
  }
}

function* applicationFilterInProgress(action) {
  try {
    const { data } = yield call(applicationFilter, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      data: data['data'],
    };
    yield put(fpoActions.applicationFilterInSuccess(payload));
  } catch (error) {
    yield put(
      fpoActions.applicationFilterInError('Problem while communicating server.')
    );
  }
}

function* loanFilterInProgress(action) {
  try {
    const { data } = yield call(loanFilter, action.payload);
    let payload = {
      inProgress: false,
      error: false,
      data: data['data'],
    };
    yield put(fpoActions.loanFilterInSuccess(payload));
  } catch (error) {
    yield put(
      fpoActions.loanFilterInError('Problem while communicating server.')
    );
  }
}

function* fpoWatcher() {
  yield takeLatest(FPO_CREATE_IN_PROCESS, loanInProgress);
  yield takeLatest(FPO_SAVE_IN_PROCESS, saveLoanInformation);
  yield takeLatest(FPO_SUBMIT_IN_PROCESS, submitLoanInformation);
  yield takeLatest(SET_SELECTED_LOAN, getLoanInformation);
  yield takeLatest(GET_FPO_DOCUMENTS_IN_PROGRESS, getFpoDocuments);
  yield takeLatest(FPO_APPLICATIONS_LIST_ALL_IN_PROGRESS, getAllApplications);
  yield takeLatest(
    FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS,
    getLendersOffer
  );
  yield takeEvery(FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS, lenderOfferDetails);
  yield takeLatest(FPO_PUT_ACCEPT_OFFER_IN_PROGRESS, putAcceptOfferInProgress);
  yield takeLatest(FPO_PUT_REJECT_OFFER_IN_PROGRESS, putRejectOfferInProgress);
  yield takeLatest(
    GET_FPO_DOCUMENT_DATA_IN_PROGRESS,
    getdDocumentDataInProgress
  );
  yield takeLatest(GET_FPO_PROFILE_DATA_IN_PROGRESS, getProfileDataInProgress);
  yield takeEvery(
    FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
    getPreSansactionRequestDocumentsInProgress
  );
  yield takeEvery(
    FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
    getPostSansactionRequestDocumentsInProgress
  );
  yield takeEvery(
    FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
    postSansactionDocumentUploadInProgress
  );

  yield takeEvery(
    FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
    preSansactionDocumentUploadInProgress
  );

  yield takeEvery(
    FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS,
    deleteDraftApplicationInProgress
  );
  yield takeLatest(FPO_LOAN_LIST_ALL_IN_PROGRESS, getAllLoans);
  yield takeLatest(FPO_GET_LOAN_SUMMARY_IN_PROGRESS, getLoanSummaryInProgress);
  yield takeLatest(
    FPO_GET_REPAYMENT_STATUS_IN_PROGRESS,
    getRepaymentStatusInProgress
  );

  yield takeLatest(
    FPO_GET_LOAN_DOCUMENTS_IN_PROGRESS,
    getLoanDocumentsInProgress
  );
  yield takeLatest(
    FPO_APPLICATION_FILTER_IN_PROGRESS,
    applicationFilterInProgress
  );

  yield takeLatest(FPO_LOAN_FILTER_IN_PROGRESS, loanFilterInProgress);
}

export { fpoWatcher };
