import { useRef, useEffect } from 'react';
import samDummy from './../../../../assets/Images/dummy/sam.png';
import { NavDropdown } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload } from '@fortawesome/free-solid-svg-icons';
import { Form } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { LOAN_FIELDS, ADMIN_APPLICATION_STATUS } from '../../../../constants';
import { useSelector } from 'react-redux';
import { Spinner } from 'react-bootstrap';

const CreditGradingDocuments = ({
  documents,
  uploadButton,
  reportType,
  getFormData,
  showSamDocsActionsButton,
  handleDocDelete,
}) => {
  const { register, handleSubmit, watch } = useForm({});
  const submitRef = useRef();
  const admin = useSelector((state) => state.admin);
  useEffect(() => {
    const subscription = watch((value, { name, type }) => {
      if (name === ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportFile]) {
        submitRef.current.click();
      }
    });
    return () => subscription.unsubscribe();
  }, [watch]);

  const handleFormSubmit = (data) => {
    const formData = new FormData();
    formData.append(
      ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportType],
      reportType
    );
    formData.append(
      ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportFile],
      data[ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportFile]][0]
    );
    getFormData(formData);
  };

  const genrateFileName = (fileName) => {
    var fileExt = fileName.split('.').pop();
    const file_name = fileName.substring(0, 10);
    return file_name + '...' + fileExt;
  };

  return (
    <div className='row'>
      {documents.map((val, index) => {
        return (
          <div
            className='col-auto mb-5 '
            key={'credit_memorundum_' + (index + 1)}
          >
            <a
              className='cursor-pointer sam-container'
              href={val.Document_Url}
              target='_blank'
              rel='noreferrer'
              key={index}
            >
              <div className='doc-box text-center'>
                <div>
                  <img src={samDummy} className='document-image p-0' alt='' />
                </div>
                <div className='dflex justify-content-between align-items-center sam-text-box'>
                  <div className=' col px-0'>
                    <h6 className='f12 medium-font  p-1 pl-2'>
                      {genrateFileName(val.filename)}
                    </h6>
                  </div>
                  {showSamDocsActionsButton && (
                    <NavDropdown
                      title={<i className='fa fa-ellipsis-v'></i>}
                      id='nav-dropdown'
                      className='dots-container'
                    >
                      <NavDropdown.Item
                        className='text-dark dots-link '
                        eventKey='delete'
                        onClick={() => handleDocDelete(val.LCD_ID)}
                      >
                        <i className='fa fa-trash-o'></i> Delete
                      </NavDropdown.Item>
                    </NavDropdown>
                  )}
                </div>
              </div>
            </a>
          </div>
        );
      })}
      {uploadButton && (
        <div className='col-auto mb-5'>
          {admin &&
          admin.selectedApplication &&
          admin.selectedApplication.samFileUpload &&
          !admin.selectedApplication.samFileUpload.inProgress ? (
            <Form onSubmit={handleSubmit(handleFormSubmit)}>
              <div className=' text-center sam-upload-container'>
                <div className='sam-icon-container'>
                  <FontAwesomeIcon
                    icon={faFileUpload}
                    className='sam-file-icon'
                  />
                </div>
                <button
                  type='button'
                  className='dflex justify-content-between align-items-center sam-text-box sam-upload-text-box'
                  onClick={() =>
                    document.getElementById('sam-input-file').click()
                  }
                >
                  <div className=' col upload-text-container'>
                    <h6 className='upload-text'>UPLOAD DOCUMENT</h6>
                  </div>
                </button>
                <input
                  id='sam-input-file'
                  type='file'
                  className='d-none'
                  {...register(
                    ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportFile]
                  )}
                />
              </div>
              <input
                type='hidden'
                {...register(ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportType])}
              />
              <input type='submit' className='d-none' ref={submitRef} />
            </Form>
          ) : (
            <div
              className='d-flex justify-content-center align-items-center'
              style={{ width: '120px', height: '140px' }}
            >
              <Spinner animation='border' role='status'>
                <span className='visually-hidden'></span>
              </Spinner>
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default CreditGradingDocuments;
