import AdminDashboard from '../pages/admin/dashboard/AdminDashboard';
import AdminLoan from '../pages/admin/loan/AdminLoan';
import FpoApplication from '../pages/admin/loan/FpoApplication';
import ApplicationStatus from '../pages/admin/loan/ApplicationStatus';
import UserManagement from '../pages/admin/userManagement';

const AdminRoutes = [
  {
    name: 'Fpo Dashboard',
    path: '/dashboard',
    base: '',
    element: <AdminDashboard />,
  },
  {
    name: 'Bank Loan',
    path: '/loan-applications',
    base: '',
    element: <AdminLoan />,
  },
  {
    name: 'Application Status',
    path: '/application/status',
    base: '',
    element: <ApplicationStatus />,
  },
  {
    name: 'Fpo Applications',
    path: '/fpo-applications',
    base: '',
    element: <FpoApplication />,
  },
  {
    name: 'User Management',
    path: '/user-management',
    base: '',
    element: <UserManagement />,
  }
];

export default AdminRoutes;
