import React, { useState, useEffect } from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import '../../../Modals/LoanApplicationModal/ApplicationModal.scss';
import LoanSummary from '../../../Forms/Common/LoanSummary';
import LoanDocuments from '../../../Forms/Common/LoanDocuments';
import RePaymentSchedule from '../../../Tables/RePaymentSchedule';
import AdminActionsTab from '../../../ActionsTab/AdminActionsTab';
import { useDispatch, useSelector } from 'react-redux';
import { adminActions } from '../../../../redux/actions';

const LoanContainer = ({ loanCategory }) => {
  const [activeTab, setActiveTab] = useState('loan_summary');
  const admin = useSelector((state) => state.admin);
  const dispatch = useDispatch();

  const handleDocsSubmit = (payload) => {
    dispatch(adminActions.sanctionLoanDocsUploadInProgress(payload));
  };

  useEffect(() => {
    if (admin.selectedLoan.sanctionDocsUpload.success) {
      dispatch(
        adminActions.getLoanDocumentsInProgress(admin.selectedLoan.loanInfo)
      );
    }
  }, [admin?.selectedLoan?.sanctionDocsUpload?.success]);

  return (
    <div>
      <Tabs
        defaultActiveKey={activeTab}
        className='mb-3 lender-loans-tab mt-3 mx-4 px-2'
        onSelect={(k) => setActiveTab(k)}
        activeKey={activeTab}
      >
        <Tab eventKey='loan_summary' title='LOAN SUMMARY'>
          {admin &&
            admin.selectedLoan &&
            admin.selectedLoan.loanSummary &&
            admin.selectedLoan.loanSummary.data && (
              <LoanSummary
                loanInfo={admin.selectedLoan.loanInfo}
                loanSummary={admin.selectedLoan.loanSummary.data}
              />
            )}
        </Tab>
        <Tab eventKey='documents' title='DOCUMENTS'>
          {admin && admin.selectedLoan && admin.selectedLoan.loanSummary && (
            <LoanDocuments
              loanInfo={admin.selectedLoan.loanInfo}
              preDocuments={admin?.selectedLoan?.loanDocuments?.preDocuments}
              postDocuments={admin?.selectedLoan?.loanDocuments?.postDocuments}
              loanDocuments={admin?.selectedLoan?.loanDocuments?.loanDocuments}
            />
          )}
        </Tab>
        <Tab eventKey='repayment_status' title={`REPAYMENT STATUS`}>
          <div className='px-5 repayments_table_styles m-0 p-0'>
            <RePaymentSchedule
              documents={admin?.selectedLoan?.repaymentStatus?.data}
            />
          </div>
        </Tab>
        <Tab eventKey='actions' title='ACTIONS'>
          <AdminActionsTab
            applicationId={admin?.selectedLoan?.loanInfo?.applicationuid}
            handleDocsSubmit={handleDocsSubmit}
            cancelApplication={false}
            loanInfo={admin?.selectedLoan?.loanInfo}
            basicDetails={admin?.selectedLoan?.loanSummary?.basicDetails}
            requestedDocuments={
              admin?.selectedLoan?.loanDocuments?.combinedDocuments
            }
            sanctionDocsUpload={admin?.selectedLoan?.sanctionDocsUpload}
          />
        </Tab>
      </Tabs>
    </div>
  );
};

export default LoanContainer;
