import { getToken } from '../../../utils/utils';
import { AdminHttpInstance } from './adminInstance';

const getApplicationsAll = (organizationid) => {
  const token = getToken();
  return AdminHttpInstance.request({
    method: 'GET',
    params: { agfinmarttoken: token, organizationid },
    url: 'application/list/all/',
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

