import React, { useEffect } from 'react';
import { Form, Spinner, Modal } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { REJECT_SHOW_INTEREST, LOAN_FIELDS } from '../../../constants';
import { useSelector } from 'react-redux';

const RejectionModal = (props) => {
  const bankState = useSelector((state) => state.bank);
  const { register, handleSubmit, reset } = useForm({
    defaultValues: {},
  });
  const [ErrorStatus, setErrorStatus] = React.useState(false);
  const validateSubmit = (data) => {
    let checkBoxSelectectedCounts = 0;
    for (const key in data) {
      if (data[key] === true) {
        checkBoxSelectectedCounts++;
      }
    }

    if (
      checkBoxSelectectedCounts === 0 &&
      data[REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectRemarks]].length < 5
    ) {
      setErrorStatus(true);
    } else {
      setErrorStatus(false);
      props.handleShowInterest(props.activeKey, 'next', 'reject', data);
      reset();
    }
  };

  useEffect(() => {
    if (props && props.show) {
      reset();
    }
  }, [props?.show]);

  return (
    <Modal
      size='md'
      aria-labelledby='contained-modal-title-vcenter'
      centered
      show={props.show}
      dialogClassName='rejection-modal'
      className='rejection-modal-container'
      onHide={() => props.closeRejectModel(false)}
    >
      <Modal.Body>
        <Form
          onSubmit={handleSubmit((data) => {
            validateSubmit(data);
          })}
          noValidate
        >
          <div className='container'>
            <div className='clearfix'>
              <h4 className='pull-left modal-title  '>
                Please select the reason for rejection
              </h4>
              <button
                type='button'
                className='close pull-right h4'
                onClick={() => props.closeRejectModel(false)}
              >
                <span aria-hidden='true'>×</span>
              </button>
            </div>
            <div className='mt-3 p-1'>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectLackOfVintage]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectLackOfVintage]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectLackOfVintage]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>
                    Lack of vintage/transaction history
                  </span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakFinancials]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakFinancials]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakFinancials]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>
                    Weak financials/applied amount disproportionate to the size
                    of the business
                  </span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectInsufficientDetails]
                  }
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectInsufficientDetails]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectInsufficientDetails]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>
                    Insufficient details on buyer-seller relationship
                  </span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectStatutoryDocuments]
                  }
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectStatutoryDocuments]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectStatutoryDocuments]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>Statutory documents inadequate</span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectAdverseCredit]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectAdverseCredit]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectAdverseCredit]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>Adverse credit bureau report</span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakDocumentation]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakDocumentation]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectWeakDocumentation]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>
                    Weak documentation with buyer/seller
                  </span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectGeography]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectGeography]
                  )}
                />
                <label
                  htmlFor={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectGeography]}
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>Geography not serviceable</span>
                </label>
              </div>
              <div className='col-12 pl-1'>
                <input
                  type='checkbox'
                  className='select-input'
                  id={REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectUnfavorableDebtor]}
                  {...register(
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectUnfavorableDebtor]
                  )}
                />
                <label
                  htmlFor={
                    REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectUnfavorableDebtor]
                  }
                  className='f14 medium-font text-muted d-flex align-items-center mb-0'
                >
                  <SVG />
                  <span className='ml-3'>
                    Unfavorable debtor/creditor ageing
                  </span>
                </label>
              </div>
            </div>
            <div className='p-2'>
              <Form.Group
                className='mb-0 mt-3'
                controlId='exampleForm.ControlTextarea1'
              >
                <Form.Control
                  as='textarea'
                  rows={3}
                  className='br-5'
                  {...register(REJECT_SHOW_INTEREST[LOAN_FIELDS.rejectRemarks])}
                />
                {ErrorStatus && (
                  <span className='text-danger'>
                    Please select any option or enter above 4 character in
                    remarks.
                  </span>
                )}
              </Form.Group>
            </div>
            <div className='pl-2 py-4'>
              {bankState && bankState.showInterest.inProgress ? (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              ) : (
                <button
                  type='submit'
                  className='loan-application-btn btn-primary'
                >
                  SHARE REASON
                </button>
              )}
            </div>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
};

function SVG() {
  return (
    <svg
      className='svg'
      width='100'
      height='100'
      viewBox='0 0 100 100'
      xmlns='http://www.w3.org/2000/svg'
    >
      <path
        d='M13 50.986L37.334 75 88 25'
        strokeWidth='6'
        stroke='#fff'
        fill='none'
        fillRule='evenodd'
        strokeDasharray='150'
        strokeDashoffset='150'
      />
    </svg>
  );
}

export default RejectionModal;
