import './DocumentIcon.scss';
const DocumentIcon = ({ download }) => {
  return (
    <>
      <span className='document-icon'>
        <i className='fa fa-file-o p-1 file-default'></i>
        {download ? (
          <i className='fa fa-download p-1 external-icon'></i>
        ) : (
          <i className='fa fa-times p-1 external-icon delete'></i>
        )}
      </span>
    </>
  );
};

export default DocumentIcon;
