const currencyNumber = (n) => {
  // format number 1000000 to 1,234,567
  // return n.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ",")
  // format number 1000000 to 12,34,567
  return n
    .replace(/\D/g, '')
    .replace(/(\d+?)(?=(\d\d)+(\d)(?!\d))(\.\d+)?/g, '$1,');
};

const formatCurrency = (input_val, blur) => {
  if (input_val.indexOf('.') >= 0) {
    // var original_len = input_val.length;
    var decimal_pos = input_val.indexOf('.');
    var left_side = input_val.substring(0, decimal_pos);
    var right_side = input_val.substring(decimal_pos);
    left_side = currencyNumber(left_side);
    right_side = currencyNumber(right_side);
    // if (blur === 'blur') {
    //   right_side += '00';
    // }
    right_side = right_side.substring(0, 2);

    let left_real_value = left_side.replace(/[^\d.]/g, '');
    let left_prefix =
      left_real_value.length === 0 && right_side.length > 0 ? '0' : '';

    input_val = left_prefix + left_side + '.' + right_side;
  } else {
    input_val = currencyNumber(input_val);
    let real_value = input_val.replace(/[^\d.]/g, '');
    let zeroPrefix = parseInt(real_value) >= 1 ? '' : '0';
    // if (blur === 'blur') {
    //   input_val += zeroPrefix + '.00';
    // }
  }
  return {
    unMastedValue: input_val.replace(/[^\d.]/g, ''),
    maskedValue: '₹ ' + input_val.replace('.00', ''),
  };
};

export default formatCurrency;
