import React, { useEffect, useState } from 'react';
import { Tabs, Tab, Alert } from 'react-bootstrap';
import { useLocation } from 'react-router';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import {
  FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS,
  SET_SELECTED_LOAN,
} from '../../../../redux/types/fpotypes';
import LendersOffers from '../../../../components/Forms/FPO/ApplicationStatus/LendersOffers';
import ShadowBox from '../../../../components/ShadowBox';
import FpoActionsTab from '../../../../components/ActionsTab/FpoActionsTab';
import LoanApplicationForm from '../../../../components/Modals/LoanApplicationModal/LoanApplicationForm';
import {
  application_status_remarks,
  STATUS_REMARKS,
} from '../../../../constants';

const ApplicationStatus = ({
  application_id_,
  whiteBg,
  fromComponent,
  hideActions,
}) => {
  const dispatch = useDispatch();
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  let qry_application_id = params.get('application_id');
  const [application_id, setApplicationId] = useState(qry_application_id);
  const [offersTab, setOffersTab] = useState(true);
  const [actionsTab, setActionsTab] = useState(true);
  const fpoState = useSelector((state) => state.fpo);
  const [loanType, setLoanType] = useState(null);

  useEffect(() => {
    if (!application_id || fromComponent) return;
    dispatch({
      type: SET_SELECTED_LOAN,
      payload: application_id,
    });
    if (!application_id) return;
    fetchOffers();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [application_id]);

  useEffect(() => {
    if (fromComponent && application_id_) {
      setApplicationId(application_id_);
      fetchOffers(application_id_);
    }
  }, [fromComponent, application_id_]);

  useEffect(() => {
    if (
      fpoState &&
      fpoState.lendersOffer &&
      fpoState.lendersOffer.fetchRandom
    ) {
      fetchOffers();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpoState.lendersOffer.fetchRandom]);

  const fetchOffers = (application_id_) => {
    let payload = {
      applicationuid: application_id_ ? application_id_ : application_id,
    };
    dispatch({
      type: FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS,
      payload: payload,
    });
  };

  useEffect(() => {
    if (
      fpoState &&
      fpoState.selectedLoan &&
      fpoState.selectedLoan.loanInfo &&
      fpoState.selectedLoan.loanInfo.data
    ) {
      setLoanType(fpoState.selectedLoan.loanInfo.data[0].Answer);
    }
  }, [fpoState]);

  useEffect(() => {
    if (fpoState && fpoState.selectedLoan && fpoState.selectedLoan.loanInfo) {
      if (
        fpoState.selectedLoan.loanInfo.status_remarks ===
          application_status_remarks[STATUS_REMARKS.canceledByAdmin] &&
        fpoState.lendersOffer.offers.length === 0
      ) {
        setOffersTab(false);
      } else {
        setOffersTab(true);
      }
    }
  }, [fpoState?.selectedLoan?.loanInfo, fpoState?.lendersOffer?.offers]);

  useEffect(() => {
    if (hideActions) {
      setActionsTab(false);
    } else {
      setActionsTab(true);
    }
  }, [hideActions]);

  return (
    <>
      {application_id && (
        <section className={`${whiteBg ? 'bg-white' : 'primary-bg'} pb-5`}>
          <div className='container'>
            <h4 className='f17 medium-font fw600 ml-1 pt-3'>
              Application Status
            </h4>
            <div className='mt-4'>
              <Tabs
                defaultActiveKey='application_details'
                id='uncontrolled-tab-example'
                className='mb-3 fpo-offers-tab'
              >
                <Tab
                  eventKey='application_details'
                  title={
                    <div className='mt-4'>
                      <i className='fa fa-file accept-offer-icon'></i>
                      <span>Application Details</span>
                    </div>
                  }
                >
                  {fpoState &&
                    fpoState.selectedLoan &&
                    fpoState.selectedLoan.loanInfo &&
                    fpoState.selectedLoan.loanInfo && (
                      <LoanApplicationForm
                        readOnly={true}
                        loanDetails={fpoState.selectedLoan.loanInfo}
                        loanType={loanType}
                        hideActionsButtons={true}
                      />
                    )}
                </Tab>
                {offersTab && (
                  <Tab
                    eventKey='offers'
                    title={
                      <div className='mt-4'>
                        <i className='fa fa-percent accept-offer-icon '></i>
                        <span>OFFERS</span>
                      </div>
                    }
                  >
                    {fpoState &&
                      fpoState.lendersOffer &&
                      fpoState.lendersOffer.offers &&
                      fpoState.lendersOffer.offers.length > 0 && (
                        <LendersOffers
                          application_id={application_id}
                          offers={
                            fpoState &&
                            fpoState.lendersOffer &&
                            fpoState.lendersOffer.offers
                          }
                        />
                      )}

                    {fpoState &&
                      fpoState.lendersOffer &&
                      fpoState.lendersOffer.offers &&
                      fpoState.lendersOffer.offers.length === 0 && (
                        <div className='d-flex justify-content-center p-5'>
                          <Alert
                            variant={'info'}
                            className='text-center'
                            ref={(element) => {
                              if (element)
                                element.style.setProperty(
                                  'max-width',
                                  '60vw',
                                  'important'
                                );
                            }}
                          >
                            <i className='fa fa-info-circle'></i> Your
                            application is under review. Please check again
                            later.
                          </Alert>
                        </div>
                      )}
                  </Tab>
                )}
                {actionsTab && (
                  <Tab
                    eventKey='action'
                    title={
                      <div className='mt-4'>
                        <i className='fa fa-hand-o-up accept-offer-icon'></i>
                        <span>ACTION</span>
                      </div>
                    }
                  >
                    <ShadowBox className='mt-4'>
                      <FpoActionsTab
                        type='fpo'
                        application_id={application_id}
                      />
                    </ShadowBox>
                  </Tab>
                )}
              </Tabs>
            </div>
          </div>
        </section>
      )}
    </>
  );
};

export default ApplicationStatus;
