import { combineReducers } from 'redux';
import { authReducer } from './auth';
import { adminReducer } from './admin';
import { bankReducer } from './bank';
import { fpoReducer } from './fpo';

export const reducer = combineReducers({
  auth: authReducer,
  admin: adminReducer,
  fpo: fpoReducer,
  bank: bankReducer,
});
