import React, { useEffect, useState } from 'react';
import PieChart, {
  Legend,
  Series,
  Tooltip,
  Format,
} from 'devextreme-react/pie-chart';

const ServiceCategoriesChart = ({ documents, title, legendName }) => {
  const [chartData, setChartData] = useState([]);
  useEffect(() => {
    if (!documents) return;

    let tempoArray = [];
    documents.map((val, index) => {
      if (index < 50) {
        tempoArray.push({
          region: val.axis ? val.axis + '' : null,
          val: val.value === '0' ? null : parseFloat(val.value),
        });
      }
    });
    setChartData(tempoArray);
  }, [documents]);
  return (
    <PieChart
      type='doughnut'
      // palette='Soft Pastel'
      dataSource={chartData}
      sizeGroup='piesGroup'
      innerRadius={0.8}
      resolveLabelOverlapping='shift'
      onPointClick={pointClickHandler}
    >
      <Series argumentField='region' size={2}></Series>
      <Legend
        marginLeft={2}
        horizontalAlignment='right'
        verticalAlignment='top'
      />
      <Tooltip enabled={true} customizeTooltip={customizeTooltip}>
        <Format type='millions' />
      </Tooltip>
    </PieChart>
  );
};
function pointClickHandler(arg) {
  arg.target.select();
}

function customizeTooltip(arg) {
  return {
    text: `${arg.originalValue}`,
  };
}

export default ServiceCategoriesChart;
