import axios from 'axios';
import { tokenValidation } from '../interceptors';
const BankHttpInstance = axios.create({
  baseURL: `${process.env.REACT_APP_API_URL}/lender/`,
  withCredentials: false,
  headers: {
    'Access-Control-Allow-Origin': '*',
  },
});

BankHttpInstance.interceptors.response.use(
  (response) => Promise.resolve(response),
  tokenValidation
);

export { BankHttpInstance };
