import {
  LOG_OUT,
  SIGNIN_ERROR,
  SIGNIN_FPO,
  SIGNIN_REQUEST,
  SIGNIN_SUCCESS,
  SIGN_TOKEN,
} from '../../types';

const actions = {};

actions.signInRequest = (user) => {
  return {
    type: SIGNIN_REQUEST,
    payload: user,
  };
};

actions.signInSuccess = (user) => {
  return {
    type: SIGNIN_SUCCESS,
    payload: user,
  };
};

actions.signInError = (err) => {
  return {
    type: SIGNIN_ERROR,
    payload: err,
  };
};

actions.signToken = (token) => {
  return {
    type: SIGN_TOKEN,
    payload: token,
  };
};

actions.logOut = () => {
  localStorage.removeItem('login');
  return {
    type: LOG_OUT,
  };
};

actions.validateFPOToken = (token) => {
  return {
    type: SIGNIN_FPO,
    payload: token,
  };
};

export { actions };
