import React, { useEffect } from 'react';
import Swiper from 'swiper';

const GeneralBox = ({
  title,
  isButtonPresent,
  loanPills,
  applicationPills,
  slider,
  description,
  children,
  select,
  isWithStatus,
  isActive,
  onTabChange,
  applicationsCounts,
  buttonPosition,
  selectBox,
  handleViewAll,
  loanTabPills,
  pills,
  viewAllBtnTypeClass,
}) => {
  useEffect(() => {
    new Swiper('.mySwiper', {
      slidesPerView: 4,
      spaceBetween: 20,
      slidesPerGroup: 4,
      loop: true,
      loopFillGroupWithBlank: true,
      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });
  }, []);
  return (
    <div
      className={`${
        isWithStatus
          ? isActive
            ? 'general-box bg-white'
            : ''
          : 'general-box bg-white'
      }  p-1`}
    >
      <div className='row pt-2'>
        <div className={`${isButtonPresent ? 'col-7' : 'col-12'} mb-3`}>
          {title && (
            <h4 className='f17 medium-font fw600 ml-1 pt-3'>{title}</h4>
          )}
          {select && (
            <div className='col-4'>
              <select name='' className='form-control br-12 mt-2' id=''>
                <option value=''>Loan Applications</option>
              </select>
            </div>
          )}

          {loanPills && (
            <div className='col-4 mt-2 text-right'>
              <ul
                className='nav nav-pills  mb-3 float-right'
                id='pills-tab'
                role='tablist'
              >
                <li className='nav-item' role='presentation'>
                  <a
                    className='nav-link br-12 ml-2 f14 medium-font active'
                    id='work-tab'
                    data-toggle='pill'
                    href='#work-home'
                    role='tab'
                    aria-controls='pills-home'
                    aria-selected='true'
                  >
                    Working Capital
                  </a>
                </li>
                <li className='nav-item' role='presentation'>
                  <a
                    className='nav-link br-12 ml-2 f14 medium-font'
                    id='term-tab'
                    data-toggle='pill'
                    href='#term-profile'
                    role='tab'
                    aria-controls='pills-profile'
                    aria-selected='false'
                  >
                    Term Loan
                  </a>
                </li>
              </ul>
            </div>
          )}
        </div>
        {selectBox && (
          <div className='col-5 mt-2   d-flex justify-content-end'>
            {selectBox}
          </div>
        )}
        {applicationPills && (
          <div className='col-12 p-0 pl-2 pt-3 remove-padding'>
            <ul className='nav nav-pills  mb-3' id='pills-tab' role='tablist'>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font active text-capitalize remove-margin-left'
                  id='pills-home-tab'
                  data-toggle='pill'
                  href='#pills-home'
                  role='tab'
                  aria-controls='pills-home'
                  aria-selected='true'
                  onClick={onTabChange.bind(this, pills ? pills[0] : 'applied')}
                >
                  {pills ? pills[0] : 'Applied'}{' '}
                  <span className='default-badge'>
                    {applicationsCounts && applicationsCounts.applied}
                  </span>
                </a>
              </li>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font remove-margin-left'
                  id='pills-profile-tab'
                  data-toggle='pill'
                  href='#pills-profile'
                  role='tab'
                  aria-controls='pills-profile'
                  aria-selected='false'
                  onClick={onTabChange.bind(this, pills ? pills[1] : 'active')}
                >
                  Active{' '}
                  <span className='default-badge'>
                    {applicationsCounts && applicationsCounts.active}
                  </span>
                </a>
              </li>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font remove-margin-left'
                  id='pills-contact-tab'
                  data-toggle='pill'
                  href='#pills-contact'
                  role='tab'
                  aria-controls='pills-contact'
                  aria-selected='false'
                  onClick={onTabChange.bind(this, pills ? pills[2] : 'closed')}
                >
                  Closed
                  <span className='default-badge'>
                    {' '}
                    <span className='default-badge'>
                      {applicationsCounts && applicationsCounts.closed}
                    </span>
                  </span>
                </a>
              </li>
            </ul>
          </div>
        )}

        {loanTabPills && (
          <div className='col-12 p-0 pl-2 pt-3 remove-padding'>
            <ul className='nav nav-pills  mb-3' id='pills-tab' role='tablist'>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font active'
                  id='pills-home-tab'
                  data-toggle='pill'
                  href='#pills-home'
                  role='tab'
                  aria-controls='pills-home'
                  aria-selected='true'
                  onClick={onTabChange.bind(this, pills ? pills[0] : 'new')}
                >
                  Sanctioned{' '}
                  <span className='default-badge'>
                    {applicationsCounts && applicationsCounts.new}
                  </span>
                </a>
              </li>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font'
                  id='pills-profile-tab'
                  data-toggle='pill'
                  href='#pills-profile'
                  role='tab'
                  aria-controls='pills-profile'
                  aria-selected='false'
                  onClick={onTabChange.bind(this, pills ? pills[1] : 'active')}
                >
                  Live{' '}
                  <span className='default-badge'>
                    {applicationsCounts && applicationsCounts.active}
                  </span>
                </a>
              </li>
              <li className='nav-item' role='presentation'>
                <a
                  className='nav-link br-12 ml-2 f14 medium-font '
                  id='pills-contact-tab'
                  data-toggle='pill'
                  href='#pills-contact'
                  role='tab'
                  aria-controls='pills-contact'
                  aria-selected='false'
                  onClick={onTabChange.bind(this, pills ? pills[2] : 'closed')}
                >
                  Matured
                  <span className='default-badge'>
                    {' '}
                    <span className='default-badge'>
                      {applicationsCounts && applicationsCounts.closed}
                    </span>
                  </span>
                </a>
              </li>
            </ul>
          </div>
        )}

        {description && (
          <div className='col-12'>
            <p className='f16 text-left'>{description}</p>
          </div>
        )}
        {isButtonPresent && buttonPosition !== 'bottom' && (
          <div className='col-5 mb-4 d-flex justify-content-end'>
            <button
              className={`btn ${
                viewAllBtnTypeClass ? viewAllBtnTypeClass : 'btn-default'
              } br-12 mt-2`}
              onClick={handleViewAll ? handleViewAll : () => {}}
            >
              View All
            </button>
          </div>
        )}

        {children}
        {isButtonPresent && buttonPosition === 'bottom' && (
          <div className='col-12 my-3 d-flex justify-content-center'>
            <button
              className={`btn ${
                viewAllBtnTypeClass ? viewAllBtnTypeClass : 'btn-default'
              } br-12 mt-2`}
              onClick={handleViewAll ? handleViewAll : () => {}}
            >
              View All
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default GeneralBox;
