import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import GeneralBox from '../../../components/GeneralBox';
import ShadowBox from '../../../components/ShadowBox';
import CardTable from '../../../components/Tables/cardTable';
import ProfileStatusTable from '../../../components/Tables/profileStatusTable';
import { Tabs, Tab, Spinner, Form } from 'react-bootstrap';
import HorizontalSwiper from '../../../components/HorizontalSwiper';
import {
  fpo_dashboard_working_capital_loans as FDWCL,
  fpo_dashboard_term_loans as FDTL,
} from '../../../data';
import { useDispatch } from 'react-redux';
import { fpoActions } from '../../../redux/actions';
import { useSelector } from 'react-redux';
import {
  CLEAR_FPO_APPLY_LOAN,
  SET_SELECTED_LOAN,
  FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS,
  CLEAR_FPO_DELETE_LOAN,
} from '../../../redux/types/fpotypes';
import { LOAN_ROUTES } from '../../../constants';
import FpoQuickViewModal from '../../../components/Modals/fpo/FpoQuickViewModal';
import UpcomingRepaymentsModal from '../../../components/Modals/UpcomingRepaymentsModal';
import Swal from 'sweetalert2';

const Fpodashboard = () => {
  const [activeTab, setActiveTab] = React.useState('working_capital');
  const [activeApplicationTab, setactiveApplicationTab] =
    React.useState('applied');
  const [activeLoanTab, setactiveLoanTab] = React.useState('new');
  const [fpoApplicationType, setFpoApplicationType] =
    React.useState('applications');
  const [upcomingShow, setUpcomingShow] = useState(false);

  const fpoState = useSelector((state) => state.fpo);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  React.useEffect(() => {
    if (
      fpoState &&
      !fpoState.apply.inProgress &&
      fpoState.apply.success &&
      fpoState.selectedLoanType
    ) {
      const path = LOAN_ROUTES[fpoState.selectedLoanType?.loanproductid];
      dispatch({ type: CLEAR_FPO_APPLY_LOAN, payload: {} });
      navigate(path + '?loanId=' + fpoState.apply?.success?.applicationuid);
    }
  }, [fpoState]);
  const [applicationId, setApplicationId] = useState(false);
  const [random, setRandom] = useState(false);

  React.useEffect(() => {
    dispatch(fpoActions.getFPOAllApplicationsInProgress());
    dispatch(fpoActions.getFPOAllLoansInProgress());
  }, []);

  React.useEffect(() => {
    if (
      fpoState &&
      fpoState.deleteDraftApplication &&
      fpoState.deleteDraftApplication.success
    ) {
      dispatch(fpoActions.getFPOAllApplicationsInProgress());
      dispatch({ type: CLEAR_FPO_DELETE_LOAN });
    }
  }, [fpoState.deleteDraftApplication]);

  const SwiperNodeGenerator = (childs) => {
    let swiperNodes = [];
    const dispatch = useDispatch();

    const onLoanClick = (loan) => {
      dispatch(
        fpoActions.applyLoan({
          loanproductid: loan.loanId,
          loanproductname: loan.title,
          loanpurpose: loan.purpose,
        })
      );
    };
    childs.forEach((val, index) => {
      swiperNodes.push(
        <div className=' fpo-loan-swiper-content-container'>
          <div className='pl-4 pr-4 pt-3'>
            <h4 className='fpo-apply-load-title'>{val.title}</h4>
            <p className='f12'>{val.info}</p>
          </div>
          {fpoState && fpoState.apply && fpoState.apply.inProgress ? (
            <div className='d-flex pb-1 justify-content-center'>
              <Spinner animation='border' role='status'>
                <span className='visually-hidden'></span>
              </Spinner>
            </div>
          ) : (
            <button
              className='btn btn-primary'
              onClick={() => {
                onLoanClick(val);
              }}
              disabled={fpoState && fpoState.apply.inProgress}
            >
              APPLY LOAN
            </button>
          )}
        </div>
      );
    });
    return swiperNodes;
  };

  const handleDraftClickView = (application_id, type) => {
    let path = '/fpo/dashboard/';

    switch (type) {
      case 'Input Loan':
        path += 'working_capital/input_loan';
        break;
      case 'Output Loan':
        path += 'working_capital/output_loan';
        break;

      case 'Infrastructure Loan':
        path += 'term_loan/catalytic_infrastructure';
        break;

      default:
        break;
    }
    dispatch({ type: CLEAR_FPO_APPLY_LOAN });
    navigate(path + '?loanId=' + application_id);
  };

  const handleDraftClickDelete = (applicationuid) => {
    Swal.fire({
      title: 'Are you sure to delete?',
      icon: 'warning',
      showDenyButton: true,
      confirmButtonText: 'Delete',
      denyButtonText: `Cancel`,
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch({
          type: FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS,
          payload: { applicationuid },
        });
      }
    });
  };

  const defaultViewModal = (application_id) => {
    dispatch({
      type: SET_SELECTED_LOAN,
      payload: application_id,
    });
    setApplicationId(application_id);
    setRandom(Math.random(1000000, 9999999));
  };

  const handleViewDetailsFpo = (application) => {
    let category = activeApplicationTab;
    if (fpoApplicationType === 'loans') {
      category = activeLoanTab === 'applied' ? 'new' : activeLoanTab;
    } else {
    }
    let path = '/fpo/loan-applications/';
    navigate(
      path +
        `?application_id=${application.applicationuid}&&type=${fpoApplicationType}&&category=${category}`
    );
  };

  const handleFpoViewAll = () => {
    let path = '/fpo/loan-applications/';
    if (fpoApplicationType === 'applications') {
      path +=
        '?type=' + fpoApplicationType + '&&category=' + activeApplicationTab;
    } else {
      path += '?type=' + fpoApplicationType + '&&category=' + activeLoanTab;
    }
    navigate(path);
  };
  const handleApplicationViewButton = (application) => {
    if (fpoApplicationType === 'applications') {
      if (activeApplicationTab === 'active') {
        navigate(
          `application-status?application_id=${application.applicationuid}`
        );
      } else {
        defaultViewModal(application.applicationuid);
      }
    } else {
      defaultViewModal(application.applicationuid);
    }
  };
  return (
    <section className='primary-bg pb-5'>
      <div className='container'>
        <div className='row'>
          <div className='col-md-9 mb-4 '>
            <ShadowBox>
              <div className='fpo-loan-apply-container'>
                <h4 className='f17 medium-font fw600 ml-1 pt-3'>
                  Choose Loan To Apply
                </h4>
                <Tabs
                  defaultActiveKey='working_capital'
                  id=''
                  className='mb-3 fpo-dashboard-loan-tabs '
                  onSelect={(key) => setActiveTab(key)}
                >
                  <Tab eventKey='working_capital' title='Working Capital'>
                    {activeTab === 'working_capital' && (
                      <div className='p-1  mt-5'>
                        <p>
                          Working capital loans are offered to borrowers for
                          meeting their working capital requirements generally
                          for a period not more than 12 months. Working capital
                          loans are helpful for covering the company’s
                          short-term operational requirements.
                        </p>
                        <div className='mt-5'>
                          <HorizontalSwiper
                            childs={SwiperNodeGenerator(FDWCL)}
                            spaceBetween={70}
                            slidesPerView={1}
                          />
                        </div>
                      </div>
                    )}
                  </Tab>
                  <Tab eventKey='term_loan' title='Term Loan'>
                    {activeTab === 'term_loan' && (
                      <div className='p-1 mt-4'>
                        <p>
                          Term Loans Are Offered To Borrowers For Incurring
                          Capital Expenditures Such As Creating Infrastructure,
                          Purchase Of Livestock, Purchase Of Equipment, Etc.,
                          Which Generally Require More Than 12 Months Credit.
                        </p>
                        <div className='mt-5'>
                          <HorizontalSwiper
                            childs={SwiperNodeGenerator(FDTL)}
                            spaceBetween={70}
                            slidesPerView={4}
                          />
                        </div>
                      </div>
                    )}
                  </Tab>
                </Tabs>
              </div>
            </ShadowBox>
          </div>

          <div className='col-md-3 mb-4 '>
            {fpoState &&
              fpoState.deleteDraftApplication &&
              fpoState.deleteDraftApplication.inProgress && (
                <div className='absolute-center'>
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                </div>
              )}

            <GeneralBox
              title='Draft Applications'
              description='We have save your last application in draft box, please continue to submit request for loan.'
            >
              <div
                className='col-12 mt-3 pb-3 custom-scrollbar'
                style={{ height: '309px' }}
              >
                <div className=''>
                  {fpoState &&
                  fpoState.applications &&
                  fpoState.applications.success &&
                  !fpoState.applications.inProgress ? (
                    <CardTable
                      titles={['Application Id']}
                      details={fpoState.applications.success.draft_application}
                      type='draftApplication'
                      handleDraftClickView={handleDraftClickView}
                      handleDraftClickDelete={handleDraftClickDelete}
                    />
                  ) : (
                    <div className=''>Loading</div>
                  )}
                </div>
              </div>
            </GeneralBox>
          </div>
        </div>
        <div className='row'>
          <div className='col-md-6 mb-4'>
            {fpoApplicationType === 'applications' && (
              <GeneralBox
                isButtonPresent
                buttonPosition='bottom'
                title='FPO Applications'
                applicationPills
                onTabChange={(tabId) => setactiveApplicationTab(tabId)}
                handleViewAll={handleFpoViewAll}
                selectBox={
                  <Form.Select
                    className='form-control'
                    onChange={(e) => setFpoApplicationType(e.target.value)}
                    value={fpoApplicationType}
                  >
                    <option value='applications'>Applications</option>
                    <option value='loans'>Loans</option>
                  </Form.Select>
                }
                type='applications'
                applicationsCounts={
                  fpoState &&
                  !fpoState.applications.inProgress &&
                  fpoState.applications.success && {
                    applied:
                      fpoState.applications.success['applied_application']
                        .length,
                    active:
                      fpoState.applications.success['active_application']
                        .length,
                    closed:
                      fpoState.applications.success['closed_application']
                        .length,
                  }
                }
              >
                {fpoState &&
                  !fpoState.applications.inProgress &&
                  fpoState.applications.success && (
                    <div
                      className='col-12 mt-4 pl-2 pr-2 custom-scrollbar fpo-app-remove-padding'
                      style={{ height: '300px' }}
                    >
                      <ProfileStatusTable
                        handleViewButton={handleApplicationViewButton}
                        applications={fpoState.applications.success[
                          activeApplicationTab + '_application'
                        ].slice(0, 4)}
                        fpoActive={
                          activeApplicationTab === 'active' ? true : false
                        }
                        fpoApplied={
                          activeApplicationTab === 'applied' ? true : false
                        }
                        fpoClosed={
                          activeApplicationTab === 'closed' ? true : false
                        }
                        handleDetailsButton={handleViewDetailsFpo}
                      />
                    </div>
                  )}
              </GeneralBox>
            )}

            {fpoApplicationType === 'loans' && (
              <GeneralBox
                isButtonPresent
                buttonPosition='bottom'
                title='FPO Applications'
                loanTabPills
                onTabChange={(tabId) => setactiveLoanTab(tabId)}
                handleViewAll={handleFpoViewAll}
                type='loans'
                selectBox={
                  <Form.Select
                    className='form-control'
                    onChange={(e) => setFpoApplicationType(e.target.value)}
                    value={fpoApplicationType}
                  >
                    <option value='applications'>Applications</option>
                    <option value='loans'>Loans</option>
                  </Form.Select>
                }
                applicationsCounts={
                  fpoState &&
                  !fpoState.applications.inProgress &&
                  fpoState.applications.success && {
                    new: fpoState.loans.success['new_loans'].length,
                    active: fpoState.loans.success['active_loans'].length,
                    closed: fpoState.loans.success['closed_loans'].length,
                  }
                }
              >
                {fpoState &&
                  !fpoState.applications.inProgress &&
                  fpoState.applications.success && (
                    <div
                      className='col-12 mt-4 pl-2 pr-2 custom-scrollbar'
                      style={{ height: '300px' }}
                    >
                      <ProfileStatusTable
                        applications={fpoState.loans.success[
                          activeLoanTab + '_loans'
                        ].slice(0, 4)}
                        fpoActive={activeLoanTab === 'active' ? true : false}
                        fpoApplied={activeLoanTab === 'applied' ? true : false}
                        fpoClosed={activeLoanTab === 'closed' ? true : false}
                        handleDetailsButton={handleViewDetailsFpo}
                        handleViewButton={handleApplicationViewButton}
                      />
                    </div>
                  )}
              </GeneralBox>
            )}
          </div>
          <div className='col-md-6 mb-4 '>
            <GeneralBox
              title='Upcoming Repayments'
              isButtonPresent
              handleViewAll={() => setUpcomingShow(true)}
            >
              <div
                className='col-12 p-0 pr-4 pl-4 mt-2 text-left custom-scrollbar PT-2 remove-padding'
                style={{ maxHeight: '350px', overflowY: 'auto' }}
              >
                <CardTable
                  titles={['Loan Id', 'Due By', 'Amount']}
                  details={[
                    {
                      id: '1-7118-16341200',
                      dueBy: '03-11-2021',
                      amount: '₹ 25,000.00',
                    },
                    {
                      id: '1-7118-16341200',
                      dueBy: '03-11-2021',
                      amount: '₹ 25,000.00',
                    },
                    {
                      id: '1-7118-16341200',
                      dueBy: '03-11-2021',
                      amount: '₹ 25,000.00',
                    },
                    {
                      id: '1-7118-16341200',
                      dueBy: '03-11-2021',
                      amount: '₹ 25,000.00',
                    },
                  ]}
                  type='repayments'
                />
              </div>
            </GeneralBox>
          </div>
        </div>
      </div>
      <FpoQuickViewModal
        applicationId={applicationId}
        random={random}
        loanInfo={
          fpoState && fpoState.selectedLoan && fpoState.selectedLoan.loanInfo
        }
      />
      <UpcomingRepaymentsModal
        show={upcomingShow}
        onHide={() => setUpcomingShow(false)}
      />
    </section>
  );
};

export default Fpodashboard;
