import React from 'react';
import { Spinner } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import './index.css';
import Slider from './Slider';
import logo from '../../../assets/Images/Logo/logo.png';
import { authActions } from '../../../redux/actions';

const Login = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const dispatch = useDispatch();
  const auth = useSelector((state) => state.auth);
  const onSubmit = (data) => {
    dispatch(authActions.signInRequest(data));
  };

  return (
    <section>
      <div className='row'>
        <div className='col-md-6 d-flex flex-column align-items-center justify-content-center '>
          <div className='logo mb-4 '>
            <img src={logo} className='logo ' alt='Samunnati Logo' srcSet='' />
          </div>
          <div className='general-box w-75 p-4 bg-white'>
            <form className='p-2' onSubmit={handleSubmit(onSubmit)}>
              <div className='form-group'>
                <h2 className='regular-font fw400 f24'>AgFinMart</h2>
                <p>
                  The most effective tool to identify potential FPOs loan
                  application.
                </p>
              </div>
              <div className='form-group'>
                <label htmlFor='email'>Email address</label>
                <input
                  type='email'
                  className='form-control'
                  id='useremail'
                  aria-describedby='emailHelp'
                  placeholder='Enter email*'
                  defaultValue=''
                  {...register('useremail', {
                    required: true,
                    pattern: /^\S+@\S+$/i,
                  })}
                  noValidate
                />
                <small id='emailHelp' className='form-text text-muted'>
                  We'll never share your email with anyone else.
                </small>
                {errors.useremail && (
                  <small className='text-danger'>Enter valid email</small>
                )}
              </div>
              <div className='form-group'>
                <label htmlFor='password'>Password*</label>
                <input
                  type='password'
                  className='form-control'
                  id='password'
                  placeholder='Password'
                  {...register('password', {
                    required: true,
                    type: 'password',
                  })}
                  noValidate
                />
                {errors.password && (
                  <small className='text-danger'>Enter valid password</small>
                )}
              </div>
              {!auth.inProgress ? (
                <input
                  type='submit'
                  className='btn btn-primary'
                  label='Submit'
                />
              ) : (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              )}
              <br />
              <br />
              {auth.error && (
                <span className='text-danger'>{auth.errorMessage}</span>
              )}
            </form>
          </div>
        </div>
        <div className='col-md-6'>
          <Slider />
        </div>
      </div>
    </section>
  );
};

export default Login;
