import React, { useState, useEffect } from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import LoanSummary from '../../../Forms/Common/LoanSummary';
import LoanDocuments from '../../../Forms/Common/LoanDocuments';
import RePaymentSchedule from '../../../Tables/RePaymentSchedule';
import FpoActionsTab from '../../../ActionsTab/FpoActionsTab';

const LoanContainer = ({ loanCategory }) => {
  const [activeTab, setActiveTab] = useState('loan_summary');
  const fpo = useSelector((state) => state.fpo);
  return (
    <div>
      <Tabs
        defaultActiveKey={activeTab}
        className='mb-3 lender-loans-tab mt-3 mx-4 px-2'
        onSelect={(k) => setActiveTab(k)}
        activeKey={activeTab}
      >
        <Tab eventKey='loan_summary' title='LOAN SUMMARY'>
          {fpo && fpo.selectedAppLoan && fpo.selectedAppLoan.loanSummary && (
            <LoanSummary
              loanInfo={fpo?.selectedAppLoan?.loanInfo}
              loanSummary={fpo.selectedAppLoan.loanSummary}
            />
          )}
        </Tab>
        <Tab eventKey='documents' title='DOCUMENTS'>
          {fpo && fpo.selectedAppLoan && fpo.selectedAppLoan.loanSummary && (
            <>
              <LoanDocuments
                loanInfo={fpo?.selectedAppLoan?.loanInfo}
                postDocuments={fpo?.selectedAppLoan?.postDocuments}
                preDocuments={fpo?.selectedAppLoan?.preDocuments}
                loanDocuments={fpo?.selectedAppLoan?.loanDocuments}
              />
            </>
          )}
        </Tab>
        <Tab eventKey='repayment_schedule' title={`REPAYMENT STATUS`}>
          <div className='px-5'>
            <RePaymentSchedule
              documents={fpo?.selectedAppLoan?.repaymentSchdules}
            />
          </div>
        </Tab>
        <Tab eventKey='actions' title='ACTIONS'>
          <FpoActionsTab
            hideBackButton={true}
            application_id={fpo?.selectedAppLoan?.loanInfo?.applicationuid}
          />
        </Tab>
      </Tabs>
    </div>
  );
};

export default LoanContainer;
