import {
  ADMIN_APPLICATIONS_LIST_ALL_IN_PROGRESS,
  ADMIN_APPLICATIONS_LIST_ALL_IN_SUCCESS,
  ADMIN_APPLICATIONS_LIST_ALL_IN_ERROR,
  ADMIN_LOAN_LIST_ALL_IN_PROGRESS,
  ADMIN_LOAN_LIST_ALL_IN_SUCCESS,
  ADMIN_LOAN_LIST_ALL_IN_ERROR,
  ADMIN_GET_APPLICATION_DETAILS_IN_PROGRESS,
  ADMIN_GET_APPLICATION_DETAILS_IN_SUCCESS,
  ADMIN_GET_APPLICATION_DETAILS_IN_ERROR,
  ADMIN_GET_PROFILE_DATA_IN_PROGRESS,
  ADMIN_GET_PROFILE_DATA_IN_SUCCESS,
  ADMIN_GET_PROFILE_DATA_IN_ERROR,
  ADMIN_GET_DOCUMENT_DATA_IN_PROGRESS,
  ADMIN_GET_DOCUMENT_DATA_IN_SUCCESS,
  ADMIN_GET_DOCUMENT_DATA_IN_ERROR,
  ADMIN_SAM_ANALYSIS_DATA_IN_PROGRESS,
  ADMIN_SAM_ANALYSIS_DATA_IN_SUCCESS,
  ADMIN_SAM_ANALYSIS_DATA_IN_ERROR,
  PUBLISH_APPLICATION_IN_PROGRESS,
  PUBLISH_APPLICATION_IN_SUCCESS,
  PUBLISH_APPLICATION_IN_ERROR,
  CANCEL_APPLICATION_IN_PROGRESS,
  CANCEL_APPLICATION_IN_SUCCESS,
  CANCEL_APPLICATION_IN_ERROR,
  SEND_BACK_APPLICATION_IN_PROGRESS,
  SEND_BACK_APPLICATION_IN_SUCCESS,
  SEND_BACK_APPLICATION_IN_ERROR,
  SAM_FILE_UPLOAD_IN_PROGRESS,
  SAM_FILE_UPLOAD_IN_SUCCESS,
  SAM_FILE_UPLOAD_IN_ERROR,
  ADMIN_GET_REQUESTED_DOCUMENTS_IN_PROGRESS,
  ADMIN_GET_REQUESTED_DOCUMENTS_IN_SUCCESS,
  ADMIN_GET_REQUESTED_DOCUMENTS_IN_ERROR,
  ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
  ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
  ADMIN_APPLICATION_FILTER_IN_PROGRESS,
  ADMIN_APPLICATION_FILTER_IN_SUCCESS,
  ADMIN_APPLICATION_FILTER_IN_ERROR,
  ADMIN_LOAN_FILTER_IN_PROGRESS,
  ADMIN_LOAN_FILTER_IN_SUCCESS,
  ADMIN_LOAN_FILTER_IN_ERROR,
  ADMIN_GET_OFFER_LIST_IN_PROGRESS,
  ADMIN_GET_OFFER_LIST_IN_SUCCESS,
  ADMIN_GET_OFFER_LIST_IN_ERROR,
  ADMIN_GET_LOAN_SUMMARY_IN_PROGRESS,
  ADMIN_GET_LOAN_SUMMARY_IN_SUCCESS,
  ADMIN_GET_LOAN_SUMMARY_IN_ERROR,
  ADMIN_GET_LOAN_DOCUMENTS_IN_PROGRESS,
  ADMIN_GET_LOAN_DOCUMENTS_IN_SUCCESS,
  ADMIN_GET_LOAN_DOCUMENTS_IN_ERROR,
  ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_PROGRESS,
  ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_SUCCESS,
  ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_ERROR,
  ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_PROGRESS,
  ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_SUCCESS,
  ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_ERROR,
  ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_PROGRESS,
  ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_SUCCESS,
  ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_ERROR,
} from '../../types/admintypes';
const apiStatus = { inProgress: false, success: false, error: false };

export const adminReducer = (
  state = {
    applications: {
      ...apiStatus,
    },
    loans: {
      ...apiStatus,
    },
    selectedApplication: {
      ...apiStatus,
      loanInfo: {},
      publish: {},
      cancelApplication: {},
      sendBackApplication: {},
      samFileUpload: {},
      requestedDocuments: {},
      sanctionDocsUpload: {},
      offerList: {},
      deleteSamDocs: {},
    },
    applicationFilter: { ...apiStatus },
    selectedLoan: {
      ...apiStatus,
      loanInfo: {},
      loanSummary: {},
      loanDocuments: {},
      repaymentStatus: {},
      sanctionDocsUpload: {},
    },
    loanFilter: { ...apiStatus },
  },
  action
) => {
  const { type, payload } = action;
  switch (type) {
    case ADMIN_APPLICATIONS_LIST_ALL_IN_PROGRESS:
      return {
        ...state,
        applications: { ...apiStatus, inProgress: true },
      };

    case ADMIN_APPLICATIONS_LIST_ALL_IN_SUCCESS:
      return {
        ...state,
        applications: {
          ...state.applications,
          ...apiStatus,
          inProgress: false,
          success: payload,
        },
      };

    case ADMIN_APPLICATIONS_LIST_ALL_IN_ERROR:
      return {
        ...state,
        applications: { ...apiStatus, error: payload },
      };

    case ADMIN_LOAN_LIST_ALL_IN_PROGRESS:
      return {
        ...state,
        loans: { ...apiStatus, inProgress: true },
      };

    case ADMIN_LOAN_LIST_ALL_IN_SUCCESS:
      return {
        ...state,
        loans: {
          ...state.loans,
          ...apiStatus,
          inProgress: false,
          success: payload,
        },
      };

    case ADMIN_LOAN_LIST_ALL_IN_ERROR:
      return {
        ...state,
        loans: { ...apiStatus, error: payload },
      };

    case ADMIN_GET_APPLICATION_DETAILS_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          loanInfo: payload,
          publish: {},
          cancelApplication: {},
          sendBackApplication: {},
          samFileUpload: {},
          requestedDocuments: {},
          sanctionDocsUpload: {},
          offerList: {},
          deleteSamDocs: {},
          details: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_APPLICATION_DETAILS_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          details: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_APPLICATION_DETAILS_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          details: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_PROFILE_DATA_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          profile: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_PROFILE_DATA_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          profile: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_PROFILE_DATA_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          profile: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_DOCUMENT_DATA_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          documentsData: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_DOCUMENT_DATA_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          documentsData: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_DOCUMENT_DATA_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          documentsData: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_SAM_ANALYSIS_DATA_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samAnalysis: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_SAM_ANALYSIS_DATA_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samAnalysis: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_SAM_ANALYSIS_DATA_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samAnalysis: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case PUBLISH_APPLICATION_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          publish: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case PUBLISH_APPLICATION_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          publish: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case PUBLISH_APPLICATION_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          publish: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case CANCEL_APPLICATION_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          cancelApplication: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case CANCEL_APPLICATION_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          cancelApplication: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case CANCEL_APPLICATION_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          cancelApplication: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case SEND_BACK_APPLICATION_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sendBackApplication: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case SEND_BACK_APPLICATION_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sendBackApplication: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case SEND_BACK_APPLICATION_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sendBackApplication: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case SAM_FILE_UPLOAD_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samFileUpload: {
            ...state.selectedApplication.samFileUpload,
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case SAM_FILE_UPLOAD_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samFileUpload: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case SAM_FILE_UPLOAD_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          samFileUpload: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_REQUESTED_DOCUMENTS_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          requestedDocuments: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_REQUESTED_DOCUMENTS_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          requestedDocuments: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_REQUESTED_DOCUMENTS_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          requestedDocuments: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sanctionDocsUpload: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sanctionDocsUpload: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          sanctionDocsUpload: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_APPLICATION_FILTER_IN_PROGRESS:
      return {
        ...state,
        applicationFilter: {
          ...apiStatus,
          inProgress: true,
        },
      };

    case ADMIN_APPLICATION_FILTER_IN_SUCCESS:
      return {
        ...state,
        applicationFilter: {
          ...apiStatus,
          success: true,
          ...payload,
        },
      };

    case ADMIN_APPLICATION_FILTER_IN_ERROR:
      return {
        ...state,
        applicationFilter: {
          ...apiStatus,
          error: true,
          ...payload,
        },
      };

    case ADMIN_LOAN_FILTER_IN_PROGRESS:
      return {
        ...state,
        loanFilter: {
          ...apiStatus,
          inProgress: true,
        },
      };

    case ADMIN_LOAN_FILTER_IN_SUCCESS:
      return {
        ...state,
        loanFilter: {
          ...apiStatus,
          success: true,
          ...payload,
        },
      };

    case ADMIN_LOAN_FILTER_IN_ERROR:
      return {
        ...state,
        loanFilter: {
          ...apiStatus,
          error: true,
          ...payload,
        },
      };

    case ADMIN_GET_OFFER_LIST_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          offerList: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_OFFER_LIST_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          offerList: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_OFFER_LIST_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          offerList: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_SUMMARY_IN_PROGRESS:
      return {
        ...state,
        selectedLoan: {
          loanInfo: payload,
          loanSummary: {
            ...apiStatus,
            inProgress: true,
          },
          loanDocuments: {},
          repaymentStatus: {},
          sanctionDocsUpload: {},
        },
      };

    case ADMIN_GET_LOAN_SUMMARY_IN_SUCCESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          loanSummary: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_SUMMARY_IN_ERROR:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          loanSummary: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_DOCUMENTS_IN_PROGRESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          loanDocuments: {
            ...apiStatus,
            combinedDocuments: { inProgress: true },
            loanDocuments: { inProgress: true },
            preDocuments: { inProgress: true },
            postDocuments: { inProgress: true },
          },
        },
      };

    case ADMIN_GET_LOAN_DOCUMENTS_IN_SUCCESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          loanDocuments: {
            ...state.selectedLoan.loanDocuments,
            ...apiStatus,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_DOCUMENTS_IN_ERROR:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          loanDocuments: {
            ...apiStatus,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_PROGRESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          repaymentStatus: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_SUCCESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          repaymentStatus: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_ERROR:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          repaymentStatus: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_PROGRESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          sanctionDocsUpload: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_SUCCESS:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          sanctionDocsUpload: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_ERROR:
      return {
        ...state,
        selectedLoan: {
          ...state.selectedLoan,
          sanctionDocsUpload: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    case ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_PROGRESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          deleteSamDocs: {
            ...apiStatus,
            inProgress: true,
          },
        },
      };

    case ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_SUCCESS:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          deleteSamDocs: {
            ...apiStatus,
            success: true,
            ...payload,
          },
        },
      };

    case ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_ERROR:
      return {
        ...state,
        selectedApplication: {
          ...state.selectedApplication,
          deleteSamDocs: {
            ...apiStatus,
            error: true,
            ...payload,
          },
        },
      };

    default:
      return state;
  }
};
