import FpoDashboard from '../pages/fpo/dashboard/FpoDashboard';
import FpoLoan from '../pages/fpo/loan/FpoLoan';
import InputLoan from '../pages/fpo/dashboard/working_capital/InputLoan';
import OutputLoan from '../pages/fpo/dashboard/working_capital/OutputLoan';
import CatalyticInfrastructure from '../pages/fpo/dashboard/term_loan/CatalyticInfrastructure';
import FPOGateway from '../pages/fpo/Gateway/FpoGateway';
import ApplicationStatus from '../pages/fpo/dashboard/application_status/ApplicationStatus';
const FpoRoutes = [
  {
    name: 'Fpo Dashboard',
    path: '/dashboard',
    base: '',
    element: <FpoDashboard />,
  },
  {
    name: 'Bank Loan',
    path: '/loan-applications',
    base: '',
    element: <FpoLoan />,
  },
  {
    name: 'Working Capital Loan Form',
    path: '/dashboard/working_capital/input_loan',
    base: '',
    element: <InputLoan />,
  },
  {
    name: 'Working Capital Output Form',
    path: '/dashboard/working_capital/output_loan',
    base: '',
    element: <OutputLoan />,
  },
  {
    name: 'Term Loan Catalytic infrastructure',
    path: '/dashboard/term_loan/catalytic_infrastructure',
    base: '',
    element: <CatalyticInfrastructure />,
  },
  {
    name: 'FPO Gateway',
    path: '/fpo-gateway',
    base: '',
    element: <FPOGateway />,
  },
  {
    name: 'FPO Creat application status',
    path: '/dashboard/application-status',
    base: '',
    element: <ApplicationStatus />,
  },
];

export default FpoRoutes;
