import React from 'react';
import Image from 'react-bootstrap/Image';
import { useState } from 'react';

import AddProductModal from '../../../components/Modals/AddProductModal';

import { product_list } from '../../../data';

export default function ProductList() {
  const [addProduct, setAddProduct] = React.useState(false);

  const [fileName, setFileName] = useState('Upload Boundary File');

  return (
    <section className='primary-bg pb-5'>
      <div className='container'>
        <div className='text-left-1'>
          <h4 className='f20 medium-font fw600 pl-2 ml-1 pt-3'>Product List</h4>
        </div>
        <div className='col-md-12 p-0'>
          <div className='general-box bg-white'>
            <div className='row '>
              <div className='mb-4 add-product-btn'>
                <button
                  className='btn btn-primary br-12 mt-3'
                  variant='primary'
                  onClick={() => setAddProduct(true)}
                >
                  {' '}
                  ADD PRODUCTS
                </button>

                <AddProductModal
                  show={addProduct}
                  onHide={() => setAddProduct(false)}
                />
              </div>
              <div className='col-12'>
                <div className='pl-3 mt-3 pr-3'>
                  <table className='product  w-100'>
                    <thead className=''>
                      <tr className='muteds f12'>
                        <th className='muteds'>S.NO.</th>
                        <th>PRODUCT CATEGORY</th>
                        <th>PRODUCT NAME</th>
                        <th>PRODUCT IMAGE</th>
                        <th>PRODUCT DESCRIPTION</th>
                        <th>PRODUCT DETAILS</th>
                        <th>PRODUCT ACTION</th>
                        <th>QUESTION VIEW</th>
                      </tr>
                    </thead>
                    <tbody className='f13 medium-font'>
                      {product_list.map((product_list, key) => (
                        <tr key={key}>
                          <td>{product_list.sNo}</td>
                          <td>{product_list.productCategory}</td>
                          <td>{product_list.productName}</td>

                          <td>
                            <Image
                              className='icon-img'
                              src='data:image/jpeg;base64,'
                            />
                          </td>
                          <td>{product_list.productDescription}</td>

                          <td>
                            <i className='fa fa-check' />
                          </td>
                          <td>
                            <i className='fa fa-pencil' />
                          </td>
                          <td>
                            <i className='fa fa-eye p-1' />
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}
