import { takeEvery, call, put } from 'redux-saga/effects';
import { decode } from 'jsonwebtoken';
import { SIGNIN_FPO, SIGNIN_REQUEST, SIGN_TOKEN } from '../../types';
import { authActions } from '../../actions';
import { signIn, signInFpo } from '../../../services/http/auth';

function* signInRequest(action) {
  try {
    const user = yield call(signIn, action.payload);
    const { data } = user;
    localStorage.setItem('login', data['agfinmarttoken']);
    let payload = {
      userInfo: decode(data['agfinmarttoken']),
      isLoggedIn: true,
      inProgress: false,
      error: false,
    };
    yield put(authActions.signInSuccess(payload));
  } catch (error) {
    const { data } = error.response;
    let payload = {
      error: true,
      errorMessage: data.message
        ? 'Enter valid username or password'
        : error.message,
      inProgress: false,
    };
    yield put(authActions.signInError(payload));
  }
}

function* logUserInWithToken(action) {
  try {
    let payload = {
      userInfo: decode(action.payload),
      isLoggedIn: true,
      inProgress: false,
      error: false,
      localTokenLogin: true,
    };
    const user = yield put(authActions.signInSuccess(payload));
  } catch (error) {
    yield put(
      authActions.signInError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* signInWithFpoToken(action) {
  try {
    const { data } = yield call(signInFpo, action.payload);
    let payload = {
      userInfo: decode(data['agfinmarttoken']),
      isLoggedIn: true,
      inProgress: false,
      error: false,
    };
    localStorage.setItem('login', data['agfinmarttoken']);
    yield put(authActions.signInSuccess(payload));
  } catch (error) {
    yield put(
      authActions.signInError({
        error: true,
        errorMessage: error,
        inProgress: false,
      })
    );
  }
}

function* authWatcher() {
  yield takeEvery(SIGNIN_REQUEST, signInRequest);
  yield takeEvery(SIGN_TOKEN, logUserInWithToken);
  yield takeEvery(SIGNIN_FPO, signInWithFpoToken);
}

export { authWatcher };
