import React, { useEffect } from 'react';
import arrow_back from '../../assets/Images/Icons/arrow-back.svg';
import {
  PUT_SANSACTION_DOCUMENT_REQUEST_IN_PROGRESS,
  GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_PROGRESS,
} from '../../redux/types/banktypes';
import { useDispatch, useSelector } from 'react-redux';
import SansactionDocumentRequest from '../Forms/SansactionDocumentRequest/SansactionDocumentRequest';
import { Spinner } from 'react-bootstrap';
import DocumentList from '../Tables/DocumentList';
import FpoInfo from './FpoInfo';
import ShadowBox from '../ShadowBox';
import LoanStatusChange from './LoanStatusChange';
import { PUT_MOVE_TO_LIVE_LOAN_IN_PROGRESS } from '../../redux/types/banktypes';
import { bankActions } from '../../redux/actions';

const BankActionTab = ({
  handleBack,
  applicationId,
  hideBackButton,
  loanInfo,
  loanStatusChangeShow,
  isSanctioned,
  repaymentCounts,
  basicDetails,
}) => {
  const dispatch = useDispatch();
  const bank = useSelector((state) => state.bank);
  const handleDocumentSubmit = (forms) => {
    let payload = {
      applicationuid: applicationId ? applicationId : loanInfo.applicationuid,
    };
    for (const key in forms) {
      if (Object.hasOwnProperty.call(forms, key)) {
        if (forms[key].length > 0) {
          if (key === 'pre') {
            payload.url = 'pre_sanction_docuemnt_request/';
          } else {
            payload.url = 'post_sanction_docuemnt_request/';
          }
          payload.jsonData = JSON.stringify(forms[key]);
          dispatch({
            type: PUT_SANSACTION_DOCUMENT_REQUEST_IN_PROGRESS,
            payload: payload,
          });
        }
      }
    }
  };

  useEffect(() => {
    if (
      bank &&
      bank.selectedAppLoan &&
      bank.selectedAppLoan.moveToLive &&
      bank.selectedAppLoan.moveToLive.success
    ) {
      dispatch(
        bankActions.getLoanDetailInProgress(loanInfo.applicationuid, loanInfo)
      );
    }
  }, [bank?.selectedAppLoan?.moveToLive?.success]);

  const fetchReqDocs = () => {
    setTimeout(() => {
      let payload = {
        applicationuid: applicationId ? applicationId : loanInfo.applicationuid,
      };
      dispatch({
        type: GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
        payload: payload,
      });
      dispatch({
        type: GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
        payload: payload,
      });
    }, 200);
  };

  useEffect(() => {
    if (bank.sansactionDocumentRequest.success) {
      fetchReqDocs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank.sansactionDocumentRequest.success]);

  useEffect(() => {
    if (applicationId) {
      fetchReqDocs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [applicationId]);

  const hanleMoveToLive = () => {
    dispatch({
      type: PUT_MOVE_TO_LIVE_LOAN_IN_PROGRESS,
      payload: { applicationuid: loanInfo.applicationuid },
    });
  };

  const handleUpload = (formData) => {
    dispatch({
      type: LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_PROGRESS,
      payload: { applicationuid: loanInfo?.applicationuid, formData },
    });
  };

  return (
    <div className='p-3'>
      {!hideBackButton ? (
        <div className='mt-1 pb-3'>
          <button className='back-btn' onClick={handleBack}>
            <img src={arrow_back} alt='back' width='20px' height='20px' />
            <span className='pl-1 '>BACK</span>
          </button>
        </div>
      ) : null}
      <div className='mt-5'>
        <FpoInfo
          details={bank?.selectedLoan?.loanInfo}
          basicDetails={basicDetails}
        />
        {loanStatusChangeShow && (
          <>
            <LoanStatusChange
              isSanctioned={isSanctioned}
              hanleMoveToLive={hanleMoveToLive}
              handleUpload={handleUpload}
              moveToLiveStatus={bank?.selectedAppLoan?.moveToLive}
              repaymentUploadStatus={
                bank?.selectedAppLoan?.repaymentScheduleUpload
              }
              repaymentCounts={repaymentCounts}
            />
            <hr />
          </>
        )}
        <>
          <div className='col-12 mt-4 py-3 px-0'>
            <div className='row nogutters' id='added'>
              <div className='col-12 pb-2'>
                <h4 className='f20 medium-font op70'>
                  Request Additional Documents
                </h4>
              </div>
              <div className='col-12 p-0'>
                <SansactionDocumentRequest
                  handleDocumentSubmit={handleDocumentSubmit}
                  handleStatus={bank.sansactionDocumentRequest}
                />
              </div>
            </div>
          </div>

          <div className='col-12 mt-3'>
            <hr />
          </div>
        </>

        <div className='col-12  mt-4 py-3 px-0'>
          <div className='row nogutters'>
            <div className='col-12 mb-2'>
              <h4 className='f20 medium-font op70'>
                View Additional Documents
              </h4>
            </div>

            <div className='col-md-12 mb-5 '>
              <ShadowBox>
                <div className='row '>
                  <div className='col-12 p-0'>
                    <h4 className='f20 medium-font op1'>
                      Post-Sanction Document
                    </h4>
                  </div>
                  {bank &&
                  bank.postSansactionRequestDocuments &&
                  bank.postSansactionRequestDocuments.inProgress ? (
                    <div className='d-flex justify-content-center'>
                      <Spinner animation='border' role='status'>
                        <span className='visually-hidden'></span>
                      </Spinner>
                    </div>
                  ) : (
                    <DocumentList
                      documents={bank.postSansactionRequestDocuments.documents}
                      userType='bank'
                      className='bank-requested-documents'
                    />
                  )}
                </div>
              </ShadowBox>
            </div>

            <div className='col-md-12'>
              <ShadowBox>
                <div className='row '>
                  <div className='col-12 p-0'>
                    <h4 className='f20 medium-font op1'>
                      Pre-Sanction Document
                    </h4>
                  </div>
                  {bank &&
                  bank.preSansactionRequestDocuments &&
                  bank.preSansactionRequestDocuments.inProgress ? (
                    <div className='d-flex justify-content-center'>
                      <Spinner animation='border' role='status'>
                        <span className='visually-hidden'></span>
                      </Spinner>
                    </div>
                  ) : (
                    <DocumentList
                      documents={bank.preSansactionRequestDocuments.documents}
                      userType='bank'
                      className='bank-requested-documents'
                    />
                  )}
                </div>
              </ShadowBox>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BankActionTab;
