import * as dayjs from 'dayjs';
import DocumentIcon from '../../DocumentIcon/DocumentIcon';
import { Accordion, Spinner, Alert } from 'react-bootstrap';
import DocumentsForm from '../../documents/DocumentsForm';

const LoanDocuments = ({
  loanInfo,
  postDocuments,
  preDocuments,
  loanDocuments,
}) => {
  return (
    <div className='mt-4'>
      <div className='row'>
        <div className='col-md-7 px-5'>
          <h4 className='f16 text-bold'>
            <span className='text-muted'>Borrower</span>
            <span className='pl-5 text-bold'>{loanInfo.fponame}</span>
          </h4>
        </div>
        <div className='col-md-5'>
          <h4 className='f16 text-bold'>
            <span className='text-muted'>Loan Start Date</span>
            <span className='ml-3 text-bold'>
              {loanInfo.loanstartdate
                ? dayjs(loanInfo.loanstartdate).format('D-M-YYYY')
                : 'NIL'}
            </span>
          </h4>
        </div>
      </div>
      <br />
      <div className='px-3'>
        <Accordion className='documents-accordion mt-5'>
          <Accordion.Item eventKey='loan' className='general-box mb-4'>
            <Accordion.Header>
              <div className='d-flex justify-content-between align-items-center'>
                <h4 className='form-section-header ml-1 f24'>Loan Documents</h4>

                <i
                  className='
                                fa
                                f16
                                text-muted
                                fa-chevron-down
                              '
                ></i>
              </div>
            </Accordion.Header>
            <Accordion.Body>
              {loanDocuments && !loanDocuments.inProgress && (
                <DocumentsForm
                  hideNavButtons={true}
                  documents={loanDocuments.documentdata}
                />
              )}

              {loanDocuments && loanDocuments.inProgress && (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              )}

              {loanDocuments && !loanDocuments.success && (
                <div className='d-flex justify-content-center'>
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {loanDocuments.message}
                  </Alert>
                </div>
              )}
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey='pre' className='general-box mb-4'>
            <Accordion.Header>
              <div className='d-flex justify-content-between align-items-center'>
                <h4 className='form-section-header ml-1 f24'>
                  Pre-Sanction Document
                </h4>

                <i
                  className='
                                fa
                                f16
                                text-muted
                                fa-chevron-down
                              '
                ></i>
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <section className='d-flex flex-wrap px-2'>
                {preDocuments &&
                  !preDocuments.inProgress &&
                  preDocuments.data &&
                  preDocuments.data.map((document, key) => {
                    let urlProps = document.documentURL
                      ? {
                          href: document.documentURL,
                          target: '_blank',
                          rel: 'noreferrer',
                        }
                      : {};
                    return (
                      <span className='px-1 mr-2' key={key}>
                        <a
                          className={`p-1 document-link ${
                            !document.documentURL && 'rejected'
                          }`}
                          {...urlProps}
                        >
                          <DocumentIcon
                            download={document.documentURL ? true : false}
                          />
                          {document.documentname}
                        </a>
                      </span>
                    );
                  })}
                {preDocuments && preDocuments.inProgress && (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}

                {preDocuments && !preDocuments.success && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {preDocuments.message}
                  </Alert>
                )}
              </section>
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey='post' className='general-box mb-4'>
            <Accordion.Header>
              <div className='d-flex justify-content-between align-items-center'>
                <h4 className='form-section-header ml-1 f24'>
                  Post-Sanction Document
                </h4>

                <i
                  className='
                                fa
                                f16
                                text-muted
                                fa-chevron-down
                              '
                ></i>
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <section className='d-flex flex-wrap px-2'>
                {postDocuments &&
                  !postDocuments.inProgress &&
                  postDocuments.data &&
                  postDocuments.data.map((document, key) => {
                    return (
                      <span className='px-1 mr-2' key={key}>
                        <a
                          className={`p-1 document-link ${
                            !document.documentURL && 'rejected'
                          }`}
                          href={`${
                            document.documentURL && document.documentURL
                          }`}
                          target='_blank'
                          rel='noreferrer'
                        >
                          <DocumentIcon
                            download={document.documentURL ? true : false}
                          />
                          {document.documentname}
                        </a>
                      </span>
                    );
                  })}
                {postDocuments && postDocuments.inProgress && (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}

                {postDocuments && !postDocuments.success && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {postDocuments.message}
                  </Alert>
                )}
              </section>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </div>
    </div>
  );
};

export default LoanDocuments;
