import React, { useState } from 'react';

import FpoInfo from './FpoInfo';
import ShadowBox from '../ShadowBox';
import { Alert, Spinner } from 'react-bootstrap';
import RequestedDocuments from './RequestedDocuments';
import CancelApplicationModal from '../Modals/CancelApplicationModal';

const AdminActionsTab = ({
  applicationId,
  handleCancelApplication,
  handleDocsSubmit,
  cancelApplication,
  loanInfo,
  requestedDocuments,
  sanctionDocsUpload,
  applicationStatus,
  basicDetails,
}) => {
  const [cancelApp, setCancelApp] = useState(false);
  const handleDocumentSubmit = (data, ld_recid, docType, documentName) => {
    const formData = new FormData();
    formData.append(documentName, data[ld_recid][0]);
    formData.append('ld_recid', ld_recid);
    let payload = { applicationuid: applicationId, formData };
    if (docType === 'post') {
      payload.url = 'post_sanction_document_upload';
    } else if (docType === 'pre') {
      payload.url = 'pre_sanction_document_upload';
    }
    payload.formData = formData;
    handleDocsSubmit(payload);
  };

  return (
    <div className=''>
      <div className='mt-5'>
        <FpoInfo details={loanInfo} basicDetails={basicDetails} />
        <div className='row px-4 py-3'>
          {cancelApplication && (
            <div className='col-md-4'>
              <ShadowBox>
                <h4 className='f-24 text-bold mb-3'>
                  Application/Loan Function
                </h4>
                {cancelApplication &&
                  !cancelApplication.inProgress &&
                  applicationStatus !== 'Draft' &&
                  applicationStatus !== 'Cancelled' && (
                    <>
                      <button
                        className='btn btn-primary mt-2 br-12 cancel-application-btn f13 text-bold'
                        variant='primary'
                        onClick={() => setCancelApp(true)}
                      >
                        CANCEL APPLICATION
                      </button>
                      <br />
                      <br />
                    </>
                  )}
                {cancelApplication && cancelApplication.inProgress && (
                  <Spinner animation='border' role='status' className='TEST'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}
                {cancelApplication &&
                  !cancelApplication.inProgress &&
                  cancelApplication.success && (
                    <Alert
                      variant={'success'}
                      style={{ width: '30vw' }}
                      className='text-center'
                    >
                      {cancelApplication.message}
                    </Alert>
                  )}
                {cancelApplication &&
                  !cancelApplication.inProgress &&
                  cancelApplication.error && (
                    <Alert
                      variant={'danger'}
                      style={{ width: '30vw' }}
                      className='text-center'
                    >
                      {cancelApplication.message}
                    </Alert>
                  )}
                <p className='pt-3 text-muted'>
                  Cancel application from the marketplace
                </p>
              </ShadowBox>
            </div>
          )}

          <div className={`${cancelApplication ? 'col-md-8' : 'col-md-12'}`}>
            <ShadowBox className='col-12 p-3'>
              <h4 className='f-24 text-bold mb-3'>Requested Documents</h4>
              {requestedDocuments && requestedDocuments.inProgress && (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              )}

              {requestedDocuments && requestedDocuments.documents && (
                <>
                  <RequestedDocuments
                    sansactionDocuments={requestedDocuments.documents}
                    handleDocumentSubmit={handleDocumentSubmit}
                  />
                </>
              )}
              {sanctionDocsUpload && sanctionDocsUpload.inProgress && (
                <div className='absolute-center'>
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                </div>
              )}
              <br />
              {sanctionDocsUpload &&
                !sanctionDocsUpload.inProgress &&
                sanctionDocsUpload.success && (
                  <div className='d-flex justify-content-center'>
                    <Alert
                      variant={'success'}
                      style={{ width: '30vw' }}
                      className='text-center'
                    >
                      {sanctionDocsUpload.message
                        ? sanctionDocsUpload.message
                        : 'Document upload Failed.'}
                    </Alert>
                  </div>
                )}
              {sanctionDocsUpload &&
                !sanctionDocsUpload.inProgress &&
                sanctionDocsUpload.error && (
                  <div className='d-flex justify-content-center'>
                    <Alert
                      variant={'danger'}
                      style={{ width: '30vw' }}
                      className='text-center'
                    >
                      {sanctionDocsUpload.message
                        ? sanctionDocsUpload.message
                        : 'Document upload Failed.'}
                    </Alert>
                  </div>
                )}
            </ShadowBox>
          </div>
        </div>
      </div>
      <CancelApplicationModal
        show={cancelApp}
        onHide={setCancelApp}
        handleCancelApplication={handleCancelApplication}
      />
    </div>
  );
};

export default AdminActionsTab;
