import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import AdminApplicationForm from '../../../components/Forms/Admin/Application/AdminApplicationForm';
import { adminActions } from '../../../redux/actions';

const ApplicationStatus = ({ applicationId }) => {
  const location = useLocation();
  const dispatch = useDispatch();
  const params = new URLSearchParams(location.search);
  let qry_application_id = params.get('application_id');
  const adminState = useSelector((state) => state.admin);

  useEffect(() => {
    if (
      adminState &&
      adminState.selectedApplication &&
      adminState.selectedApplication.loanInfo &&
      !adminState.selectedApplication.loanInfo.applicationuid
    ) {
      dispatch(
        adminActions.getApplicationDetailsInProgress({
          applicationuid: qry_application_id,
        })
      );
    }
  }, [qry_application_id]);

  return (
    <div className='container px-5'>
      <h3 className='f24 text-bold'>Loan Details</h3>
      <div className='bg-white  br-12 mt-4'>
        <AdminApplicationForm />
      </div>
    </div>
  );
};

export default ApplicationStatus;
