import {
  LOG_OUT,
  SIGNIN_ERROR,
  SIGNIN_REQUEST,
  SIGNIN_SUCCESS,
} from '../../types';

export const authReducer = (
  state = {
    inProgress: false,
    error: false,
    isLoggedIn: false,
    userInfo: null,
  },
  action
) => {
  const { type, payload } = action;
  switch (type) {
    case SIGNIN_REQUEST:
      return { ...state, inProgress: true, error: false, isLoggedIn: false };

    case SIGNIN_SUCCESS:
      return { ...state, ...payload };

    case SIGNIN_ERROR:
      return { ...state, ...payload, inProgress: false, error: true };

    case LOG_OUT:
      return { inProgress: false, ...payload, error: false };

    default:
      return state;
  }
};
