import _ from 'lodash';
import React, { useState, useEffect } from 'react';
import OutputLoanForm from '../../Forms/FPO/WorkingCapital/OutputLoan/OutputLoanForm';
import InputLoanForm from '../../Forms/FPO/WorkingCapital/InputLoan/InputLoanForm';
import CatalyticInfrastructureForm from '../../Forms/FPO/TermLoan/CatalyticInfrastructure/CatalyticInfrastructureForm';

const LoanApplicationForm = (props) => {
  const [formData, setFormData] = useState({});
  useEffect(() => {
    if (!props.loanDetails) return;
    const { data } = props.loanDetails;
    const answer = {};
    _.each(data, (obj) => {
      answer[obj.Question_Number] = obj.Answer;
    });
    setFormData((prev) => ({
      ...prev,
      fill_application: answer,
    }));
  }, [props.loanDetails]);
  return (
    <>
      <div className='text-right'>
        <button className='loan-application-btn btn-default mr-3'>
          Download Application
        </button>
      </div>
      <div
        style={{
          paddingLeft: props.contentPadding,
          paddingRight: props.contentPadding,
        }}
      >
        {props.loanType === 'Infrastructure Loan' ||
        props.loanType === 'Term loan - Infrastructure' ? (
          <CatalyticInfrastructureForm
            readOnly={true}
            handleTabChange={props.handleTabChange}
            activeKey={props.activeKey}
            defaultValues={formData.fill_application}
            hideActionsButtons={props.hideActionsButtons}
          />
        ) : props.loanType === 'Output Loan' ||
          props.loanType === 'Working capital - Output' ? (
          <OutputLoanForm
            readOnly={true}
            handleTabChange={props.handleTabChange}
            activeKey={props.activeKey}
            defaultValues={formData.fill_application}
            hideActionsButtons={props.hideActionsButtons}
          />
        ) : props.loanType === 'Input Loan' ||
          props.loanType === 'Working capital - Input' ? (
          <InputLoanForm
            readOnly={true}
            handleTabChange={props.handleTabChange}
            activeKey={props.activeKey}
            defaultValues={formData.fill_application}
            hideActionsButtons={props.hideActionsButtons}
          />
        ) : (
          <div className='d-flex justify-content-center '>
            <h5 className='text-danger text-bold'>
              Application type not matched
            </h5>
          </div>
        )}
      </div>
    </>
  );
};

export default LoanApplicationForm;
