import { useState, useEffect } from 'react';
import InputBox from '../../../../FormInputs/InputBox';
import SelectBox from '../../../../FormInputs/SelectBox';
import { useForm, Controller } from 'react-hook-form';
import { Form } from 'react-bootstrap';
import {
  revenue_as_on_years,
  input_categories,
  input_sub_categories,
  required_units,
} from '../../../../../data.js';
import { INPUT_LOAN_TYPES, LOAN_FIELDS } from '../../../../../constants';
import CurrencyInput from '../../../../FormInputs/CurrencyInput';

const FillApplication = ({
  readOnly,
  handleTabChange,
  activeKey,
  defaultValues,
  backButton,
  formModified,
  hideActionsButtons,
  revenue,
}) => {
  const {
    register,
    handleSubmit,
    setValue,
    getValues,
    watch,
    control,
    formState: { errors, isDirty },
  } = useForm({
    defaultValues: {
      [INPUT_LOAN_TYPES[LOAN_FIELDS.loanType]]: 'Working capital - Input',
    },
  });
  const [inputCategory, setInputCategory] = useState('');
  const [requiredUnit, setRequiredUnit] = useState('Unit');

  useEffect(() => {
    const subscription = watch((value, { name, type }) => {
      if (
        name == [INPUT_LOAN_TYPES[LOAN_FIELDS.costUnit]] ||
        name == [INPUT_LOAN_TYPES[LOAN_FIELDS.requiredVolume]]
      ) {
        setValue(
          INPUT_LOAN_TYPES[LOAN_FIELDS.totalCost],
          parseFloat(getValues([INPUT_LOAN_TYPES[LOAN_FIELDS.costUnit]])) *
            parseFloat(
              getValues([INPUT_LOAN_TYPES[LOAN_FIELDS.requiredVolume]])
            ),
          {
            shouldValidate: true,
          }
        );
      }
    });
    return () => subscription.unsubscribe();
  }, [watch]);

  useEffect(() => {
    if (
      revenue &&
      (!defaultValues || !defaultValues[INPUT_LOAN_TYPES[LOAN_FIELDS.revenue]])
    ) {
      setValue(INPUT_LOAN_TYPES[LOAN_FIELDS.revenue], revenue, {
        shouldValidate: !readOnly,
      });
    }
  }, [revenue]);

  useEffect(() => {
    if (!defaultValues) return;
    setInputCategory(
      () => defaultValues[INPUT_LOAN_TYPES[LOAN_FIELDS.inputCategory]]
    );
    setTimeout(() => {
      for (var key in defaultValues) {
        setValue(key, defaultValues[key], {
          shouldValidate: !readOnly,
        });
      }
      if (defaultValues) {
        if (defaultValues) {
          if (defaultValues[INPUT_LOAN_TYPES[LOAN_FIELDS.requiredUnit]]) {
            setRequiredUnit(
              defaultValues[INPUT_LOAN_TYPES[LOAN_FIELDS.requiredUnit]]
            );
          }
        }
      }
    }, 520);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [defaultValues]);
  return (
    <Form
      onSubmit={handleSubmit((data) => {
        handleTabChange(
          activeKey,
          'next',
          data,
          readOnly ? formModified : isDirty
        );
      })}
      noValidate
    >
      <div className='p-1'>
        <h4 className='form-section-header mt-4 mb-2 fs-16'>Loan Details</h4>
        <section className='d-flex flex-wrap '>
          <InputBox
            label='Loan Type'
            className=''
            register={register}
            required
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.loanType]}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.loanType]]}
            readOnly={true}
            directInput={true}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Loan Amount'
                className=''
                setValue={setValue}
                error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.loanAmount]]}
                readOnly={readOnly}
                required={true}
                {...field}
              />
            )}
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.loanAmount], {
              required: { value: true, message: 'This is Mandatory field' },
              min: { value: 1, message: 'Please enter the value above 0' },
              max:{ value: 100000000, message: 'Please enter the value below 100000000' },
              pattern: {
                value: /([0-9].[0]$)|([0-9]*[0]+[.]+[0-9]*$)/,
                message:
                  'Entered value must have end with zero or multiples of 10',
              },
            })}
            control={control}
          />

          <InputBox
            label='Expected tenure(Months)'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            max={{ value: 4, message: 'Please enter the value below 5' }}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]}
            type='number'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]]}
            readOnly={readOnly}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Revenue'
                className=''
                setValue={setValue}
                error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.revenue]]}
                readOnly={readOnly}
                required={true}
                {...field}
              />
            )}
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.revenue], {
              required:{ value: true, message: 'This is Mandatory field' },
              min:{ value: 0.01, message: 'Please enter the value above 0.01' } ,
            })}
            control={control}
          />

          <SelectBox
            label='Revenue as-on year'
            className=''
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]}
            type='number'
            options={revenue_as_on_years}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]]}
            readOnly={readOnly}
          />
          <SelectBox
            label='Revenue as per:'
            className=''
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]}
            type='number'
            options={[
              { value: 'Provisional', text: 'Provisional' },
              { value: 'Audited', text: 'Audited' },
            ]}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]]}
            readOnly={readOnly}
          />
        </section>
        <h4 className='form-section-header mt-4 mb-2 fs-16'>Input Details</h4>
        <section className='d-flex flex-wrap '>
          <SelectBox
            label='Input Category'
            className=''
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.inputCategory], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.inputCategory]}
            required
            type='number'
            options={input_categories}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.inputCategory]]}
            handleChange={(val) => setInputCategory(val)}
            readOnly={readOnly}
          />

          <SelectBox
            label='Input Sub-Category'
            className=''
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.inputSubCategory], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.inputSubCategory]}
            type='number'
            options={input_sub_categories[inputCategory]}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.inputSubCategory]]}
            readOnly={readOnly}
          />
          <InputBox
            label='Required volume'
            className=''
            register={register}
            required ={{ value: true, message: 'This is Mandatory field' }}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.requiredVolume]}
            type='number'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.requiredVolume]]}
            readOnly={readOnly}
          />

          <SelectBox
            label='Required Unit'
            className=''
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.requiredUnit], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.requiredUnit]}
            type='number'
            options={required_units}
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.requiredUnit]]}
            readOnly={readOnly}
            handleChange={(val) => setRequiredUnit(val)}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label={`Cost/(${requiredUnit})`}
                className=''
                setValue={setValue}
                error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.costUnit]]}
                readOnly={readOnly}
                required={true}
                {...field}
              />
            )}
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.costUnit], {
              required: { value: true, message: 'This is Mandatory field' },
              min:{ value: 1, message: 'Please enter the value above 0' },
            })}
            control={control}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label={`Total cost`}
                className=''
                setValue={setValue}
                error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.totalCost]]}
                readOnly={readOnly}
                required={true}
                {...field}
              />
            )}
            {...register(INPUT_LOAN_TYPES[LOAN_FIELDS.totalCost], {
              required: { value: true, message: 'This is Mandatory field' },
              min:{ value: 1, message: 'Please enter the value above 0' },
            })}
            control={control}
          />

          <InputBox
            label='Name of supplier'
            register={register}
            required={false}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.nameOfSupplier]}
            type='text'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.nameOfSupplier]]}
            readOnly={readOnly}
          />
          <InputBox
            label='Supplier address'
            register={register}
            required={false}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.supplierAddress]}
            type='text'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.supplierAddress]]}
            readOnly={readOnly}
          />

          <InputBox
            label='Supplier contact'
            register={register}
            required={false}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.supplierContact]}
            type='number'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.supplierContact]]}
            minLength={10}
            maxLength={10}
            readOnly={readOnly}
          />

          <InputBox
            label='Brand'
            register={register}
            required={false}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.brand]}
            type='text'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.brand]]}
            readOnly={readOnly}
          />
          <InputBox
            label='Product name'
            register={register}
            required={false}
            name={INPUT_LOAN_TYPES[LOAN_FIELDS.productName]}
            type='text'
            error={errors[INPUT_LOAN_TYPES[LOAN_FIELDS.productName]]}
            readOnly={readOnly}
          />
        </section>
        {!hideActionsButtons && (
          <section className='d-flex justify-content-end my-5 custom-width'>
            {backButton && (
              <button
                type='button'
                className='loan-application-btn btn-secondary mr-5 marg_1 custom-loan-application-btn btn-margin-right'
                onClick={() => handleTabChange(activeKey, 'prev')}
              >
                PREVIOUS
              </button>
            )}

            {readOnly ? (
              <button
                type='button'
                className='loan-application-btn btn-primary mr-5 custom-loan-application-btn btn-margin-right'
                onClick={() =>
                  handleTabChange(
                    activeKey,
                    'next',
                    defaultValues,
                    readOnly ? formModified : isDirty
                  )
                }
              >
                NEXT
              </button>
            ) : (
              <button className='loan-application-btn btn-primary MR_1'>
                NEXT
              </button>
            )}
          </section>
        )}
      </div>
    </Form>
  );
};
export default FillApplication;
