import React, { useEffect } from 'react';
// import Chart from 'Chart.js/auto';

const FpoStatusChart = ({ counts }) => {
  let data = {};
  for (const key in counts) {
    if (Object.hasOwnProperty.call(counts, key)) {
      data[key] = counts[key] ? counts[key] : 0;
    }
  }

  useEffect(() => {
    if (counts) {
      window.Chart.defaults.RoundedDoughnut = window.Chart.helpers.clone(
        window.Chart.defaults.doughnut
      );
      window.Chart.controllers.RoundedDoughnut =
        window.Chart.controllers.doughnut.extend({
          draw: function (ease) {
            var ctx = this.chart.ctx;
            var easingDecimal = ease || 1;
            var arcs = this.getMeta().data;
            window.Chart.helpers.each(arcs, function (arc, i) {
              arc.transition(easingDecimal).draw();

              var pArc = arcs[i === 0 ? arcs.length - 1 : i - 1];
              var pColor = pArc._view.backgroundColor;

              var vm = arc._view;
              var radius = (vm.outerRadius + vm.innerRadius) / 2;
              var thickness = (vm.outerRadius - vm.innerRadius) / 2;
              var startAngle = Math.PI - vm.startAngle - Math.PI / 2;
              var angle = Math.PI - vm.endAngle - Math.PI / 2;

              ctx.save();
              ctx.translate(vm.x, vm.y);

              ctx.fillStyle = i === 0 ? vm.backgroundColor : pColor;
              ctx.beginPath();
              ctx.arc(
                radius * Math.sin(startAngle),
                radius * Math.cos(startAngle),
                thickness,
                0,
                2 * Math.PI
              );
              ctx.fill();

              ctx.fillStyle = vm.backgroundColor;
              ctx.beginPath();
              ctx.arc(
                radius * Math.sin(angle),
                radius * Math.cos(angle),
                thickness,
                0,
                2 * Math.PI
              );
              ctx.fill();

              ctx.restore();
            });
          },
        });
      new window.Chart(document.getElementById('usersChart'), {
        type: 'RoundedDoughnut',
        data: {
          labels: ['Requested Documents', 'Approved', 'Rejected'],
          datasets: [
            {
              data: [
                data.Requested,
                data.Approved_Application_Count,
                data.Rejected_Application_Count,
              ],
              backgroundColor: ['#00A3FF', '#20D489', '#D31717'],
              borderWidth: 0,
            },
          ],
        },
        options: {
          animation: {
            animateScale: true,
          },
          legend: {
            position: 'bottom',
            // labels: {
            //   generateLabels: (chart) => {
            //     return chart.data.labels;
            //   },
            // },
          },
          cutoutPercentage: 70,
        },
      });
    }
  }, []);
  return (
    <div className='wrapper'>
      <canvas id='usersChart' width='1' height='1'></canvas>
    </div>
  );
};

export default FpoStatusChart;
