import React, { useState } from 'react';
import { Tabs, Tab, Alert } from 'react-bootstrap';
import './ApplicationModal.scss';
import { useSelector } from 'react-redux';
import LoanSummary from '../../Forms/Common/LoanSummary';
import LoanDocuments from '../../Forms/Common/LoanDocuments';
import RePaymentSchedule from '../../Tables/RePaymentSchedule';
import BankActionTab from '../../ActionsTab/BankActionsTab';

const BankLoanContainer = ({ loanCategory }) => {
  const [activeTab, setActiveTab] = useState('loan_summary');
  const bank = useSelector((state) => state.bank);
  return (
    <div>
      <Tabs
        defaultActiveKey={activeTab}
        className='mb-3 lender-loans-tab mt-3 mx-4 px-2'
        onSelect={(k) => setActiveTab(k)}
        activeKey={activeTab}
      >
        <Tab eventKey='loan_summary' title='LOAN SUMMARY'>
          {bank && bank.selectedAppLoan && bank.selectedAppLoan.loanSummary && (
            <LoanSummary
              loanInfo={bank.selectedAppLoan.loanInfo}
              loanSummary={bank.selectedAppLoan.loanSummary}
            />
          )}
        </Tab>
        <Tab eventKey='documents' title='DOCUMENTS'>
          {bank && bank.selectedAppLoan && bank.selectedAppLoan.loanSummary && (
            <LoanDocuments
              loanInfo={bank.selectedAppLoan.loanInfo}
              loanDocuments={bank?.documentData?.documents}
              preDocuments={
                bank?.preSansactionRequestDocuments.documents
                  ? {
                      ...bank.preSansactionRequestDocuments,
                      data: bank.preSansactionRequestDocuments.documents,
                    }
                  : bank?.preSansactionRequestDocuments
              }
              postDocuments={
                bank?.postSansactionRequestDocuments.documents
                  ? {
                      ...bank.postSansactionRequestDocuments,
                      data: bank.postSansactionRequestDocuments.documents,
                    }
                  : bank?.postSansactionRequestDocuments
              }
            />
          )}
        </Tab>
        <Tab eventKey='repayment_status' title={`REPAYMENT STATUS`}>
          <div className='px-5'>
            {bank &&
              bank.selectedAppLoan &&
              bank.selectedAppLoan.repaymentSchedules &&
              !bank.selectedAppLoan.repaymentSchedules.inProgress &&
              bank.selectedAppLoan.repaymentSchedules.error && (
                <div className='d-flex justify-content-center'>
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {bank.selectedAppLoan.repaymentSchedules.message}
                  </Alert>
                </div>
              )}

            {bank &&
              bank.selectedAppLoan &&
              bank.selectedAppLoan.repaymentSchedules &&
              !bank.selectedAppLoan.repaymentSchedules.error && (
                <RePaymentSchedule
                  documents={
                    bank?.selectedAppLoan?.repaymentSchedules?.documents
                  }
                />
              )}
          </div>
        </Tab>
        <Tab eventKey='actions' title='ACTIONS'>
          <BankActionTab
            hideBackButton={true}
            loanInfo={bank.selectedAppLoan.loanInfo}
            basicDetails={bank?.selectedAppLoan?.basicDetails}
            loanStatusChangeShow={true}
            isSanctioned={
              bank?.selectedAppLoan?.loanSummary?.loanstatus === 'Sanctioned'
                ? true
                : false
            }
            repaymentCounts={
              bank &&
              bank.selectedAppLoan &&
              bank.selectedAppLoan.repaymentSchedules &&
              bank.selectedAppLoan.repaymentSchedules.documents
                ? bank.selectedAppLoan.repaymentSchedules.documents.length
                : 0
            }
          />
        </Tab>
      </Tabs>
    </div>
  );
};

export default BankLoanContainer;
