function* saveShowInterest(action) {
  try {
    const { data } = yield call(showInterest, action.payload);
    if (data.status === 200) {
      let payload = {
        error: false,
        inProgress: false,
        success: true,
        message: data.message,
        proposal_id: data.actiondata.proposal_id,
      };
      yield put(
        bankActions.saveShowInterestInSuccess({ showInterest: payload })
      );
    } else {
      let payload = {
        error: true,
        inProgress: false,
        success: false,
        message: data.message,
      };
      yield put(bankActions.saveShowInterestInError({ showInterest: payload }));
    }
  } catch (error) {
    yield put(
      bankActions.saveShowInterestInError({
        showInterest: { error },
      })
    );
  }
}

