function* saveMakeAnOffer(action) {
  try {
    const { data } = yield call(makeAnOffer, action.payload);
    if (data.status === 200) {
      let payload = {
        error: false,
        inProgress: false,
        success: true,
        message: data.message,
      };
      yield put(bankActions.saveMakeAnOfferInSuccess({ makeAnOffer: payload }));
    } else {
      let payload = {
        error: true,
        inProgress: false,
        success: false,
        message: data.message,
      };
      yield put(bankActions.saveMakeAnOfferInError({ makeAnOffer: payload }));
    }
  } catch (error) {
    let payload = {
      error: true,
      inProgress: false,
      success: false,
      message: error.response.data.message,
    };
    yield put(
      bankActions.saveMakeAnOfferInError({
        makeAnOffer: payload,
      })
    );
  }
}

