function* postCompareFpoInProgress(action) {
  try {
    let { data } = yield call(postCompareFpo, action.payload);

    let fpo_list = data['data'];

    let id_arr = [];
    let data_obj = Object.assign(
      {},
      ...fpo_list.map((fpo) => {
        id_arr.push(fpo.fpoid);
        return { [fpo.fpoid]: fpo };
      })
    );

    let payload = {
      inProgress: false,
      error: false,
      fpo: { fpoIds: id_arr, fpoObjs: data_obj },
    };
    yield put(bankActions.postCompareFpoInSuccess(payload));
  } catch (error) {
    yield put(
      bankActions.postCompareFpoInError({
        message: 'Problem while communicating with server.',
      })
    );
  }
}

