function* bankWatcher() {
  yield takeEvery(GET_PENDING_LOAN_IN_PROGRESS, getAllOrganizaiontApplications);
  yield takeEvery(GET_PENDING_LOAN_APP_IN_PROGRESS, getAllOrganizaionLoans);
  yield takeEvery(GET_LOAN_APP_DETAILS_IN_PROGRESS, getSelectedLoanInfo);
  yield takeEvery(GET_LOAN_DETAILS_IN_PROGRESS, getSelectedLoanApplicationInfo);
  yield takeEvery(SAVE_SHOW_INTEREST_IN_PROGRESS, saveShowInterest);
  yield takeEvery(SAVE_REJECT_INTEREST_IN_PROGRESS, saveRejectInterest);
  yield takeEvery(SAVE_MAKE_AN_OFFER_IN_PROGRESS, saveMakeAnOffer);
  yield takeEvery(GET_PROFILE_DATA_IN_PROGRESS, getProfileDataInProgress);
  yield takeEvery(GET_DOCUMENT_DATA_IN_PROGRESS, getdDocumentDataInProgress);
  yield takeEvery(GET_SAM_ANALYSIS_IN_PROGRESS, getdSamAnalysisInProgress);
  yield takeEvery(
    PUT_SANSACTION_DOCUMENT_REQUEST_IN_PROGRESS,
    putSansactionDocumentRequireInProgress
  );
  yield takeEvery(
    GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
    getPreSansactionRequestDocumentsInProgress
  );
  yield takeEvery(
    GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
    getPostSansactionRequestDocumentsInProgress
  );
  yield takeEvery(GET_SAVED_OFFER_IN_PROGRESS, getSavedOfferInProgress);
  yield takeEvery(PUT_MOVE_TO_LIVE_LOAN_IN_PROGRESS, moveToLiveLoanInProgress);
  yield takeEvery(
    LOAN_UPLOAD_REPAYMENT_SCHEDULE_IN_PROGRESS,
    uploadReapaymentScheduleInProgress
  );
  yield takeEvery(
    GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS,
    getLoanRepaymentSchdulesInProgress
  );

  yield takeEvery(
    FPO_GET_SAM_KEY_FINANCIALS_IN_PROGRESS,
    getSamKeyFinancialsInProgress
  );

  yield takeEvery(
    FPO_GET_SAM_RATIO_ANALYSIS_IN_PROGRESS,
    getSamRatioANalysisInProgress
  );

  yield takeLatest(
    BANK_APPLICATION_FILTER_IN_PROGRESS,
    applicationFilterInProgress
  );

  yield takeEvery(
    FPO_GET_SAM_FINANCIAL_PERFORMANCE_IN_PROGRESS,
    getSamFinancialPerformanceInProgress
  );
  yield takeLatest(BANK_LOAN_FILTER_IN_PROGRESS, loanFilterInProgress);
  yield takeLatest(BANK_GET_EXPLORE_FPO_IN_PROGRESS, getExploreFpoInProgress);
  yield takeLatest(BANK_POST_EXPLORE_FPO_IN_PROGRESS, postExploreFpoInProgress);
  yield takeLatest(BANK_POST_COMPARE_FPO_IN_PROGRESS, postCompareFpoInProgress);
  yield takeLatest(
    BANK_GET_FPO_STATUS_COUNTS_IN_PROGRESS,
    getFpoStatusCountsInProgress
  );
  yield takeLatest(
    BANK_GET_DASHBOARD_METRICS_IN_PROGRESS,
    getDashboardMetricsInProgress
  );
}

export { bankWatcher };
