import { Tabs, Tab, Badge, ProgressBar } from 'react-bootstrap';
import PublishApplication from '../../../ActionsTab/PublishApplication';
import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import _ from 'lodash';
import OutputLoanForm from '../../FPO/WorkingCapital/OutputLoan/OutputLoanForm';
import InputLoanForm from '../../FPO/WorkingCapital/InputLoan/InputLoanForm';
import CatalyticInfrastructureForm from '../../FPO/TermLoan/CatalyticInfrastructure/CatalyticInfrastructureForm';
import ProfileData from '../../../Profile/ProfileData';
import { adminActions, bankActions } from '../../../../redux/actions';
import DocumentsForm from '../../../documents/DocumentsForm';
import SamAnalysis from '../../../Modals/LoanApplicationModal/SamAnalysis/SamAnalysis';
import AdminActionsTab from '../../../ActionsTab/AdminActionsTab';
import LendersOffers from './LendersOffers';
import {
  STATUS_REMARKS,
  application_status_remarks,
} from '../../../../constants';

import { LOAN_FIELDS, ADMIN_SAM_DOCUMENT } from '../../../../constants';

let tabMenusArr = [
  'application_details',
  'review_profile',
  'review_documents',
  'sam_analysis',
  'publish_application',
  'offers',
  'actions',
];

const AdminApplicationForm = ({
  formType,
  publishStatusFlag,
  closedStatusFlag,
}) => {
  const [tabKey, setTabKey] = useState('application_details');
  const [formData, setFormData] = useState({});
  const [publishStatus, setPublishStatus] = useState(false);
  const [closedStatus, setClosedStatus] = useState(false);
  const [actionsBtnStatus, setActionsBtnStatus] = useState(true);

  const admin = useSelector((state) => state.admin);
  const bank = useSelector((state) => state.bank);
  const dispatch = useDispatch();

  useEffect(() => {
    if (publishStatusFlag) {
      setPublishStatus(true);
    } else {
      setPublishStatus(false);
    }
    setTabKey('application_details');
  }, [publishStatusFlag]);

  useEffect(() => {
    if (closedStatusFlag) {
      setClosedStatus(true);
    } else {
      setClosedStatus(false);
    }
    setTabKey('application_details');
  }, [closedStatusFlag]);

  useEffect(() => {
    if (
      admin &&
      admin.selectedApplication &&
      admin.selectedApplication.details &&
      admin.selectedApplication.details.applicationuid
    ) {
      let applicationuid = admin.selectedApplication.details.applicationuid;
      if (!publishStatus) {
        dispatch(adminActions.getProfileDataInProgress(applicationuid));
        dispatch(adminActions.getDocumentDataInProgress(applicationuid));
        dispatch(adminActions.getSamAnalysisInProgress(applicationuid));

        if (admin.selectedApplication.details.applicationstatus !== 'Applied') {
          setPublishStatus(true);
        }
        let fpo_id = applicationuid.split('-')[1];
        dispatch(bankActions.getSamKeyFinancialsInProgress({ fpo_id }));
        dispatch(bankActions.getSamRatioAnalysisInProgress({ fpo_id }));
        dispatch(bankActions.getSamFinancialPerformanceInProgress({ fpo_id }));
      } else {
        if (!closedStatus) {
          dispatch(
            adminActions.getRequestedDocumentsInProgress({
              applicationuid: applicationuid,
            })
          );
        }
        dispatch(
          adminActions.getOfferListInProgress({
            applicationuid: applicationuid,
          })
        );
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.selectedApplication?.details, publishStatus]);

  useEffect(() => {
    if (admin.selectedApplication.sanctionDocsUpload.success) {
      dispatch(
        adminActions.getRequestedDocumentsInProgress({
          applicationuid: admin.selectedApplication.details.applicationuid,
        })
      );
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.selectedApplication?.sanctionDocsUpload?.success]);

  useEffect(() => {
    if (
      admin &&
      admin.selectedApplication &&
      admin.selectedApplication.details &&
      admin.selectedApplication.details.data
    ) {
      const { data } = admin.selectedApplication.details;
      const answer = {};
      _.each(data, (obj) => {
        answer[obj.Question_Number] = obj.Answer;
      });
      setFormData((prev) => ({
        ...prev,
        fill_application: answer,
      }));
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.selectedApplication]);

  useEffect(() => {
    if (
      admin &&
      admin.selectedApplication &&
      admin.selectedApplication.samFileUpload &&
      admin.selectedApplication.samFileUpload.success
    ) {
      dispatch(
        adminActions.getSamAnalysisInProgress(
          admin.selectedApplication.details.applicationuid
        )
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.selectedApplication?.samFileUpload?.success]);

  useEffect(() => {
    if (
      admin &&
      admin.selectedApplication &&
      admin.selectedApplication.deleteSamDocs &&
      admin.selectedApplication.deleteSamDocs.success
    ) {
      dispatch(
        adminActions.getSamAnalysisInProgress(
          admin.selectedApplication.details.applicationuid
        )
      );
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [admin?.selectedApplication?.deleteSamDocs?.success]);

  useEffect(() => {
    if (admin.selectedApplication.publish.success) {
      setPublishStatus(true);
      setTabKey('actions');
    }
  }, [admin?.selectedApplication?.publish?.success]);

  function handleTabChange(key, type, form_data, modified) {
    setFormData({ ...formData, [key]: form_data });
    let position = tabMenusArr.indexOf(key);
    position = type === 'next' ? position + 1 : position - 1;
    setTabKey(tabMenusArr[position]);
  }

  const handlePublishApplication = () => {
    dispatch(
      adminActions.publishApplicationInProgress(
        admin.selectedApplication.details.applicationuid
      )
    );
  };

  const handleCancelApplication = (data) => {
    let formData = new FormData();

    for (const key in data) {
      if (Object.hasOwnProperty.call(data, key)) {
        formData.append(key, data[key]);
      }
    }

    let payload = {
      applicationuid: admin.selectedApplication.details.applicationuid,
      formData: formData,
    };
    dispatch(adminActions.cancelApplicationInProgress(payload));
  };

  const handleSendBackApplication = (data) => {
    let formData = new FormData();

    for (const key in data) {
      if (Object.hasOwnProperty.call(data, key)) {
        formData.append(key, data[key]);
      }
    }

    let payload = {
      applicationuid: admin.selectedApplication.details.applicationuid,
      formData: formData,
    };

    dispatch(adminActions.sendBackApplicationInProgress(payload));
  };

  const handleSamFileUpload = (formData) => {
    let payload = {
      applicationuid: admin.selectedApplication.details.applicationuid,
      formData: formData,
    };
    dispatch(adminActions.samFileUploadInProgress(payload));
  };

  const handleDocsSubmit = (payload) => {
    dispatch(adminActions.sanctionDocsUploadInProgress(payload));
  };

  const handleDocDelete = (lcd_id) => {
    let payload = {
      applicationuid: admin.selectedApplication.loanInfo.applicationuid,
    };
    const formData = new FormData();
    formData.append(ADMIN_SAM_DOCUMENT[LOAN_FIELDS.deleteSamFile], lcd_id);
    payload.formData = formData;
    dispatch(adminActions.applicationSamDeleteDocsInProgress(payload));
  };

  useEffect(() => {
    if (
      admin &&
      admin.selectedApplication &&
      ((admin.selectedApplication.sendBackApplication &&
        admin.selectedApplication.sendBackApplication.success) ||
        (admin.selectedApplication.cancelApplication &&
          admin.selectedApplication.cancelApplication.success))
    ) {
      setActionsBtnStatus(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    admin?.selectedApplication?.sendBackApplication?.success,
    admin?.selectedApplication?.cancelApplication?.success,
  ]);

  return (
    <>
      <ProgressBar
        now={
          (100 *
            (tabMenusArr.indexOf(tabKey) + 1 + (publishStatus ? 0.6 : 0))) /
            5 -
          (tabKey === 'offers' ? 60 : 0)
        }
        className='loan-application-progress-bar'
      />
      <Tabs
        activeKey={tabKey}
        className='mb-3 px-3 mt-3 loan-application-tab-container'
        onSelect={(key) => {
          setTabKey(key);
        }}
      >
        <Tab
          disabled={false}
          eventKey='application_details'
          title={
            <>
              <Badge pill bg='secondary' className='text-white'>
                {tabMenusArr.indexOf('application_details') + 1}
              </Badge>
              <label className='ml-2 '>Application Details</label>
            </>
          }
          tabClassName={
            tabMenusArr.indexOf('application_details') <
            tabMenusArr.indexOf(tabKey)
              ? 'active'
              : ''
          }
        >
          {formType === 'working-capital-inputloan' ||
          (formData?.fill_application &&
            formData?.fill_application['1'] === 'Working capital - Input') ? (
            <InputLoanForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              hideActionsButtons={true}
            />
          ) : formType === 'working-capital-outputloan' ||
            (formData?.fill_application &&
              formData?.fill_application['1'] ===
                'Working capital - Output') ? (
            <OutputLoanForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              hideActionsButtons={true}
            />
          ) : formType === 'term-loan-catalyticInfrastructure' ||
            (formData?.fill_application &&
              formData?.fill_application['1'] ===
                'Term loan - Infrastructure') ? (
            <CatalyticInfrastructureForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              hideActionsButtons={true}
            />
          ) : null}
        </Tab>
        {!publishStatus && (
          <Tab
            disabled={false}
            eventKey='review_profile'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('review_profile') + 1}
                </Badge>
                <label className='ml-2'>Review Profile</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('review_profile') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            <>
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.profile &&
                admin.selectedApplication.profile.FyearId && (
                  <div className='pb-3'>
                    <ProfileData
                      profileData={{
                        documents: {
                          profiledata: admin.selectedApplication.profile,
                        },
                      }}
                    />
                    {/* <section className='d-flex justify-content-end mb-5 mr-4'>
                      <div className='pb-5'>
                        <button
                          className='loan-application-btn btn-secondary'
                          onClick={() => handleTabChange(tabKey, 'prev')}
                        >
                          PREVIOUS
                        </button>
                        <button
                          className='loan-application-btn btn-primary ml-4'
                          onClick={() => handleTabChange(tabKey, 'next')}
                        >
                          NEXT
                        </button>
                      </div>
                    </section> */}
                  </div>
                )}
            </>
          </Tab>
        )}

        {!publishStatus && (
          <Tab
            disabled={false}
            eventKey='review_documents'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('review_documents') + 1}
                </Badge>
                <label className='ml-2'>REVIEW DOCUMENTS</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('review_documents') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            {admin.selectedApplication &&
              admin.selectedApplication.documentsData &&
              admin.selectedApplication.documentsData.documentdata && (
                <DocumentsForm
                  readOnly={true}
                  handleTabChange={handleTabChange}
                  activeKey={tabKey}
                  documents={
                    admin.selectedApplication.documentsData.documentdata
                  }
                  hideNavButtons={true}
                />
              )}
          </Tab>
        )}
        {!publishStatus && (
          <Tab
            disabled={false}
            eventKey='sam_analysis'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('sam_analysis') + 1}
                </Badge>
                <label className='ml-2'>SAM ANALYSIS</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('sam_analysis') < tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            {admin.selectedApplication &&
              admin.selectedApplication.samAnalysis && (
                <SamAnalysis
                  readOnly={true}
                  handleTabChange={handleTabChange}
                  activeKey={tabKey}
                  samDocuments={{
                    documents: admin.selectedApplication.samAnalysis.data,
                  }}
                  showSamDocsActionsButton={true}
                  uploadButton={actionsBtnStatus}
                  getFormData={handleSamFileUpload}
                  samKeyFinanials={bank?.samKeyFinanials?.documents}
                  samRatioAnalysis={bank?.samRatioAnalysis?.documents}
                  samFinancialPerformance={
                    bank?.samFinancialPerformance?.documents
                  }
                  handleDocDelete={handleDocDelete}
                  hideNavButtons={true}
                />
              )}
          </Tab>
        )}

        {!publishStatus && (
          <Tab
            disabled={false}
            eventKey='publish_application'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('publish_application') + 1}
                </Badge>
                <label className='ml-2'>PUBLISH APPLICATION</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('publish_application') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            <PublishApplication
              handlePublishApplication={handlePublishApplication}
              handleCancelApplication={handleCancelApplication}
              handleSendBackApplication={handleSendBackApplication}
              actionsBtnStatus={actionsBtnStatus}
            />
          </Tab>
        )}
        {publishStatus &&
          (admin &&
          admin.selectedApplication &&
          admin.selectedApplication.details &&
          admin.selectedApplication.details.applicationstatus === 'Cancelled'
            ? admin.selectedApplication &&
              admin.selectedApplication.offerList &&
              admin.selectedApplication.offerList.data &&
              admin.selectedApplication.offerList.data.length > 0
            : true) && (
            <Tab
              disabled={false}
              eventKey='offers'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    2
                  </Badge>
                  <label className='ml-2'>OFFERS</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('offers') < tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <LendersOffers
                application_id={
                  admin?.selectedApplication?.details?.applicationuid
                }
                offers={
                  admin &&
                  admin.selectedApplication &&
                  admin.selectedApplication.offerList.success &&
                  admin.selectedApplication.offerList.data
                }
                handleCancelApplication={handleCancelApplication}
              />
            </Tab>
          )}
        {((publishStatus && !closedStatus) ||
          (!publishStatus && closedStatus) ||
          (admin &&
            admin.selectedApplication &&
            admin.selectedApplication.details &&
            admin.selectedApplication.details.status_remarks ===
              application_status_remarks[STATUS_REMARKS.canceledByAdmin])) && (
          <Tab
            disabled={false}
            eventKey='actions'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  3
                </Badge>
                <label className='ml-2'>ACTIONS</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('actions') < tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            <AdminActionsTab
              applicationId={
                admin?.selectedApplication?.details?.applicationuid
              }
              handleCancelApplication={handleCancelApplication}
              handleDocsSubmit={handleDocsSubmit}
              cancelApplication={admin?.selectedApplication?.cancelApplication}
              loanInfo={admin?.selectedApplication?.details}
              requestedDocuments={
                admin?.selectedApplication?.requestedDocuments
              }
              sanctionDocsUpload={
                admin?.selectedApplication?.sanctionDocsUpload
              }
              actionsBtnStatus={actionsBtnStatus}
              applicationStatus={
                admin?.selectedApplication?.details?.applicationstatus
              }
            />
          </Tab>
        )}
      </Tabs>
    </>
  );
};
export default AdminApplicationForm;
