/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Card, Col, Form, Row } from 'react-bootstrap';
import DocumentIcon from '../DocumentIcon/DocumentIcon';
import DocumentsReview from '../Modals/DocumentsReview/DocumentsReview';
import ReviewDocumentsCard from '../Cards/ReviewDocumentsCard';

const DocumentsForm = (props) => {
  const [documentModalStatus, setDocumentModalStatus] = React.useState(false);
  const [docsData, setDocsData] = React.useState({});
  function handleDocsData(data) {
    setDocumentModalStatus(true);
    setDocsData(data);
  }

  const genrateDocumentLinks = (key1, key2) => {
    let elemnts = [];
    if (props.documents[key1]) {
      // eslint-disable-next-line array-callback-return
      props.documents[key1].map((value, index) => {
        if (value[key2].length === 0) {
          elemnts.push(
            <span className='col-12 px-1' key={index}>
              <a className={'p-1 document-link rejected'}>
                <DocumentIcon />
                {value.documentName}
              </a>
            </span>
          );
        } else if (value[key2].length === 1) {
          elemnts.push(
            <span className='col-12 px-1' key={index}>
              <a
                href={value[key2][0].documentPath}
                target='_blank'
                className={'p-1 document-link'}
                rel='noreferrer'
              >
                <DocumentIcon download={true} />
                {value.documentName}
              </a>
            </span>
          );
        } else {
          elemnts.push(
            <span className='col-12 px-1' key={index}>
              <a
                className={'p-1 document-link'}
                onClick={() =>
                  handleDocsData({
                    ...value,
                    title: 'KYC Company',
                    key: key2,
                  })
                }
              >
                <DocumentIcon download={true} />
                {value.documentName}
              </a>
              <small>{value[key2] && ' + ' + value[key2].length}</small>
            </span>
          );
        }
      });
    } else {
      return null;
    }
    return elemnts;
  };
  return (
    <>
      {props.showEdit && (
        <div className='text-right'>
          <a
            className='f12 default-anchor cursor-pointer mr-5 Mr-15'
            href='https://fpogateway.com/fpo/documents'
          >
            EDIT DOCUMENTS
          </a>
        </div>
      )}
      {props.documents && (
        <div>
          <Row className='mb-5 px-3 remove-padding mT-10'>
            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='KYC Company'
                badge={props.documents.CompanyKYC.reduce(
                  (n, val) => n + (val.companyKYCs.length > 0),
                  0
                )}
              >
                {props.documents.CompanyKYC &&
                  genrateDocumentLinks('CompanyKYC', 'companyKYCs')}
              </ReviewDocumentsCard>
            </Col>

            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='Credit Data'
                badge={props.documents.CreditData.reduce(
                  (n, val) => n + (val.creditData.length > 0),
                  0
                )}
              >
                {props.documents.CompanyKYC &&
                  genrateDocumentLinks('CreditData', 'creditData')}
              </ReviewDocumentsCard>
            </Col>

            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='Director Documents'
                badge={props.documents.DirectorKYC.reduce(
                  (n, val) => n + (val.directManagements.length > 0),
                  0
                )}
              >
                {props.documents.CompanyKYC &&
                  genrateDocumentLinks('DirectorKYC', 'directManagements')}
              </ReviewDocumentsCard>
            </Col>

            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='Financials Documents'
                badge={props.documents.Financials.reduce(
                  (n, val) => n + (val.financials.length > 0),
                  0
                )}
              >
                {props.documents.CompanyKYC &&
                  genrateDocumentLinks('Financials', 'financials')}
              </ReviewDocumentsCard>
            </Col>

            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='Statutory License'
                badge={props.documents.StatutoryLicense.reduce(
                  (n, val) => n + (val.statutoryLicenses.length > 0),
                  0
                )}
              >
                {props.documents.CompanyKYC &&
                  genrateDocumentLinks('StatutoryLicense', 'statutoryLicenses')}
              </ReviewDocumentsCard>
            </Col>

            <Col md={4} className='remove-padding'>
              <ReviewDocumentsCard
                title='Business Plan'
                badge={props.documents.BusinessPlan.count}
              >
                {props.documents.BusinessPlan.rows.map((value, key) => (
                  <span className='col-12 px-1' key={key}>
                    <a
                      href={value.documentUrl}
                      target='_blank'
                      className={'p-1 document-link'}
                      rel='noreferrer'
                    >
                      <DocumentIcon download={true} />
                      {value.fymYear}
                    </a>
                  </span>
                ))}
              </ReviewDocumentsCard>
            </Col>
          </Row>
        </div>
      )}
      {!props.hideNavButtons && (
        <div className='row'>
          <div className='offset-md-6 col-md-6 offset-lg-8 col-lg-4 col-sm-12'>
            <div className=' d-flex justify-content-between m-3'>
              <button
                type='button'
                className='loan-application-btn btn-secondary'
                onClick={() => props.handleTabChange(props.activeKey, 'prev')}
              >
                PREVIOUS
              </button>
              <button
                type='button'
                className='loan-application-btn btn-primary'
                onClick={() => props.handleTabChange(props.activeKey, 'next')}
              >
                NEXT
              </button>
            </div>
          </div>
        </div>
      )}
      <DocumentsReview
        show={documentModalStatus}
        docsData={docsData}
        handleClose={setDocumentModalStatus}
      />
    </>
  );
};

export default DocumentsForm;
