import Modal from 'react-bootstrap/Modal';
import React, { useState } from 'react';
import Image from 'react-bootstrap/Image';
import Form from 'react-bootstrap/Form';
import './adminStyle.css';
import { RocketSharp, CloseOutline } from 'react-ionicons';
import { all_notification_lists } from '../../data';

function AllNotifications({ show, onHide }) {
  return (
    <Modal
      size='lg'
      show={show}
      onHide={() => onHide(false)}
      aria-labelledby='contained-modal-title-vcenter'
      centered
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 ml-3'
        >
          All Notifications
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body className='custom-scrollbar' style={{ height: '500px' }}>
        <table className='request w-100'>
          <thead className=''>
            <tr className='muteds f12'>
              <th>REQUEST ID</th>
              <th>STATUS</th>
              <th>DATE</th>
              <th>ACTION</th>
            </tr>
          </thead>
          <tbody className='f12 medium-font'>
            {all_notification_lists.map((all_notification_list, key) => (
              <tr key={key}>
                <td>{all_notification_list.requestId}</td>
                <td>{all_notification_list.status}</td>
                <td>{all_notification_list.date}</td>
                <td>
                  <i className='muted fa fa-eye p-1' />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </Modal.Body>
    </Modal>
  );
}
export default AllNotifications;
