import React, { useEffect, useState } from 'react';
import './charts.scss';
import {
  Chart,
  Label,
  Legend,
  Series,
  Tooltip,
  CommonSeriesSettings,
  ValueAxis,
} from 'devextreme-react/chart';
import formatCurrency from '../../utils/currency';

const FpoVintageChart = ({ documents, title, legendName }) => {
  const [chartData, setchartData] = useState([]);
  useEffect(() => {
    if (!documents) return;

    let tempoArray = [];
    documents.map((val, index) => {
      tempoArray.push({
        arg: val.axis ? val.axis + '' : null,
        val: val.value === '0' ? null : parseFloat(val.value),
      });
    });
    setchartData(tempoArray);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [documents]);

  const toLaksh = (num) => {
    return num;
    // return (
    //   formatCurrency(parseFloat(parseFloat(num / 100000)).toFixed(2))[
    //     'maskedValue'
    //   ] + ' L'
    // );
  };

  const customizeText = (arg) => {
    return toLaksh(arg.value);
  };

  const customizeTooltip = (pointInfo) => {
    return {
      html: `
    <table className='table chart-tooltip-table table-borderless'>
    <thead>
    <tr>
    <th colspan='2' >${pointInfo.points[0].argumentText}:</th><th>${
        /* toLaksh(
      pointInfo.points[0].originalValue
    ) */ pointInfo.points[0].originalValue
      }</th></tr></thead>
    </table>
   `,
    };
  };

  return (
    <div className='sam-vintage-chart-container'>
      {chartData.length > 3 && (
        <>
          <Chart dataSource={chartData} className='chart'>
            <CommonSeriesSettings
              argumentField='state'
              type='stackedBar'
              barPadding={5}
            />

            <Series
              valueField='val'
              argumentField='arg'
              color='#00A3FF'
              name={legendName && legendName.length > 0 ? legendName[0] : ''}
            />

            <Tooltip
              enabled={true}
              shared={true}
              customizeTooltip={customizeTooltip}
            />
            <Legend
              verticalAlignment='bottom'
              horizontalAlignment='center'
              visible={false}
            />
            <ValueAxis maxValueMargin={0.01}>
              <Label customizeText={customizeText} />
            </ValueAxis>
          </Chart>
        </>
      )}
    </div>
  );
};

export default FpoVintageChart;
