const ProfileCategoryTable = ({ categories, key1, key2 }) => {
  return (
    <>
      {categories && (
        <table className='table'>
          <tbody>
            {categories.map((value, key) => {
              return (
                <tr key={key}>
                  <td>
                    <div className='pl-2 medium-font bg-muted'>
                      {value[key1][key2]}
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>
      )}
    </>
  );
};

export default ProfileCategoryTable;
