import React, { useEffect, useState } from 'react';
import { Form, Alert, Spinner } from 'react-bootstrap';
import FileUploadButton from '../../FileUploadButton/FileUploadButton';
import InputBox from '../../FormInputs/InputBox';
import SelectBox from '../../FormInputs/SelectBox';
import { useForm, Controller } from 'react-hook-form';
import { MAKE_AN_OFFER_TYPES, LOAN_FIELDS } from '../../../constants';
import {
  tenure_category,
  repayment_types,
  loan_options,
  validity_of_limit_categories,
} from '../../../data';
import { useSelector, useDispatch } from 'react-redux';
import OfferDocumentList from '../../Tables/OfferDocumentList';
import * as dayjs from 'dayjs';
import { bankActions } from '../../../redux/actions';
import { CLEAR_POST_MAKE_OFFER } from '../../../redux/types/banktypes';
import CurrencyInput from '../../FormInputs/CurrencyInput';

const MakeAnOffer = (props) => {
  const bankState = useSelector((state) => state.bank);
  const {
    register,
    handleSubmit,
    setValue,
    control,
    formState: { errors },
  } = useForm({
    defaultValues: {},
  });
  const [offerDocuments, setOfferDocuments] = useState([]);
  const auth = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  useEffect(() => {
    if (bankState?.makeAnOffer?.success === true && props.closeMainStatus) {
      const organizationid = auth.userInfo.organizationid;
      dispatch(bankActions.getApplicationDetails({ organizationid }));
      dispatch({ type: CLEAR_POST_MAKE_OFFER });
      props.closeMainStatus(false);
    }
  }, [bankState?.makeAnOffer?.success]);

  useEffect(() => {
    if (props.defaultValues) {
      let documents = { ...offerDocuments };
      for (const key in props.defaultValues) {
        if (key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]) {
          if (props.defaultValues[key]) {
            documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]] = [];
            documents[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]][0] = {
              type: 'Sanction Letter',
              url: props.defaultValues[key],
              name: props.defaultValues['sanction_letter_filename'],
            };
          }

          continue;
        }

        if (
          key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments] &&
          MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments].length > 0
        ) {
          // documents.push({
          //   type: 'Other Documents',
          //   url: props.defaultValues[key],
          // });
          continue;
        }

        if (key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentDate]) {
          setValue(
            key,
            props.defaultValues[key]
              ? dayjs(props.defaultValues[key]).format('YYYY-MM-DD')
              : '',
            {
              shouldValidate: false,
            }
          );
          continue;
        }
        setValue(key, props.defaultValues[key], {
          shouldValidate: false,
        });
      }
      setOfferDocuments(documents);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.defaultValues]);

  React.useEffect(() => {
    if (!props.formDetails || !props.formDetails.data || props.isFPO) return;
    if (props.formDetails.data[0]) {
      setValue(
        MAKE_AN_OFFER_TYPES[LOAN_FIELDS.purpose],
        props.formDetails.data[0].Answer,
        {
          shouldValidate: true,
        }
      );
    }
    if (
      props.formDetails.data[1] &&
      props &&
      props.defaultValues &&
      !props.defaultValues['loanamount']
    ) {
      setValue(
        MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanAmount],
        props.formDetails.data[1].Answer,
        {
          shouldValidate: true,
        }
      );
    }

    return () => {};
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props]);

  const handleFileChange = (e, field_name, type) => {
    let documents = { ...offerDocuments };
    documents[field_name] = [];
    const { files } = e.target;
    for (let i = 0; i < files.length; i++) {
      let file = files.item(i);
      let temp_file = [...documents[`${field_name}`]];
      temp_file.push({
        type: type,
        name: file.name,
      });
      documents[field_name] = temp_file;
    }
    setOfferDocuments(documents);
  };

  return (
    <Form
      onSubmit={handleSubmit((data) => {
        // handleTabChange(activeKey, 'next', data);
        // props.handleCleanup();
        props.handleMakeAnOffer(data);
      })}
      noValidate
    >
      <div
        style={{
          paddingLeft: props.contentPadding,
          paddingRight: props.contentPadding,
        }}
        className='mt-5 pt-1'
      >
        <h4 className='form-section-header mt-4 pl-2 ml-4 mb-2 fs-16'>
          Loan Details
        </h4>
        <section className='row'>
          <InputBox
            label='Purpose'
            className=''
            register={register}
            required
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.purpose]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.purpose]]}
            readOnly={true}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Loan Amount'
                className=''
                setValue={setValue}
                error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanAmount]]}
                readOnly={props.isFPO}
                min={0}
                max={100000000}
                required={true}
                {...field}
              />
            )}
            {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanAmount], {
              max:{ value: 100000000, message: 'Please enter the value below 100000000' } ,
              required: { value: true, message: 'This is Mandatory field' },
            })}
            control={control}
            defaultValue=''
          />

          <InputBox
            label='Rate Of Interest (%)'
            className=''
            register={register}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            max={{ value: 99, message: 'Please enter the value below 100' }}
            required ={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.rateOfInterest]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.rateOfInterest]]}
            readOnly={props.isFPO}
          />

          <InputBox
            label='Tenure'
            type='number'
            className=''
            register={register}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            max={{ value: 99, message: 'Please enter the value below 100' }}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenure]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenure]]}
            readOnly={props.isFPO}
          />

          <SelectBox
            label='Tenure Category'
            className=''
            {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenureCategory], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenureCategory]}
            options={tenure_category}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenureCategory]]}
            readOnly={props.isFPO}
            handleChange={(val) =>
              setValue(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.tenureCategory], val, {
                shouldValidate: true,
              })
            }
          />

          <InputBox
            label='Processing Fee(INR)'
            type='number'
            className=''
            register={register}
            max={{ value: 1000000, message: 'Please enter the value below 10 Lakhs' }}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.processingFee]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.processingFee]]}
            readOnly={props.isFPO}
          />

          <InputBox
            label='Guarantee'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.guarantee]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.guarantee]]}
            readOnly={props.isFPO}
          />

          <InputBox
            label='Repayment date'
            className=''
            type='date'
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentDate]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentDate]]}
            readOnly={props.isFPO}
            min={dayjs(new Date()).format('YYYY-MM-DD')}
          />

          <SelectBox
            label='Repayment type'
            className=''
            {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentType], {
              required:{ value: true, message: 'This is Mandatory field' },
            })}
            required
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentType]}
            options={repayment_types}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentType]]}
            readOnly={props.isFPO}
            handleChange={(val) =>
              setValue(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.repaymentType], val, {
                shouldValidate: true,
              })
            }
          />

          <SelectBox
            label='Loan option'
            className=''
            {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanOption], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanOption]}
            options={loan_options}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanOption]]}
            readOnly={props.isFPO}
            handleChange={(val) =>
              setValue(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.loanOption], val, {
                shouldValidate: true,
              })
            }
          />

          <InputBox
            label='Validity Of limit'
            className=''
            type='number'
            min={{ value: 0, message: 'Please enter the Positive number' }}
            max={{ value: 99, message: 'Please enter the value below 100' }}
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimit]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimit]]}
            readOnly={props.isFPO}
          />

          <SelectBox
            label='Validity Of limit category'
            className=''
            {...register(
              MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimitCategory],
              {
                required: { value: true, message: 'This is Mandatory field' },
              }
            )}
            required
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimitCategory]}
            options={validity_of_limit_categories}
            error={
              errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimitCategory]]
            }
            readOnly={props.isFPO}
            handleChange={(val) =>
              setValue(
                MAKE_AN_OFFER_TYPES[LOAN_FIELDS.validityOfLimitCategory],
                val,
                {
                  shouldValidate: true,
                }
              )
            }
          />

          <InputBox
            label='Margin %'
            className=''
            type='number'
            min={{ value: 0, message: 'Please enter the Positive number' }}
            max={{ value: 99, message: 'Please enter the value below 100' }}
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.margin]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.margin]]}
            readOnly={props.isFPO}
          />

          <InputBox
            label='Primary Security'
            className=''
            type='text'
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.primarySecurity]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.primarySecurity]]}
            readOnly={props.isFPO}
          />
          <InputBox
            label='Collateral Security'
            className=''
            type='text'
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={MAKE_AN_OFFER_TYPES[LOAN_FIELDS.collateralSecurity]}
            error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.collateralSecurity]]}
            readOnly={props.isFPO}
          />
        </section>
        {!props.readOnly && (
          <>
            <h4 className='form-section-header mt-4 pl-2 ml-4 mb-2 fs-16'>
              Upload Documents
            </h4>
            <section className='row pt-2'>
              <FileUploadButton
                label='Sanction Letter'
                {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter], {
                  required: false,
                })}
                multiple={false}
                error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]]}
                accept='application/pdf'
                handleFileChanges={handleFileChange}
              />
              <FileUploadButton
                label='Other Documents'
                {...register(MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments], {
                  required: false,
                })}
                multiple={true}
                error={errors[MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments]]}
                handleFileChanges={handleFileChange}
              />
            </section>
          </>
        )}
        {offerDocuments && (
          <div className='px-4 no-padding-mobile my-3'>
            <OfferDocumentList
              documents={offerDocuments}
              title={props.readOnly ? 'Documents' : 'Selected documents'}
              readOnly={props.readOnly}
            />
          </div>
        )}
        {!props.isFpo && (
          <section className='d-flex justify-content-end mb-5 mr-4 mt-5'>
            <div className='text-center'>
              {bankState && bankState.makeAnOffer.inProgress ? (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              ) : props.isFPO ? null : (
                <button
                  type='submit'
                  className='loan-application-btn btn-primary ml-4'
                >
                  {props.defaultValues?.deal_status === 'offer'
                    ? 'UPDATE'
                    : 'SUBMIT'}
                </button>
              )}
              <br />
              <br />
              {bankState &&
              bankState.makeAnOffer.error &&
              !bankState.makeAnOffer.inProgress ? (
                <Alert
                  variant={'danger'}
                  style={{ width: '30vw' }}
                  className='text-center'
                >
                  {bankState.makeAnOffer.message
                    ? bankState.makeAnOffer.message
                    : 'Failed to save Make An Offer'}
                </Alert>
              ) : (
                <>
                  {bankState && bankState.makeAnOffer.success && (
                    <Alert
                      variant={'success'}
                      style={{ width: '30vw' }}
                      className='text-center'
                    >
                      {bankState.makeAnOffer.message
                        ? bankState.makeAnOffer.message
                        : 'Successfully saved save Make An Offer'}
                    </Alert>
                  )}
                </>
              )}
            </div>
          </section>
        )}
      </div>
    </Form>
  );
};

export default MakeAnOffer;
