import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Badge, ProgressBar } from 'react-bootstrap';
import DocumentsForm from '../../documents/DocumentsForm';
import SubmitApplication from './SubmitApplication';
import _ from 'lodash';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { APPLICATION_STATUS } from '../../../constants';
import {
  CLEAR_FPO_APPLY_LOAN,
  FPO_SAVE_IN_PROCESS,
  FPO_SUBMIT_IN_PROCESS,
  FPO_CLEAR_SUBMIT_LOAN,
} from '../../../redux/types/fpotypes';
import ProfileData from '../../Profile/ProfileData';
import OutputLoanForm from './WorkingCapital/OutputLoan/OutputLoanForm';
import InputLoanForm from './WorkingCapital/InputLoan/InputLoanForm';
import CatalyticInfrastructureForm from './TermLoan/CatalyticInfrastructure/CatalyticInfrastructureForm';
import {
  GET_FPO_DOCUMENTS_IN_PROGRESS,
  SET_SELECTED_LOAN,
  GET_FPO_PROFILE_DATA_IN_PROGRESS,
} from '../../../redux/types/fpotypes';
import ApplicationStatus from '../../../pages/fpo/dashboard/application_status/ApplicationStatus';

let tabMenusArr = [
  'fill_application',
  'review_application',
  'review_profile',
  'review_documents',
  'submit_application',
];

const Form = ({ readOnly, formType, loanInfo, loanId, hideNavButton }) => {
  const [tabKey, setTabKey] = useState('fill_application');
  const [formData, setFormData] = useState({});
  const [formModified, setFormModified] = useState(false);
  const [appliedStatus, setAppliedStatus] = useState(false);
  const [publishedStatus, setPublishedStatus] = useState(false);
  const [progressPercent, setProgressPercent] = useState(
    (1 * 100) / tabMenusArr.length
  );
  const dispatch = useDispatch();
  const fpoState = useSelector((state) => state.fpo);

  useEffect(() => {
    dispatch({ type: FPO_CLEAR_SUBMIT_LOAN });
  }, []);

  useEffect(() => {
    if (!loanInfo || loanInfo === null) return;
    const { data } = loanInfo;
    const answer = {};
    _.each(data, (obj) => {
      answer[obj.Question_Number] = obj.Answer;
    });

    setFormData((prev) => ({
      ...prev,
      fill_application: answer,
    }));
    setAppliedStatus(false);
    setPublishedStatus(false);
    if (loanInfo.applicationstatus === 'Applied') {
      setAppliedStatus(true);
      setTabKey('review_application');
    } else if (loanInfo.applicationstatus === 'Published') {
      setPublishedStatus(true);
    }
  }, [loanInfo]);

  useEffect(() => {
    if (loanId && !loanInfo) {
      dispatch({
        type: SET_SELECTED_LOAN,
        payload: loanId,
      });
      dispatch({
        type: GET_FPO_DOCUMENTS_IN_PROGRESS,
        payload: loanId,
      });
      let payload = { applicationuid: loanId };
      dispatch({
        type: GET_FPO_PROFILE_DATA_IN_PROGRESS,
        payload: payload,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loanId]);

  function handleTabChange(key, type, form_data, modified) {
    setFormData({ ...formData, [key]: form_data });
    let position = tabMenusArr.indexOf(key);
    position = type === 'next' ? position + 1 : position - 1;
    setProgressPercent(((position + 1) * 100) / tabMenusArr.length);
    setTabKey(tabMenusArr[position]);

    if (formModified) {
      handleSave();
    }
    if (modified) {
      setFormModified(true);
    } else {
      setFormModified(false);
    }
  }

  function handleSave() {
    let loanInfo = formData['fill_application'];
    let loanId = fpoState.selectedLoan.loanId;
    loanInfo.applicationuid = loanId;
    dispatch({ type: FPO_SAVE_IN_PROCESS, payload: loanInfo });
    return true;
  }

  function handleSubmit() {
    let loanInfo = formData['fill_application'];
    let loanId = fpoState.selectedLoan.loanId;
    loanInfo.applicationuid = loanId;
    dispatch({ type: FPO_SUBMIT_IN_PROCESS, payload: loanInfo });
    return true;
  }

  return (
    <>
      <ProgressBar
        now={progressPercent}
        key={3}
        className='loan-application-progress-bar'
      />
      <Tabs
        activeKey={tabKey}
        className='mb-3 mt-3 px-2 loan-application-tab-container'
      >
        {!appliedStatus && (
          <Tab
            className='apply-loan-tab'
            disabled={true}
            eventKey='fill_application'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('fill_application') + 1}
                </Badge>
                <label className='ml-2 '>Fill APPLICATION</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('fill_application') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            {formType === 'working-capital-inputloan' ||
            (formData?.fill_application &&
              formData?.fill_application['1'] === 'Working capital - Input') ? (
              <InputLoanForm
                readOnly={readOnly}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                defaultValues={formData.fill_application}
                revenue={
                  formData.fill_application &&
                  !formData.fill_application['4'] &&
                  fpoState &&
                  fpoState.profileData &&
                  fpoState.profileData.documents &&
                  fpoState.profileData.documents.profiledata &&
                  fpoState.profileData.documents.profiledata.turnOver
                }
                hideActionsButtons={hideNavButton}
              />
            ) : formType === 'working-capital-outputloan' ||
              (formData?.fill_application &&
                formData?.fill_application['1'] ===
                  'Working capital - Output') ? (
              <OutputLoanForm
                readOnly={readOnly}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                defaultValues={formData.fill_application}
                revenue={
                  formData.fill_application &&
                  !formData.fill_application['4'] &&
                  fpoState &&
                  fpoState.profileData &&
                  fpoState.profileData.documents &&
                  fpoState.profileData.documents.profiledata &&
                  fpoState.profileData.documents.profiledata.turnOver
                }
                hideActionsButtons={hideNavButton}
              />
            ) : formType === 'term-loan-catalyticInfrastructure' ||
              (formData?.fill_application &&
                formData?.fill_application['1'] ===
                  'Term loan - Infrastructure') ? (
              <CatalyticInfrastructureForm
                readOnly={readOnly}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                defaultValues={formData.fill_application}
                revenue={
                  formData.fill_application &&
                  !formData.fill_application['4'] &&
                  fpoState &&
                  fpoState.profileData &&
                  fpoState.profileData.documents &&
                  fpoState.profileData.documents.profiledata &&
                  fpoState.profileData.documents.profiledata.turnOver
                }
                hideActionsButtons={hideNavButton}
              />
            ) : (
              <div className='d-flex justify-content-center '>
                <h5 className='text-danger text-bold'>
                  Application type not matched
                </h5>
              </div>
            )}
          </Tab>
        )}

        <Tab
          disabled={true}
          eventKey='review_application'
          title={
            <>
              <Badge pill bg='secondary' className='text-white'>
                {appliedStatus
                  ? 1
                  : tabMenusArr.indexOf('review_application') + 1}
              </Badge>
              <label className='ml-2 '>REVIEW APPLICATION</label>
            </>
          }
          tabClassName={
            tabMenusArr.indexOf('review_application') <
            tabMenusArr.indexOf(tabKey)
              ? 'active'
              : ''
          }
        >
          {formType === 'working-capital-inputloan' ||
          (formData?.fill_application &&
            formData?.fill_application['1'] === 'Working capital - Input') ? (
            <InputLoanForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              backButton
              formModified={formModified}
              hideActionsButtons={hideNavButton}
            />
          ) : formType === 'working-capital-outputloan' ||
            (formData?.fill_application &&
              formData?.fill_application['1'] ===
                'Working capital - Output') ? (
            <OutputLoanForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              backButton
              formModified={formModified}
              hideActionsButtons={hideNavButton}
            />
          ) : formType === 'term-loan-catalyticInfrastructure' ||
            (formData?.fill_application &&
              formData?.fill_application['1'] ===
                'Term loan - Infrastructure') ? (
            <CatalyticInfrastructureForm
              readOnly={true}
              handleTabChange={handleTabChange}
              activeKey={tabKey}
              defaultValues={formData.fill_application}
              backButton
              formModified={formModified}
              hideActionsButtons={hideNavButton}
            />
          ) : null}
        </Tab>
        {!appliedStatus && (
          <Tab
            disabled={true}
            eventKey='review_profile'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('review_profile') + 1}
                </Badge>
                <label className='ml-2 '>REVIEW PROFILE</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('review_profile') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            <>
              {fpoState.profileData.success && (
                <ProfileData
                  profileData={fpoState.profileData}
                  showEdit={true}
                />
              )}
              <div className='row'>
                <div className='offset-md-6 col-md-6 offset-lg-8 col-lg-4 col-sm-12'>
                  <div className=' d-flex justify-content-between m-3'>
                    <button
                      className='loan-application-btn btn-secondary'
                      onClick={() => handleTabChange(tabKey, 'prev')}
                    >
                      PREVIOUS
                    </button>
                    <button
                      className='loan-application-btn btn-primary ml-2'
                      onClick={() => handleTabChange(tabKey, 'next')}
                    >
                      NEXT
                    </button>
                  </div>
                </div>
              </div>
            </>
          </Tab>
        )}
        {!appliedStatus && (
          <Tab
            disabled={true}
            eventKey='review_documents'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('review_documents') + 1}
                </Badge>
                <label className='ml-2 '>REVIEW DOCUMENTS</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('review_documents') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            {fpoState.documentdata && (
              <DocumentsForm
                readOnly={true}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                documents={fpoState.documentdata}
                showEdit={true}
              />
            )}
          </Tab>
        )}
        {!appliedStatus && (
          <Tab
            disabled={true}
            eventKey='submit_application'
            title={
              <>
                <Badge pill bg='secondary' className='text-white'>
                  {tabMenusArr.indexOf('submit_application') + 1}
                </Badge>
                <label className='ml-2 '>SUBMIT APPLICATION</label>
              </>
            }
            tabClassName={
              tabMenusArr.indexOf('submit_application') <
              tabMenusArr.indexOf(tabKey)
                ? 'active'
                : ''
            }
          >
            {tabKey === 'submit_application' && (
              <>
                {loanInfo.applicationstatus === APPLICATION_STATUS.APPLIED ? (
                  <div>Awaiting Approval</div>
                ) : (
                  <SubmitApplication
                    readOnly={true}
                    handleTabChange={handleTabChange}
                    handleSubmit={handleSubmit}
                    handleSave={handleSave}
                    activeKey={tabKey}
                    defaultValues={formData.fill_application}
                  />
                )}
              </>
            )}
          </Tab>
        )}
      </Tabs>
    </>
  );
};

export default Form;
