import { useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';
import ShadowBox from '../../../../components/ShadowBox';
import { APPLICATION_STATUS } from '../../../../constants';
import Form from '../../../../components/Forms/FPO/Form';

const InputLoan = () => {
  const location = useLocation();
  const fpoState = useSelector((state) => state.fpo);
  const params = new URLSearchParams(location.search);
  let loanId = params.get('loanId');

  return (
    <section className='primary-bg'>
      <div className='container pb-7'>
        <div className='container track-repayments'>
          <h4 className='mb-4 heading-text'>
            Apply for a New Loan{' '}
            <span className='pull-right h5 text-bold text-muted mT-10'>
              Application ID ({loanId})
            </span>
          </h4>
          {fpoState &&
          fpoState.selectedLoan &&
          fpoState.selectedLoan.isLoading ? (
            <div className=''>Loading</div>
          ) : (
            <ShadowBox className='p-0 form-box'>
              <Form
                readOnly={
                  fpoState.selectedLoan?.loanInfo?.applicationstatus ===
                  APPLICATION_STATUS.APPLIED
                }
                loanInfo={fpoState.selectedLoan?.loanInfo}
                formType='working-capital-inputloan'
                loanId={loanId}
              />
            </ShadowBox>
          )}
        </div>
      </div>
    </section>
  );
};

export default InputLoan;
