import { Table } from 'react-bootstrap';
import { useState, useEffect } from 'react';

const OfferDocumentList = ({ documents, title, readOnly }) => {
  const [myDocs, setMyDocs] = useState([]);
  useEffect(() => {
    if (documents) {
      let myDocsL = [];
      for (const key in documents) {
        if (Object.hasOwnProperty.call(documents, key)) {
          myDocsL.push(...documents[key]);
        }
      }
      setMyDocs(myDocsL);
    }
  }, [documents]);

  return (
    <div className='mx-3'>
      <h4 className='text-muted'>{title && title}</h4>
      <Table>
        <thead>
          <tr>
            <th className='text-muted f12'>S.NO</th>
            <th className='text-muted f12'>TYPE</th>
            <th className='text-muted f12'>DOCUMENT</th>
          </tr>
        </thead>
        <tbody>
          {myDocs.map((document, key) => (
            <tr key={key}>
              <td>{key + 1}</td>
              <td>{document.type}</td>
              <td>
                {document.url ? (
                  <a
                    href={document.url}
                    className=' br-12'
                    target='_blank'
                    rel='noreferrer'
                  >
                    {document.name}
                  </a>
                ) : (
                  document.name
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
};

export default OfferDocumentList;
