import { FpoGatewayHttpInstance } from './fpogatewayInstance';

const getSamKeyFinancials = ({ fpo_id }) => {
  return FpoGatewayHttpInstance.request({
    method: 'GET',
    url: `financial/analysis/${fpo_id}`,
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

const getSamRatioAnalysis = ({ fpo_id }) => {
  return FpoGatewayHttpInstance.request({
    method: 'GET',
    url: `financial/analysis/ratio/${fpo_id}`,
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

const getSamFinancialPerformance = ({ fpo_id }) => {
  return FpoGatewayHttpInstance.request({
    method: 'GET',
    url: `financial/analysis/performance/${fpo_id}`,
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

export { getSamKeyFinancials, getSamRatioAnalysis, getSamFinancialPerformance };
