/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from 'react';
import './index.css';
import '../../../styles/CommonStyles.css';
import GeneralBox from '../../../components/GeneralBox';
import ProfileStatusTable from '../../../components/Tables/profileStatusTable';
import FpoStatusChart from '../../../components/Charts/FPOStatusChart';
import CardTable from '../../../components/Tables/cardTable';
import FpoEcoSystemTable from '../../../components/Tables/FpoEcoSystemTable';
import FpoVintageChart from '../../../components/Charts/FPOVintageChart';
import ServiceCategoriesChart from '../../../components/Charts/ServiceCategoriesChart';
import MapChart from '../../../components/Charts/MapChart';
import { useDispatch, useSelector } from 'react-redux';
import { GET_PENDING_LOAN_IN_PROGRESS } from '../../../redux/types/banktypes';
import { bankActions } from '../../../redux/actions';
import { Spinner } from 'react-bootstrap';
import ApplicationModal from '../../../components/Modals/LoanApplicationModal/ApplicationModal';
import { useNavigate } from 'react-router-dom';
import UpcomingRepaymentsModal from '../../../components/Modals/UpcomingRepaymentsModal';
import BankRequestManagementModal from '../../../components/Modals/BankRequestManagementModal';
import { bank_request_management } from '../../../data';

const BankDashboard = () => {
  const auth = useSelector((state) => state.auth);
  const bank = useSelector((state) => state.bank);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [loanApplicationModalVisible, setLoanApplicationModalVisible] =
    useState(false);
  const [selectedApplication, setselectedApplication] = useState(null);
  const [upcomingShow, setUpcomingShow] = useState(false);
  const [requestModalShow, setRequestModalShow] = useState(false);

  useEffect(() => {
    if (!auth.isLoggedIn) return;
    const organizationid = auth.userInfo.organizationid;
    dispatch(bankActions.getApplicationDetails({ organizationid }));
    dispatch(bankActions.getFpoStatusCountsInProgress({}));
    dispatch(bankActions.getDashboardMetricsInProgress({}));
  }, [auth, dispatch]);

  const handleApplicationViewButton = (application) => {
    setLoanApplicationModalVisible(!loanApplicationModalVisible);
    setselectedApplication(application);
    dispatch(
      bankActions.getApplicationSingleDetailsInProgress(
        application.applicationuid,
        application
      )
    );
  };

  const handleViewDetailsFpo = (application) => {
    let path = '/bank/loan-applications/';
    navigate(
      path +
        `?application_id=${application.applicationuid}&&type=applications&&category=new`
    );
  };

  const handleFpoViewAll = () => {
    let path = '/bank/loan-applications/';
    navigate(path /* + '?type=applications' */);
  };

  return (
    <section>
      <div className='min-vh-100 primary-bg pt-5'>
        <div className='container '>
          <div className='row'>
            {/* <!-- box1 --> */}
            <div className='col-md-6 eres remove-padding'>
              <GeneralBox
                title='Applications'
                handleViewAll={handleFpoViewAll}
                isButtonPresent
                buttonPosition='bottom'
              >
                <div className='col-12'>
                  <div
                    className='responsive mt-4 pl-2 pr-2 table-style custom-scrollbar'
                    style={{ maxHeight: '470px' }}
                  >
                    {bank &&
                    bank.applications &&
                    bank.applications.inProgress ? (
                      <div className='d-flex justify-content-center mb-3'>
                        <Spinner
                          animation='border'
                          role='status'
                          className='TEST'
                        >
                          <span className='visually-hidden'></span>
                        </Spinner>
                      </div>
                    ) : (
                      <>
                        {bank &&
                        bank.applications &&
                        !bank.applications.error ? (
                          <ProfileStatusTable
                            applications={
                              bank.applications.documents.new_application
                            }
                            handleViewButton={handleApplicationViewButton}
                            handleDetailsButton={handleViewDetailsFpo}
                          />
                        ) : (
                          <div>Failed to load data</div>
                        )}
                        {bank &&
                          bank.applications &&
                          !bank.applications.inProgress &&
                          bank.applications.documents &&
                          bank.applications.documents.new_application &&
                          bank.applications.documents.new_application.length ===
                            0 && (
                            <h3 className='text-center text-danger'>
                              There is no new applications
                            </h3>
                          )}
                      </>
                    )}
                  </div>
                </div>
              </GeneralBox>
            </div>

            {/* <!-- box2 --> */}
            <div className='col-md-6 p-0'>
              <div className='row'>
                {/* <!-- pie chart --> */}
                <div className='col-md-6 p-0 pr-2 remove-padding mT-20'>
                  <GeneralBox title='FPOs Status'>
                    <div className='col-12 mt-2'>
                      {/* <!-- pie chart --> */}
                      {bank &&
                        bank.fpoStatusCounts &&
                        bank.fpoStatusCounts.success && (
                          <FpoStatusChart counts={bank.fpoStatusCounts.data} />
                        )}
                      {bank &&
                        bank.fpoStatusCounts &&
                        bank.fpoStatusCounts.inProgress && (
                          <div className='d-flex justify-content-center mb-3'>
                            <Spinner
                              animation='border'
                              role='status'
                              className='TEST'
                            >
                              <span className='visually-hidden'></span>
                            </Spinner>
                          </div>
                        )}
                    </div>
                  </GeneralBox>
                </div>

                {/* <!-- table Track Repayments --> */}
                <div className='col-md-6 p-0 remove-padding mT-20'>
                  <GeneralBox
                    title='Track Repayments'
                    isButtonPresent={true}
                    handleViewAll={() => setUpcomingShow(true)}
                  >
                    <div className='col-12 track-repayments'>
                      <div className='pl-3 pr-3 track-repayments-table'>
                        <CardTable
                          titles={['Loan Id', 'Due By', 'Amount']}
                          details={[
                            {
                              id: '1-7118-16341200',
                              dueBy: '03-11-2021',
                              amount: '₹ 25,000.00',
                            },
                            {
                              id: '1-7118-16341200',
                              dueBy: '03-11-2021',
                              amount: '₹ 25,000.00',
                            },
                            {
                              id: '1-7118-16341200',
                              dueBy: '03-11-2021',
                              amount: '₹ 25,000.00',
                            },
                          ]}
                          type='repayments'
                        />
                      </div>
                    </div>
                  </GeneralBox>
                </div>

                {/* <!-- table Request Management --> */}
                <div className='col-md-7 p-0 mt-3 pr-2 remove-padding mT-20'>
                  <GeneralBox
                    title='Request Management'
                    isButtonPresent={true}
                    handleViewAll={() => setRequestModalShow(true)}
                  >
                    <div className='col-12 track-repayments'>
                      <div className='pl-3 pr-3 track-repayments-table'>
                        <CardTable
                          titles={['Loan Id', 'Due By', 'Status', 'Action']}
                          details={[
                            {
                              id: '1-7118-16341200',
                              date: '03-11-2021',
                              status: 'Sent',
                            },
                            {
                              id: '1-7118-16341200',
                              date: '03-11-2021',
                              status: 'Sent',
                            },
                            {
                              id: '1-7118-16341200',
                              date: '03-11-2021',
                              status: 'Sent',
                            },
                          ]}
                          type='management'
                        />
                      </div>
                    </div>
                  </GeneralBox>
                </div>

                {/* <!-- table Request Management --> */}
                <div className='col-md-5 p-0 mt-3 remove-padding mT-20'>
                  <GeneralBox title='Agfinmart Ecosystem'>
                    {bank &&
                      bank.dashboardMetrics &&
                      bank.dashboardMetrics.success && (
                        <FpoEcoSystemTable
                          data={bank.dashboardMetrics.data.metrics}
                        />
                      )}
                    {bank &&
                      bank.dashboardMetrics &&
                      bank.dashboardMetrics.inProgress && (
                        <div className='d-flex col-12 justify-content-center mb-3'>
                          <Spinner
                            animation='border'
                            role='status'
                            className='TEST'
                          >
                            <span className='visually-hidden'></span>
                          </Spinner>
                        </div>
                      )}
                  </GeneralBox>
                </div>
              </div>
            </div>
          </div>
          <div className='row mt-3 mT-20'>
            {/* <!-- box1 --> */}
            <div className='col-md-3 remove-padding'>
              <GeneralBox title='FPOs Vintage'>
                <div className='col-12 mt-2'>
                  {/* <!-- pie chart --> */}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.success && (
                      <FpoVintageChart
                        documents={bank.dashboardMetrics.data.fpovintagedata}
                        keyName={['tableData']}
                        title={'FPOs Vintage'}
                        legendName={['FPOs Vintage']}
                      />
                    )}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.inProgress && (
                      <div className='d-flex col-12 justify-content-center mb-3'>
                        <Spinner
                          animation='border'
                          role='status'
                          className='TEST'
                        >
                          <span className='visually-hidden'></span>
                        </Spinner>
                      </div>
                    )}
                </div>
              </GeneralBox>
            </div>
            {/* <!-- box1 --> */}

            <div className='col-md-4 p-0 pr-2 remove-padding mT-20'>
              <GeneralBox title='Service Categories'>
                <div className='col-12 mt-2'>
                  {/* <!-- pie chart --> */}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.success && (
                      <ServiceCategoriesChart
                        documents={
                          bank.dashboardMetrics.data.fposervicescategories
                        }
                      />
                    )}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.inProgress && (
                      <div className='d-flex col-12 justify-content-center mb-3'>
                        <Spinner
                          animation='border'
                          role='status'
                          className='TEST'
                        >
                          <span className='visually-hidden'></span>
                        </Spinner>
                      </div>
                    )}
                </div>
              </GeneralBox>
            </div>

            {/* <!-- box1 --> */}
            <div className='col-md-5 p-0 remove-padding mT-20'>
              <GeneralBox>
                <div className='col-12 mt-2'>
                  {/* <!-- pie chart --> */}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.success && (
                      <MapChart
                        states={bank.dashboardMetrics.data.statemetrics}
                      />
                    )}
                  {bank &&
                    bank.dashboardMetrics &&
                    bank.dashboardMetrics.inProgress && (
                      <div className='d-flex col-12 justify-content-center mb-3'>
                        <Spinner
                          animation='border'
                          role='status'
                          className='TEST'
                        >
                          <span className='visually-hidden'></span>
                        </Spinner>
                      </div>
                    )}
                </div>
              </GeneralBox>
            </div>
          </div>
        </div>
      </div>
      <ApplicationModal
        show={loanApplicationModalVisible}
        onHide={(val) => setLoanApplicationModalVisible(val)}
        selectedApplication={selectedApplication}
      />
      <UpcomingRepaymentsModal
        show={upcomingShow}
        onHide={() => setUpcomingShow(false)}
        title={'Track Repayments'}
      />
      <BankRequestManagementModal
        show={requestModalShow}
        onHide={() => setRequestModalShow(false)}
        title={'Track Repayments'}
        applications={bank_request_management}
      />
    </section>
  );
};

export default BankDashboard;
