import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Form as BForm, Spinner } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { fpoActions } from '../../../redux/actions';
import Form from '../../../components/Forms/FPO/Form';
import { APPLICATION_STATUS, LOAN_BUCKETS } from '../../../constants';
import { SET_SELECTED_LOAN } from '../../../redux/types/fpotypes';
import { useLocation } from 'react-router';
import LoanContainer from '../../../components/Modals/fpo/LoanContainer/LoanContainer';
import ApplicationCard from '../../../components/Cards/ApplicationCard';
import ApplcationFilter from '../../../components/Forms/Common/ApplcationFilter';
import ApplicationStatus from '../dashboard/application_status/ApplicationStatus';
import Toast from '../../../utils/toast';

export default function FpoLoan() {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  let qry_application_id = params.get('application_id');
  let qry_type = params.get('type');
  let qry_category = params.get('category');
  const auth = useSelector((state) => state.auth);
  const fpo = useSelector((state) => state.fpo);
  const dispatch = useDispatch();

  const [selectedApplication, setselectedApplication] = useState(null);
  const [selectedLoan, setselectedLoan] = useState(null);
  const [category, setCategory] = useState();
  const [loanCategory, setLoanCategory] = useState('');
  const [applicationList, setapplicationList] = useState([]);
  const [loanList, setloanList] = useState([]);
  const [activeTab, setActiveTab] = useState('applications');

  useEffect(() => {
    if (!auth.isLoggedIn) return;
    const organizationid = auth.userInfo.organizationid;
    dispatch(fpoActions.getFPOAllApplicationsInProgress({ organizationid }));
    dispatch(fpoActions.getFPOAllLoansInProgress({ organizationid }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [auth]);

  const fetchApplicationApi = (application_id) => {
    dispatch({
      type: SET_SELECTED_LOAN,
      payload: application_id,
    });
  };

  useEffect(() => {
    dispatch(fpoActions.clearSelectedLoan());
  }, []);

  const setApplication = (application_p) => {
    let application = application_p ? application_p : {};
    setselectedApplication(application);
    fetchApplicationApi(application?.applicationuid);
  };

  const fetchLoanApi = (loan) => {
    dispatch(fpoActions.getLoanSummaryInProgress(loan));
    dispatch(
      fpoActions.getRepaymentStatusInProgress({
        applicationuid: loan.applicationuid,
      })
    );
    dispatch(
      fpoActions.getLoanDocumentsInProgress({
        applicationuid: loan.applicationuid,
      })
    );
  };

  const setLoan = (loan_p) => {
    let loan = loan_p ? loan_p : {};
    setselectedLoan(loan);
    fetchLoanApi(loan);
  };

  useEffect(() => {
    if (
      fpo &&
      fpo.applications &&
      fpo.applications.success &&
      fpo.loans &&
      fpo.loans.success
    ) {
      if (qry_type && qry_category && !qry_application_id) {
        setActiveTab(qry_type);
        if (qry_type === 'applications' && !category) {
          let applications = {
            applied_application: fpo.applications.success.applied_application,
            active_application: fpo.applications.success.active_application,
            closed_application: fpo.applications.success.closed_application,
          };
          let l_cat = qry_category + '_application';
          setCategory(l_cat);
          if (applications[l_cat].length > 0) {
            setapplicationList(applications[l_cat]);
            setselectedApplication(applications[l_cat][0]);
          } else {
            setapplicationList([]);
            setselectedApplication({});
          }
          setApplication(applications[l_cat][0]);
        } else if (qry_type === 'loans' && !loanCategory) {
          let loans = {
            new_loans: fpo.loans.success.new_loans,
            active_loans: fpo.loans.success.active_loans,
            closed_loans: fpo.loans.success.closed_loans,
          };
          let l_cat = qry_category + '_loans';
          setLoanCategory(l_cat);
          if (loans[l_cat].length > 0) {
            setloanList(loans[l_cat]);
            setselectedLoan(loans[l_cat][0]);
          } else {
            setloanList([]);
            setselectedLoan({});
          }
          setLoan(loans[l_cat][0]);
        }
      }
    }
  }, [fpo?.applications?.success, fpo?.loans?.success]);

  const defaultSetFunctions = () => {
    let applications = {
      applied_application: fpo.applications.success.applied_application,
      active_application: fpo.applications.success.active_application,
      closed_application: fpo.applications.success.closed_application,
    };

    for (let key in applications) {
      if (applications.hasOwnProperty(key)) {
        if (applications[key].length > 0 && !category) {
          setCategory(key);
          setselectedApplication(applications[key][0]);
          setapplicationList(applications[key]);
          fetchApplicationApi(applications[key][0].applicationuid);
          break;
        }
      }
    }

    let loans = {
      new_loans: fpo.loans.success.new_loans,
      active_loans: fpo.loans.success.active_loans,
      closed_loans: fpo.loans.success.closed_loans,
    };
    if (!selectedLoan) {
      for (let key in loans) {
        if (loans.hasOwnProperty(key)) {
          if (loans[key].length > 0 && !loanCategory) {
            setLoanCategory(key);
            setselectedLoan(loans[key][0]);
            setloanList(loans[key]);
            fetchLoanApi(loans[key][0]);
            break;
          }
        }
      }
    }
  };

  // default filter
  useEffect(() => {
    if (
      !category &&
      fpo &&
      fpo.applications.success &&
      fpo.loans.success &&
      !qry_type &&
      !qry_application_id &&
      !qry_category
    ) {
      defaultSetFunctions();
    }
  }, [fpo]);

  useEffect(() => {
    if (fpo && fpo.applications.success && fpo.loans.success && activeTab) {
      defaultSetFunctions();
    }
  }, [activeTab]);

  // applications filter
  useEffect(() => {
    if (
      !category &&
      fpo &&
      fpo.applications &&
      fpo.applications.success &&
      qry_type &&
      qry_application_id &&
      qry_category
    ) {
      setActiveTab(qry_type);
      if (qry_type === 'applications') {
        let applications = {
          applied_application: fpo.applications.success.applied_application,
          active_application: fpo.applications.success.active_application,
          closed_application: fpo.applications.success.closed_application,
        };
        if (applications[`${qry_category}_application`]) {
          setCategory(`${qry_category}_application`);
          setapplicationList(applications[`${qry_category}_application`]);
          let find_application = applications[
            `${qry_category}_application`
          ].find((o) => o.applicationuid === qry_application_id);
          if (find_application) {
            setselectedApplication(find_application);
            fetchApplicationApi(find_application.applicationuid);
          } else {
            invalidApplicationBucket(
              qry_application_id,
              qry_category,
              qry_type
            );
          }
        }
      }
    }
  }, [fpo.applications.success]);

  const invalidApplicationBucket = (qpplication_id, category, type) => {
    Toast({
      type: 'error',
      title: `The application ID "${qpplication_id}" is not in the ${category} ${type} bucket.`,
    });
  };

  // loan filter
  useEffect(() => {
    if (
      !loanCategory &&
      fpo &&
      fpo.loans &&
      fpo.loans.success &&
      qry_type &&
      qry_application_id &&
      qry_category
    ) {
      if (qry_type === 'loans') {
        let loans = {
          new_loan: fpo.loans.success.new_loans,
          active_loan: fpo.loans.success.active_loans,
          closed_loan: fpo.loans.success.closed_loans,
        };
        if (loans[`${qry_category}_loan`]) {
          setLoanCategory(`${qry_category}_loans`);
          setloanList(loans[`${qry_category}_loan`]);
          let find_application = loans[`${qry_category}_loan`].find(
            (o) => o.applicationuid === qry_application_id
          );

          if (find_application) {
            setselectedLoan(find_application);
            fetchLoanApi(find_application);
          } else {
            invalidApplicationBucket(
              qry_application_id,
              LOAN_BUCKETS[qry_category],
              qry_type
            );
          }
        }
      }
    }
  }, [fpo.loans.success]);

  const onApplicationClick = (application) => {
    setselectedApplication(application);
    fetchApplicationApi(application.applicationuid);
  };

  const onLoanClick = (loan) => {
    setselectedLoan(loan);
    fetchLoanApi(loan);
  };

  const handleCategory = (key) => {
    setCategory(key);
    let applications = [];
    if (key) {
      applications = fpo.applications.success[key];
    }
    setapplicationList(applications);
    setApplication(applications?.[0]);
  };

  const handleLoanCategory = (key) => {
    setLoanCategory(key);
    let loans = [];
    if (key) {
      loans = fpo.loans.success[key];
    }
    setloanList(loans);
    setLoan(loans?.[0]);
  };

  const getDocsCounts = (type, keys) => {
    let count = 0;
    keys.forEach((key) => {
      if (fpo[type].success[key]) count += fpo[type].success[key].length;
    });
    return count;
  };

  useEffect(() => {
    if (fpo.applicationFilter.success) {
      if (fpo.applicationFilter.data.length > 0) {
        setapplicationList(fpo.applicationFilter.data);
        setselectedApplication(fpo.applicationFilter.data[0]);
        fetchApplicationApi(fpo.applicationFilter.data[0].applicationuid);
      } else {
        setselectedApplication('');
        setapplicationList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpo?.applicationFilter?.success]);

  useEffect(() => {
    if (fpo.loanFilter.success) {
      if (fpo.loanFilter.data.length > 0) {
        setloanList(fpo.loanFilter.data);
        setselectedLoan(fpo.loanFilter.data[0]);
        fetchLoanApi(fpo.loanFilter.data[0]);
      } else {
        setselectedLoan('');
        setloanList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [fpo?.loanFilter?.success]);

  const handleFormData = (formData, type) => {
    let payload = { formData, category };
    if (type === 'application') {
      dispatch(fpoActions.applicationFilterInProgress(payload));
    } else if (type === 'loan') {
      dispatch(fpoActions.loanFilterInProgress(payload));
    }
  };

  return (
    <div className='container-fluid pb-5'>
      <div className='row'>
        <div className='col-md-3 col-left-width'>
          <h2 className='medium-font f24 mb-4'>Loan Applications</h2>
          <div>
            <Tabs
              defaultActiveKey='applications'
              id='uncontrolled-tab-example'
              className='mb-3 bank-loan-tabs'
              activeKey={activeTab}
              onSelect={(k) => setActiveTab(k)}
            >
              <Tab
                eventKey='applications'
                title={
                  <>
                    APPLICATIONS{' '}
                    <span className='default-badge'>
                      {fpo.applications.success &&
                        getDocsCounts('applications', [
                          'applied_application',
                          'active_application',
                        ])}
                    </span>
                  </>
                }
              >
                <div>
                  <ApplcationFilter
                    category={category}
                    type={'application'}
                    handleFormData={handleFormData}
                    spinner={fpo && fpo.applicationFilter.inProgress}
                  />
                  <BForm.Select
                    className='form-control br-12'
                    onChange={(e) => handleCategory(e.target.value)}
                    value={category}
                  >
                    <option value='applied_application'>
                      Applied Applications{' '}
                      {(fpo.applications.success &&
                        fpo.applications.success.applied_application &&
                        fpo.applications.success.applied_application.length) ||
                        '0'}
                    </option>
                    <option value='active_application'>
                      Active Applications{' '}
                      {(fpo.applications.success &&
                        fpo.applications.success.active_application &&
                        fpo.applications.success.active_application.length) ||
                        '0'}
                    </option>
                    <option value='closed_application'>
                      Closed Applications{' '}
                      {(fpo.applications.success &&
                        fpo.applications.success.closed_application &&
                        fpo.applications.success.closed_application.length) ||
                        '0'}
                    </option>
                  </BForm.Select>
                  <div
                    className='py-3 custom-scrollbar'
                    style={{ height: '600px' }}
                  >
                    {applicationList.map((value, key) => {
                      return (
                        <div className='mb-2' key={key}>
                          <ApplicationCard
                            isActive={
                              selectedApplication &&
                              selectedApplication.applicationuid ===
                                value.applicationuid
                                ? true
                                : false
                            }
                            applicationId={value.applicationuid}
                            fpoName={value.fponame}
                            fpoState={value.fpostate}
                            applicationDate={value.applicationdate}
                            loanName={value.loanproductname}
                            loanAmount={value.loanamount}
                            onClick={() => onApplicationClick(value)}
                          />
                        </div>
                      );
                    })}
                  </div>
                </div>
              </Tab>
              <Tab
                eventKey='loans'
                title={
                  <>
                    LOANS{' '}
                    <span className='default-badge'>
                      {fpo.loans.success &&
                        getDocsCounts('loans', ['new_loans', 'active_loans'])}
                    </span>
                  </>
                }
              >
                <div>
                  <ApplcationFilter
                    category={loanCategory}
                    type={'loan'}
                    handleFormData={handleFormData}
                    spinner={fpo && fpo.loanFilter.inProgress}
                  />
                  <BForm.Select
                    className='form-control br-12'
                    onChange={(e) => handleLoanCategory(e.target.value)}
                    value={loanCategory}
                  >
                    <option value='new_loans'>
                      Sanctioned Loans{' '}
                      {(fpo.loans.success &&
                        fpo.loans.success.new_loans &&
                        fpo.loans.success.new_loans.length) ||
                        '0'}
                    </option>
                    <option value='active_loans'>
                      Live Loans{' '}
                      {(fpo.loans.success &&
                        fpo.loans.success.active_loans &&
                        fpo.loans.success.active_loans.length) ||
                        '0'}
                    </option>
                    <option value='closed_loans'>
                      Matured Loans{' '}
                      {(fpo.loans.success &&
                        fpo.loans.success.closed_loans &&
                        fpo.loans.success.closed_loans.length) ||
                        '0'}
                    </option>
                  </BForm.Select>
                  <div
                    className='py-3 custom-scrollbar'
                    style={{ height: '600px' }}
                  >
                    {loanList &&
                      loanList.map((value, key) => {
                        return (
                          <div className='mb-2' key={key}>
                            <ApplicationCard
                              isActive={
                                selectedLoan &&
                                selectedLoan.applicationuid ===
                                  value.applicationuid
                                  ? true
                                  : false
                              }
                              applicationId={value.applicationuid}
                              fpoName={value.fponame}
                              fpoState={value.fpostate}
                              applicationDate={value.applicationdate}
                              loanName={value.loanproductname}
                              loanAmount={value.loanamount}
                              onClick={() => onLoanClick(value)}
                            />
                          </div>
                        );
                      })}
                  </div>
                </div>
              </Tab>
            </Tabs>
          </div>
        </div>
        <div className='col-md-9 bg-white br-3 general-box col-right-width fpo-app-status-padding'>
          <br />

          {activeTab === 'applications' && (
            <>
              {fpo.selectedLoan && fpo.selectedLoan.isLoading && (
                <div className='d-flex justify-content-center pt-5'>
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                </div>
              )}
              {fpo &&
                fpo.selectedLoan &&
                fpo.selectedLoan.loanInfo &&
                fpo.selectedLoan.loanInfo.applicationstatus === 'Applied' && (
                  <Form
                    readOnly={
                      fpo.selectedLoan?.loanInfo?.applicationstatus !==
                      APPLICATION_STATUS.APPLIED
                    }
                    loanInfo={fpo.selectedLoan?.loanInfo}
                    documents={fpo.documents}
                    hideNavButton={true}
                  />
                )}

              {fpo &&
                fpo.selectedLoan &&
                fpo.selectedLoan.loanInfo &&
                (fpo.selectedLoan.loanInfo.applicationstatus === 'Published' ||
                  fpo.selectedLoan.loanInfo.applicationstatus === 'Cancelled' ||
                  fpo.selectedLoan.loanInfo.applicationstatus === 'Closed') && (
                  <ApplicationStatus
                    application_id_={fpo.selectedLoan?.loanInfo?.applicationuid}
                    fromComponent={true}
                    whiteBg={true}
                    hideActions={
                      fpo.selectedLoan.loanInfo.applicationstatus !==
                      'Published'
                    }
                  />
                )}
            </>
          )}

          {activeTab === 'loans' &&
            (fpo && fpo.selectedAppLoan && fpo.selectedAppLoan.inProgress ? (
              <div className='d-flex justify-content-center pt-5'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            ) : (
              <>
                <LoanContainer
                  readOnly={
                    fpo.selectedAppLoan?.loanInfo?.applicationstatus !==
                    APPLICATION_STATUS.APPLIED
                  }
                  loanInfo={fpo.selectedAppLoan?.loanInfo}
                  documents={fpo.documents}
                />
              </>
            ))}
        </div>
      </div>
    </div>
  );
}
