import { Tabs, Tab } from 'react-bootstrap';
import DocumentList from '../Tables/DocumentList';
import _ from 'lodash';
const RequestedDocuments = ({ sansactionDocuments, handleDocumentSubmit }) => {
  return (
    <Tabs className='mb-3 sansaction-document-tabs'>
      {Object.keys(sansactionDocuments).map((key) => (
        <Tab
          eventKey={key}
          title={<span className='h6 text-capitalize '>{key}</span>}
        >
          <DocumentList
            documents={_.orderBy(
              sansactionDocuments[key],
              ['documentURL'],
              ['desc']
            )}
            uploadDoc
            saveDoc
            handleDocumentSubmit={handleDocumentSubmit}
            documentKey={key}
          />
        </Tab>
      ))}
    </Tabs>
  );
};

export default RequestedDocuments;
