import React from 'react';
import './Profile.scss';
import groupIcon from '../../assets/Images/Icons/group.svg';
import male_black_24dp from '../../assets/Images/Icons/person_black_24dp.svg';
import female_black_24dp from '../../assets/Images/Icons/Group 160258.svg';
import currency_black_24dp from '../../assets/Images/Icons/currency_rupee_black_24dp.svg';
import map_black_24dp from '../../assets/Images/Icons/map_black_24dp.svg';
import village_black_24dp from '../../assets/Images/Icons/holiday_village_black_24dp.svg';
import dayjs from 'dayjs';
import ProfileCategoryTable from '../Tables/ProfileCategoryTable';
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css';
import 'tippy.js/themes/light.css';
import info from '../../assets/Images/Icons/info.svg';
import avatarDefault from '../../assets/Images/Icons/Profile picture-null.jpg';

//NEED TO CONVERT INTO SUB-COMPONENTS FOR CLEAN CODE - 30/11/2021 10:56:00 pm - Jabez Sanjay
const ProfileData = ({ profileData, showEdit }) => {
  return (
    <>
      {showEdit && (
        <div className='text-right'>
          <a className='f12 default-anchor cursor-pointer mr-5 Mr-15' href='https://fpogateway.com/fpo/profile'>EDIT PROFILE</a>
        </div>
      )}
      {profileData.documents && profileData.documents.profiledata && (
        <section>
          <div className='container remove-padding remove-margin'>
            <div className='row mt-5'>
              {/* <!-- image section --> */}
              <div className='col-md-3'>
                <div
                  className='bg-box'
                  style={{
                    background: `url("${profileData.documents.profiledata.fpoLogoURL}")`,
                    backgroundRepeat: 'no-repeat',
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                  }}
                >
                  <div className='row text-justify-center text-center'>
                    <div className='col-12'>
                      {/* <span className='profile-bg bold-font '>
                        <span> KU</span>
                      </span> */}
                    </div>
                    <div
                      className='col-12 mt-4'
                      style={{ textShadow: ' 1px 1px 1px rgb(255 255 255)' }}
                    >
                      <p className='bold-font f20'>
                        {profileData.documents.profiledata.fpoName}
                      </p>
                      <p>
                        <span className='f14 medium-font'>
                          {
                            profileData.documents.profiledata.stateMaster
                              .stateName
                          }{' '}
                          | Vintage{' '}
                          {profileData.documents.profiledata.foundedOn
                            ? dayjs(
                                profileData.documents.profiledata.foundedOn
                              ).format('YYYY-MM-DD')
                            : 'NIL'}
                        </span>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- image section ends --> */}

              {/* <!-- crops section --> */}
              <div className='col-md-5 our-crops-height'>
                <div className='row  align-left'>
                  <div className='col-12 review-profile-padding'>
                    <h4 className='f20 bold-font mT-20'>
                      Our Crops
                      <Tippy
                        content='A variety of crops are grown by our member farmers depending on season, demand, and profitability. The most common crops are displayed here.'
                        placement='bottom-start'
                        maxWidth={400}
                        theme='light'
                      >
                        <button className='info'>
                          <img src={info} alt='' />
                        </button>
                      </Tippy>
                    </h4>
                  </div>
                  <div className='col-12 review-profile-padding'>
                    {/* <!-- slidshow     --> */}
                    <div className='row text-left h40 custom-scrollbar'>
                      {profileData.documents.profiledata.cropMapping.map(
                        (value, key) => {
                          return (
                            <div className='col-md-3 our-crops-col-width' key={key}>
                              <div className='pt-2 pb-2 '>
                                <img
                                  src={value.cropsDetails.cropIconUrl}
                                  alt=''
                                  height={'50px'}
                                  width={'50px'}
                                />
                                <h4 className='medium-font mt-2 f12'>
                                  {value.cropsDetails.cropName}
                                </h4>
                              </div>
                            </div>
                          );
                        }
                      )}
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- end crops --> */}
              <div className='col-md-4'>
                <div className='row  align-left'>
                  <div className='col-12 review-profile-padding'>
                    <h4 className='f20 bold-font mT-20'>
                      Our Team
                      <Tippy
                        content='Our FPO is managed by professionals and is governed by member farmers. We follow all necessary compliance and governance requirements to ensure smooth functioning of our FPO.'
                        placement='bottom-start'
                        maxWidth={300}
                        theme='light'
                      >
                        <button className='info'>
                          <img src={info} alt='' />
                        </button>
                      </Tippy>
                    </h4>
                  </div>
                  <div className='col-12 review-profile-padding'>
                    {/* <!-- slidshow     --> */}
                    <div className='row text-left h40 custom-scrollbar'>
                      {profileData.documents.profiledata.teamMembers.bodList.map(
                        (value, key) => {
                          return (
                            <div className='col-md-4 our-crops-col-width' key={key}>
                              <div className='pt-2 pb-2'>
                                <img
                                  src={
                                    value.profileImageUrl
                                      ? value.profileImageUrl
                                      : avatarDefault
                                  }
                                  className='profile-img'
                                  alt=''
                                  height={'50px'}
                                  width={'50px'}
                                />
                                <h4 className='medium-font mt-2 f12'>
                                  <b className='f12 medium-font'>{`${value.firstName} ${value.lastName}`}</b>
                                  <span className='op70 f12'> BOD</span>
                                </h4>
                              </div>
                            </div>
                          );
                        }
                      )}
                      {profileData.documents.profiledata.teamMembers.staffList.map(
                        (value, key) => {
                          return (
                            <div key={key} className='col-md-4'>
                              <div className='pt-2 pb-2'>
                                <img
                                  src={
                                    value.profileImageUrl
                                      ? value.profileImageUrl
                                      : avatarDefault
                                  }
                                  className='profile-img'
                                  alt=''
                                  height={'50px'}
                                  width={'50px'}
                                />
                                <h4 className='medium-font mt-2 f12'>
                                  <b className='f12 medium-font'>{`${value.firstName} ${value.lastName}`}</b>
                                  <span className='op70 f12'> Staff</span>
                                </h4>
                              </div>
                            </div>
                          );
                        }
                      )}
                    </div>
                  </div>
                </div>
              </div>

              {/* <!-- end crops --> */}
            </div>
            <div className='row'>
              {/* <!-- stats section --> */}
              <div className='col-md-3 text-center remove-padding mT-20'>
                <div className='row mt-4  align-left no-gutters ml-3 remove-margin'>
                  {/* <!-- total farmer --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12 remove-padding remove-margin'>
                        <span>
                          <img
                            src={groupIcon}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12 remove-padding remove-margin'>
                        <p className='bold-font bg-muted f16 mb-0'>
                          Total Farmers
                        </p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.membershipBase}
                        </h4>
                      </div>
                    </div>
                  </div>

                  {/* <!-- male --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12 remove-padding remove-margin'>
                        <span>
                          <img
                            src={male_black_24dp}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12 remove-padding remove-margin'>
                        <p className='bold-font bg-muted f16 mb-0'>Male</p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.maleMembers}
                        </h4>
                      </div>
                    </div>
                  </div>

                  <div className='col-12 mt-3'></div>
                  {/* <!-- female --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12 remove-padding remove-margin'>
                        <span>
                          <img
                            src={female_black_24dp}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12 remove-padding remove-margin'>
                        <p className='bold-font bg-muted f16 mb-0'>Female</p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.femaleMembers}
                        </h4>
                      </div>
                    </div>
                  </div>

                  {/* <!-- Share Capital --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12 remove-padding remove-margin'>
                        <span>
                          <img
                            src={currency_black_24dp}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12 remove-padding remove-margin'>
                        <p className='bold-font bg-muted f16 mb-0'>
                          Share Capital
                        </p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.shareCapital /
                            100000}{' '}
                          <small className='bg-muted f12'>Lakhs</small>
                        </h4>
                      </div>
                    </div>
                  </div>

                  <div className='col-12 mt-3'></div>
                  {/* <!-- Map --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12'>
                        <span>
                          <img
                            src={map_black_24dp}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12'>
                        <p className='bold-font bg-muted f16 mb-0'>Acres</p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.totalAverage}
                        </h4>
                      </div>
                    </div>
                  </div>
                  {/* <!-- Villages --> */}
                  <div className='col-md-6 our-crops-col-width'>
                    <div className='row'>
                      <div className='col-12'>
                        <span>
                          <img
                            src={village_black_24dp}
                            className='profile-stat'
                            alt='Icon'
                            srcSet=''
                          />
                        </span>
                      </div>
                      <div className='col-12'>
                        <p className='bold-font bg-muted f16 mb-0'>Villages</p>
                      </div>
                      <div className='col-md-12'>
                        <h4 className='bold-font f24'>
                          {profileData.documents.profiledata.villagesCovered}
                        </h4>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- end stats --> */}
              <div className='col-md-9 remove-padding remove-margin'>
                <div className='row'>
                  {/* <!-- bussiness activity --> */}
                  <div className='col-md-4 px-1 mT-20'>
                    <div className='general-box'>
                      <div
                      >
                        <div className='col-12 '>
                          <h4 className='bold-font f20 pt-4 pb-2 text-left'>
                          Business Activities
                            <Tippy
                              content='Through aggregation we enable collective business for member farmers.'
                              placement='bottom-start'
                              maxWidth={300}
                              theme='light'
                            >
                              <button className='info'>
                                <img src={info} alt='' />
                              </button>
                            </Tippy>
                          </h4>
                        </div>
                        <div className='col-12 row  custom-scrollbar'
                        style={{ maxHeight: '50vh' }}>
                          <ProfileCategoryTable
                            categories={
                              profileData.documents.profiledata.businessMapping
                            }
                            key1='businessDetails'
                            key2='businessName'
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  {/* <!-- licences --> */}
                  <div className='col-md-4 px-1 remove-padding remove-margin '>
                    <div className='general-box mT-20'>
                      <div
                        className='row custom-scrollbar'
                        style={{ maxHeight: '50vh' }}
                      >
                        <div className='col-12'>
                          <h4 className='bold-font f20 pt-4 pb-2 text-left'>
                            Licences
                            <Tippy
                              content='We ensure that high quality inputs are available for the farming activities within our FPO’s operating areas. Licences are managed per the state and central government guidelines and are renewed periodically as needed.'
                              placement='bottom-start'
                              maxWidth={300}
                              theme='light'
                            >
                              <button className='info'>
                                <img src={info} alt='' />
                              </button>
                            </Tippy>
                          </h4>
                          {/* <p className='text-left muted'>
                        We ensure that high quality inputs are available for the
                        farming activities within our FPO’s operating areas.
                        Licences are managed per the state and central
                        government guidelines and are renewed periodically as
                        needed.
                      </p> */}
                        </div>
                        <div className='col-12'>
                          <ProfileCategoryTable
                            categories={
                              profileData.documents.profiledata.licenseMapping
                            }
                            key1='licenseDetails'
                            key2='licenseName'
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                  {/* <!-- bussiness activity --> */}
                  <div className='col-md-4 remove-padding remove-margin'>
                    <div className='general-box  mT-20 '>
                      <div
                        className='row custom-scrollbar'
                        style={{ maxHeight: '50vh' }}
                      >
                        <div className='col-12'>
                          <h4 className='bold-font f20 pt-4 pb-2 text-left'>
                            Infrastructure
                            <Tippy
                              content='Our FPO facilitates supportive infrastructures for farmers, to handle primary and secondary agri value chain activities.'
                              placement='bottom-start'
                              maxWidth={300}
                              theme='light'
                            >
                              <button className='info'>
                                <img src={info} alt='' />
                              </button>
                            </Tippy>
                          </h4>
                        </div>
                        <div className='col-12'>
                          <ProfileCategoryTable
                            categories={
                              profileData.documents.profiledata.infraMapping
                            }
                            key1='infraDetails'
                            key2='infrastructureName'
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      )}
    </>
  );
};

export default ProfileData;
