import { useEffect } from 'react';
import { Form, Spinner } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import {
  APPLICATION_FILTER,
  LOAN_FIELDS,
  APPLICATION_FILTER_CATEGORY,
} from '../../../constants';

const ApplcationFilter = ({ category, handleFormData, type, spinner }) => {
  const { register, handleSubmit, setValue } = useForm();

  const handleFormSubmit = (data) => {
    const formData = new FormData();
    for (const key in data) {
      if (Object.hasOwnProperty.call(data, key)) {
        formData.append(key, data[key]);
      }
    }
    handleFormData(formData, type);
  };

  useEffect(() => {
    if (category) {
      setValue(
        APPLICATION_FILTER[LOAN_FIELDS.applicationCategory],
        APPLICATION_FILTER_CATEGORY[LOAN_FIELDS[category]]
      );
    }
  }, [category]);

  return (
    <Form onSubmit={handleSubmit(handleFormSubmit)}>
      <Form.Group className='searchInputWrapper mt-4 mb-3'>
        <Form.Control
          className='searchInput'
          type='text'
          placeholder='Search'
          {...register(APPLICATION_FILTER[LOAN_FIELDS.searchField])}
        />
        {category && (
          <Form.Control
            className='d-none'
            type='text'
            {...register(APPLICATION_FILTER[LOAN_FIELDS.applicationCategory])}
          />
        )}
        {!spinner ? (
          <button className='searchInputIcon btn p-0 shadow-none'>
            <i className=' fa fa-search'></i>
          </button>
        ) : (
          <Spinner
            animation='border'
            size='sm'
            variant='primary'
            role='status'
            className='searchInputLoader'
          >
            <span className='visually-hidden'></span>
          </Spinner>
        )}
      </Form.Group>
    </Form>
  );
};

export default ApplcationFilter;
