import React from 'react';
import { Modal } from 'react-bootstrap';
import LoanApplicationContainer from './LoanApplicationContainer';

const ApplicationModal = (props) => {
  const [closeModal, setCloseModal] = React.useState(false);
  return (
    <>
      <Modal
        onHide={() => {
          props.onHide(false);
          setCloseModal(true);
        }}
        show={props.show}
        className='modal-container loan-application-modal'
        aria-labelledby='example-custom-modal-styling-title'
        size='xl'
        keyboard={false}
      >
        <Modal.Body>
          <LoanApplicationContainer
            loanApplicationTabPadding='10%'
            selectedApplication={props.selectedApplication}
            closeModal={closeModal}
            show={props.show}
            closeMainStatus={props.onHide}
          />
        </Modal.Body>
      </Modal>
    </>
  );
};

export default ApplicationModal;
