import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import SliderImg1 from '../../../assets/Images/slider/logon-4.jpg';
import '../Login.scss';

const Slider = () => {
    const params = {
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 2500,
          disableOnInteraction: false
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      }
    
    return (
    <Swiper {...params}>
    <SwiperSlide><img
          src={SliderImg1}
          className="img-responsive h100 slider-img"
          alt=""
        /></SwiperSlide>
    <SwiperSlide> <img
          src={SliderImg1}
          className="img-responsive h100 slider-img"
          alt=""
        /></SwiperSlide>
    <SwiperSlide> <img
          src={SliderImg1}
          className="img-responsive h100 slider-img"
          alt=""
        /></SwiperSlide>
  </Swiper>
 
);
    };

export default Slider;
