
import Modal from 'react-bootstrap/Modal';
import { Col, Row, Form, Button } from 'react-bootstrap';
import { CloseOutline } from 'react-ionicons';
import { Label } from 'devextreme-react/bar-gauge';


function AddProductModal({ show, onHide, props }) {
  const handleClose = () => onHide(false);
    return (
      <Modal
        show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      dialogClassName='modal-100w'
      size="lg"
      >
        <Modal.Header>
          <Modal.Title id="contained-modal-title-vcenter" className='f20 text-bold mt-3 ml-3'>
          Add products
          </Modal.Title>
          <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
        </Modal.Header>
        <Modal.Body>
        <Row className="g-2">
            <Col md>
                <p className='f14 form-lable'>Product Category</p>
                <Form.Select aria-label="Floating label select example" className='select-list'>
                    <option>Working Capital-Input Loan</option>
                    <option value="1">One</option>
                    <option value="2">Two</option>
                    <option value="3">Three</option>
                </Form.Select>
            </Col>
            <Col md>
                <p className='f14 form-lable'>Product Name</p>
                <Form.Control type="text" placeholder="Buy Seeds" className='select-list'/>                
            </Col>
            </Row>
            <br/>
            <Row>
            <Col>
            <p className='f14 form-lable'>Product Discription</p>
            <Form.Control as="textarea" className='select-list'/>
            </Col>
            <Col>
            <p className='f14 form-lable'>Product Details</p>
            <Form.Control as="textarea" className='select-list'/>
            </Col>
        </Row>
        <Row>
        <div className="m-3">
          <input type="file" className="custom-file-inpust mt-3" placeholder='Upload'/>
        </div>
            </Row>
            <Row><Button variant="primary" type="submit" className='m-3 pl-3 pr-3 pt-2 pb-1 br-12 f16'>
    ADD PRODUCT
  </Button></Row>
        </Modal.Body>
      </Modal>
    );
  }

  export default AddProductModal;