import { Modal } from 'react-bootstrap';
import DocumentIcon from '../../DocumentIcon/DocumentIcon';
const DocumentsReview = ({ show, handleClose, docsData }) => {
  return (
    <Modal
      size='md'
      aria-labelledby='contained-modal-title-vcenter'
      centered
      show={show}
      onHide={() => handleClose(false)}
      className='document-review-sub-modal'
    >
      <Modal.Body>
        <div className='p-2'>
          <div className='d-flex justify-content-between align-items-center'>
            <h4 className='f20 text-bold'>Review Documents</h4>
            <button
              type='button'
              className='close f20 h4'
              onClick={() => handleClose(false)}
            >
              ×
            </button>
          </div>
          <div className='mt-3'>
            <h4 className='f20'>{docsData.title}</h4>
            <div className='row py-2 px-3'>
              <h6 className='docs-modal-doc-title'>{docsData.documentName}</h6>
              {docsData[docsData.key] &&
                docsData[docsData.key].map((value, key) => {
                  return (
                    <span className='col-md-12 bt-2 px-0' key={key}>
                      <a
                        href={`${value.documentPath}`}
                        target='_blank'
                        className={' py-0 document-link  ' + value.documentName}
                        rel='noreferrer'
                      >
                        <DocumentIcon download={true} />
                        <span>{value.documentName}</span>
                      </a>
                    </span>
                  );
                })}
            </div>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default DocumentsReview;
