import { toast } from 'react-toastify';

let config = {
  position: 'top-center',
  autoClose: 5000,
  hideProgressBar: false,
  closeOnClick: true,
  pauseOnHover: true,
  draggable: true,
  progress: undefined,
  theme: 'colored',
  style: { width: 'auto' },
};
const Toast = ({ type, title }, configs) => {
  configs = configs ? configs : {};
  config = { ...config, ...configs };
  type = type ? type : 'success';
  switch (type) {
    case 'success':
      toast.success(title, {
        ...config,
      });
      break;

    case 'error':
      toast.error(title, {
        ...config,
      });
      break;

    case 'warning':
      toast.warn(title, {
        ...config,
      });
      break;

    case 'info':
      toast.info(title, {
        ...config,
      });
      break;

    default:
      toast(title, {
        ...config,
      });
      break;
  }
};
export default Toast;
