import Modal from 'react-bootstrap/Modal';
import { Col, Row, Form, Accordion } from 'react-bootstrap';

import React from 'react';
import './adminStyle.css';
import { CloseOutline } from 'react-ionicons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFileUpload } from '@fortawesome/free-solid-svg-icons';
import { LOAN_FIELDS, ADMIN_APPLICATION_STATUS } from './../../constants';
import { useForm } from 'react-hook-form';

function AddLendarPartner({ show, onHide, props }) {
  const { register, handleSubmit, watch } = useForm({});
  const handleClose = () => onHide(false);
  const ColoredLine = ({ color }) => (
    <hr
      style={{
        color: color,
        backgroundColor: color,
        height: 5,
      }}
    />
  );

  return (
    <Modal
      show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      dialogClassName='modal-100w'
      size='lg'
    >
      <Modal.Header>
        <Modal.Title id='contained-modal-title-vcenter'></Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>

      <Modal.Body>
        <Accordion defaultActiveKey='0'>
          <Accordion.Item eventKey='0'>
            <div className='hr'>
              <hr />
            </div>
            <Accordion.Header className='f20'>
              {' '}
              <div className='d-flex justify-content-between align-items-center'>
                <h4 className='form-section-header ml-1 f24'>
                  Add Lendar / Partner
                </h4>
                <i className='fa f16 text-muted down-arrow fa-chevron-down'></i>
              </div>
            </Accordion.Header>

            <div className='hr'>
              <hr />
            </div>
            <Accordion.Body>
              <Row className='g-2'>
                <Col md className='mt-2'>
                  <p className='f14 form-lable'>Company Name</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-2'>
                  <p className='f14 form-lable'>Assigned Organization ID</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Contact Person</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Phone</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Email</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Address</p>
                  <Form.Control as='textarea' className='select-list mt-1' />
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>City</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>State</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>District</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Pincode</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Company GST</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Upload Bank Logo</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
              </Row>
              <Row>
                <Col>
                  <div className='mt-5'>
                    <button className='btn btn-primary br-12'> Save</button>
                  </div>
                </Col>
                <Col></Col>
                <Col>
                  <div className='text-right mt-5'>
                    <button className='f12 de-activate-btn text-bold'>
                      {' '}
                      DELETE PROFILE
                    </button>
                  </div>
                </Col>
              </Row>
            </Accordion.Body>
          </Accordion.Item>
          <Accordion.Item eventKey='1'>
            <div className='hr'>
              <hr />
            </div>
            <Accordion.Header className='f20'>
              <div className='d-flex justify-content-between align-items-center'>
                <h4 className='form-section-header ml-1 f24'>
                  Add Partnership
                </h4>
                <i className='fa f16 text-muted down-arrow1 fa-chevron-down'></i>
              </div>
            </Accordion.Header>
            <div className='hr'>
              <hr />
            </div>
            <Accordion.Body>
              <Row className='g-2'>
                <Col md className='mt-2'>
                  <p className='f14 form-lable'>Partnership Name</p>
                  <Form.Control type='text' className='select-list mt-1' />
                </Col>
                <Col md className='mt-2'>
                  <p className='f14 form-lable'>Co-Category</p>
                  <Form.Select
                    aria-label='Floating label select example'
                    className='select-list'
                  >
                    <option>Co Lending</option>
                    <option value='1'>One</option>
                    <option value='2'>Two</option>
                    <option value='3'>Three</option>
                  </Form.Select>
                </Col>
              </Row>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Email</p>
                  <Form.Select
                    aria-label='Floating label select example'
                    className='select-list'
                  >
                    <option>Regional Manager</option>
                    <option value='1'>One</option>
                    <option value='2'>Two</option>
                    <option value='3'>Three</option>
                  </Form.Select>
                </Col>
                <Col md className='mt-4'>
                  <p className='f14 form-lable'>Description</p>
                  <Form.Control as='textarea' className='select-list mt-1' />
                </Col>
              </Row>
              <p className='f14 form-lable ml-3 '>Upload</p>
              <Row className='g-2'>
                <Col md className='mt-4'>
                  {/* <div className=' text-center sam-upload-container'>
                <div className='sam-icon-container'>
                  <FontAwesomeIcon
                    icon={faFileUpload}
                    className='sam-file-icon'
                  />
                </div>
                <button
                  type='button'
                  className='dflex justify-content-between align-items-center sam-text-box sam-upload-text-box'
                  onClick={() =>
                    document.getElementById('sam-input-file').click()
                  }
                >
                  <div className=' col upload-text-container'>
                    <h6 className='upload-text'>UPLOAD DOCUMENT</h6>
                  </div>
                </button>
                <input
                  id='sam-input-file'
                  type='file'
                  className='d-none'
                  {...register(
                    ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportFile]
                  )}
                />
              </div> */}

                  <input
                    type='file'
                    className='custom-file-inpust'
                    placeholder='Upload'
                  />
                </Col>
                <Col md className='mt-4'>
                  <input
                    type='file'
                    className='custom-file-inpust'
                    placeholder='Upload'
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <div className='mt-5'>
                    <button className='btn btn-secondary br-12'>
                      {' '}
                      ADD NEW PARTNERSHIP
                    </button>
                  </div>
                </Col>
                <Col></Col>
                <Col>
                  <div className='text-right mt-5'>
                    <button className='btn btn-primary br-12'> Save</button>
                  </div>
                </Col>
              </Row>
            </Accordion.Body>
          </Accordion.Item>
        </Accordion>
      </Modal.Body>
    </Modal>
  );
}

export default AddLendarPartner;
