import { useState, useEffect } from 'react';
import { Modal } from 'react-bootstrap';
import _ from 'lodash';
import InputLoanForm from '../../Forms/FPO/WorkingCapital/InputLoan/InputLoanForm';
import OutputLoanForm from '../../Forms/FPO/WorkingCapital/OutputLoan/OutputLoanForm';
import CatalyticInfrastructureForm from '../../Forms/FPO/TermLoan/CatalyticInfrastructure/CatalyticInfrastructureForm';

const FpoQuickViewModal = ({ random, applicationId, loanInfo }) => {
  const [show, setShow] = useState(false);
  const [formData, setFormData] = useState({});

  useEffect(() => {
    if (random) setShow(true);
  }, [random]);

  useEffect(() => {
    if (!loanInfo || loanInfo === null) return;
    const { data } = loanInfo;
    const answer = {};
    _.each(data, (obj) => {
      answer[obj.Question_Number] = obj.Answer;
    });

    setFormData((prev) => ({
      ...prev,
      answer,
    }));
  }, [loanInfo]);

  return (
    <>
      <Modal
        onHide={() => setShow(false)}
        show={show}
        className='modal-container loan-application-modal'
        aria-labelledby='example-custom-modal-styling-title'
        size='xl'
        keyboard={false}
      >
        <Modal.Header>
          <Modal.Title className='h5 text-muted text-bold'>
            Application ID ( {applicationId})
          </Modal.Title>
          <button
            className='h5 text-muted text-bold f24 mt-n2 btn p-0 m-0 shadow-none'
            onClick={() => setShow(false)}
          >
            <i className='fa fa-close '></i>
          </button>
        </Modal.Header>
        <Modal.Body>
          {formData &&
          formData['answer'] &&
          formData['answer']['1'] === 'Working capital - Input' ? (
            <InputLoanForm
              readOnly={true}
              defaultValues={formData['answer']}
              hideActionsButtons={true}
            />
          ) : formData &&
            formData['answer'] &&
            formData['answer']['1'] === 'Working capital - Output' ? (
            <OutputLoanForm
              readOnly={true}
              defaultValues={formData['answer']}
              hideActionsButtons={true}
            />
          ) : formData &&
            formData['answer'] &&
            formData['answer']['1'] === 'Term loan - Infrastructure' ? (
            <CatalyticInfrastructureForm
              readOnly={true}
              defaultValues={formData['answer']}
              hideActionsButtons={true}
            />
          ) : (
            <div className='d-flex justify-content-center '>
              <h5 className='text-danger text-bold'>
                Application type not matched
              </h5>
            </div>
          )}
        </Modal.Body>
      </Modal>
    </>
  );
};

export default FpoQuickViewModal;
