import { useEffect, useState } from 'react';
import { Col, Row, Form, Spinner } from 'react-bootstrap';
import FpoCard from '../../../components/Cards/FpoCard';
import ShadowBox from '../../../components/ShadowBox';
import {
  states,
  explore_fpo_filter_membership,
  explore_fpo_filter_vintage,
  explore_fpo_filter_grading,
  explore_fpo_filter_turnover,
  explore_fpo_filter_share_capital,
  explore_fpo_filter_share_infrastructure,
} from '../../../data';
import { bankActions } from '../../../redux/actions';
import { useDispatch, useSelector } from 'react-redux';
import FilterPills from '../../../components/FormInputs/FilterPills';
import _ from 'lodash';

import { FPO_LIST_FILTER, LOAN_FIELDS } from '../../../constants';
import CompareFpoModal from '../../../components/Modals/CompareFpoModal';

export default function ExploreFpo() {
  const bank = useSelector((state) => state.bank);
  const dispatch = useDispatch();

  const [FilterMembership, setFilterMembership] = useState(false);
  const [FilterVintage, setFilterVintage] = useState(false);
  const [FilterGrading, setFilterGrading] = useState([]);
  const [FilterTurnover, setFilterTurnover] = useState(false);
  const [FilterShareCapital, setFilterShareCapital] = useState(false);
  const [FilterInfraStructure, setFilterInfraStructure] = useState(false);
  const [FilterState, setFilterState] = useState(false);
  const [SelectedFpos, setSelectedFpos] = useState([]);
  const [CompareFpoModalStatus, setCompareFpoModalStatus] = useState(false);

  // To get or post fpo fetch api based on filtered values
  useEffect(() => {
    if (
      FilterState ||
      FilterMembership ||
      FilterVintage ||
      FilterGrading.length > 0 ||
      FilterTurnover ||
      FilterShareCapital ||
      FilterInfraStructure
    ) {
      fetchPostExploreFpo();
    } else {
      dispatch(bankActions.getExploreFpoInProgress());
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    FilterState,
    FilterMembership,
    FilterVintage,
    FilterGrading,
    FilterTurnover,
    FilterShareCapital,
    FilterInfraStructure,
  ]);

  //   To clear selected fpo on every api calls
  useEffect(() => {
    if (bank && bank.exploreFpo && bank.exploreFpo.fpoList) setSelectedFpos([]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.exploreFpo?.fpoList]);

  const fetchPostExploreFpo = () => {
    const formData = new FormData();

    if (FilterState) {
      formData.append(FPO_LIST_FILTER[LOAN_FIELDS.fpoFilterState], FilterState);
    }

    if (FilterMembership) {
      formData.append(
        FPO_LIST_FILTER[LOAN_FIELDS.fpoFilterMembership],
        FilterMembership
      );
    }

    if (FilterVintage) {
      formData.append(
        FPO_LIST_FILTER[LOAN_FIELDS.fpoFilterVintage],
        FilterVintage
      );
    }

    if (FilterTurnover) {
      formData.append(
        FPO_LIST_FILTER[LOAN_FIELDS.fpoFilterTurnover],
        FilterTurnover
      );
    }

    if (FilterShareCapital) {
      formData.append(
        FPO_LIST_FILTER[LOAN_FIELDS.fpoFilterShareCapital],
        FilterShareCapital
      );
    }

    if (FilterGrading && FilterGrading.length > 0) {
      FilterGrading.map((field) => {
        formData.append(field, FilterShareCapital);
        return null;
      });
    }
    dispatch(bankActions.postExploreFpoInProgress({ formData }));
  };

  const handleMemberShip = (value) => {
    value = value === FilterMembership ? false : value;
    setFilterMembership(value);
  };

  const handleVintage = (value) => {
    value = value === FilterVintage ? false : value;
    setFilterVintage(value);
  };

  const handleTurnover = (value) => {
    value = value === FilterTurnover ? false : value;
    setFilterTurnover(value);
  };

  const handleGrading = (value) => {
    let status = FilterGrading.includes(value);
    let arrays = [...FilterGrading];
    if (status) {
      _.remove(arrays, function (array) {
        return array === value;
      });
    } else {
      arrays.push(value);
    }
    setFilterGrading([...arrays]);
  };

  const handleShareCapital = (value) => {
    value = value === FilterShareCapital ? false : value;
    setFilterShareCapital(value);
  };

  const handleInfraStructure = (value) => {
    value = value === FilterInfraStructure ? false : value;
    setFilterInfraStructure(value);
  };

  const handleState = (value) => {
    value = value === FilterState ? false : value;
    setFilterState(value);
  };

  const handleFpoCompareSelect = (fpoid) => {
    let status = SelectedFpos.includes(fpoid);
    let arrays = [...SelectedFpos];
    if (status) {
      _.remove(arrays, function (array) {
        return array === fpoid;
      });
    } else {
      arrays.push(fpoid);
    }
    setSelectedFpos([...arrays]);
  };

  const handleCompareFpo = () => {
    if (SelectedFpos.length >= 2 && SelectedFpos.length <= 3) {
      const formData = new FormData();
      formData.append(
        FPO_LIST_FILTER[LOAN_FIELDS.compareFpoList],
        _.join(SelectedFpos, ',')
      );
      dispatch(bankActions.postCompareFpoInProgress({ formData }));
    } else {
      let payload = {
        inProgress: false,
        error: true,
        message: "Please select 2-3 FPO's.",
      };
      dispatch(bankActions.postCompareFpoInError(payload));
    }
    setCompareFpoModalStatus(true);
  };

  return (
    <div className='container-fluid pb-5'>
      <div className='col-md-3'>
        <h2 className='medium-font f24 mb-4'>Explore FPOs</h2>
      </div>
      <Row>
        <Col md={3}>
          <ShadowBox className='p-1'>
            <div className='siderBar px-2'>
              <p className='mb-0 mt-3'>State</p>
              <Form.Select
                className='form-control'
                onChange={(e) => handleState(e.target.value)}
                value={FilterState}
              >
                <option value=''>Please Select Your State</option>
                {states.map((state, key) => (
                  <option value={state.value} key={key}>
                    {state.name}
                  </option>
                ))}
              </Form.Select>
              <div className='membership'>
                <p className='mt-3 mb-2'>Membership</p>
                {explore_fpo_filter_membership.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleMemberShip}
                    active={FilterMembership === val.value ? true : false}
                  />
                ))}
              </div>
              <div className='vintage'>
                <p className='mt-3 mb-2'>Vintage</p>
                {explore_fpo_filter_vintage.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleVintage}
                    active={FilterVintage === val.value ? true : false}
                  />
                ))}
              </div>
              <div className='GradingScore'>
                <p className='mt-3 mb-2'>Grading Score</p>
                {explore_fpo_filter_grading.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleGrading}
                    active={FilterGrading.includes(val.value) ? true : false}
                  />
                ))}
              </div>
              <div className='Turn Over'>
                <p className='mt-3 mb-2'>Turn Over</p>
                {explore_fpo_filter_turnover.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleTurnover}
                    active={FilterTurnover === val.value ? true : false}
                  />
                ))}
              </div>
              <div className='ShareCapital'>
                <p className='mt-3 mb-2'>Share Capital</p>
                {explore_fpo_filter_share_capital.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleShareCapital}
                    active={FilterShareCapital === val.value ? true : false}
                  />
                ))}
              </div>
              <div className='Infrastructure'>
                <p className='mt-3 mb-2'>Infrastructure</p>
                {explore_fpo_filter_share_infrastructure.map((val, key) => (
                  <FilterPills
                    {...val}
                    key={key}
                    handleClick={handleInfraStructure}
                    active={FilterInfraStructure === val.value ? true : false}
                  />
                ))}
              </div>
            </div>
          </ShadowBox>
        </Col>

        <Col md={9}>
          <ShadowBox className='p-1 pb-5'>
            <div className='searchbar-section'>
              <div className='search-area d-flex justify-content-between'>
                <div className='search-box'>
                  <form className='search-container'>
                    <input type='text' id='search-bar' placeholder='search' />
                    <a href='#'>
                      <i className='fa fa-search search-icon'></i>
                    </a>
                  </form>
                </div>
                <div>
                  <button
                    className=' m-2 br-10 px-3rem text-center p-1 btn-secondary'
                    onClick={handleCompareFpo}
                  >
                    Compare FPOs
                  </button>
                </div>
              </div>
            </div>
            {bank && bank.exploreFpo && !bank.exploreFpo.inProgress && (
              <>
                {bank &&
                bank.exploreFpo &&
                bank.exploreFpo.fpoList &&
                bank.exploreFpo.fpoList.length > 0 ? (
                  <>
                    <Row className='fpo-list-container'>
                      {bank.exploreFpo.fpoList.map((fpo, key) => (
                        <Col lg={4} md={6} className='mb-5' key={key}>
                          <FpoCard
                            {...fpo}
                            handleFpoCompareSelect={handleFpoCompareSelect}
                          />
                        </Col>
                      ))}
                    </Row>
                  </>
                ) : (
                  <h5 className='text-danger text-bold text-center'>
                    FPO list is empty.
                  </h5>
                )}
              </>
            )}
            {bank && bank.exploreFpo && bank.exploreFpo.inProgress && (
              <div className='d-flex justify-content-center'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            )}
          </ShadowBox>
        </Col>
      </Row>
      <CompareFpoModal
        compareFpo={bank?.compareFpo}
        show={CompareFpoModalStatus}
        onHide={setCompareFpoModalStatus}
      />
    </div>
  );
}
