import React from 'react';
import fileIcon from '../../assets/Images/Icons/file.jpg';

const FpoEcoSystemTable = ({ data }) => {
  return (
    <div className='col-12'>
      <div className='mb-2 pr-3'>
        <table>
          <tbody>
            <tr>
              <td>
                <img src={fileIcon} style={{ width: 60 + 'px' }} alt='' />
              </td>
              <td className='line-height2 text-left'>
                <p className='muted f14'>FPOs</p>
                <strong className='p-0 bold-font f14'>{data.fpo}</strong>
              </td>
            </tr>
            <tr>
              <td>
                <img src={fileIcon} style={{ width: 60 + 'px' }} alt='' />
              </td>
              <td className='line-height2 text-left'>
                <p className='muted f14'>Villages</p>
                <strong className='p-0 bold-font f14'>{data.village}</strong>
              </td>
            </tr>
            <tr>
              <td>
                <img src={fileIcon} style={{ width: 60 + 'px' }} alt='' />
              </td>
              <td className='line-height2 text-left'>
                <p className='muted f14'>Members</p>
                <strong className='p-0 bold-font f14'>{data.member}</strong>
              </td>
            </tr>
            <tr>
              <td>
                <img src={fileIcon} style={{ width: 60 + 'px' }} alt='' />
              </td>
              <td className='line-height2 text-left'>
                <p className='muted f14'>Turn Over (Lakhs)</p>
                <strong className='pt-0 bold-font f14'>{data.turnover}</strong>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default FpoEcoSystemTable;
