const FilterPills = ({ value, text, handleClick, active }) => {
  return (
    <button
      className={`btn btn-secondary fpo-filter-pills ${active ? 'active' : ''}`}
      type='button'
      onClick={() => (handleClick ? handleClick(value) : null)}
    >
      <span>{text}</span>
    </button>
  );
};

export default FilterPills;
