import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Row } from 'react-bootstrap';
import CreditGradingDocuments from './CreditGradingDocuments';
import './SamAnalysis.scss';
import { LOAN_FIELDS, ADMIN_APPLICATION_STATUS } from '../../../../constants/';
import SamVintageChart from '../../../Charts/SamVintageChart';
import SamGauge from '../../../Charts/SamGauge';

const SamAnalysis = ({
  handleTabChange,
  activeKey,
  samDocuments,
  uploadButton,
  getFormData,
  samKeyFinanials,
  samRatioAnalysis,
  samFinancialPerformance,
  showSamDocsActionsButton,
  handleDocDelete,
  hideNavButtons,
}) => {
  const [activeTab, setActiveTab] = useState('credit_memorandum');
  const [activePerformanceTab, setActivePerformanceTab] = useState(0);
  const [performanceDocs, setPerformanceDocs] = useState();

  useEffect(() => {
    if (
      !performanceDocs &&
      samFinancialPerformance &&
      samFinancialPerformance.length > 0 &&
      activeTab === 'financial_performance'
    ) {
      setPerformanceDocs(samFinancialPerformance[0]);
    }
  }, [performanceDocs, activeTab]);

  useEffect(() => {
    if (activeKey !== 'sam_analysis') {
      setActiveTab('credit_memorandum');
    }
  }, [activeKey]);

  return (
    <>
      {samDocuments && samDocuments.documents && (
        <div className='mx-3 py-2'>
          <Tabs
            defaultActiveKey={activeTab}
            className='mb-3  my-5 sam-analysis-tab-container'
            onSelect={(k) => setActiveTab(k)}
          >
            <Tab eventKey='credit_memorandum' title='CREDIT MEMORANDUM'>
              {samDocuments && activeTab === 'credit_memorandum' && (
                <>
                  <CreditGradingDocuments
                    uploadButton={uploadButton}
                    documents={samDocuments.documents.filter(
                      (doc) =>
                        doc.Report_Type ===
                        ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportTypeCredit]
                    )}
                    reportType={
                      ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportTypeCredit]
                    }
                    getFormData={getFormData}
                    showSamDocsActionsButton={showSamDocsActionsButton}
                    handleDocDelete={handleDocDelete}
                  />
                </>
              )}
            </Tab>
            <Tab eventKey='grading_report' title='GRADING REPORT'>
              {samDocuments && activeTab === 'grading_report' && (
                <CreditGradingDocuments
                  uploadButton={uploadButton}
                  documents={samDocuments.documents.filter(
                    (doc) =>
                      doc.Report_Type ===
                      ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportTypeGrading]
                  )}
                  reportType={
                    ADMIN_APPLICATION_STATUS[LOAN_FIELDS.reportTypeGrading]
                  }
                  getFormData={getFormData}
                  showSamDocsActionsButton={showSamDocsActionsButton}
                  handleDocDelete={handleDocDelete}
                />
              )}
            </Tab>
            <Tab eventKey='key_financials' title='KEY FINANCIALS'>
              <>
                {activeTab === 'key_financials' && samKeyFinanials && (
                  <div className='row mb-5'>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'totalRevenues'
                        )}
                        keyName={['tableData']}
                        title={'Total Revenue'}
                        legendName={['Revenue']}
                      />
                    </div>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'expensesAndTaxes'
                        )}
                        keyName={['tableData']}
                        title={'Expenses & Taxes'}
                        legendName={['Expenses & Taxes']}
                      />
                    </div>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'profitAfterTax'
                        )}
                        keyName={['tableData']}
                        title={'Profit after Tax'}
                        legendName={['Profit after tax']}
                      />
                    </div>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'compositionOfLiabilities'
                        )}
                        keyName={[
                          'currentLiabilities',
                          'nonCurrentLiabilities',
                        ]}
                        title={'Composition of Liabilities'}
                        legendName={[
                          'Current Liabilities',
                          'Non-Current Liabilities',
                        ]}
                      />
                    </div>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'compositionOfAssets'
                        )}
                        title={'Composition of Assets'}
                        keyName={['currentAssets', 'nonCurrentAssets']}
                        legendName={['Current Assets', 'Non-Current Assets']}
                      />
                    </div>
                    <div className='col-md-4 px-2'>
                      <SamVintageChart
                        documents={samKeyFinanials.find(
                          (obj) => obj.keyName === 'shareHolderFund'
                        )}
                        title={"Shareholder's Funds"}
                        keyName={['tableData']}
                        legendName={['Revenue']}
                      />
                    </div>
                  </div>
                )}
              </>
            </Tab>
            <Tab eventKey='ratio_analysis' title='RATIO ANALYSIS'>
              <>
                {activeTab === 'ratio_analysis' && samRatioAnalysis && (
                  <div className='row mb-5'>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'debtToEquity'
                        )}
                        title={'Dept Equity Ratio'}
                        keyName={['tableData']}
                        legendName={['Ratio']}
                      />
                    </div>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'financeToEbitda'
                        )}
                        title={'Finance Cost : EBITDA'}
                        keyName={['tableData']}
                        legendName={['Finance Cost']}
                      />
                    </div>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'patToRevenue'
                        )}
                        title={'PAT : Revenue'}
                        keyName={['tableData']}
                        legendName={['Revenue']}
                      />
                    </div>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'ebitda'
                        )}
                        title={'EBITDA'}
                        keyName={['tableData']}
                        legendName={['EBITDA']}
                      />
                    </div>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'fixedToTotalAssets'
                        )}
                        title={'Fixed Assets to Total Assets'}
                        keyName={['tableData']}
                        legendName={['Assets']}
                      />
                    </div>
                    <div className='col-md-4'>
                      <SamVintageChart
                        documents={samRatioAnalysis.find(
                          (obj) => obj.keyName === 'currentRatio'
                        )}
                        title={'Current Ratio'}
                        keyName={['tableData']}
                        legendName={['Current Ratio']}
                      />
                    </div>
                  </div>
                )}
              </>
            </Tab>
            <Tab eventKey='financial_performance' title='FINANCIAL PERFORMANCE'>
              {activeTab === 'financial_performance' &&
                samFinancialPerformance && (
                  <Tabs
                    activeKey={activePerformanceTab}
                    defaultActiveKey={samFinancialPerformance[0].year}
                    className='financial_performance_tabs '
                    onSelect={(k) => {
                      setPerformanceDocs(samFinancialPerformance[k]);
                      setActivePerformanceTab(k);
                    }}
                  >
                    {samFinancialPerformance.map((document, key) => (
                      <Tab eventKey={key} title={document.year} key={key}></Tab>
                    ))}
                  </Tabs>
                )}

              {activeTab === 'financial_performance' && performanceDocs && (
                <Row className='my-5'>
                  <div className='col-md-3'>
                    <SamGauge
                      document={performanceDocs.tableData.find(
                        (obj) => obj.thresholdValues.parameter === 'Revenues'
                      )}
                      title='Revenues'
                    />
                  </div>
                  <div className='col-md-3'>
                    <SamGauge
                      document={performanceDocs.tableData.find(
                        (obj) => obj.thresholdValues.parameter === 'PAT'
                      )}
                      title='Profit After Tax'
                    />
                  </div>
                  <div className='col-md-3'>
                    <SamGauge
                      document={performanceDocs.tableData.find(
                        (obj) =>
                          obj.thresholdValues.parameter === 'shareHolderFund'
                      )}
                      title='Shareholder’s Fund'
                    />
                  </div>
                  <div className='col-md-3'>
                    <SamGauge
                      document={performanceDocs.tableData.find(
                        (obj) => obj.thresholdValues.parameter === 'Revenues'
                      )}
                      title='Balance Sheet'
                    />
                  </div>
                </Row>
              )}
            </Tab>
          </Tabs>
          {!hideNavButtons && (
            <section className='h-100 mb-5 mr-4 d-flex justify-content-end'>
              <div className=''>
                <button
                  className='loan-application-btn btn-secondary'
                  onClick={() => handleTabChange(activeKey, 'prev')}
                >
                  PREVIOUS
                </button>
                <button
                  className='loan-application-btn btn-primary ml-4'
                  onClick={() => handleTabChange(activeKey, 'next')}
                >
                  NEXT
                </button>
              </div>
            </section>
          )}
        </div>
      )}
    </>
  );
};

export default SamAnalysis;
