import { all } from 'redux-saga/effects';
import { authWatcher } from './auth';
import { bankWatcher } from './bank';
import { fpoWatcher } from './fpo';
import { adminWatcher } from './admin';

function* rootSaga() {
  yield all([authWatcher(), fpoWatcher(), bankWatcher(), adminWatcher()]);
}

export { rootSaga };
