import { Table } from 'react-bootstrap';
import * as dayjs from 'dayjs';

const RePaymentSchedule = ({ documents }) => {
  return (
    <>
      <Table className='f12 repayment-schedule-table' responsive="md">
        <thead>
          <tr className='text-muted text-bold'>
            <th className='px-1'>S.No</th>
            <th className='px-1'>DATE</th>
            <th className='text-right px-1'>DEMAND AMOUNT</th>
            <th className='text-right px-1'>OPENING BALANCE</th>
            <th className='text-right px-1'>PRINCIPAL</th>
            <th className='text-right px-1'>INTEREST</th>
            <th className='text-right px-1'>ENDING BALANCE</th>
            <th className='text-right px-1'>PAID AMOUNT</th>
          </tr>
        </thead>
        <tbody style={{ opacity: '0.5' }} className='text-bold'>
          {documents &&
            documents.map((document, key) => (
              <tr key={key}>
                <td className='px-1'>{key + 1}</td>
                <td className='px-1'>
                  {document.DemandDate
                    ? dayjs(document.DemandDate).format('YYYY-MM-DD')
                    : 'NIL'}
                </td>
                <td className='text-right px-1'>{document.DemandAmount}</td>
                <td className='text-right px-1'>{document.OpeningBalance}</td>
                <td className='text-right px-1'>{document.Principal}</td>
                <td className='text-right px-1'>{document.Interest}</td>
                <td className='text-right px-1'>{document.Ending_Balance}</td>
                <td className='text-right px-1'>{document.Payment_Amount}</td>
              </tr>
            ))}
        </tbody>
      </Table>
    </>
  );
};

export default RePaymentSchedule;
