import { useState } from 'react';
import ApplicationPublishModal from '../Modals/ApplicationPublishModal';
import CancelApplicationModal from '../Modals/CancelApplicationModal';
import ApplicationSendbackModal from '../Modals/ApplicationSendbackModal';
import FpoInfo from '../ActionsTab/FpoInfo';
import { Row, Col, Alert, Spinner } from 'react-bootstrap';
import { CloseOutline, ReturnDownBack, RocketSharp } from 'react-ionicons';
import { useSelector } from 'react-redux';

const PublishApplication = ({
  handlePublishApplication,
  handleCancelApplication,
  handleSendBackApplication,
  actionsBtnStatus,
}) => {
  const [publish, setPublish] = useState(false);
  const [sendBack, setSendBack] = useState(false);
  const [cancelApp, setCancelApp] = useState(false);
  const admin = useSelector((state) => state.admin);

  return (
    <section className='primary-bg pb-5 bg-white'>
      <div className='container'>
        <div className='text-left-1'>
          <h4 className='f20 medium-font fw600 pl-2 ml-1 pt-3'>Loan Details</h4>
        </div>
        <FpoInfo details={admin?.selectedApplication?.details} />
        <div className='text-left-1 pl-5 pr-3 pt-5 pb-5'>
          <h4 className='text-bold f17'>Application/Loan Function</h4>
          <Row className='pt-5 app-loan-fun'>
            <Col className='text-center'>
              <span className=' publish-icon'>
                <RocketSharp color={'#8BFACC'} height='33px' width='33px' />
              </span>
              <br />
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.publish &&
                !admin.selectedApplication.publish.inProgress && (
                  <>
                    <button
                      className='btn btn-primary my-2 br-12 f13 publish-btn text-bold'
                      onClick={() => setPublish(true)}
                      disabled={!actionsBtnStatus}
                    >
                      PUBLISH
                    </button>
                    <p className='p-2'>Publish to marketplace</p>
                  </>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.publish &&
                admin.selectedApplication.publish.inProgress && (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.publish &&
                !admin.selectedApplication.publish.inProgress &&
                admin.selectedApplication.publish.success && (
                  <Alert
                    variant={'success'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.publish.message}
                  </Alert>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.publish &&
                !admin.selectedApplication.publish.inProgress &&
                admin.selectedApplication.publish.error && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.publish.message}
                  </Alert>
                )}
            </Col>
            <Col className='text-center publish-sec'>
              <ReturnDownBack
                color={'#FFE3A0'}
                height='40px'
                width='40px'
                className='send-back-icon'
              />
              <br />
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.sendBackApplication &&
                !admin.selectedApplication.sendBackApplication.inProgress && (
                  <>
                    <button
                      className='btn btn-primary br-12 mt-2 send-back-btn f13 text-bold'
                      variant='primary'
                      onClick={() => setSendBack(true)}
                      disabled={!actionsBtnStatus}
                    >
                      SENDBACK
                    </button>
                    <p className='p-2'>
                      Sendback application to FPO for correction
                    </p>
                  </>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.sendBackApplication &&
                admin.selectedApplication.sendBackApplication.inProgress && (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.sendBackApplication &&
                !admin.selectedApplication.sendBackApplication.inProgress &&
                admin.selectedApplication.sendBackApplication.success && (
                  <Alert
                    variant={'success'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.sendBackApplication.message}
                  </Alert>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.sendBackApplication &&
                !admin.selectedApplication.sendBackApplication.inProgress &&
                admin.selectedApplication.sendBackApplication.error && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.sendBackApplication.message}
                  </Alert>
                )}
            </Col>
            <Col className='text-center'>
              <CloseOutline
                color={'#FF9393'}
                height='40px'
                width='40px'
                className='cancel-icon'
              />
              <br />
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.cancelApplication &&
                !admin.selectedApplication.cancelApplication.inProgress && (
                  <>
                    <button
                      className='btn btn-primary mt-2 br-12 cancel-application-btn f13 text-bold'
                      variant='primary'
                      onClick={() => setCancelApp(true)}
                      disabled={!actionsBtnStatus}
                    >
                      CANCEL APPLICATION
                    </button>
                    <p className='p-2'>
                      Cancel application from the marketplace
                    </p>
                  </>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.cancelApplication &&
                admin.selectedApplication.cancelApplication.inProgress && (
                  <Spinner animation='border' role='status'>
                    <span className='visually-hidden'></span>
                  </Spinner>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.cancelApplication &&
                !admin.selectedApplication.cancelApplication.inProgress &&
                admin.selectedApplication.cancelApplication.success && (
                  <Alert
                    variant={'success'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.cancelApplication.message}
                  </Alert>
                )}
              {admin &&
                admin.selectedApplication &&
                admin.selectedApplication.cancelApplication &&
                !admin.selectedApplication.cancelApplication.inProgress &&
                admin.selectedApplication.cancelApplication.error && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {admin.selectedApplication.cancelApplication.message}
                  </Alert>
                )}
            </Col>
          </Row>
          <Row className='pt-2 app-loan-fun'>
            <Col className='text-center'></Col>
            <Col className='text-center'></Col>
          </Row>
        </div>
      </div>
      <ApplicationPublishModal
        show={publish}
        onHide={setPublish}
        handlePublishApplication={handlePublishApplication}
      />
      <ApplicationSendbackModal
        show={sendBack}
        onHide={setSendBack}
        handleSendBackApplication={handleSendBackApplication}
      />
      <CancelApplicationModal
        show={cancelApp}
        onHide={setCancelApp}
        handleCancelApplication={handleCancelApplication}
      />
    </section>
  );
};

export default PublishApplication;
