import { getToken } from '../../../utils/utils';
import { BankHttpInstance } from './bankInstance';

const getAllApplications = (organizationid) => {
  const token = getToken();
  return BankHttpInstance.request({
    method: 'GET',
    params: { agfinmarttoken: token, organizationid },
    url: 'application/list/all/',
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};
