import { apply } from '@redux-saga/core/effects';
import {
  FPO_APPLICATIONS_LIST_ALL_IN_ERROR,
  FPO_APPLICATIONS_LIST_ALL_IN_PROGRESS,
  FPO_APPLICATIONS_LIST_ALL_IN_SUCCESS,
  FPO_CREATE_IN_ERROR,
  FPO_CREATE_IN_PROCESS,
  FPO_CREATE_IN_SUCESS,
  FPO_SAVE_IN_ERROR,
  FPO_SAVE_IN_PROCESS,
  FPO_SAVE_IN_SUCESS,
  FPO_SUBMIT_IN_ERROR,
  FPO_SUBMIT_IN_PROCESS,
  FPO_SUBMIT_IN_SUCESS,
  GET_FPO_DOCUMENTS_IN_ERROR,
  GET_FPO_DOCUMENTS_IN_PROGRESS,
  GET_FPO_DOCUMENTS_IN_SUCCESS,
  SET_SELECTED_LOAN,
  SET_SELECTED_LOAN_ERROR,
  SET_SELECTED_LOAN_SUCCESS,
  FPO_GET_APPLICATION_LENDERS_OFFER_IN_ERROR,
  FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS,
  FPO_GET_APPLICATION_LENDERS_OFFER_IN_SUCCESS,
  FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS,
  FPO_GET_LENDER_OFFER_DETAILS_IN_SUCCESS,
  FPO_GET_LENDER_OFFER_DETAILS_IN_ERROR,
  FPO_PUT_ACCEPT_OFFER_IN_PROGRESS,
  FPO_PUT_ACCEPT_OFFER_IN_SUCCESS,
  FPO_PUT_ACCEPT_OFFER_IN_ERROR,
  FPO_PUT_REJECT_OFFER_IN_PROGRESS,
  FPO_PUT_REJECT_OFFER_IN_SUCCESS,
  FPO_PUT_REJECT_OFFER_IN_ERROR,
  GET_FPO_PROFILE_DATA_IN_PROGRESS,
  GET_FPO_PROFILE_DATA_IN_SUCCESS,
  GET_FPO_PROFILE_DATA_IN_ERROR,
  FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS,
  FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR,
  FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS,
  FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR,
  FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
  FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
  FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
  FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
  CLEAN_SANSACTION_DOCUMETN_UPLOADS,
  CLEAR_FPO_APPLY_LOAN,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS,
  FPO_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR,
  FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS,
  FPO_DELETE_DRAFT_APPLICATION_IN_SUCCESS,
  FPO_DELETE_DRAFT_APPLICATION_IN_ERROR,
  CLEAR_FPO_DELETE_LOAN,
  FPO_LOAN_LIST_ALL_IN_PROGRESS,
  FPO_LOAN_LIST_ALL_IN_SUCCESS,
  FPO_LOAN_LIST_ALL_IN_ERROR,
  FPO_GET_LOAN_SUMMARY_IN_PROGRESS,
  FPO_GET_LOAN_SUMMARY_IN_SUCCESS,
  FPO_GET_LOAN_SUMMARY_IN_ERROR,
  FPO_GET_REPAYMENT_STATUS_IN_PROGRESS,
  FPO_GET_REPAYMENT_STATUS_IN_SUCCESS,
  FPO_GET_REPAYMENT_STATUS_IN_ERROR,
  FPO_GET_LOAN_DOCUMENTS_IN_PROGRESS,
  FPO_GET_LOAN_DOCUMENTS_IN_SUCCESS,
  FPO_GET_LOAN_DOCUMENTS_IN_ERROR,
  FPO_CLEAR_SUBMIT_LOAN,
  FPO_APPLICATION_FILTER_IN_PROGRESS,
  FPO_APPLICATION_FILTER_IN_SUCCESS,
  FPO_APPLICATION_FILTER_IN_ERROR,
  FPO_LOAN_FILTER_IN_PROGRESS,
  FPO_LOAN_FILTER_IN_SUCCESS,
  FPO_LOAN_FILTER_IN_ERROR,
  FPO_LOAN_PRE_DOCS_IN_SUCCESS,
  FPO_LOAN_POST_DOCS_IN_SUCCESS,
  FPO_CLEAR_SELECTED_LOAN,
} from '../../types/fpotypes';
const apiStatus = { inProgress: false, success: false, error: false };

const initialState = {
  selectedLoanType: undefined,
  apply: {
    inProgress: false,
    success: undefined,
    error: false,
    errorStatus: null,
  },
  save: {
    inProgress: false,
    success: undefined,
    error: false,
    errorStatus: null,
  },
  submit: {
    inProgress: false,
    success: undefined,
    error: false,
    errorStatus: null,
  },
  selectedLoan: {
    loanId: null,
    loanInfo: null,
    isLoading: false,
    error: undefined,
  },
  documents: {
    inProgress: false,
    success: undefined,
    error: undefined,
  },
  applications: {
    inProgress: false,
    success: undefined,
    error: undefined,
  },
  loans: {
    inProgress: false,
    success: undefined,
    error: undefined,
  },
  lendersOffer: {
    inProgress: false,
    success: undefined,
    offers: [],
    error: undefined,
  },
  selectedOffer: {},
  acceptReject: {
    inProgress: false,
    success: undefined,
    error: undefined,
    message: undefined,
  },
  profileData: {
    inProgress: false,
    error: undefined,
    success: undefined,
    message: undefined,
  },
  preSansactionRequestDocuments: {
    ...apiStatus,
    documents: [],
  },
  postSansactionRequestDocuments: {
    ...apiStatus,
    documents: [],
  },
  postSansactionDocumentUpload: {
    ...apiStatus,
  },
  preSansactionDocumentUpload: {
    ...apiStatus,
  },
  sansactionDocumentUpload: {
    ...apiStatus,
  },
  deleteDraftApplication: {
    ...apiStatus,
  },
  selectedAppLoan: {
    loanInfo: {},
    preDocuments: {},
    postDocuments: {},
    loanDocuments: {},
    ...apiStatus,
  },
  applicationFilter: {
    ...apiStatus,
  },
  loanFilter: {
    ...apiStatus,
  },
};

export const fpoReducer = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case FPO_CREATE_IN_PROCESS:
      return {
        ...initialState,
        ...(initialState.apply.inProgress = true),
        selectedLoanType: payload,
      };

    case FPO_CREATE_IN_SUCESS:
      return {
        ...state,
        ...(state.apply.success = payload),
        ...(state.apply.inProgress = false),
        ...(state.apply.error = false),
      };

    case FPO_CREATE_IN_ERROR:
      return {
        ...state,
        ...(state.save = initialState.save),
        ...(state.submit = initialState.submit),
        ...(state.apply.success = null),
        ...(state.apply.inProgress = false),
        ...(state.apply.error = true),
      };

    //FPO SAVE LOAN INFO => Process

    case FPO_SUBMIT_IN_SUCESS:
      return {
        ...state,
        ...(state.save = initialState.save),
        ...(state.submit.success = payload),
        ...(state.submit.inProgress = false),
        ...(state.submit.error = false),
      };

    case FPO_SUBMIT_IN_PROCESS:
      return {
        ...state,
        ...(state.save = initialState.save),
        ...(state.submit.success = undefined),
        ...(state.submit.inProgress = true),
        ...(state.submit.error = false),
      };

    case FPO_SUBMIT_IN_ERROR:
      return {
        ...state,
        ...(state.save = initialState.save),
        ...(state.submit.success = undefined),
        ...(state.submit.inProgress = false),
        ...(state.submit.error = payload.error),
        ...(state.submit.errorStatus = payload.errorMessage),
      };

    case FPO_SAVE_IN_SUCESS:
      return {
        ...state,
        ...(state.save.success = payload),
        ...(state.save.inProgress = false),
        ...(state.save.error = false),
        ...(state.submit = initialState.submit),
      };

    case FPO_SAVE_IN_PROCESS:
      return {
        ...state,
        ...(state.submit = initialState.submit),
        ...(state.save.success = undefined),
        ...(state.save.inProgress = true),
        ...(state.save.error = false),
      };

    case FPO_SAVE_IN_ERROR:
      return {
        ...state,
        ...(state.submit = initialState.submit),
        ...(state.save.success = undefined),
        ...(state.save.inProgress = false),
        ...(state.save.error = payload.error),
        ...(state.save.errorStatus = payload.errorMessage),
      };

    //loan flow
    case SET_SELECTED_LOAN:
      return {
        ...state,
        ...(state.selectedLoan.loanId = payload),
        ...(state.selectedLoan.isLoading = true),
        ...(state.selectedLoan.loanInfo = null),
        ...(state.selectedLoan.error = undefined),
      };

    case SET_SELECTED_LOAN_SUCCESS:
      return {
        ...state,
        ...(state.selectedLoan.loanInfo = payload),
        ...(state.selectedLoan.isLoading = false),
        ...(state.selectedLoan.error = undefined),
      };

    case SET_SELECTED_LOAN_ERROR:
      return {
        ...state,
        ...(state.selectedLoan.loanInfo = undefined),
        ...(state.selectedLoan.isLoading = false),
        ...(state.selectedLoan.error = payload),
      };

    case GET_FPO_DOCUMENTS_IN_PROGRESS:
      return {
        ...state,
        ...(state.documents.inProgress = true),
      };
    case GET_FPO_DOCUMENTS_IN_SUCCESS:
      return {
        ...state,
        ...(state.documents.success = payload),
        ...(state.documents.error = undefined),
        ...(state.documents.inProgress = false),
      };
    case GET_FPO_DOCUMENTS_IN_ERROR:
      return {
        ...state,
        ...(state.documents.error = payload),
        ...(state.documents.success = undefined),
        ...(state.documents.inProgress = false),
      };

    case FPO_APPLICATIONS_LIST_ALL_IN_PROGRESS:
      return {
        ...state,
        ...(state.applications.inProgress = true),
        ...(state.applications.success = undefined),
        ...(state.applications.error = undefined),
      };

    case FPO_APPLICATIONS_LIST_ALL_IN_SUCCESS:
      return {
        ...state,
        ...(state.applications.inProgress = false),
        ...(state.applications.success = payload),
        ...(state.applications.error = undefined),
      };

    case FPO_APPLICATIONS_LIST_ALL_IN_ERROR:
      return {
        ...state,
        ...(state.applications.inProgress = false),
        ...(state.applications.success = undefined),
        ...(state.applications.error = payload),
      };

    case FPO_LOAN_LIST_ALL_IN_PROGRESS:
      return {
        ...state,
        loans: { ...apiStatus, ...state.loans, inProgress: true },
      };

    case FPO_LOAN_LIST_ALL_IN_SUCCESS:
      return {
        ...state,
        loans: { ...apiStatus, ...state.loans, success: payload },
      };

    case FPO_LOAN_LIST_ALL_IN_ERROR:
      return {
        ...state,
        loans: { ...apiStatus, ...state.loans, error: payload },
      };

    case FPO_GET_APPLICATION_LENDERS_OFFER_IN_PROGRESS:
      return {
        ...state,
        ...(state.lendersOffer.inProgress = true),
        ...(state.lendersOffer.success = undefined),
        ...(state.lendersOffer.offers = []),
        ...(state.lendersOffer.error = undefined),
      };

    case FPO_GET_APPLICATION_LENDERS_OFFER_IN_SUCCESS:
      return {
        ...state,
        lendersOffer: {
          ...state.lendersOffer,
          inProgress: false,
          success: true,
          offers: payload,
          error: undefined,
        },
      };

    case FPO_GET_APPLICATION_LENDERS_OFFER_IN_ERROR:
      return {
        ...state,
        ...(state.lendersOffer.inProgress = false),
        ...(state.lendersOffer.success = undefined),
        ...(state.lendersOffer.offers = []),
        ...(state.lendersOffer.error = payload),
      };

    case FPO_GET_LENDER_OFFER_DETAILS_IN_PROGRESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            inProgress: true,
            success: undefined,
            error: undefined,
            actions: {
              inProgress: false,
              success: false,
              error: undefined,
            },
          },
        },
      };

    case FPO_GET_LENDER_OFFER_DETAILS_IN_SUCCESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            inProgress: false,
            success: true,
            details: payload,
            error: undefined,
            actions: {
              inProgress: false,
              success: false,
              error: undefined,
            },
          },
        },
      };

    case FPO_GET_LENDER_OFFER_DETAILS_IN_ERROR:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            inProgress: false,
            success: false,
            details: {},
            error: true,
            message: payload,
            actions: {
              inProgress: undefined,
              success: undefined,
              error: undefined,
            },
          },
        },
      };

    case FPO_PUT_REJECT_OFFER_IN_SUCCESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: false,
              success: true,
              error: false,
              message: payload.message,
            },
          },
        },
        lendersOffer: {
          ...state.lendersOffer,
          fetchRandom: Math.random(1000000, 9999999),
        },
      };

    case FPO_PUT_ACCEPT_OFFER_IN_PROGRESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: true,
              success: false,
              error: undefined,
            },
          },
        },
      };

    case FPO_PUT_ACCEPT_OFFER_IN_SUCCESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: false,
              success: true,
              error: false,
              message: payload.message,
            },
          },
        },
        lendersOffer: {
          ...state.lendersOffer,
          fetchRandom: Math.random(1000000, 9999999),
        },
      };

    case FPO_PUT_ACCEPT_OFFER_IN_ERROR:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: false,
              success: true,
              error: false,
              message: payload.message,
            },
          },
        },
      };

    case FPO_PUT_REJECT_OFFER_IN_PROGRESS:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: true,
              success: false,
              error: undefined,
            },
          },
        },
      };

    case FPO_PUT_REJECT_OFFER_IN_ERROR:
      return {
        ...state,
        selectedOffer: {
          ...state.selectedOffer,
          [payload.proposal_id]: {
            ...state.selectedOffer[payload.proposal_id],
            actions: {
              inProgress: false,
              success: false,
              error: true,
              message: payload.message,
            },
          },
        },
      };

    case GET_FPO_PROFILE_DATA_IN_PROGRESS: {
      return {
        ...state,
        ...(state.profileData.inProgress = false),
        ...(state.profileData.error = undefined),
        ...(state.profileData.success = undefined),
      };
    }

    case GET_FPO_PROFILE_DATA_IN_SUCCESS: {
      return {
        ...state,
        profileData: {
          ...payload,
          inProgress: false,
          error: false,
          success: true,
        },
      };
    }

    case GET_FPO_PROFILE_DATA_IN_ERROR: {
      return {
        ...state,
        profileData: {
          ...payload,
          inProgress: false,
          error: true,
          success: false,
        },
      };
    }

    case FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR: {
      return {
        ...state,
        preSansactionRequestDocuments: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_SUCCESS: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_ERROR: {
      return {
        ...state,
        postSansactionRequestDocuments: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS: {
      return {
        ...state,
        postSansactionDocumentUpload: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS: {
      return {
        ...state,
        postSansactionDocumentUpload: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_POST_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR: {
      return {
        ...state,
        postSansactionDocumentUpload: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS: {
      return {
        ...state,
        preSansactionDocumentUpload: {
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS: {
      return {
        ...state,
        preSansactionDocumentUpload: {
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_PRE_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR: {
      return {
        ...state,
        preSansactionDocumentUpload: {
          ...payload,
          ...apiStatus,
          error: true,
        },
      };
    }

    case CLEAN_SANSACTION_DOCUMETN_UPLOADS: {
      return {
        ...state,
        postSansactionDocumentUpload: {
          ...apiStatus,
        },
        preSansactionDocumentUpload: {
          ...apiStatus,
        },
      };
    }

    case CLEAR_FPO_APPLY_LOAN: {
      return {
        ...state,
        apply: {
          ...apiStatus,
        },
        selectedLoanType: undefined,
        selectedLoan: {
          ...apiStatus,
          loanInfo: undefined,
        },
      };
    }

    case FPO_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS: {
      return {
        ...state,
        sansactionDocumentUpload: {
          ...state.sansactionDocumentUpload,
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_SANSACTION_DOCUMENT_UPLOAD_IN_SUCCESS: {
      return {
        ...state,
        sansactionDocumentUpload: {
          ...state.sansactionDocumentUpload,
          ...apiStatus,
          success: true,
          message: payload.message,
        },
      };
    }

    case FPO_SANSACTION_DOCUMENT_UPLOAD_IN_ERROR: {
      return {
        ...state,
        sansactionDocumentUpload: {
          ...state.sansactionDocumentUpload,
          ...apiStatus,
          error: true,
          message: payload.message,
        },
      };
    }

    case FPO_DELETE_DRAFT_APPLICATION_IN_PROGRESS: {
      return {
        ...state,
        deleteDraftApplication: {
          ...state.deleteDraftApplication,
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_DELETE_DRAFT_APPLICATION_IN_SUCCESS: {
      return {
        ...state,
        deleteDraftApplication: {
          ...state.deleteDraftApplication,
          ...apiStatus,
          success: true,
          message: payload.message,
        },
      };
    }

    case FPO_DELETE_DRAFT_APPLICATION_IN_ERROR: {
      return {
        ...state,
        deleteDraftApplication: {
          ...state.deleteDraftApplication,
          ...apiStatus,
          error: true,
          message: payload.message,
        },
      };
    }

    case CLEAR_FPO_DELETE_LOAN: {
      return {
        ...state,
        deleteDraftApplication: {
          ...apiStatus,
        },
      };
    }

    case FPO_GET_LOAN_SUMMARY_IN_PROGRESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...apiStatus,
          loanInfo: action.payload,
          inProgress: true,
        },
      };
    }

    case FPO_GET_LOAN_SUMMARY_IN_SUCCESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_LOAN_SUMMARY_IN_ERROR: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case FPO_GET_REPAYMENT_STATUS_IN_PROGRESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          inProgress: true,
        },
      };
    }

    case FPO_GET_REPAYMENT_STATUS_IN_SUCCESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_GET_REPAYMENT_STATUS_IN_ERROR: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case FPO_GET_LOAN_DOCUMENTS_IN_PROGRESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          preDocuments: { ...apiStatus, inProgress: true },
          postDocuments: { ...apiStatus, inProgress: true },
          loanDocuments: { ...apiStatus, inProgress: true },
        },
      };
    }

    case FPO_GET_LOAN_DOCUMENTS_IN_SUCCESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          loanDocuments: {
            ...apiStatus,
            ...payload,
          },
        },
      };
    }

    case FPO_LOAN_PRE_DOCS_IN_SUCCESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          preDocuments: {
            ...apiStatus,
            ...payload,
          },
        },
      };
    }

    case FPO_LOAN_POST_DOCS_IN_SUCCESS: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          postDocuments: {
            ...apiStatus,
            ...payload,
          },
        },
      };
    }

    case FPO_GET_LOAN_DOCUMENTS_IN_ERROR: {
      return {
        ...state,
        selectedAppLoan: {
          ...state.selectedAppLoan,
          preDocuments: {
            ...apiStatus,
            error: true,
            ...payload.preDocuments,
          },
          postDocuments: {
            ...apiStatus,
            error: true,
            ...payload.postDocuments,
          },
          loanDocuments: {
            ...apiStatus,
            error: true,
            ...payload.loanDocuments,
          },
        },
      };
    }

    case FPO_CLEAR_SUBMIT_LOAN: {
      return {
        ...state,
        submit: {
          ...apiStatus,
        },
        save: {
          ...apiStatus,
        },
      };
    }

    case FPO_APPLICATION_FILTER_IN_PROGRESS: {
      return {
        ...state,
        applicationFilter: {
          ...apiStatus,
          category: payload.category,
          inProgress: true,
        },
      };
    }

    case FPO_APPLICATION_FILTER_IN_SUCCESS: {
      return {
        ...state,
        applicationFilter: {
          ...state.applicationFilter,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_APPLICATION_FILTER_IN_ERROR: {
      return {
        ...state,
        applicationFilter: {
          ...state.applicationFilter,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case FPO_LOAN_FILTER_IN_PROGRESS: {
      return {
        ...state,
        loanFilter: {
          ...apiStatus,
          category: payload.category,
          inProgress: true,
        },
      };
    }

    case FPO_LOAN_FILTER_IN_SUCCESS: {
      return {
        ...state,
        loanFilter: {
          ...state.loanFilter,
          ...apiStatus,
          ...payload,
          success: true,
        },
      };
    }

    case FPO_LOAN_FILTER_IN_ERROR: {
      return {
        ...state,
        loanFilter: {
          ...state.loanFilter,
          ...apiStatus,
          ...payload,
          error: true,
        },
      };
    }

    case FPO_CLEAR_SELECTED_LOAN: {
      return {
        ...state,
        selectedAppLoan: {
          ...apiStatus,
        },
      };
    }

    default:
      return state;
  }
};
