import React, { useEffect } from 'react';
import icoIcon from '../../assets/Images/Icons/ico.jpg';
import formatCurrency from '../../utils/currency';
import './../../styles/CommonStyles.css';

const ProfileStatusTable = ({
  applications,
  handleDetailsButton,
  handleViewButton,
}) => {
  useEffect(() => {
    let flexiblebar = document.querySelectorAll('.flexiblebar');
    let newflexiblebar = Array.from(flexiblebar);
    let percent = document.querySelectorAll('.percent');
    let newpercent = Array.from(percent);
    // eslint-disable-next-line array-callback-return
    newflexiblebar.map((item) => {
      let count = 0;
      function progressbar() {
        count++;
        item.style.width = `${count}%`;
        // eslint-disable-next-line eqeqeq
        if (count == item.dataset.percent) {
          clearInterval(stop);
        }
      }
      let stop = setInterval(() => {
        progressbar();
      }, 5);
    });
    // eslint-disable-next-line array-callback-return
    newpercent.map((item) => {
      let count = 0;

      function numbers() {
        item.innerHTML = `${count}%`;
        count++;
        if (count > item.dataset.number) {
          clearInterval(stop_two);
        }
      }
      let stop_two = setInterval(() => {
        numbers();
      }, 5);
    });
  }, []);

  const onApplicationClick = (application) => {
    handleViewButton(application);
  };

  return (
    <>
      {applications && (
        <>
          <table className='text-left w-100 table-custom-space  table-data-style'>
            <tbody>
              {applications &&
                applications.map((application, index) => {
                  return (
                    <tr key={index}>
                      <td className='d-none d-lg-block'>
                        <img src={icoIcon} className='table-ico mx-1' alt='' />
                      </td>
                      <td className='line-height0 f14'>
                        <p className='p-0 m-0 pb-1'>
                          <strong className='f15'>
                            {application.applicationuid}
                          </strong>
                        </p>
                        <p className='d-none d-lg-block p-0 m-0 pb-1 line-height18 muted'>
                          {application.fponame}
                          {/* Kisan Unnati Farmer Producer */}
                        </p>
                        <p className='d-none d-lg-block p-0 m-0 pb-1 muted'>
                          {application.fpostate}
                          <strong className='d-none d-lg-block medium-font muted  pt-3'>
                            {
                              formatCurrency(application.loanamount + '')
                                .maskedValue
                            }
                          </strong>
                        </p>
                      </td>
                      <td
                        className='line-height8 f14'
                        style={{ width: 150 + 'px' }}
                      >
                        <p className='d-none d-lg-block'>
                          <strong className='f15'>Profile Status</strong>
                        </p>
                        <div className='dflex mt-3 '>
                          <div className='bar'>
                            <div
                              className={`flexiblebar ${
                                application.status < 50
                                  ? 'bg-danger'
                                  : 'bg-success'
                              }`}
                              data-percent={application.status}
                            ></div>
                          </div>
                          <div className=''>
                            <span className='muted pl-1 f14'>
                              {application.status}%
                            </span>
                          </div>
                        </div>
                      </td>
                      <td
                        className='pl-2 d-none d-lg-block'
                        style={{ width: 50 + 'px' }}
                      >
                        <span
                          className='bg-circle br50 p6-0 cursor-pointer'
                          onClick={onApplicationClick.bind(this, application)}
                        >
                          <i className='muted fa fa-eye p-1 '> </i>
                        </span>
                      </td>
                      <td className='pl-2' style={{ width: 50 + 'px' }}>
                        <span
                          className='bg-circle br50 p6-4 cursor-pointer'
                          onClick={() => handleDetailsButton(application)}
                        >
                          <i className='muted fa fa-chevron-right p-1 '> </i>
                        </span>
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </>
      )}
    </>
  );
};

export default ProfileStatusTable;
