import Image from 'react-bootstrap/Image';
import React, { useState } from 'react';
import SBI from '../../../assets/Images/sbi.png';
import ICICI from '../../../assets/Images/icici.png';
import HDFC from '../../../assets/Images/hdfc.png';
import PersonBlack from '../../../assets/Images/person_black_24dp.png';
import UserPlus from '../../../assets/Images/Icon feather-user-plus.png';
import UserEdit from '../../../assets/Images/Group 160699.png';
import EditUserModal from '../../../components/Modals/EditUserModal';
import AddLendarPartner from '../../../components/Modals/AddLendarPartner';
import ProgramsList from '../../../components/Modals/ProgramsList';

import { product_list } from '../../../data';
import { Col, Row } from 'react-bootstrap';

export default function UserManagement() {
  const [addProduct, setAddProduct] = React.useState(false);

  const [fileName, setFileName] = useState('Upload Boundary File');

  const [addUser, setAddUser] = useState(false);

  const [addPrograms, setAddPrograms] = useState(false);

  const [editUser, setEditUser] = useState(false);

  return (
    <section className='primary-bg pb-5'>
      <div className='container'>
        <Row className='mb-3'>
          <Col>
            <h4 className='f20 medium-font fw600 pt-3'>User Management</h4>
          </Col>
          <Col className='text-right'>
            <button
              className='btn btn-default br-12 mt-3'
              onClick={() => setAddProduct(true)}
            >
              {' '}
              + ADD LENDER / PARTNER
            </button>
            <AddLendarPartner
              show={addProduct}
              onHide={() => setAddProduct(false)}
            />
          </Col>
        </Row>

        <div className='col-md-12 p-0'>
          <div className='general-box bg-white'>
            <Row className='text-left'>
              <Col className='shadow none text-left br-12 m-3 p-2'>
                <Image className='ml-2' src={SBI} alt='' />
                <table className='userManage'>
                  <tbody className='f15 medium-font'>
                    <tr>
                      <td>
                        <Image src={PersonBlack} alt='' />2 Active
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddPrograms(true)}
                        >
                          2 Programs
                        </button>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddUser(true)}
                        >
                          {' '}
                          <Image src={UserPlus} alt='' />
                          Add A User
                        </button>
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setEditUser(true)}
                        >
                          {' '}
                          <Image src={UserEdit} alt='' />
                          Edit A User
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Col>
              <Col className='shadow none text-left br-12 m-3 p-2'>
                <Image className='ml-2' src={ICICI} alt='' />
                <table className='userManage'>
                  <tbody className='f15 medium-font'>
                    <tr>
                      <td>
                        <Image src={PersonBlack} alt='' />2 Active
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddPrograms(true)}
                        >
                          2 Programs
                        </button>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddUser(true)}
                        >
                          {' '}
                          <Image src={UserPlus} alt='' />
                          Add A User
                        </button>
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setEditUser(true)}
                        >
                          {' '}
                          <Image src={UserEdit} alt='' />
                          Edit A User
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Col>
              <Col className='shadow none text-left br-12 m-3 p-2'>
                <Image className='ml-2' src={HDFC} alt='' />
                <table className='userManage'>
                  <tbody className='f15 medium-font'>
                    <tr>
                      <td>
                        <Image src={PersonBlack} alt='' />2 Active
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddPrograms(true)}
                        >
                          2 Programs
                        </button>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setAddUser(true)}
                        >
                          {' '}
                          <Image src={UserPlus} alt='' />
                          Add A User
                        </button>
                      </td>
                      <td>
                        <button
                          className='btn'
                          onClick={() => setEditUser(true)}
                        >
                          {' '}
                          <Image src={UserEdit} alt='' />
                          Edit A User
                        </button>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </Col>
            </Row>
            <EditUserModal show={editUser} onHide={() => setEditUser(false)} />
            <ProgramsList
              show={addPrograms}
              onHide={() => setAddPrograms(false)}
            />
          </div>
        </div>
      </div>
    </section>
  );
}
