import React from 'react';
import { Col, Row, Card } from 'react-bootstrap';
import './Card.css';
import dayjs from 'dayjs';
import formatCurrency from '../../utils/currency';

function ApplicationCard({
  applicationId,
  isActive,
  fpoName,
  fpoState,
  applicationDate,
  loanName,
  loanAmount,
  onClick,
}) {
  return (
    <Card
      className={`br-12 ${
        isActive ? 'card-border' : 'in-active-application-card'
      }`}
    >
      <Card.Body className='p-0 mt-3 mb-3 ml-3 mr-1'>
        <div className='card-content'>
          <p className='text-bold f14'>{applicationId}</p>
          <p className='f10'>{fpoName}</p>
          <Row>
            <Col className='pl-0 f10'>
              <p>{fpoState}</p>
              <p className='text-bold'>{loanName}</p>
            </Col>
            <Col className='f10 remove-left-padding'>
              <p> {dayjs(applicationDate).format('MM-DD-YYYY')}</p>
              <p className='text-bold'>
                {formatCurrency(String(loanAmount))['maskedValue']}
              </p>
            </Col>
            {/* <Col></Col> */}
            <Col className='m-0 p-0 text-right'>
              <br />
              <button
                className='card-view-btn text-bold'
                onClick={() => onClick()}
              >
                VIEW
              </button>
            </Col>
          </Row>
        </div>
      </Card.Body>
    </Card>
  );
}
export default ApplicationCard;
