import { Table } from 'react-bootstrap';
import * as dayjs from 'dayjs';
import { Link } from 'react-router-dom';

const SimpleApplicationListTable = ({ applications, detailsUrl }) => {
  return (
    <div className='container-fluide'>
      <Table>
        <thead className=''>
          <tr className='muted f12'>
            <th>
              <span className='ml-2'>REQUEST ID</span>
            </th>
            <th>DATE</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {applications &&
            applications.length > 0 &&
            applications.map((application, key) => (
              <tr key={key}>
                <td>
                  <span className='ml-2'>{application.applicationuid}</span>
                </td>
                <td>
                  {application && application.applicationdate
                    ? dayjs(application.applicationdate).format('DD/MM/YYYY')
                    : 'NIL'}
                </td>
                <td>
                  <Link
                    to={`${detailsUrl}&&application_id=${application.applicationuid}`}
                    className='btn shadow-none p-0 mr-2'
                  >
                    <i className='fa fa-angle-right f19' aria-hidden='true'></i>
                  </Link>
                </td>
              </tr>
            ))}

          {applications && applications.length === 0 && (
            <tr>
              <td colSpan={3} className='ml-2 text-center text-danger h6'>
                No Data
              </td>
            </tr>
          )}
        </tbody>
      </Table>
    </div>
  );
};

export default SimpleApplicationListTable;
