import { getToken } from '../../../utils/utils';
import { axiosInstanceV1 } from '../httpClient';

export const getLoanId = (loanInfo) => {
  const token = getToken();
  return axiosInstanceV1.request({
    method: 'POST',
    params: { agfinmarttoken: token },
    data: loanInfo,
    url: '/fpo/apply_loan/',
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

export const saveFpoLoanInfo = ({ loanInfo, applicationId }) => {
  const token = getToken();
  return axiosInstanceV1.request({
    method: 'POST',
    params: { agfinmarttoken: token, applicationuid: applicationId },
    data: loanInfo,
    url: '/fpo/application_question_replies/',
    headers: { 'Content-Type': 'multipart/form-data' },
  });
};

