import { useRef } from 'react';
import fileIcon from '../../assets/Images/file.svg';
import lineIcon from '../../assets/Images/line.jpg';
import uploadCloudIcon from '../../assets/Images/Icons/upload-cloud.svg';
import { Alert, Form, Spinner } from 'react-bootstrap';
import CSVFile from '../../assets/Files/Repayment_Template.csv';

const LoanStatusCh = ({
  hanleMoveToLive,
  moveToLiveStatus,
  handleUpload,
  repaymentUploadStatus,
  isSanctioned,
  repaymentCounts,
}) => {
  const hanleMoveToLiveClick = () => {
    hanleMoveToLive && hanleMoveToLive();
  };

  const inputFileRef = useRef(null);
  const formRef = useRef(null);

  const handleFileChange = (e) => {
    const formData = new FormData();
    formData.append('repayreport', e.target.files[0]);
    handleUpload(formData);
    formRef.current.reset();
  };
  return (
    <>
      <div className='col-12 '>
        <div className='row nogutters '>
          <div className='col-md-4 px-0'>
            <div className=''>
              <h6 className='text-right f12 bold-font text-blue mb-1'>
                <u>
                  {repaymentCounts > 0 ? (
                    <a href='#'>
                      <i className='fa fa-arrow-down mr-1'></i>DOWNLOAD STATUS
                    </a>
                  ) : (
                    <a href={CSVFile} download='Repayment_Template.csv'>
                      <i className='fa fa-arrow-down mr-1'></i>DOWNLOAD TEMPLATE
                    </a>
                  )}
                </u>
              </h6>
              <Form ref={formRef}>
                <div className='bg-light-muted br-12 '>
                  <div className='p-2'>
                    <h4 className='f14 medium-font'>
                      Application/Loan Function
                    </h4>
                    <p className='f12 op70'> Upload Repayment Status</p>
                  </div>
                </div>
                <button
                  type='button'
                  className='btn btn-default brd-12 f16 bold-font w-100 upload-cloud-button'
                  onClick={() => inputFileRef.current.click()}
                >
                  <img
                    src={uploadCloudIcon}
                    style={{ height: '20px' }}
                    alt=''
                  />{' '}
                  Upload Repayment {repaymentCounts > 0 ? 'Status' : 'Schedule'}
                </button>
                <input
                  type='file'
                  className='d-none'
                  ref={inputFileRef}
                  onChange={handleFileChange}
                />
              </Form>
            </div>
            <div className='d-flex justify-content-center pt-2'>
              {repaymentUploadStatus && repaymentUploadStatus.inProgress && (
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              )}
              {repaymentUploadStatus && repaymentUploadStatus.success && (
                <Alert
                  variant={'success'}
                  style={{ width: '30vw' }}
                  className='text-center'
                >
                  {repaymentUploadStatus.message}
                </Alert>
              )}
              {repaymentUploadStatus && repaymentUploadStatus.error && (
                <Alert
                  variant={'danger'}
                  style={{ width: '30vw' }}
                  className='text-center'
                >
                  {repaymentUploadStatus.message}
                </Alert>
              )}
            </div>
          </div>
          <div className='col-md-2'></div>
          {isSanctioned && (
            <div className='col-md-6 px-0 pt-1'>
              <div className=''>
                <div className='bg-light-muted br-12 mt-3'>
                  <div className='row dflex-center gutters'>
                    <div className='col-md-5 pb-1'>
                      <div className='pt-3'>
                        <h4 className='f14 medium-font'>Transfer Bucket</h4>
                        <p className='f12 op70'>
                          Move the application to the active loan (after
                          disbursement)
                        </p>
                      </div>
                    </div>

                    <div className='col-md-7'>
                      <div className='d-flex justify-content-center '>
                        {moveToLiveStatus && moveToLiveStatus.success ? (
                          <div className=' py-3 px-5 br-10 box dotted-box'>
                            <div
                              className=' text-center f12 text-bold'
                              id='div1'
                              ondrop='drop(event)'
                              ondragover='allowDrop(event)'
                            >
                              <i
                                className='fa fa-check f20 mb-2 text-success'
                                id='icon'
                              ></i>
                              <h3 id='text2' className='f12 bold-font'>
                                ACTIVATED
                              </h3>
                            </div>
                          </div>
                        ) : (
                          <>
                            {moveToLiveStatus && moveToLiveStatus.inProgress ? (
                              <Spinner animation='border' role='status'>
                                <span className='visually-hidden'></span>
                              </Spinner>
                            ) : (
                              <>
                                <img
                                  src={fileIcon}
                                  style={{ height: '51px' }}
                                  alt=''
                                  draggable={true}
                                />
                                <div className='line'>
                                  <img src={lineIcon} alt='' srcSet='' />
                                </div>
                                <div
                                  className='box'
                                  onDrop={hanleMoveToLiveClick}
                                  onDragOver={(event) => {
                                    event.preventDefault();
                                  }}
                                >
                                  <div className='dotted-box br-12 f12 bold-font bg-white'>
                                    Active Loan
                                  </div>
                                </div>
                              </>
                            )}
                          </>
                        )}
                      </div>
                    </div>
                    <span className='brd-12 f12 op-70 text-blue text-center mb-2 mb-1  w-100'>
                      Drag and drop in Active Loan box.
                    </span>
                  </div>
                </div>
              </div>
              <div className='d-flex justify-content-center pt-2'>
                {moveToLiveStatus && moveToLiveStatus.error && (
                  <Alert
                    variant={'danger'}
                    style={{ width: '30vw' }}
                    className='text-center'
                  >
                    {moveToLiveStatus.message}
                  </Alert>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default LoanStatusCh;
