import { forwardRef, useEffect, useState } from 'react';
import { Form } from 'react-bootstrap';
import formatCurrency from '../../utils/currency';

const CurrencyInput = forwardRef(
  (
    {
      readOnly,
      label,
      className,
      name,
      required,
      error,
      setValue,
      value,
      onChange,
      handleChange,
    },
    ref
  ) => {
    const [inputValue, setinputValue] = useState('');
    const [inputMaskedValue, setinputMaskedValue] = useState('');

    const handleInputChange = (e, blur) => {
      let input_value = e.target.value;
      let { maskedValue, unMastedValue } = formatCurrency(input_value, blur);
      var decimal_pos = input_value.indexOf('.');
      var left_side = input_value.substring(0, decimal_pos);
      var right_side = input_value.substring(decimal_pos);
      maskedValue =
        // eslint-disable-next-line eqeqeq
        unMastedValue.length === 0 ? '' : maskedValue;

      setinputMaskedValue(maskedValue);
      setinputValue(unMastedValue);
      // setValue(name, unMastedValue, { shouldValidate: readOnly });
      if (handleChange) {
        handleChange(unMastedValue);
      }
      onChange((e.target.value = unMastedValue));
    };

    useEffect(() => {
      if (value !== inputValue && !readOnly) {
        if (value >= 0) {
          const { maskedValue, unMastedValue } = formatCurrency(value + '');
          setinputMaskedValue(maskedValue);
          setValue(name, unMastedValue, { shouldValidate: readOnly });
        }
      }
    }, [value]);
    return (
      <>
        <Form.Group
          className={`mb-3 col-md-6 col-lg-4 ${className && className}`}
          style={{ padding: '0% 3%' }}
          ref={ref}
        >
          <Form.Label htmlFor={name} className='f14 form-label'>
            {label} {}
            {required && <small className='text-danger f20'>*</small>}
          </Form.Label>
          {!readOnly ? (
            <Form.Control
              readOnly={readOnly}
              name={name}
              required={required}
              placeholder='₹ 0.00'
              // onBlur={(e) => handleInputChange(e, 'blur')}
              onChange={(e) => handleInputChange(e)}
              type='text'
              className=''
              autoComplete='off'
              value={inputMaskedValue}
            />
          ) : (
            <Form.Control
              readOnly={readOnly}
              name={name}
              placeholder='₹ 0.00'
              type='text'
              className=''
              autoComplete='off'
              value={formatCurrency(value + '', 'blur')['maskedValue']}
            />
          )}
          {error ? (
            <>
              {error && error.message ? (
                <small className='text-danger'>{error.message}</small>
              ) : (
                <small className='text-danger'>Enter valid {label}</small>
              )}
            </>
          ) : null}
        </Form.Group>
      </>
    );
  }
);

export default CurrencyInput;
