import { useState, useEffect } from 'react';
import InputBox from '../../../../FormInputs/InputBox';
import SelectBox from '../../../../FormInputs/SelectBox';
import { useForm, Controller } from 'react-hook-form';
import { Form } from 'react-bootstrap';
import { revenue_as_on_years, required_units } from '../../../../../data.js';
import { OUTPUT_LOAN_TYPES, LOAN_FIELDS } from '../../../../../constants';
import CurrencyInput from '../../../../FormInputs/CurrencyInput';

const OutputLoanForm = ({
  readOnly,
  handleTabChange,
  activeKey,
  defaultValues,
  backButton,
  formModified,
  hideActionsButtons,
  revenue,
}) => {
  const {
    register,
    handleSubmit,
    setValue,
    control,
    formState: { errors, isDirty },
  } = useForm({
    defaultValues: {
      [OUTPUT_LOAN_TYPES[LOAN_FIELDS.loanType]]: 'Working capital - Output',
    },
  });

  useEffect(() => {
    if (!defaultValues) return;

    setTimeout(() => {
      for (var key in defaultValues) {
        setValue(key, defaultValues[key], {
          shouldValidate: !readOnly,
        });
      }
    }, 50);
  }, [defaultValues, setValue]);

  useEffect(() => {
    if (
      revenue &&
      (!defaultValues || !defaultValues[OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenue]])
    ) {
      setValue(OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenue], revenue, {
        shouldValidate: !readOnly,
      });
    }
  }, [revenue]);

  return (
    <Form
      onSubmit={handleSubmit((data) => {
        handleTabChange(
          activeKey,
          'next',
          data,
          readOnly ? formModified : isDirty
        );
      })}
      noValidate
    >
      <div className='p-2'>
        <h4 className='form-section-header mt-4 ml-0 mb-2 fs-16'>
          Loan Details
        </h4>
        <section className='d-flex flex-wrap'>
          <InputBox
            label='Loan Type'
            className=''
            register={register}
            required
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.loanType]}
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.loanType]]}
            readOnly={true}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Loan Amount'
                className=''
                setValue={setValue}
                error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.loanAmount]]}
                readOnly={readOnly}
                {...field}
              />
            )}
            {...register(OUTPUT_LOAN_TYPES[LOAN_FIELDS.loanAmount], {
              required: true,
              min: 1,
              max: 100000000,
              pattern: {
                value: /([0-9].[0]$)|([0-9]*[0]+[.]+[0-9]*$)/,
                message:
                  'Entered value must have end with zero or multiples of 10',
              },
            })}
            control={control}
          />

          <InputBox
            label='Expected tenure(Months)'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            max={{ value: 4, message: 'Please enter the value below 5' }}
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]}
            type='number'
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]]}
            readOnly={readOnly}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Revenue'
                className=''
                setValue={setValue}
                error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenue]]}
                readOnly={readOnly}
                {...field}
              />
            )}
            {...register(OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenue], {
              required: { value: true, message: 'This is Mandatory field' },
              min: { value: 0.01, message: 'Please enter the value above 0.01' },
            })}
            control={control}
          />

          <SelectBox
            label='Revenue as-on year'
            className=''
            {...register(OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]}
            type='number'
            options={revenue_as_on_years}
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]]}
            readOnly={readOnly}
          />

          <SelectBox
            label='Revenue as per:'
            className=''
            {...register(OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]}
            type='number'
            options={[
              { value: 'Provisional', text: 'Provisional' },
              { value: 'Audited', text: 'Audited' },
            ]}
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]]}
            readOnly={readOnly}
          />
        </section>
        <h4 className='form-section-header mt-4 ml-0 mb-2 fs-16'>
          Output Details
        </h4>
        <section className='d-flex flex-wrap'>
          <InputBox
            label='Product/Commodity 1'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.productCommodityOne]}
            type='text'
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.productCommodityOne]]}
            readOnly={readOnly}
          />

          <InputBox
            label='Variety'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.variety]}
            type='text'
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.variety]]}
            readOnly={readOnly}
          />

          <InputBox
            label='Quantity'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            min={{ value: 1, message: 'Please enter the value above 0' }}
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.quantity]}
            type='number'
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.quantity]]}
            readOnly={readOnly}
          />

          <SelectBox
            label='Quantity unit'
            className=''
            {...register(OUTPUT_LOAN_TYPES[LOAN_FIELDS.quantityUnit], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={OUTPUT_LOAN_TYPES[LOAN_FIELDS.quantityUnit]}
            type='number'
            options={required_units}
            error={errors[OUTPUT_LOAN_TYPES[LOAN_FIELDS.quantityUnit]]}
            readOnly={readOnly}
          />
        </section>
        {!hideActionsButtons && (
          <section className='d-flex justify-content-end my-5'>
            {backButton && (
              <button
                type='button'
                className='loan-application-btn btn-secondary mr-5'
                onClick={() => handleTabChange(activeKey, 'prev')}
              >
                PREVIOUS
              </button>
            )}
            {readOnly ? (
              <button
                type='button'
                className='loan-application-btn btn-primary mr-5'
                onClick={() =>
                  handleTabChange(
                    activeKey,
                    'next',
                    defaultValues,
                    readOnly ? formModified : isDirty
                  )
                }
              >
                NEXT
              </button>
            ) : (
              <button className='loan-application-btn btn-primary '>
                NEXT
              </button>
            )}
          </section>
        )}
      </div>
    </Form>
  );
};
export default OutputLoanForm;
