import axios from 'axios';

const authInstance = axios.create({
  baseURL: `${process.env.REACT_APP_API_URL}`,
  withCredentials: false,
  headers: {
    'Access-Control-Allow-Origin': '*',
  },
});

const axiosInstanceV1 = axios.create({
  baseURL: `${process.env.REACT_APP_FPO_GATEWAY_LOGIN_API_URL}`,
  withCredentials: false,
  headers: {
    'Access-Control-Allow-Origin': '*',
  },
});

export const signIn = (user) =>
  authInstance.request({
    method: 'GET',
    params: { ...user },
    url: '/auth/loginuser',
  });

export const signInFpo = (token) =>
  axiosInstanceV1.request({
    method: 'GET',
    params: { token },
    url: '/agfinmart/gateway/',
  });
