import React, { useRef, forwardRef, useState, useEffect } from 'react';
import dayjs from 'dayjs';
import uploadIcon from '../../assets/Images/Icons/uoh.jpg';
import { useForm } from 'react-hook-form';
import { Form, NavDropdown } from 'react-bootstrap';
import { LOAN_FIELDS, SANSACTION_FILE_UPLOAD } from '../../constants';
import { useSelector } from 'react-redux';
import _ from 'lodash';

const DocumentList = ({
  documents,
  uploadDoc,
  saveDoc,
  handleDocumentSubmit,
  userType,
  documentKey,
  className,
}) => {
  const formRef = useRef([]);

  const [MyDocuments, setMyDocuments] = useState([]);
  useEffect(() => {
    setMyDocuments(_.orderBy(documents, ['documentURL'], ['desc']));
  }, [documents]);

  return (
    <>
      <div
        className={`col-12 p-0 table-responsive custom-scrollbar ${className}`}
        style={{ maxHeight: '300px', width: '100%' }}
      >
        <table className='table responsive-table' >
          <thead>
            <tr>
              <th className='text-muted f12'>S.NO</th>
              <th className='text-muted f12'>DOC NAME</th>
              <th className='text-muted f12'>DETAILS</th>
              <th className='text-muted f12'>DATE</th>
              {uploadDoc && <th className='text-muted f12'>UPLOAD DOC</th>}
              {saveDoc && <th></th>}
            </tr>
          </thead>
          <tbody>
            {documents &&
              MyDocuments.map((document, key) => (
                <tr key={key}>
                  <td>{key + 1}</td>
                  <td
                    className={`${
                      !document.documentURL &&
                      userType !== 'bank' &&
                      'text-danger'
                    }`}
                  >
                    {!document.documentURL ? (
                      document.documentname
                    ) : (
                      <a
                        href={document.documentURL}
                        target='_blank'
                        rel='noreferrer'
                      >
                        {document.documentname}
                      </a>
                    )}
                  </td>
                  <td>{document.documentdescription}</td>
                  <td>
                    {document.request_date
                      ? dayjs(document.request_date).format('D-M-YYYY')
                      : ''}
                  </td>
                  {uploadDoc && !document.documentURL ? (
                    <GenrateFileUploadForm
                      formRef={formRef}
                      ldRecid={document.ld_recid}
                      handleUpload={handleDocumentSubmit}
                      documentName={
                        document.type === 'post'
                          ? SANSACTION_FILE_UPLOAD[LOAN_FIELDS.postDocFile]
                          : SANSACTION_FILE_UPLOAD[LOAN_FIELDS.preDocFile]
                      }
                      docType={document.type}
                      documentKey={documentKey}
                    />
                  ) : (
                    <>
                      <td></td>
                      <td></td>
                    </>
                  )}
                </tr>
              ))}
          </tbody>
        </table>
      </div>
    </>
  );
};

export default DocumentList;

const GenrateFileUploadForm = ({
  formRef,
  ldRecid,
  handleUpload,
  documentName,
  docType,
  documentKey,
}) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const fpo = useSelector((state) => state.fpo);
  const [formReset, setFormReset] = useState(null);
  return (
    <>
      <td>
        <Form
          onSubmit={handleSubmit((data) => {
            handleUpload(data, ldRecid, docType, documentName);
            setFormReset(Math.random(1000000, 9999999));
            setTimeout(() => {
              reset();
            }, 2000);
          })}
        >
          <FileUploadButton
            {...register(ldRecid + '', {
              required: true,
            })}
            formRef={formRef}
            ldRecid={ldRecid}
            error={errors[ldRecid + '']}
            documentKey={documentKey}
            formReset={formReset}
          />
        </Form>
      </td>
      <td className='d-flex align-items-center'>
        <NavDropdown
          title={<i className='fa fa-ellipsis-v px-1 py-1 f16 dots'></i>}
          id='nav-dropdown'
          className='dots-container requested-document-upload'
        >
          <NavDropdown.Item
            className='text-dark dots-link f14'
            eventKey='delete'
            onClick={() => {
              reset();
              setFormReset(Math.random(1000000, 9999999));
            }}
          >
            Delete
          </NavDropdown.Item>
        </NavDropdown>
        {/* <img src={dotIcon} style={{ height: '20px' }} alt='' srcset='' /> */}
        {fpo && (
          <button
            className='pl-1 btn shadow-none p-0 request-doc-save-btn'
            onClick={() => formRef.current[ldRecid].click()}
            type='button'
          >
            SAVE
          </button>
        )}
      </td>
    </>
  );
};

const FileUploadButton = forwardRef(
  (
    { error, onChange, formRef, ldRecid, name, documentKey, formReset },
    ref
  ) => {
    const [fileName, setFileName] = useState(null);
    useEffect(() => {
      if (formReset) {
        setFileName('');
      }
    }, [formReset]);
    return (
      <>
        <button
          className='btn shadow-none p-0'
          type='button'
          onClick={() =>
            document.getElementById(documentKey + '_' + ldRecid).click()
          }
        >
          <img src={uploadIcon} style={{ height: '20px' }} alt='' srcset='' />
          <span className='pl-1'>Upload</span>
        </button>
        <input
          type='file'
          className='d-none'
          name={name}
          id={documentKey + '_' + ldRecid}
          ref={ref}
          onChange={(e) => {
            onChange(e);
            let file_name = e.target.value.split('\\');
            file_name = file_name[file_name.length - 1];
            var file_ext = file_name.substring(file_name.lastIndexOf('.') + 1);
            if (file_name.length > 11) {
              file_name = file_name.substring(0, 6) + '...(' + file_ext + ')';
            }
            setFileName(file_name);
          }}
          accept='application/pdf'
        />
        <br />
        {fileName && <span className='text-info'>{fileName}</span>}
        {error && <span className='text-danger'>Select a file.</span>}
        <button
          className='d-none shadow-none'
          ref={(el) => (formRef.current[ldRecid] = el)}
        ></button>
      </>
    );
  }
);
