import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Badge, ProgressBar, Alert } from 'react-bootstrap';
import LoanApplicationForm from './LoanApplicationForm';
import ShowInterestForm from './ShowInterestForm';
import MakeAnOffer from './MakeAnOffer';
import DocumentsForm from '../../documents/DocumentsForm';
import './ApplicationModal.scss';
import SamAnalysis from './SamAnalysis/SamAnalysis';
import RejectionModal from './RejectionModal';
import ActionsButton from '../../ActionsButton/ActionsButton';
import { useDispatch, useSelector } from 'react-redux';
import {
  SAVE_SHOW_INTEREST_IN_PROGRESS,
  SAVE_REJECT_INTEREST_IN_PROGRESS,
  CLEAN_SHOW_INTEREST,
  SAVE_MAKE_AN_OFFER_IN_PROGRESS,
  GET_PROFILE_DATA_IN_PROGRESS,
  GET_DOCUMENT_DATA_IN_PROGRESS,
  GET_SAM_ANALYSIS_IN_PROGRESS,
  GET_SAVED_OFFER_IN_PROGRESS,
  CLEAR_POST_MAKE_OFFER,
} from '../../../redux/types/banktypes';
import { MAKE_AN_OFFER_TYPES, LOAN_FIELDS } from '../../../constants';
import ProfileData from '../../Profile/ProfileData';
import BankActionTab from '../../ActionsTab/BankActionsTab';
import { bankActions } from '../../../redux/actions/';

let tabMenusArr = [
  'loan_application',
  'borrower_profile',
  'show_interest',
  'documents',
  'sam_analysis',
  'make_an_offer',
];

const LoanApplicationContainer = (props) => {
  const [tabKey, setTabKey] = useState('loan_application');
  const [activeTabs, setActiveTabs] = useState(['loan_application']);

  const [showInterestStatus, setShowInterestStatus] = useState(true);
  const [rejectionModalVisible, setRejectionModalVisible] = useState(false);
  const [loanType, setloanType] = useState(null);
  const [formDetails, setformDetails] = useState(null);
  const [actionBtn, setActionBtn] = useState(false);
  const [disableShowInterestBtn, setDisableShowInterestBtn] = useState(false);

  const bank = useSelector((state) => state.bank);
  const auth = useSelector((state) => state.auth);
  const [actionStatus, setActionStatus] = useState(false);
  const dispatch = useDispatch();
  useEffect(() => {
    if (!bank || !bank.selectedLoan || !bank.selectedLoan.loanInfo) return;
    setloanType(bank.selectedLoan.loanInfo.loanproductname);
    setformDetails(bank.selectedLoan.documents.documents);

    return () => {};
  }, [bank]);
  useEffect(() => {
    if (
      bank &&
      bank.selectedLoan &&
      bank.selectedLoan.documents &&
      bank.selectedLoan.documents.documents &&
      bank.selectedLoan.documents.documents.deal_status != 'NULL' &&
      showInterestStatus === true
    ) {
      var index = tabMenusArr.indexOf('show_interest');
      tabMenusArr.splice(index, 1);
      setShowInterestStatus(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank]);

  useEffect(() => {
    if (
      bank &&
      bank.selectedLoan &&
      bank.selectedLoan.documents &&
      bank.selectedLoan.documents.documents
    ) {
      resetTabs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.selectedLoan?.documents?.documents]);

  function resetTabs() {
    setTabKey('loan_application');
    setActionStatus(false);
    setActiveTabs(['loan_application']);
    if (tabMenusArr.length < 6) {
      tabMenusArr.splice(2, 0, 'show_interest');
    }
    setShowInterestStatus(true);
    dispatch({
      type: CLEAN_SHOW_INTEREST,
      payload: {},
    });
    dispatch({ type: CLEAR_POST_MAKE_OFFER });
    tabMenusArr = [
      'loan_application',
      'borrower_profile',
      'show_interest',
      'documents',
      'sam_analysis',
      'make_an_offer',
    ];
  }

  useEffect(() => {
    if (
      bank &&
      bank.selectedLoan &&
      bank.selectedLoan.documents &&
      bank.selectedLoan.documents.documents &&
      bank.selectedLoan.documents.documents.deal_status !== 'NULL'
    ) {
      setActionBtn(true);
    } else {
      setActionBtn(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.selectedLoan?.documents?.documents]);

  useEffect(() => {
    if (bank && bank.showInterest && bank.showInterest.success) {
      setActionBtn(true);
    } else {
      // setActionBtn(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.showInterest?.success]);

  useEffect(() => {
    if (props.closeModal) {
      resetTabs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.closeModal]);

  useEffect(() => {
    if (props.proposalId) {
      resetTabs();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.proposalId]);

  useEffect(() => {
    if (bank && bank.showInterest && bank.showInterest.success === true) {
      handleTabChange('show_interest', 'next');
      var index = tabMenusArr.indexOf('show_interest');
      tabMenusArr.splice(index, 1);
      setShowInterestStatus(false);
      dispatch({
        type: CLEAN_SHOW_INTEREST,
        payload: {},
      });
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank.showInterest.success]);

  useEffect(() => {
    if (
      (bank &&
        bank.rejectShowInterest &&
        bank.rejectShowInterest.success === true) ||
      bank.rejectShowInterest.success === false
    ) {
      setRejectionModalVisible(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank]);

  React.useEffect(() => {
    if (
      bank &&
      bank.selectedLoan.documents &&
      bank.selectedLoan.documents.documents
    ) {
      let payload = {
        applicationuid: bank.selectedLoan.loanInfo.applicationuid,
      };
      dispatch({
        type: GET_PROFILE_DATA_IN_PROGRESS,
        payload: payload,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank.selectedLoan.documents]);

  useEffect(() => {
    if (props.proposalId) {
      dispatch({
        type: GET_SAVED_OFFER_IN_PROGRESS,
        payload: { proposal_id: props.proposalId },
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.proposalId]);

  useEffect(() => {
    if (bank && bank.rejectShowInterest && bank.rejectShowInterest.success) {
      setDisableShowInterestBtn(true);
    } else {
      setDisableShowInterestBtn(false);
    }
  }, [bank?.rejectShowInterest?.success]);

  // fetching 2nd phase apis
  useEffect(() => {
    if (!showInterestStatus) {
      fetchAPplicationApiPhase2();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showInterestStatus]);

  const fetchAPplicationApiPhase2 = () => {
    let payload = {
      applicationuid: bank.selectedLoan.loanInfo.applicationuid,
    };
    dispatch({
      type: GET_DOCUMENT_DATA_IN_PROGRESS,
      payload: payload,
    });

    dispatch({
      type: GET_SAM_ANALYSIS_IN_PROGRESS,
      payload: payload,
    });
    let fpo_id = payload.applicationuid.split('-')[1];
    dispatch(bankActions.getSamKeyFinancialsInProgress({ fpo_id }));
    dispatch(bankActions.getSamRatioAnalysisInProgress({ fpo_id }));
    dispatch(bankActions.getSamFinancialPerformanceInProgress({ fpo_id }));
  };

  function handleTabChange(key, type) {
    let position = tabMenusArr.indexOf(key);
    position = type === 'next' ? position + 1 : position - 1;
    setTabKey(tabMenusArr[position]);
    if (type === 'next') {
      activeTabs.push(tabMenusArr[position]);
      setActiveTabs(activeTabs);
    } else {
      activeTabs.pop();
    }
    setActiveTabs(activeTabs);
  }

  function handleShowInterest(key, type, formType, fields) {
    let payload = {
      applicationuid: formDetails.applicationuid,
      organizationid: auth.userInfo.organizationid,
    };
    if (formType === 'reject') {
      const formData = new FormData();
      for (const key in fields) {
        if (fields[key]) {
          formData.append(key, fields[key]);
        }
      }
      payload.formData = formData;
      dispatch({ type: SAVE_REJECT_INTEREST_IN_PROGRESS, payload: payload });
      // setRejectionModalVisible(false);
    } else if (formType === 'interest') {
      dispatch({ type: SAVE_SHOW_INTEREST_IN_PROGRESS, payload: payload });
    }
  }

  const handleMakeAnOffer = (data) => {
    let LProposoalID =
      bank?.selectedLoan?.documents?.documents?.applicationuid +
      '-' +
      auth?.userInfo?.organizationid;
    let proposal_id = props.proposalId
      ? props.proposalId
      : bank.showInterest.proposal_id
      ? bank.showInterest.proposal_id
      : LProposoalID;
    let payload = {
      params: {
        applicationuid: formDetails.applicationuid,
        proposal_id: proposal_id,
      },
    };

    const formData = new FormData();
    for (const key in data) {
      if (key === 'data' || key === 'usertype' || key === 'userrole') continue;
      if (key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.sanctionLetter]) {
        formData.append(key, data[key][0]);
      } else if (key === MAKE_AN_OFFER_TYPES[LOAN_FIELDS.otherDocuments]) {
        for (let i = 0; i < data[key].length; i++) {
          formData.append(key + '[' + i + ']', data[key][i]);
        }
      } else {
        formData.append(key, data[key]);
      }
    }
    payload.formData = formData;
    dispatch({ type: SAVE_MAKE_AN_OFFER_IN_PROGRESS, payload: payload });
  };

  return (
    <>
      <ActionsButton
        handleChange={() => setActionStatus(!actionStatus)}
        disabled={!actionBtn}
      />

      {actionStatus ? (
        bank &&
        bank.selectedLoan &&
        bank.selectedLoan.documents.documents && (
          <BankActionTab
            handleBack={() => setActionStatus(!actionStatus)}
            applicationId={bank.selectedLoan.documents.documents.applicationuid}
            type='bank'
            hideBackButton={true}
          />
        )
      ) : (
        <>
          <ProgressBar
            now={
              (100 * (tabMenusArr.indexOf(tabKey) + 1)) /
              (6 - (showInterestStatus ? 0 : 1))
            }
            className='loan-application-progress-bar'
          />
          <Tabs
            activeKey={tabKey}
            className='mb-3 mt-3 loan-application-tab-container'
            onSelect={(key) => {
              setTabKey(key);
            }}
          >
            <Tab
              disabled={showInterestStatus}
              eventKey='loan_application'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    {tabMenusArr.indexOf('loan_application') + 1}
                  </Badge>
                  <label className='ml-2 '>LOAN APPLICATION</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('loan_application') <
                tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <LoanApplicationForm
                readOnly={true}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                contentPadding={
                  props.loanApplicationTabPadding
                    ? props.loanApplicationTabPadding
                    : '0%'
                }
                loanType={loanType}
                loanDetails={formDetails}
              />
            </Tab>
            <Tab
              disabled={showInterestStatus}
              eventKey='borrower_profile'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    {tabMenusArr.indexOf('borrower_profile') + 1}
                  </Badge>
                  <label className='ml-2'>BORROWER PROFILE</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('borrower_profile') <
                tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <>
                {bank &&
                  bank.selectedLoan &&
                  bank.selectedLoan.documents &&
                  bank.selectedLoan.documents.documents && (
                    <ProfileData
                      profileData={bank.profileData}
                      // showEdit={true}
                    />
                  )}
                <section className='d-flex justify-content-end my-5 mr-4'>
                  <div className=''>
                    <button
                      className='loan-application-btn btn-secondary'
                      onClick={() => handleTabChange(tabKey, 'prev')}
                    >
                      PREVIOUS
                    </button>
                    <button
                      className='loan-application-btn btn-primary ml-4'
                      onClick={() => handleTabChange(tabKey, 'next')}
                    >
                      NEXT
                    </button>
                  </div>
                </section>
              </>
            </Tab>
            {showInterestStatus ? (
              <Tab
                disabled={true}
                eventKey='show_interest'
                title={
                  <>
                    <label className='ml-2'>SHOW INTEREST</label>
                  </>
                }
                tabClassName={
                  activeTabs.includes('show_interest')
                    ? 'active text-green'
                    : ''
                }
              >
                <ShowInterestForm
                  handleTabChange={handleTabChange}
                  handleShowInterest={handleShowInterest}
                  activeKey={tabKey}
                  openRejectModel={(val) => setRejectionModalVisible(val)}
                  disableShowInterestBtn={disableShowInterestBtn}
                />
              </Tab>
            ) : null}

            <Tab
              disabled={showInterestStatus}
              eventKey='documents'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    {3}
                  </Badge>
                  <label className='ml-2'>DOCUMENTS</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('documents') < tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <>
                {bank.documentData && bank.documentData.documents && (
                  <DocumentsForm
                    handleTabChange={handleTabChange}
                    activeKey={tabKey}
                    documents={bank.documentData.documents.documentdata}
                  />
                )}
                {bank.documentData &&
                  bank.documentData.success &&
                  !bank.documentData.documents.documentdata && (
                    <div className='d-flex justify-content-center'>
                      <Alert
                        variant={'danger'}
                        style={{ width: '30vw' }}
                        className='text-center'
                      >
                        {bank.documentData.documents.message}
                      </Alert>
                    </div>
                  )}
              </>
            </Tab>
            <Tab
              disabled={showInterestStatus}
              eventKey='sam_analysis'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    {4}
                  </Badge>
                  <label className='ml-2'>SAM ANALYSIS</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('sam_analysis') <
                tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <SamAnalysis
                readOnly={true}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                samDocuments={bank.samAnalysis}
                samKeyFinanials={bank?.samKeyFinanials?.documents}
                samRatioAnalysis={bank?.samRatioAnalysis?.documents}
                samFinancialPerformance={
                  bank?.samFinancialPerformance?.documents
                }
              />
            </Tab>
            <Tab
              disabled={showInterestStatus}
              eventKey='make_an_offer'
              title={
                <>
                  <Badge pill bg='secondary' className='text-white'>
                    {5}
                  </Badge>
                  <label className='ml-2'>MAKE AN OFFER</label>
                </>
              }
              tabClassName={
                tabMenusArr.indexOf('make_an_offer') <
                tabMenusArr.indexOf(tabKey)
                  ? 'active'
                  : ''
              }
            >
              <MakeAnOffer
                readOnly={false}
                handleTabChange={handleTabChange}
                activeKey={tabKey}
                contentPadding={
                  props.loanApplicationTabPadding
                    ? props.loanApplicationTabPadding
                    : '0%'
                }
                formDetails={formDetails}
                handleMakeAnOffer={handleMakeAnOffer}
                proposalId={props.proposalId}
                defaultValues={bank?.savedOffer?.data}
                closeMainStatus={props.closeMainStatus}
              />
            </Tab>
          </Tabs>
        </>
      )}
      <RejectionModal
        show={rejectionModalVisible}
        onHide={(val) => setRejectionModalVisible(val)}
        handleShowInterest={handleShowInterest}
        activeKey={tabKey}
        closeRejectModel={(val) => setRejectionModalVisible(val)}
      />
    </>
  );
};

export default LoanApplicationContainer;
