import { useState, useEffect, forwardRef } from 'react';
import { Form } from 'react-bootstrap';
import FilePlus from './../../assets/Images/Icons/file-plus.png';
import './FileUploadButton.scss';

const FileUploadButton = forwardRef(
  (
    {
      name,
      className,
      label,
      error,
      multiple,
      onChange,
      accept,
      handleFileChanges,
    },
    ref
  ) => {
    const [inputID, setInputID] = useState(
      'file-input-' + Math.floor(1000 + Math.random() * 9000)
    );

    const [fileName, setFileName] = useState('');

    useEffect(() => {
      setInputID(name ? name : inputID);
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [name]);

    function handleClick() {
      var ele = document.getElementById(inputID);
      ele.value = null;
      ele.click();
    }

    const handleFileChange = (e) => {
      setFileName(e.target.value.split('\\').pop());
      handleFileChanges(e, name, label);
    };

    return (
      <Form.Group
        className={`mb-3 col-md-6 col-lg-4 ${className && className}`}
        style={{ padding: '0% 3%' }}
      >
        <button
          className={`file-input ${className}`}
          type='button'
          onClick={() => handleClick()}
          title={'File:' + fileName}
        >
          <img src={FilePlus} alt='file-add' />
          {label}
        </button>
        <input
          type='file'
          accept={`${accept}`}
          name={`${multiple ? name + '[]' : name}`}
          style={{ display: 'none' }}
          id={inputID}
          ref={ref}
          onChange={(e) => {
            onChange(e);
            handleFileChange(e);
          }}
          multiple={multiple}
        />
        {error && <small className='text-danger'>Select valid {label}</small>}
      </Form.Group>
    );
  }
);

export default FileUploadButton;
