import { useState, useEffect } from 'react';
import {
  CircularGauge,
  Scale,
  Label,
  RangeContainer,
  Range,
  ValueIndicator,
  Title,
  Margin,
  Font,
  Tooltip,
} from 'devextreme-react/circular-gauge';
import formatCurrency from '../../utils/currency';

const SamGauge = ({ document, title }) => {
  const [docVal, setdocVal] = useState(null);
  const [docThreshold, setdocThreshold] = useState(null);
  useEffect(() => {
    if (document) {
      const { value, thresholdValues } = document;
      setdocVal(value);
      setdocThreshold(thresholdValues);
    }
  }, [document]);

  return (
    <div className='px-2 py-2 sam-vintage-chart-container'>
      {docVal && docThreshold && (
        <>
          <CircularGauge value={docVal}>
            <Scale startValue={0} endValue={docThreshold.peak} tickInterval={0}>
              <Label useRangeColors={true} customizeText={customizeLabel} />
            </Scale>
            <Tooltip
              enabled={true}
              customizeTooltip={customizeTooltip}
              paddingLeftRight={10}
              paddingTopBottom={5}
            >
              <Font size={10} color='#DCD0FF' />
            </Tooltip>
            <Title
              text={title}
              horizontalAlignment='center'
              verticalAlignment='bottom'
            >
              <Font size={16} color='#00A3FF' weight={'bold'} />
              <Margin top={20} />
            </Title>

            <ValueIndicator
              type='triangleNeedle'
              color='#8FBC8F'
            ></ValueIndicator>
            <RangeContainer palette='pastel'>
              <Range startValue={0} endValue={docThreshold.threshold} />
              <Range
                startValue={docThreshold.threshold}
                endValue={docThreshold.median}
              />
              <Range
                startValue={docThreshold.median}
                endValue={docThreshold.peak}
              />
            </RangeContainer>
          </CircularGauge>
        </>
      )}
    </div>
  );
};

export default SamGauge;

const customizeTooltip = (data) => {
  return {
    text: `<p style='font-size:12px !important; font-weight:bold;' >${
      formatCurrency(data.valueText)['maskedValue']
    }</p>`,
    color: '#a61c1ce0',
  };
};

const customizeLabel = (data) => {
  return `<p style='font-size:10px !important' >${
    formatCurrency(String(data.value / 100000))['maskedValue']
  } L</p>`;
};
