import { Table, Spinner, Alert } from 'react-bootstrap';

const CompareFpoTable = ({ compareFpo }) => {
  const genrateFpoDataColumn = (fieldName, header) => {
    return compareFpo.fpo.fpoIds.map((fpoId, key) => (
      <>
        <th>{compareFpo.fpo.fpoObjs[fpoId][fieldName]}</th>
        {compareFpo.fpo.fpoIds.length !== key + 1 && (
          <th className={`${header ? 'border-none' : ''}`}></th>
        )}
      </>
    ));
  };
  return (
    <>
      <div className='p-3 custom-scrollbar'>
        {compareFpo &&
          !compareFpo.inProgress &&
          compareFpo.success &&
          compareFpo.fpo && (
            <Table className='compare-fpo-table'>
              <tbody>
                <tr>
                  <th className='border-none'></th>
                  {genrateFpoDataColumn('fponame', true)}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Location</span>
                  </th>
                  {genrateFpoDataColumn('stateName')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Membership base</span>
                  </th>
                  {genrateFpoDataColumn('membershipbase')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Villages covered</span>
                  </th>
                  {genrateFpoDataColumn('villagescovered')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Turnover</span>
                  </th>
                  {genrateFpoDataColumn('turnover')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Resourcing Institution</span>
                  </th>
                  {genrateFpoDataColumn('ResourcingInstitution')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Promoting Institution</span>
                  </th>
                  {genrateFpoDataColumn('PromotingInstitution')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'> Share Capital</span>
                  </th>
                  {genrateFpoDataColumn('ShareCapital')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Total Average</span>
                  </th>
                  {genrateFpoDataColumn('TotalAverage')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>CropName</span>
                  </th>
                  {genrateFpoDataColumn('FpoGrown_crop')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Business</span>
                  </th>
                  {genrateFpoDataColumn('businessname')}
                </tr>
                <tr>
                  <th className='muted text-right single-line'>
                    <span className='mr-3'>Infrastructure</span>
                  </th>
                  {genrateFpoDataColumn('infrastructureName')}
                </tr>
              </tbody>
            </Table>
          )}
        {compareFpo && compareFpo.inProgress && (
          <div className='d-flex justify-content-center'>
            <Spinner animation='border' role='status'>
              <span className='visually-hidden'></span>
            </Spinner>
          </div>
        )}
      </div>
      {compareFpo && !compareFpo.inProgress && compareFpo.error && (
        <div className='d-flex justify-content-center mb-5'>
          <Alert
            variant={'danger'}
            style={{ width: '30vw' }}
            className='text-center'
          >
            {compareFpo.message}
          </Alert>
        </div>
      )}
    </>
  );
};

export default CompareFpoTable;
