import * as dayjs from 'dayjs';
const FpoInfo = ({ applicationStatus, details, basicDetails }) => {
  return (
    <>
      {/* <div className='col-12'>
        <span className='pl-3 pr-3'>
          <span className='f16 pr-4 '>Borrower</span>
          <span className='f16  pr-4 bold-font'>
            {details && details.fponame}{' '}
          </span>
          <span className='f16'>
            Loan Start Date.{' '}
            <b className='bold-font'>
              {basicDetails && basicDetails.loanstartdate
                ? dayjs(basicDetails.loanstartdate).format('D-M-YYYY')
                : 'NIL'}
            </b>
          </span>
        </span>
      </div>
      <hr /> */}

      <div className='col-12'>
        <div className='row nogutters pl-1 pr-1'>
          <div className='col-md-2'>
            <span className='f12'>Application ID </span>
            <h4 className='f12 pt-2 bold-font'>
              {details && details.applicationuid}
            </h4>
          </div>
          <div className='col-md-2'>
            <span className='f12'>FPO Name </span>
            <h4 className='f12 pt-2 bold-font'>{details && details.fponame}</h4>
          </div>
          <div className='col-md-2'>
            <span className='f12'>Location </span>
            <h4 className='f12 pt-2 bold-font'>
              {basicDetails ? basicDetails.fpostate : details?.fpostate}
            </h4>
          </div>
          <div className='col-md-2'>
            <span className='f12'>Application Date </span>
            <h4 className='f12 pt-2 bold-font'>
              {details && details.applicationdate
                ? dayjs(details.applicationdate).format('D-M-YYYY')
                : 'NIL'}
            </h4>
          </div>
          {applicationStatus && (
            <div className='col-md-2'>
              <span className='f12'>Application Status </span>
              <h4 className='f12 pt-2 bold-font'>
                {details && details.applicationstatus}
              </h4>
            </div>
          )}

          <div className='col-md-2'>
            <span className='f12'>Participating Lenders </span>
            <h4 className='f12 pt-2 bold-font'>
              {' '}
              {details && details.participating_lenders_count
                ? details.participating_lenders_count
                : 0}
            </h4>
          </div>
        </div>
      </div>

      <hr />
    </>
  );
};

export default FpoInfo;
