/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Card } from 'react-bootstrap';

const ReviewDocumentsCard = ({ title, badge, children }) => {
  return (
    <>
      <Card className='mb-4 pb-2'>
        <Card.Header>
          <h4 className='heading ml-2'>
            {title} <span className='default-badge'>{badge}</span>
          </h4>
        </Card.Header>
        <Card.Body className='custom-scrollbar' style={{ height: '300px' }}>
          <Card.Text>
            <div className='files d-flex flex-wrap '>{children}</div>
          </Card.Text>
        </Card.Body>
      </Card>
    </>
  );
};

export default ReviewDocumentsCard;
