import { useState, useEffect } from 'react';
import { Alert, Spinner } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import ShadowBox from '../../ShadowBox';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { FPO_CLEAR_SUBMIT_LOAN } from '../../../redux/types/fpotypes';

const SubmitApplication = ({
  handleSubmit,
  activeKey,
  handleTabChange,
  handleSave,
}) => {
  const [FormStatus, setFormStatus] = useState(false);
  const fpoState = useSelector((state) => state.fpo);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useEffect(() => {
    if (fpoState && fpoState.submit.success) {
      navigate('/fpo/dashboard');
      dispatch({ type: FPO_CLEAR_SUBMIT_LOAN });
    }
  }, [fpoState]);

  return (
    <div
      className='d-flex flex-column justify-content-center align-items-center'
      style={{ height: '70vh', position: 'relative' }}
    >
      <ShadowBox className='submit-app-box'>
        <div className='py-3 px-5 submit-app-content-padding'>
          <p className='text-center'>Your loan application is ready!</p>
          <h3 className='text-center f-24'>
            Submit Your Application to Samunnati
          </h3>
          <div className='d-flex justify-content-around m-0 mt-5'>
            {fpoState && fpoState.save.inProgress ? (
              <Spinner animation='border' role='status'>
                <span className='visually-hidden'></span>
              </Spinner>
            ) : (
              <button
                type='button'
                className='loan-application-btn ml-5 btn-secondary submit-app-box-margin'
                onClick={() => setFormStatus(handleSave())}
              >
                SAVE DRAFT
              </button>
            )}

            {fpoState && fpoState.submit.inProgress ? (
              <Spinner animation='border' role='status'>
                <span className='visually-hidden'></span>
              </Spinner>
            ) : (
              <button
                type='button'
                className='loan-application-btn mr-5 btn-primary'
                onClick={() => {
                  // handleTabChange(activeKey, 'next');
                  setFormStatus(handleSubmit());
                }}
              >
                SUBMIT
              </button>
            )}
          </div>
        </div>
      </ShadowBox>
      <br />
      <br />
      {fpoState && fpoState.save.error && !fpoState.save.inProgress ? (
        <Alert
          variant={'danger'}
          style={{ width: '30vw' }}
          className='text-center'
        >
          Failed to save form data
        </Alert>
      ) : (
        <>
          {fpoState && fpoState.save.success && (
            <Alert
              variant={'success'}
              style={{ width: '30vw' }}
              className='text-center'
            >
              Successfully Saved!
            </Alert>
          )}
        </>
      )}
      {fpoState && fpoState.submit.error && !fpoState.submit.inProgress ? (
        <Alert
          variant={'danger'}
          style={{ width: '30vw' }}
          className='text-center'
        >
          Failed to Submit form data
        </Alert>
      ) : (
        <>
          {fpoState && fpoState.submit.success && (
            <Alert
              variant={'success'}
              style={{ width: '30vw' }}
              className='text-center'
            >
              Successfully Submitted!
            </Alert>
          )}
        </>
      )}
      <button
        className='loan-application-btn btn-secondary show-interest-back-btn '
        onClick={() => handleTabChange(activeKey, 'prev')}
      >
        PREVIOUS
      </button>
    </div>
  );
};

export default SubmitApplication;
