import { useState, useEffect } from 'react';
import InputBox from '../../../../FormInputs/InputBox';
import SelectBox from '../../../../FormInputs/SelectBox';
import { useForm, Controller } from 'react-hook-form';
import { Form } from 'react-bootstrap';
import {
  revenue_as_on_years,
  infrastructure_types,
} from '../../../../../data.js';
import {
  INFRASTRUCTURE_LOAN_TYPES,
  LOAN_FIELDS,
} from '../../../../../constants';
import CurrencyInput from '../../../../FormInputs/CurrencyInput';

const CatalyticInfrastructureForm = ({
  readOnly,
  handleTabChange,
  activeKey,
  defaultValues,
  backButton,
  formModified,
  hideActionsButtons,
  revenue,
}) => {
  const {
    register,
    unregister,
    handleSubmit,
    setValue,
    control,
    formState: { errors, isDirty },
  } = useForm({
    defaultValues: {
      ...defaultValues,
      [INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.loanType]]:
        'Term loan - Infrastructure',
    },
  });

  useEffect(() => {
    if (
      revenue &&
      (!defaultValues ||
        !defaultValues[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenue]])
    ) {
      setValue(INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenue], revenue, {
        shouldValidate: !readOnly,
      });
    }
  }, [revenue]);

  useEffect(() => {
    if (!defaultValues) return;
    unregister('22', { keepValid: true, keepError: true });

    setTimeout(() => {
      for (var key in defaultValues) {
        setValue(key, defaultValues[key], {
          shouldValidate: !readOnly,
        });
      }
    }, 50);
  }, [defaultValues]);
  return (
    <Form
      onSubmit={handleSubmit((data) => {
        handleTabChange(
          activeKey,
          'next',
          data,
          readOnly ? formModified : isDirty
        );
      })}
      noValidate
    >
      <div className='p-2'>
        <h4 className='form-section-header mt-4 ml-0 mb-2 fs-16'>
          Loan Details
        </h4>
        <section className='d-flex flex-wrap'>
          <InputBox
            label='Loan Type'
            className=''
            register={register}
            required
            name={INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.loanType]}
            error={errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.loanType]]}
            readOnly={true}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Loan Amount'
                className=''
                setValue={setValue}
                error={
                  errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.loanAmount]]
                }
                readOnly={readOnly}
                min={0}
                max={100000000}
                required={true}
                {...field}
              />
            )}
            {...register(INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.loanAmount], {
              required: { value: true, message: 'This is Mandatory field' },
              min: { value: 1, message: 'Please enter the value above 0' },
              max:{ value: 100000000, message: 'Please enter the value below 100000000' },
              pattern: {
                value: /([0-9].[0]$)|([0-9]*[0]+[.]+[0-9]*$)/,
                message:
                  'Entered value must have end with zero or multiples of 10',
              },
            })}
            control={control}
            defaultValue=''
          />

          <InputBox
            label='Expected tenure(Months)'
            className=''
            register={register}
            required={{ value: true, message: 'This is Mandatory field' }}
            min={{ value: 2, message: 'Please enter the value above 2' }}
            max={{ value: 24, message: 'Please enter the value below 24' }}
            name={INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]}
            type='number'
            error={
              errors[
                INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.expextedTenureMonths]
              ]
            }
            readOnly={readOnly}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Revenue'
                className=''
                setValue={setValue}
                error={errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenue]]}
                readOnly={readOnly}
                required={true}
                {...field}
              />
            )}
            {...register(INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenue], {
              required:{ value: true, message: 'This is Mandatory field' },
              min:{ value: 0.01, message: 'Please enter the value above 0.01' } ,
            })}
            control={control}
          />

          <SelectBox
            label='Revenue as-on year'
            className=''
            {...register(
              INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear],
              {
                required:{ value: true, message: 'This is Mandatory field' },
              }
            )}
            required
            name={INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]}
            type='number'
            options={revenue_as_on_years}
            error={
              errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsOnYear]]
            }
            readOnly={readOnly}
          />

          <SelectBox
            label='Revenue as per:'
            className=''
            {...register(INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsPer], {
              required: { value: true, message: 'This is Mandatory field' },
            })}
            required
            name={INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]}
            type='number'
            options={[
              { value: 'Provisional', text: 'Provisional' },
              { value: 'Audited', text: 'Audited' },
            ]}
            error={errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.revenueAsPer]]}
            readOnly={readOnly}
          />
        </section>
        <h4 className='form-section-header mt-4 ml-0 mb-2 fs-16'>
          Infrastructure details
        </h4>
        <section className='d-flex flex-wrap'>
          <SelectBox
            label='Infrastructure type'
            className=''
            {...register(
              INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.infrastructureType],
              { required: { value: true, message: 'This is Mandatory field' } }
            )}
            required
            name={INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.infrastructureType]}
            options={infrastructure_types}
            error={
              errors[INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.infrastructureType]]
            }
            readOnly={readOnly}
            directInput={true}
          />

          <Controller
            render={({ field }) => (
              <CurrencyInput
                label='Total cost of the Infrastructure'
                className=''
                setValue={setValue}
                error={
                  errors[
                    INFRASTRUCTURE_LOAN_TYPES[
                      LOAN_FIELDS.totalCostOfTheInfrastructure
                    ]
                  ]
                }
                readOnly={readOnly}
                required={{ value: true, message: 'This is Mandatory field' }}
                {...field}
              />
            )}
            {...register(
              INFRASTRUCTURE_LOAN_TYPES[
                LOAN_FIELDS.totalCostOfTheInfrastructure
              ],
              {
                required: { value: true, message: 'This is Mandatory field' },
                min: { value: 1, message: 'Please enter the value above 1' },
              }
            )}
            control={control}
          />

          <InputBox
            label='Name of supplier/Contractor'
            className=''
            register={register}
            name={
              INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.nameOfSupplierContractor]
            }
            type='text'
            error={
              errors[
                INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.nameOfSupplierContractor]
              ]
            }
            readOnly={readOnly}
          />

          <InputBox
            label='Supplier/Contractor address'
            className=''
            register={register}
            name={
              INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.supplierContractorAddress]
            }
            type='text'
            error={
              errors[
                INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.supplierContractorAddress]
              ]
            }
            readOnly={readOnly}
          />

          <InputBox
            label='Supplier/Contractor contact'
            register={register}
            required={false}
            name={
              INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.supplierContractorContact]
            }
            type='number'
            error={
              errors[
                INFRASTRUCTURE_LOAN_TYPES[LOAN_FIELDS.supplierContractorContact]
              ]
            }
            minLength={{ value: 10, message: 'Please enter the value in 10 Digits' }}
            maxLength={{ value: 10, message: 'Please enter the value in 10 Digits' }}
            readOnly={readOnly}
          />
        </section>
        {!hideActionsButtons && (
          <section className='d-flex justify-content-end my-5'>
            {backButton && (
              <button
                type='button'
                className='loan-application-btn btn-secondary mr-5'
                onClick={() => handleTabChange(activeKey, 'prev')}
              >
                PREVIOUS
              </button>
            )}
            {readOnly ? (
              <button
                type='button'
                className='loan-application-btn btn-primary mr-5'
                onClick={() =>
                  handleTabChange(
                    activeKey,
                    'next',
                    defaultValues,
                    readOnly ? formModified : isDirty
                  )
                }
              >
                NEXT
              </button>
            ) : (
              <button className='loan-application-btn btn-primary '>
                NEXT
              </button>
            )}
          </section>
        )}
      </div>
    </Form>
  );
};
export default CatalyticInfrastructureForm;
