import { forwardRef } from 'react';
import { Form } from 'react-bootstrap';

const SelectBox = forwardRef(
  (
    {
      readOnly,
      label,
      className,
      onChange,
      onBlur,
      name,
      error,
      options,
      handleChange,
      required,
    },
    ref
  ) => {
    if (typeof handleChange !== 'function') handleChange = function () {};

    if (!options) options = [];

    return (
      <Form.Group
        className={`mb-3 col-md-6 col-lg-4 ${className && className}`}
        style={{ padding: '0% 3%' }}
      >
        <Form.Label htmlFor='purpose' className='f14 form-label'>
          {label} {required && <small className='text-danger f20'>*</small>}
        </Form.Label>
        <Form.Select
          className={`form-control `}
          disabled={readOnly ? true : false}
          onChange={(e)=>{
            onChange(e);
            handleChange(e.target.value);
            }}
          onBlur={onBlur}
          name={name}
          ref={ref}
          // eslint-disable-next-line react/jsx-no-duplicate-props
        >
          <option value=''>Select</option>
          {options.map(({ value, text }, index) => (
            <option key={index} value={value}>
              {text}
            </option>
          ))}
        </Form.Select>
        {error && (error.message ? 
        <small className='text-danger'>{error.message}</small>
       : 
        <small className='text-danger'>Select valid {label}</small>)
      }
      </Form.Group>
    );
  }
);

export default SelectBox;
