import { takeEvery, call, put } from 'redux-saga/effects';
import Toast from '../../../utils/toast';
import {
  ADMIN_APPLICATIONS_LIST_ALL_IN_PROGRESS,
  ADMIN_LOAN_LIST_ALL_IN_PROGRESS,
  ADMIN_GET_APPLICATION_DETAILS_IN_PROGRESS,
  ADMIN_GET_PROFILE_DATA_IN_PROGRESS,
  ADMIN_GET_DOCUMENT_DATA_IN_PROGRESS,
  ADMIN_SAM_ANALYSIS_DATA_IN_PROGRESS,
  PUBLISH_APPLICATION_IN_PROGRESS,
  CANCEL_APPLICATION_IN_PROGRESS,
  SEND_BACK_APPLICATION_IN_PROGRESS,
  SAM_FILE_UPLOAD_IN_PROGRESS,
  ADMIN_GET_REQUESTED_DOCUMENTS_IN_PROGRESS,
  ADMIN_SANSACTION_DOCUMENT_UPLOAD_IN_PROGRESS,
  ADMIN_APPLICATION_FILTER_IN_PROGRESS,
  ADMIN_LOAN_FILTER_IN_PROGRESS,
  ADMIN_GET_OFFER_LIST_IN_PROGRESS,
  ADMIN_GET_LOAN_SUMMARY_IN_PROGRESS,
  ADMIN_GET_LOAN_DOCUMENTS_IN_PROGRESS,
  ADMIN_GET_LOAN_REPAYMENT_STATUS_IN_PROGRESS,
  ADMIN_SANSACTION_LOAN_DOCUMENT_UPLOAD_IN_PROGRESS,
  ADMIN_APPLICATION_DELETE_SAM_DOCS_IN_PROGRESS,
} from '../../types/admintypes';
import { adminActions } from '../../actions';
import {
  getApplicationsAll,
  getloansAll,
  getApplicationDetails,
  getProfileDataDetails,
  getDocumentDataDetails,
  getSamAnalysisFiles,
  publishApplication,
  cancelApplication,
  sendBackApplication,
  samFileUpload,
  getPreRequestedDocuments,
  getPostRequestedDocuments,
  sanctionDocsUpload,
  applicationFilter,
  loanFilter,
  getOfferList,
  getLoanSummary,
  getLoanRepaymentStatus,
  deleteSamDocs,
} from '../../../services/http/admin';
import _ from 'lodash';

function* getApplicationListInprogress(action) {
  try {
    const applications = yield call(getApplicationsAll, action.payload);
    const { data } = applications;
    yield put(adminActions.getAllApplicationsInSuccess(data['data']));
  } catch (error) {
    yield put(adminActions.getAllApplicationsInError(error));
  }
}

