function* publishApplicationInProgress(action) {
  try {
    const data = yield call(publishApplication, action.payload);
    yield put(
      adminActions.publishApplicationInSuccess({
        message: data['data']['message'],
      })
    );
    yield call(Toast, { title: data['data']['message'] });
  } catch (error) {
    yield put(
      adminActions.publishApplicationInError(
        'Problem while communicating server.'
      )
    );
    yield call(Toast, {
      title: 'Problem while communicating server.',
      type: 'error',
    });
  }
}

