function* getLoanDocumentsInProgress(action) {
  try {
    let loanData = yield call(getDocumentDataDetails, action.payload);
    yield put(
      adminActions.getLoanDocumentsInSuccess({
        loanDocuments: {
          documentdata: loanData['data']['documentdata'],
          inProgress: false,
          success: true,
        },
      })
    );

    let preDocs = yield call(getPreRequestedDocuments, action.payload);
    yield put(
      adminActions.getLoanDocumentsInSuccess({
        preDocuments: {
          data: preDocs['data']['data'],
          inProgress: false,
          success: true,
        },
      })
    );

    let postDocs = yield call(getPostRequestedDocuments, action.payload);
    yield put(
      adminActions.getLoanDocumentsInSuccess({
        postDocuments: {
          data: postDocs['data']['data'],
          inProgress: false,
          success: true,
        },
      })
    );

    preDocs = preDocs['data']['data'];
    postDocs = postDocs['data']['data'];
    preDocs.map((ele) => (ele.type = 'pre'));
    postDocs.map((ele) => (ele.type = 'post'));
    let documents_ = [...preDocs, ...postDocs];
    let documents = { all: documents_ };
    documents = { ...documents, ..._.groupBy(documents_, 'Lender_Name') };
    yield put(
      adminActions.getLoanDocumentsInSuccess({
        combinedDocuments: {
          documents: documents,
          inProgress: false,
          success: true,
        },
      })
    );
  } catch (error) {
    yield put(
      adminActions.getLoanDocumentsInError(
        'Problem while communicating server.'
      )
    );
  }
}

