import Modal from 'react-bootstrap/Modal';
import React, { useState } from 'react';
import Form from 'react-bootstrap/Form';
import './adminStyle.css';
import { RocketSharp, CloseOutline } from 'react-ionicons';

function ApplicationPublishModal({
  show,
  onHide,
  handlePublishApplication,
  handleCancelApplication,
}) {
  const handleClose = () => onHide(false);

  return (
    <Modal
      show={show}
      onHide={handleClose}
      aria-labelledby='example-custom-modal-styling-title'
      keyboard={true}
      centered
      dialogClassName='modal-40w'
    >
      <Modal.Header>
        <Modal.Title
          id='contained-modal-title-vcenter'
          className='f20 text-bold mt-3 ml-3'
        >
          Publish Application
        </Modal.Title>
        <button
          className='h5 text-muted text-bold f24 mt-1 mr-2 btn p-0 shadow-none'
          onClick={() => onHide(false)}
        >
          <CloseOutline
            color={'#6c757d'}
            height='30px'
            width='30px'
            className='cancel-icon'
          />
        </button>
      </Modal.Header>
      <Modal.Body>
        <div className='text-center cancel-app-sec'>
          <span className=' publish-icon'>
            <RocketSharp color={'#8BFACC'} height='33px' width='33px' />
          </span>
          <p className='f17 ml-3 mr-3'>
            Are You Sure You Want To Publish This Application? <br /> This
            Action Cannot Be Reversed
          </p>
          <button
            className='btn btn-danger br-12 cancle-app-btn1 mb-3 mt-5'
            onClick={() => onHide(false)}
          >
            CANCEL
          </button>
          <button
            className='btn btn-primary br-12 publish-app-btn mb-3 mt-5'
            onClick={() => {
              handlePublishApplication();
              onHide(false);
            }}
          >
            PUBLISH
          </button>
        </div>
      </Modal.Body>
    </Modal>
  );
}
export default ApplicationPublishModal;
