import { Modal } from 'react-bootstrap';
import closeIcon from '../../assets/Images/Icons/material-close.svg';
import CompareFpoTable from '../Tables/CompareFpoTable';

const CompareFpoModal = ({ show, onHide, compareFpo }) => {
  return (
    <Modal
      show={show}
      onHide={() => onHide(false)}
      dialogClassName='modal-90w'
      aria-labelledby='example-custom-modal-styling-title'
    >
      <Modal.Body>
        <div className='d-flex justify-content-end'>
          <button
            className='btn shadow-none p-0 m-0'
            onClick={() => onHide(false)}
          >
            <img src={closeIcon} alt='close icon' />
          </button>
        </div>
        <div>
          <CompareFpoTable compareFpo={compareFpo} />
        </div>
      </Modal.Body>
    </Modal>
  );
};

export default CompareFpoModal;
