import React, { useState, useEffect } from 'react';
import { Tabs, Tab, Form, Alert, Spinner } from 'react-bootstrap';
import LoanApplicationContainer from './../../../components/Modals/LoanApplicationModal/LoanApplicationContainer';
import BankLoanContainer from '../../../components/Modals/LoanApplicationModal/BankLoanContainer';
import { useDispatch, useSelector } from 'react-redux';
import { bankActions } from '../../../redux/actions';
import 'react-perfect-scrollbar/dist/css/styles.css';
import { useLocation } from 'react-router';
import {
  GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
  GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS,
  GET_DOCUMENT_DATA_IN_PROGRESS,
} from '../../../redux/types/banktypes';
import ApplcationFilter from '../../../components/Forms/Common/ApplcationFilter';
import ApplicationCard from '../../../components/Cards/ApplicationCard';

export default function BankLoan() {
  const auth = useSelector((state) => state.auth);
  const bank = useSelector((state) => state.bank);
  const dispatch = useDispatch();

  const [selectedApplication, setselectedApplication] = useState(null);
  const [selectedLoan, setselectedLoan] = useState(null);
  const [activeKey, setActiveKey] = useState('applications');
  const [category, setCategory] = useState('');
  const [loanCategory, setLoanCategory] = useState('');
  const [applicationList, setapplicationList] = useState([]);
  const [loansList, setLoansList] = useState([]);
  const [applicationProposalId, setApplicationProposalId] = useState(0);
  const [loanProposalId, setLoanProposalId] = useState(0);
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  let qry_application_id = params.get('application_id');
  let qry_type = params.get('type');
  let qry_category = params.get('category');
  useEffect(() => {
    if (!auth.isLoggedIn) return;
    const organizationid = auth.userInfo.organizationid;
    dispatch(bankActions.getApplicationDetails({ organizationid }));
    dispatch(bankActions.getAllLoans({ organizationid }));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [auth]);

  // default application filter
  useEffect(() => {
    if (
      !category &&
      bank.applications.success &&
      !qry_type &&
      !qry_application_id &&
      !qry_category
    ) {
      for (let key in bank.applications.documents) {
        if (bank.applications.documents.hasOwnProperty(key)) {
          if (bank.applications.documents[key].length > 0 && !category) {
            setCategory(key);
            setselectedApplication(bank.applications.documents[key][0]);
            setapplicationList(bank.applications.documents[key]);

            fetchApplicationApi(bank.applications.documents[key][0]);
            break;
          }
        }
      }
    }
  }, [category, bank]);

  // default loan filter
  useEffect(() => {
    if (
      !loanCategory &&
      bank.loans.documents &&
      ((!qry_type && !qry_application_id && !qry_category) ||
        qry_type === 'applications')
    ) {
      for (let key in bank.loans.documents) {
        if (bank.loans.documents.hasOwnProperty(key)) {
          if (bank.loans.documents[key].length > 0 && !loanCategory) {
            setLoanCategory(key);
            setselectedLoan(bank.loans.documents[key][0]);
            setLoansList(bank.loans.documents[key]);
            fetchLoanDetails(bank.loans.documents[key][0]);
            break;
          }
        }
      }
    }
  }, [loanCategory, bank.loans?.documents]);

  const fetchLoanDetails = (loan) => {
    setselectedLoan(loan);
    dispatch(bankActions.getLoanDetailInProgress(loan.applicationuid, loan));
    dispatch({
      type: GET_POST_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
      payload: { applicationuid: loan.applicationuid },
    });
    dispatch({
      type: GET_PRE_SANSACTION_REQUEST_DOCUMENTS_IN_PROGRESS,
      payload: { applicationuid: loan.applicationuid },
    });
    dispatch({
      type: GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS,
      payload: { applicationuid: loan.applicationuid },
    });
    dispatch({
      type: GET_DOCUMENT_DATA_IN_PROGRESS,
      payload: { applicationuid: loan.applicationuid },
    });

    setLoanProposalId(loan.applicationuid + '-' + auth.userInfo.organizationid);
  };

  useEffect(() => {
    if (
      bank &&
      bank.selectedAppLoan &&
      bank.selectedAppLoan.repaymentScheduleUpload &&
      bank.selectedAppLoan.repaymentScheduleUpload.success
    ) {
      dispatch({
        type: GET_LOAN_REPAYMENT_SCHEDULES_IN_PROGRESS,
        payload: {
          applicationuid: bank.selectedAppLoan.loanInfo.applicationuid,
        },
      });
    }
  }, [bank?.selectedAppLoan?.repaymentScheduleUpload?.success]);

  const onLoanClick = (loan) => {
    fetchLoanDetails(loan);
  };

  const onApplicationClick = (application) => {
    setselectedApplication(application);
    fetchApplicationApi(application);
  };

  const handleCategory = (key) => {
    setCategory(key);
    let applications = bank.applications.documents[key];
    setapplicationList(applications);
    let application = applications.length > 0 ? applications[0] : {};
    setselectedApplication(application);
    fetchApplicationApi(application);
  };

  const handleLoanCategory = (key) => {
    setLoanCategory(key);
    let loans = bank.loans.documents[key];
    setLoansList(loans);
    let loan = loans.length > 0 ? loans[0] : {};
    setselectedLoan(loan);
    fetchLoanDetails(loan);
  };

  const getDocsCounts = (type, keys) => {
    let count = 0;
    keys.forEach((key) => {
      if (bank[type].documents[key]) count += bank[type].documents[key].length;
    });
    return count;
  };

  const fetchApplicationApi = (application) => {
    dispatch(
      bankActions.getApplicationSingleDetailsInProgress(
        application.applicationuid,
        application
      )
    );
  };

  useEffect(() => {
    if (
      bank.selectedLoan &&
      bank.selectedLoan.documents &&
      bank.selectedLoan.documents.documents &&
      bank.selectedLoan.documents.documents.deal_status != 'NULL'
    ) {
      if (
        selectedApplication &&
        bank.selectedLoan.documents.documents.applicationuid &&
        bank.selectedLoan.documents.documents.applicationuid ===
          selectedApplication.applicationuid
      ) {
        setApplicationProposalId(
          bank.selectedLoan.documents.documents.applicationuid +
            '-' +
            auth.userInfo.organizationid
        );
      }
    }
  }, [bank?.selectedLoan?.documents?.documents, selectedApplication]);

  // applications filter select
  useEffect(() => {
    if (
      !category &&
      bank &&
      bank.applications &&
      bank.applications.documents &&
      qry_type &&
      qry_application_id &&
      qry_category
    ) {
      setActiveKey(qry_type);

      if (qry_type === 'applications') {
        let applications = {
          new_application: bank.applications.documents.new_application,
          active_application: bank.applications.documents.active_application,
          closed_application: bank.applications.documents.closed_application,
        };
        if (applications[`${qry_category}_application`]) {
          setCategory(`${qry_category}_application`);
          setapplicationList(applications[`${qry_category}_application`]);
          let find_application = applications[
            `${qry_category}_application`
          ].find((o) => o.applicationuid === qry_application_id);
          setselectedApplication(find_application);
          fetchApplicationApi(find_application);
        }
      }
    }
  }, [bank.applications]);

  useEffect(() => {
    if (bank.applicationFilter.success) {
      if (bank.applicationFilter.data.length > 0) {
        let application = bank.applicationFilter.data[0];
        setselectedApplication(application);
        fetchApplicationApi(application);
        setapplicationList(bank.applicationFilter.data);
      } else {
        setselectedApplication('');
        setapplicationList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.applicationFilter?.success]);

  useEffect(() => {
    if (bank.loanFilter.success) {
      if (bank.loanFilter.data.length > 0) {
        let loan = bank.loanFilter.data[0];
        setselectedLoan(loan);
        setLoansList(bank.loanFilter.data);
        fetchLoanDetails(loan);
      } else {
        setselectedLoan('');
        setLoansList([]);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [bank?.loanFilter?.success]);

  const handleFormData = (formData, type) => {
    let payload = { formData, category };
    if (type === 'application') {
      dispatch(bankActions.applicationFilterInProgress(payload));
    } else if (type === 'loan') {
      dispatch(bankActions.loanFilterInProgress(payload));
    }
  };

  return (
    <div className='container-fluid pb-5'>
      <div className='row'>
        <div className='col-md-3'>
          <h2 className='medium-font f24 mb-4'>Loan Applications</h2>
          <div>
            <Tabs
              defaultActiveKey={activeKey}
              activeKey={activeKey}
              onSelect={(k) => setActiveKey(k)}
              id='uncontrolled-tab-example'
              className='mb-3 bank-loan-tabs'
            >
              <Tab
                eventKey='applications'
                title={
                  <>
                    APPLICATIONS{' '}
                    <span className='default-badge'>
                      {`${
                        bank.applications.documents &&
                        getDocsCounts('applications', [
                          'new_application',
                          'active_application',
                        ]) | 0
                      }`}
                    </span>
                  </>
                }
              >
                <div>
                  <ApplcationFilter
                    category={category}
                    type={'application'}
                    handleFormData={handleFormData}
                    spinner={bank && bank.applicationFilter.inProgress}
                  />
                  <Form.Select
                    className='form-control br-12'
                    onChange={(e) => handleCategory(e.target.value)}
                    value={category}
                  >
                    <option value='new_application'>
                      New Applications{' '}
                      {(bank.applications.documents.new_application &&
                        bank.applications.documents.new_application.length) ||
                        '0'}
                    </option>
                    <option value='active_application'>
                      Active Applications{' '}
                      {(bank.applications.documents.active_application &&
                        bank.applications.documents.active_application
                          .length) ||
                        '0'}
                    </option>
                    <option value='closed_application'>
                      Close Applications{' '}
                      {(bank.applications.documents.closed_application &&
                        bank.applications.documents.closed_application
                          .length) ||
                        '0'}
                    </option>
                  </Form.Select>
                  <div
                    className='py-3 custom-scrollbar'
                    style={{ height: '600px' }}
                  >
                    {applicationList.map((value, key) => {
                      return (
                        <div className='mb-2' key={key}>
                          <ApplicationCard
                            isActive={
                              selectedApplication &&
                              selectedApplication.applicationuid ===
                                value.applicationuid
                                ? true
                                : false
                            }
                            applicationId={value.applicationuid}
                            fpoName={value.fponame}
                            fpoState={value.fpostate}
                            applicationDate={value.applicationdate}
                            loanName={value.loanproductname}
                            loanAmount={value.loanamount}
                            onClick={() => onApplicationClick(value)}
                          />
                        </div>
                      );
                    })}
                  </div>
                </div>
              </Tab>
              <Tab
                eventKey='loans'
                title={
                  <>
                    LOANS{' '}
                    <span className='default-badge'>{`${
                      bank.applications.documents &&
                      getDocsCounts('loans', ['new_loans', 'active_loans']) | 0
                    }`}</span>
                  </>
                }
              >
                <div>
                  <ApplcationFilter
                    category={loanCategory}
                    type={'loan'}
                    handleFormData={handleFormData}
                    spinner={bank && bank.loanFilter.inProgress}
                  />
                  <Form.Select
                    className='form-control br-12'
                    onChange={(e) => handleLoanCategory(e.target.value)}
                    value={loanCategory}
                  >
                    <option value='new_loans'>
                      Sanctioned Loans{' '}
                      {(bank.loans.documents.new_loans &&
                        bank.loans.documents.new_loans.length) ||
                        '0'}
                    </option>
                    <option value='active_loans'>
                      Live Loans{' '}
                      {(bank.loans.documents.active_loans &&
                        bank.loans.documents.active_loans.length) ||
                        '0'}
                    </option>
                    <option value='closed_loans'>
                      Matured Loans{' '}
                      {(bank.loans.documents.closed_loans &&
                        bank.loans.documents.closed_loans.length) ||
                        '0'}
                    </option>
                  </Form.Select>
                  <div
                    className='py-3 custom-scrollbar'
                    style={{ height: '600px' }}
                  >
                    {loansList.map((value, key) => {
                      return (
                        <div className='mb-2' key={key}>
                          <ApplicationCard
                            isActive={
                              selectedLoan &&
                              selectedLoan.applicationuid ===
                                value.applicationuid
                                ? true
                                : false
                            }
                            applicationId={value.applicationuid}
                            fpoName={value.fponame}
                            fpoState={value.fpostate}
                            applicationDate={value.applicationdate}
                            loanName={value.loanproductname}
                            loanAmount={value.loanamount}
                            onClick={() => onLoanClick(value)}
                          />
                        </div>
                      );
                    })}
                  </div>
                </div>
              </Tab>
            </Tabs>
          </div>
        </div>
        {activeKey === 'applications' ? (
          <div className='col-md-9 bg-white br-3 general-box'>
            <br />
            {bank && bank.selectedLoan && bank.selectedLoan.inProgress && (
              <div className='d-flex justify-content-center'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            )}
            {bank &&
            selectedApplication &&
            bank.selectedLoan &&
            bank.selectedLoan.documents &&
            bank.selectedLoan.documents.documents &&
            bank.selectedLoan.documents.documents.applicationuid ? (
              <>
                {bank.selectedLoan.documents.documents.applicationuid ===
                  selectedApplication.applicationuid && (
                  <LoanApplicationContainer
                    proposalId={applicationProposalId}
                    applicationID={selectedApplication?.applicationuid}
                  />
                )}
              </>
            ) : (
              <>
                {bank.selectedLoan &&
                  bank.selectedLoan.documents &&
                  bank.selectedLoan.documents.documents &&
                  bank.selectedLoan.documents.documents.message && (
                    <div className='d-flex justify-content-center'>
                      <Alert
                        variant={'danger'}
                        style={{ width: '30vw' }}
                        className='text-center'
                      >
                        {bank.selectedLoan.documents.documents.message}
                      </Alert>
                    </div>
                  )}
              </>
            )}
          </div>
        ) : (
          <div className='col-md-9 bg-white br-3 general-box'>
            {bank && bank.selectedAppLoan && bank.selectedAppLoan.inProgress ? (
              <div className='d-flex justify-content-center pt-5'>
                <Spinner animation='border' role='status'>
                  <span className='visually-hidden'></span>
                </Spinner>
              </div>
            ) : (
              <BankLoanContainer
                proposalId={loanProposalId}
                loanCategory={loanCategory}
              />
            )}
          </div>
        )}
      </div>
    </div>
  );
}
